import pdfjs from 'pdfjs-dist';
import { Annotation } from '../context/SsieAnnotationStore';
import { Bounds } from './Bounds';
import { Token } from './Token';
import { TokenId } from './TokenId';
import { blockToToken, getTokenBounds, Optional, spanningBound } from '../utils/SsieUtils';
import { Block } from './Block';

/**
 * Object containing a PDF page's information such as its bounds, dimensions, and tokens.
 */
export class PDFPageInfo {
    public tokens: Token[] = [];
    public bounds?: Bounds = undefined;

    constructor(
        public readonly pdfPageProxy: pdfjs.PDFPageProxy,
        blocks: Block[] = [],
        public readonly width: number,
        public readonly height: number
    ) {
        this.tokens = blocks.map((b) => blockToToken(b, width, height));
    }

    getAnnotationForTokens(tokenValues: { index: number; startIndex?: number; endIndex?: number }[], tag: string, blocks: Block[]): Optional<Annotation> {
        const ids: TokenId[] = [];
        const tokenBounds: Bounds[] = [];
        for (let i = 0; i < tokenValues.length; i++) {
            const token = this.tokens[tokenValues[i].index];
            const tokenBound = getTokenBounds(token);
            const tokenId = new TokenId(
                this.pdfPageProxy.pageNumber - 1,
                tokenValues[i].index,
                token.type,
                tokenValues[i].startIndex ? tokenValues[i].startIndex! : 0,
                tokenValues[i].endIndex ? tokenValues[i].endIndex! : blocks[tokenValues[i].index].Text.length
            );
            ids.push(tokenId);
            tokenBounds.push(tokenBound);
        }
        if (ids.length === 0) {
            return undefined;
        }
        const bounds = spanningBound(tokenBounds);
        return new Annotation(bounds, this.pdfPageProxy.pageNumber, tag, ids);
    }

    getAnnotationForBounds(selection: Bounds, tag: string, blocks: Block[], tokenIds?: Set<string>): Optional<Annotation> {
        /* This function is quite complicated. Our objective here is to
           compute overlaps between a bounding box provided by a user and
           grobid token spans associated with a pdf. The complexity here is
           that grobid spans are relative to an absolute scale of the pdf,
           but our user's bounding box is relative to the pdf rendered in their
           client. 

           The critical key here is that anything we *store* must be relative
           to the underlying pdf. So for example, inside the for loop, we are
           computing: 
           
           whether a grobid token (tokenBound), scaled to the current scale of the
           pdf in the client (scaled(tokenBound, this.scale)), is overlapping with
           the bounding box drawn by the user (selection).

           But! Once we have computed this, we store the grobid tokens and the bound
           that contains all of them relative to the *original grobid tokens*.

           This means that the stored data is not tied to a particular scale, and we
           can re-scale it when we need to (mainly when the user resizes the browser window).
         */

        if (this.bounds === undefined) {
            throw new Error('Unknown Page Bounds');
        }
        const ids: TokenId[] = [];
        const tokenBounds: Bounds[] = [];
        for (let i = 0; i < this.tokens.length; i++) {
            const token = this.tokens[i];
            const tokenBound = getTokenBounds(token);
            if (doOverlap(scaleBounds(tokenBound, this.scale), selection)) {
                const tokenId = new TokenId(this.pdfPageProxy.pageNumber - 1, i, token.type, 0, blocks[i].Text.length);
                if (!tokenIds || !tokenIds.has(tokenId.toString())) {
                    ids.push(tokenId);
                    tokenBounds.push(tokenBound);
                }
            }
        }
        if (ids.length === 0) {
            return undefined;
        }
        const bounds = spanningBound(tokenBounds);
        return new Annotation(bounds, this.pdfPageProxy.pageNumber, tag, ids);
    }

    getScaledTokenBounds(t: Token): Bounds {
        return this.getScaledBounds(getTokenBounds(t));
    }

    getScaledBounds(b: Bounds): Bounds {
        return scaleBounds(b, this.scale);
    }

    get scale(): number {
        if (this.bounds === undefined) {
            throw new Error('Unknown Page Bounds');
        }
        const pdfPageWidth = this.width;
        const domPageWidth = this.bounds.right - this.bounds.left;
        return domPageWidth / pdfPageWidth;
    }
}

/**
 * Returns the provided bounds scaled by the provided factor.
 */
function scaleBounds(bounds: Bounds, scale: number): Bounds {
    return {
        left: bounds.left * scale,
        top: bounds.top * scale,
        right: bounds.right * scale,
        bottom: bounds.bottom * scale,
    };
}

/**
 * Returns true if the provided bounds overlap.
 */
export function doOverlap(a: Bounds, b: Bounds): boolean {
    if (a.left >= b.right || a.right <= b.left) {
        return false;
    } else if (a.bottom <= b.top || a.top >= b.bottom) {
        return false;
    }
    return true;
}

export default PDFPageInfo;
