import {DocumentStateStore} from "./DocumentStateStore";
import {Namespaces} from "../core/Namespaces";
import {UFSchema, LABELING_TASK_EVENTS_RE_SLOT_FILLING} from "../core/UFSchema";
import {SpanFactory} from "../core/Spans";
import RangeTree from "../core/RangeTree";
import {EventArgumentPropertyStore} from "./EventArgumentPropertyStore";
import {EVENT_EMPTY} from "./EventInstanceDataStore";
import {RangeOverlapChecker} from "../core/RangeOverlapChecker";
import {RangeMap} from "../core/RangeMap";
import {get} from "lodash";


const SPAN_TYPE = "spanType";
const TRIGGER_SPAN_TYPE = "trigger";
const ARGUMENT_SPAN_TYPE = "argument";
const ARGUMENT_IDX = "argumentIdx";

class EventsStateStore extends DocumentStateStore {
    constructor(documentObject,
                namespace = Namespaces.EVENTS,
                eventDefinitionsMap = undefined,
                eventPropertiesConfig = undefined) {
        super(documentObject, LABELING_TASK_EVENTS_RE_SLOT_FILLING);
        this.schemaKey = namespace;
        // usually read from schemaConfig
        // a Map that stores (trigger, argument definitions)
        this.eventDefinitionsMap = eventDefinitionsMap === undefined ? new Map() : eventDefinitionsMap;
        this.eventPropertiesConfig = eventPropertiesConfig;
        this.eventArgumentPropertyStore = new EventArgumentPropertyStore(this.eventDefinitionsMap);
        // for v2 annotations, read only after initialization
        this.eventArgumentPropertyStoreV2 = new EventArgumentPropertyStore(this.eventDefinitionsMap);

        this.getSpanFromSpanTree = this.getSpanFromSpanTree.bind(this);
        this.getCandidateArguments = this.getCandidateArguments.bind(this);
        this.instantiateExistingEventAnnotation = this.instantiateExistingEventAnnotation.bind(this);
        this.toggleActiveSpan = this.registerViewAction(this.toggleActiveSpan.bind(this));
        this.addSelectedEventArgument = this.registerAction(this.addSelectedEventArgument.bind(this));
        this.removeSelectedEventArgument = this.registerAction(this.removeSelectedEventArgument.bind(this));
        this.addSelectedEventProperty = this.registerAction(this.addSelectedEventProperty.bind(this));
        this.removeSelectedEventProperty = this.registerAction(this.removeSelectedEventProperty.bind(this));
        this.switchTriggerInstance = this.registerViewAction(this.switchTriggerInstance.bind(this));
        this.switchTriggerInstanceV2 = this.registerViewAction(this.switchTriggerInstanceV2.bind(this));
        this.removeTriggerInstance = this.registerAction(this.removeTriggerInstance.bind(this));
        this.addTriggerInstance = this.registerAction(this.addTriggerInstance.bind(this));
        this.removeEventArgumentFromTrigger = this.registerAction(this.removeEventArgumentFromTrigger.bind(this));
        this.doGetActiveTriggerInstanceArgument = this.doGetActiveTriggerInstanceArgument.bind(this);
        this.getActiveTriggerInstanceArgument = this.getActiveTriggerInstanceArgument.bind(this);
        this.getActiveTriggerInstanceArgumentV2 = this.getActiveTriggerInstanceArgumentV2.bind(this);
        this.doGetActiveTriggerInstanceProperties = this.doGetActiveTriggerInstanceProperties.bind(this);
        this.getActiveTriggerInstanceProperties = this.getActiveTriggerInstanceProperties.bind(this);
        this.getActiveTriggerInstancePropertiesV2 = this.getActiveTriggerInstancePropertiesV2.bind(this);
        this.createActiveArgumentsMap = this.createActiveArgumentsMap.bind(this);
        this.getTriggerSpansForArguments = this.getTriggerSpansForArguments.bind(this);

        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        this.states.rawText = rawText;
        this.states.spanFactory = new SpanFactory(rawText);
        this.states.triggerSpans = [];
        this.states.argumentSpans = [];
        this.states.activeInstanceId = 0;
        // v2 had different logic as to switch/display
        this.states.activeInstanceIdV2 = 0;
        
        // combine both entities (NER like annotations) from event_arguments and event_trigger
        // namespaces and push them to one single spanTree;
        // TODO: need to consolidate the potential conflict between
        // two sets of annotations
        let argumentAnnotations = documentObject[UFSchema.ANNOTATIONS][Namespaces.EVENT_ARGUMENTS];
        let triggerAnnotations = documentObject[UFSchema.ANNOTATIONS][Namespaces.EVENT_TRIGGERS];
        argumentAnnotations = argumentAnnotations === undefined ? [] : argumentAnnotations;
        triggerAnnotations = triggerAnnotations === undefined ? [] : triggerAnnotations;
        let spanObjects = [];

        // TODO: call super.expandSpanToHaveMultibyteOffset for these spans
        triggerAnnotations.forEach((s1) => {
            const s2 = this.states.spanFactory.copySpan(s1);
            s2.setAttribute(SPAN_TYPE, TRIGGER_SPAN_TYPE);
            this.states.triggerSpans.push(s2);
            spanObjects.push(s2);
        });
        // we need to filter out all argument spans that overlaps with
        // trigger spans, the current resolution is to remove arguments
        // spans and only keep trigger spans

        const triggerSpanOverlapChecker = new RangeOverlapChecker(this.states.triggerSpans);

        // TODO: call super.expandSpanToHaveMultibyteOffset for these spans
        argumentAnnotations.forEach((s1) => {
            if (triggerSpanOverlapChecker.overlap(s1) === -1) {
                const s2 = this.states.spanFactory.copySpan(s1);
                s2.setAttribute(SPAN_TYPE, ARGUMENT_SPAN_TYPE);
                this.states.argumentSpans.push(s2);
                spanObjects.push(s2);
            }
        });

        // assign active spans
        this.states.activeTriggerSpan = undefined;
        this.states.activeArgumentSpan = undefined;

        this.states.spanTree = RangeTree.createRangeTree(spanObjects);
        // now init with the existing annotations
        let eventAnnotations = documentObject[UFSchema.ANNOTATIONS][this.schemaKey];
        eventAnnotations = eventAnnotations === undefined ? [] : eventAnnotations;
        // set the content from 'annotations' field as annotations v1
        this.instantiateExistingEventAnnotation(eventAnnotations, this.eventArgumentPropertyStore);
        // now read the v2
        this.hasV2InnerObject = get(this.arbitrationAnnotationObject, [this.schemaKey, '0']) !== undefined;
        if (this.hasV2InnerObject) {
            this.instantiateExistingEventAnnotation(this.arbitrationAnnotationObject[this.schemaKey],
                this.eventArgumentPropertyStoreV2);
        }
    }

    get spans() {
        const tree = this.states.spanTree;
        const rawText = this.states.rawText;
        const spanFactory = this.states.spanFactory;
        let ranges = RangeTree.retrieveAllNonOverlappingRanges(tree, rawText.length);
        const activeArgumentMapV1 = this.createActiveArgumentsMap(this.getActiveTriggerInstanceArgument());
        const activeArgumentMapV2 = this.createActiveArgumentsMap(this.getActiveTriggerInstanceArgumentV2());
        let spans = [];
        ranges.forEach(r => {
            let [start, end, value] = r;
            let tag = undefined;
            let attrs = undefined;
            if (value) {
                tag = value.tag;
                attrs = value.attrs;
            }
            const newSpan = spanFactory.createSpan(start, end, tag, attrs);
            if (tag !== undefined) {
                // reset previous set active argument status
                // otherwise the attributes will be preserved when spans() called next time
                // which make the display incorrect
                newSpan.setAttribute("v1", undefined);
                newSpan.setAttribute("v2", undefined);
                newSpan.setAttribute("idx", undefined);
                newSpan.setAttribute("idx_v2", undefined);

                // arguments can only be tagged entities
                if (activeArgumentMapV1.has(newSpan)) {
                    const {idx} = activeArgumentMapV1.get(newSpan);
                    newSpan.setAttribute("v1", true);
                    // v1 idx as the master version of idx
                    newSpan.setAttribute("idx", idx);
                }
                if (activeArgumentMapV2.has(newSpan)) {
                    const {idx} = activeArgumentMapV2.get(newSpan);
                    newSpan.setAttribute("v2", true);
                    newSpan.setAttribute("idx_v2", idx);
                }
            }
            spans.push(newSpan);
        });
        return spans;
    }

    getSpanFromSpanTree(span) {
        // Note the span here is not necessary a SpanContainer
        // any object that can destructured to have a start/end
        // can be used here as input
        const {start, end} = span;
        if (RangeTree.exists(this.states.spanTree, [start, end])) {
            return this.states.spanTree.get([start, end]);
        }
        return;
    }

    instantiateExistingEventAnnotation(eventAnnotations, argumentPropertyStore) {
        eventAnnotations.forEach((eventObject) => {
            const trigger = eventObject.trigger;
            const triggerSpan = this.getSpanFromSpanTree(trigger);
            if (triggerSpan) {
                // only if the trigger exists
                // first create event instance
                const instance = argumentPropertyStore.createTriggerEventInstance(triggerSpan);
                if (instance) {
                    // in case that the event definition is valid
                    const eventArguments = eventObject["arguments"];
                    const eventProperties = eventObject["properties"];
                    if (eventArguments) {
                        eventArguments.forEach((eventArg) => {
                            // console.log("eventArg", eventArg);
                            let argumentName = eventArg["name"];
                            let valueList = eventArg["values"];
                            valueList.forEach((arg) => {
                                const argumentSpan = this.getSpanFromSpanTree(arg);
                                if (argumentSpan) {
                                    // the argument span should also exist
                                    // in tree in the first place
                                    // instance id is the index of latest event instance pushed
                                    const instanceId = argumentPropertyStore.eventInstanceMap.get(triggerSpan).length - 1;
                                    argumentPropertyStore.attachArgumentToTrigger(triggerSpan, argumentName, argumentSpan, instanceId);
                                }
                            })
                        });
                    }
                    if (eventProperties) {
                        for (const [name, value] of Object.entries(eventProperties)) {
                            instance.addPropertyToInstance(name, value);
                        }
                    }
                }
            }
        });
    }

    toggleActiveSpan(span) {
        const spanType = span.getAttribute(SPAN_TYPE);
        if (spanType === TRIGGER_SPAN_TYPE) {
            // if already is active trigger span
            // toggle off
            if (span.rangeEqual(this.states.activeTriggerSpan)) {
                this.states.activeTriggerSpan = undefined;
            } else {
                this.states.activeTriggerSpan = span;
                if (!this.eventArgumentPropertyStore.eventInstanceMap.get(span)) {
                    // this trigger hasn't yet had an instance created
                    this.eventArgumentPropertyStore.createTriggerEventInstance(span);
                }
            }
            // reset the activeInstanceId to 0
            this.states.activeInstanceId = 0;
            // reset the activeInstanceIdV2 to 0 as well
            this.states.activeInstanceIdV2 = 0;
        } else {
            // this is an argument type
            if (span.rangeEqual(this.states.activeArgumentSpan)) {
                this.states.activeArgumentSpan = undefined;
            } else {
                this.states.activeArgumentSpan = span;
            }
        }
    }

    getCandidateArguments() {
        const triggerSpan = this.states.activeTriggerSpan;
        const argumentSpan = this.states.activeArgumentSpan;
        if (!triggerSpan || !argumentSpan) {
            return []
        }
        const eventDef = this.eventDefinitionsMap.get(triggerSpan.tag);
        if (!eventDef) {
            // no definition found
            return [];
        }
        const argumentList = eventDef.argumentList;
        const argumentSpanType = argumentSpan.tag;
        const candidateArgument = [];
        argumentList.forEach((arg) => {
            const argTypes = arg.argumentTypes;
            if (argTypes.indexOf(argumentSpanType) > -1) {
                candidateArgument.push(arg.argumentName);
            }
        });
        return candidateArgument;
    }

    getTriggerSpansForArguments(argumentSpan) {
        return this.eventArgumentPropertyStore.argumentValueTriggerReverseMap.get(argumentSpan) || []
    }

    addSelectedEventArgument(argumentName) {
        const triggerSpan = this.states.activeTriggerSpan;
        const argumentSpan = this.states.activeArgumentSpan;
        if (triggerSpan !== undefined && argumentSpan !== undefined) {
            this.eventArgumentPropertyStore.attachArgumentToTrigger(triggerSpan,
                argumentName, argumentSpan, this.states.activeInstanceId);
        }

    }

    removeSelectedEventArgument(argumentName, argumentSpan) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            this.eventArgumentPropertyStore.detachArgumentFromTrigger(triggerSpan,
                argumentName, argumentSpan, this.states.activeInstanceId);
        }
    }

    removeEventArgumentFromTrigger(argumentName, argumentSpan, triggerSpan) {
        if (triggerSpan !== undefined && this.eventArgumentPropertyStore.eventInstanceMap !== undefined) {
            const instanceList = this.eventArgumentPropertyStore.eventInstanceMap.get(triggerSpan) || [];
            for (const [index, instance] of instanceList.entries()) {
                this.eventArgumentPropertyStore.detachArgumentFromTrigger(triggerSpan,
                    argumentName, argumentSpan, index);
            }
        }
    }

    addSelectedEventProperty(propertyName, propertyValue) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            this.eventArgumentPropertyStore.attachPropertyToTrigger(triggerSpan,
                propertyName, propertyValue, this.states.activeInstanceId);
        }
    }

    removeSelectedEventProperty(propertyName) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            this.eventArgumentPropertyStore.detachPropertyFromTrigger(triggerSpan,
                propertyName, this.states.activeInstanceId);
        }
    }

    switchTriggerInstance(instanceId) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            const instanceList = this.eventArgumentPropertyStore.eventInstanceMap.get(triggerSpan);
            if (instanceList) {
                if (instanceId >= 0 && instanceId < instanceList.length) {
                    this.states.activeInstanceId = instanceId;
                }
            }
        }
    }

    switchTriggerInstanceV2(instanceId) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            const instanceList = this.eventArgumentPropertyStoreV2.eventInstanceMap.get(triggerSpan);
            if (instanceList) {
                if (instanceId >= 0 && instanceId < instanceList.length) {
                    this.states.activeInstanceIdV2 = instanceId;
                }
            }
        }
    }

    removeTriggerInstance(instanceId) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            this.eventArgumentPropertyStore.removeTriggerEventInstance(triggerSpan, instanceId);
        }
    }

    addTriggerInstance() {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan !== undefined) {
            this.eventArgumentPropertyStore.createTriggerEventInstance(triggerSpan);

            // now update the activeInstanceId to the last one
            const instanceList = this.eventArgumentPropertyStore.eventInstanceMap.get(triggerSpan);
            if (instanceList && instanceList.length > 0) {
                this.states.activeInstanceId = instanceList.length - 1;
            }
        }
    }

    doGetActiveTriggerInstanceArgument(argumentPropertyStore, activeInstanceId) {
        // implementation of retrieving argumentInstance from argumentPropertyStore
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan) {
            const instanceList = argumentPropertyStore.eventInstanceMap.get(triggerSpan);
            if (!instanceList || instanceList.length === 0
                || instanceList.length <= activeInstanceId) {
                // instance doesn't exist
                return
            }
            const instance = instanceList[activeInstanceId];
            const eventDef = this.eventDefinitionsMap.get(triggerSpan.tag);
            const flattenedValueList = [];
            eventDef.argumentList.forEach((arg) => {
                const argumentName = arg.argumentName;
                const valueList = instance.argumentValueMap.get(argumentName);
                valueList.forEach((argSpan) => {
                    flattenedValueList.push({
                        argumentName: argumentName,
                        span: argSpan
                    })
                });
                if (valueList.length === 0) {
                    flattenedValueList.push({
                        argumentName: argumentName,
                        span: undefined,
                    })
                }
            });
            return flattenedValueList;
        }

    }

    getActiveTriggerInstanceArgument() {
        return this.doGetActiveTriggerInstanceArgument(this.eventArgumentPropertyStore,
            this.states.activeInstanceId)
    }

    getActiveTriggerInstanceArgumentV2() {
        // get the v2 version of the current selected instance
        return this.doGetActiveTriggerInstanceArgument(this.eventArgumentPropertyStoreV2,
            this.states.activeInstanceIdV2)
    }


    doGetActiveTriggerInstanceProperties(argumentPropertyStore, activeInstanceId) {
        const triggerSpan = this.states.activeTriggerSpan;
        if (triggerSpan) {
            const instanceList = argumentPropertyStore.eventInstanceMap.get(triggerSpan);
            if (!this.eventPropertiesConfig || !triggerSpan || !instanceList || instanceList.length === 0
                || instanceList.length <= activeInstanceId) {
                return;
            }

            const instance = instanceList[activeInstanceId];
            const instanceProperties = instance.propertyValueMap;
            const propertyList = this.eventPropertiesConfig.propertyList;

            propertyList.forEach((propertyObject) => {
                const {name, values} = propertyObject;
                const instanceValue = instanceProperties.get(name);
                if (instanceValue !== undefined && values.indexOf(instanceValue) !== -1) {
                    // the value exists, and is allowed
                    propertyObject["currentValue"] = instanceValue;
                    propertyObject["set"] = true;
                } else {
                    propertyObject["set"] = false;
                }
            });

            return propertyList;
        }
    }

    getActiveTriggerInstanceProperties() {
        return this.doGetActiveTriggerInstanceProperties(this.eventArgumentPropertyStore,
            this.states.activeInstanceId);
    }

    getActiveTriggerInstancePropertiesV2() {
        return this.doGetActiveTriggerInstanceProperties(this.eventArgumentPropertyStoreV2,
            this.states.activeInstanceIdV2);
    }

    convertStatesToAnnotations() {
        console.log("event states called");
        let annotations = {};
        const eventAnnotations = [];
        this.states.triggerSpans.forEach((triggerSpan) => {
            // skip empty/un-filled event annotation
            const instancesAnnotations = this.eventArgumentPropertyStore.getTriggerEventObject(triggerSpan);
            if (instancesAnnotations) {
                instancesAnnotations.forEach((instanceAnnotation, idx) => {
                    if (this.eventArgumentPropertyStore.getInstanceStatus(triggerSpan, idx) !== EVENT_EMPTY) {
                        eventAnnotations.push(instanceAnnotation);
                    }
                })

            }
        });
        if (eventAnnotations.length > 0) {
            annotations[this.schemaKey] = eventAnnotations;
        }
        console.log("updated annotations", annotations);
        return annotations;
    }

    createActiveArgumentsMap(activeArguments) {
        const activeArgumentMap = new RangeMap();
        if (activeArguments) {
            activeArguments.forEach((v, idx) => {
                const {argumentName, span} = v;
                if (span) {
                    activeArgumentMap.add(span, {
                        argumentName: argumentName,
                        idx: idx + 1
                    });

                }
            })
        }
        return activeArgumentMap;
    }
}

export {
    EventsStateStore,
    TRIGGER_SPAN_TYPE, ARGUMENT_SPAN_TYPE, SPAN_TYPE, ARGUMENT_IDX
}
