import React from 'react';
import {Button} from 'reactstrap';
import {observer} from 'mobx-react';
import './TopNavigationBar.css'
import {Pages} from "../store/PageStateStore";


const PageDisplayNames = ["EDIT", "SCHEMA", "BACKUP", "ABOUT"];
const TopNavigationBar = observer(
    class AlertModal extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            // subscribe to pageStateStoreUpdated
            // change appearance when page switches
            const subscribePageStateUpdate = this.props.store.pageStateStore.pageStateStoreUpdated;
            let displayOption = "";
            if (process.env.REACT_APP_VERSION === "vendor") {
                displayOption = "none";
            }
            const jumpTo = (pageId) => {
                return () => {
                    this.props.store.pageStateStore.jumpToPage(pageId);
                }
            };
            const buttonFactory = (pageId) => {
                const displayName = PageDisplayNames[pageId];
                if (pageId === this.props.store.pageStateStore.currentPage) {
                    // active button
                    return (
                        <Button
                            active
                            className="NavigationButton"
                            style={{"background":"#63768d"}}
                            onClick={jumpTo(pageId)}
                            key={displayName}
                        >
                            {displayName}
                        </Button>
                    );
                } else {
                    const style = {};
                    // hide CONFIG from vendor
                    if (pageId === Pages.CONFIG || pageId === Pages.ABOUT) {
                        style["display"] = displayOption;
                    }

                    return (
                        <Button
                            className="NavigationButton"
                            style={style}
                            onClick={jumpTo(pageId)}
                            key={displayName}
                        >
                            {displayName}
                        </Button>
                    );
                }
            };
            let buttons = [Pages.EDIT, Pages.CONFIG, Pages.BACKUP, Pages.ABOUT].map((p) => buttonFactory(p));
            return (
                <div style={{"display": ""}}>
                    {buttons}
                </div>
            );
        }
    });

export {TopNavigationBar}