import React, {Component} from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.css';
import {
    Row, Col, Nav, NavItem, Card, Button
} from 'reactstrap';
import {ThemeProvider} from 'styled-components';
import EditorSidePanel from 'editor/sidepanel/EditorSidePanel.js'
import {observer} from 'mobx-react'
import {TopNavigationBar} from "editor/top/TopNavigationBar";
import {PageRouter} from "editor/pages/PageRouter";
import {EditPage} from "editor/pages/EditPage";

import {theme} from 'styles/theme';

import SsieContextProviders from './annotator/tagging/pdf/context/SsieContextProviders';

export const EDITOR_CONTROL_COLUMN_WIDTH = 220;

const App = observer(
    class App extends Component {

        constructor(props) {
            super(props);
            this.state = {
                showSidebar: true
            };
        }

        toggleSidebar = () => {
            this.setState({
                showSidebar: !this.state.showSidebar
            });
        }

        render() {
            // when vh is set to 100 there is weird shaking effects
            // when switching document view, so use a slightly smaller
            // number but visually indistinguishable
            let logo = "ACEditor";
            if (!(process.env.REACT_APP_VERSION === "vendor")) {
                logo += " Prime";
            }
            return (
                <SsieContextProviders>
                    <ThemeProvider theme={theme}>
                        <div className="App">
                            <Nav className="AppTopNav">
                                <div className="d-flex flex-row">
                                    <div className="LogoSection">
                                        <h1 className="LogoFont">{logo}</h1>
                                    </div>
                                    <NavItem>
                                        <TopNavigationBar store={this.props.store} onToggleSidebar={this.toggleSidebar}/>
                                    </NavItem>
                                </div>
                                <NavItem>
                                    <Button className="NavigationButton" onClick={this.toggleSidebar}>
                                        <svg viewBox="0 0 100 100" width="20" height="20">
                                            <rect fill="white" width="100" height="15"></rect>
                                            <rect fill="white" y="30" width="100" height="15"></rect>
                                            <rect fill="white" y="60" width="100" height="15"></rect>
                                        </svg>
                                    </Button>
                                </NavItem>
                            </Nav>
                            <div className="AppBody">
                                <div className="EditorControlColumn" style={{
                                    display: this.state.showSidebar ? 'block' : 'none',
                                    width: `${EDITOR_CONTROL_COLUMN_WIDTH}px`
                                }}>
                                    <EditorSidePanel store={this.props.store}/>
                                </div>
                                <div className="WorkshopAreaColumn">
                                    <PageRouter store={this.props.store}/>
                                </div>
                            </div>
                        </div>
                    </ThemeProvider>
                </SsieContextProviders>
            );
        }
    }
);

const GroundTruthApp = (props) => {
    
    const subscribePageStateStore = props.store.pageStateStore.pageStateStoreUpdated;
    return (
        <SsieContextProviders {...props.ssieProps}>
            <ThemeProvider theme={theme}>
                <div className="PageContainer" id="AnnotatePage">
                    <EditPage store={props.store}/>
                </div>
            </ThemeProvider>
        </SsieContextProviders>
    );
}


export { App, GroundTruthApp };