class RangeMap {
    constructor() {
        this.map = new Map();

        this.get = this.get.bind(this);
        this.add = this.add.bind(this);
        this.has = this.has.bind(this);
        this.remove = this.remove.bind(this);
        this.values = this.values.bind(this);
    }

    getRangeKey(range) {
        const {start, end} = range;
        return start.toString() + "_" + end.toString();
    }

    get(range) {
        const rangeKey = this.getRangeKey(range);
        if (this.map.has(rangeKey)) {
            return this.map.get(rangeKey);
        }
        return
    }

    add(range, value) {
        const rangeKey = this.getRangeKey(range);
        return this.map.set(rangeKey, value);
    }

    has(range) {
        if (!range) {
            return false;
        }
        const rangeKey = this.getRangeKey(range);
        return this.map.has(rangeKey);
    }


    remove(range) {
        const rangeKey = this.getRangeKey(range);
        return this.map.delete(rangeKey);
    }

    values() {
        let valueIter = this.map.values();
        return Array.from(valueIter);
    }

    get size() {
        return this.map.size;
    }

    static create(spans) {
        let m = new RangeMap();
        spans.forEach((s) => {
            m.add(s, s);
        });
        return m;
    }
}


export {RangeMap}