import React, {Component} from 'react'
import {Button, Label} from "reactstrap";
import 'editor/sidepanel/EditorSidePanel.css';
import {observer} from "mobx-react";

const CheckpointPanel = observer(
    class CheckpointPanel extends Component {

        constructor(props) {
            super(props);
            this.doSave = this.doSave.bind(this);
            this.doResume = this.doResume.bind(this);

            // fresh every 10 secs
            setInterval(() => {
                this.forceUpdate();
            }, 10 * 1000);

            this.state = {
                autoSave: true
            };
        }

        doSave() {
            const checkpointHandler = () => {
                this.props.store.checkpointManager.checkpointEditorState("manual");
            };

            this.props.store.alertStore.sendAlert(
                "Save Checkpoint?",
                <span>Click <b>Confirm</b> to proceed</span>,
                checkpointHandler,
                true
            );
        }

        doResume() {
            const resumeHandler = () => {
                this.props.store.checkpointManager.resumeFromCheckpoint();
            };

            this.props.store.alertStore.sendAlert(
                "Resume the previous checkpoint?",
                <span>This will resume ACEditor from the most recent checkpoint.
                    Please make sure to save current work, then click <b>Confirm</b> to proceed</span>,
                resumeHandler,
                true
            );
        }

        render() {
            const lastSaveTime = this.props.store.checkpointManager.lastSaveTimestamp;
            let message = "not saved";
            if (lastSaveTime) {
                let minutesAgo = Math.floor((new Date().getTime() - lastSaveTime) / (1000 * 60));
                message = minutesAgo + " mins ago";
            }
            return <div>
                <div>
                    <Label className="TaskPanelText"
                           style={{"fontSize": "16px"}}>AUTO-SAVE</Label>
                    <input className="CheckBox"
                           type="checkbox"
                           style={{"marginLeft": "10px"}}
                           checked={this.state.autoSave}
                           onChange={() => {
                               const nextValue = !this.state.autoSave;
                               this.props.store.checkpointManager.setEnableAutoSave(nextValue);
                               this.setState({
                                   autoSave: !this.state.autoSave
                               });}
                           }/>
                </div>

                <Button className="PanelButton"
                        color="warning"
                        style={{"minWidth": "80px"}}
                        onClick={() => {
                            this.doSave();
                        }}>Stage
                </Button>

                <Button className="PanelButton"
                        color="warning"
                        style={{"margin": "5px"}}
                        onClick={() => this.doResume()}>Resume
                </Button>
                <div className="TaskLabelContainer">
                    <div className="TaskLabel">LAST SAVED</div>
                    {message}
                </div>

            </div>

        }
    }
);

export {CheckpointPanel}