import React, {Component} from 'react';
import {Button, CardFooter, CardHeader, Collapse} from "reactstrap";
import 'annotator/AnnotatorTagSpan.css'
import 'annotator/AnnotatorButton.css'
import uuid from 'uuid'


class CommentBox extends Component {
    constructor(props) {
        super(props);
        this.textAreaNode = undefined;
    }


    render() {
        const {comment, commentHandler} = this.props;
        // IMPORTANT: 'key' attribute here is to force the box to refresh upon receiving new props
        return <div id="AnnotatorFooterCommentBox"
                    key={uuid.v4()}>
            <CardFooter>
                <textarea name="comment" ref={(textarea) => this.textAreaNode = textarea}
                          style={{
                              "width" : "100%",
                              "fontSize": "20px"
                          }}
                          placeholder="You need to click SUBMIT button below to save the comment"
                          defaultValue={comment}>
                </textarea>
            </CardFooter>

            <CardFooter>
                <Button className="TagButton"
                        color="success"
                        onClick={() => {
                            commentHandler(this.textAreaNode.value);
                        }}>
                    SUBMIT
                </Button>
            </CardFooter>

        </div>
    }
}

export {CommentBox}