import RangeTree from "annotator/core/RangeTree";
import {SpanFactory} from "annotator/core/Spans";

const Attributes = {
    Base: "base",
    Tags: "tags"
};


class SpanLayerPlanner {

    findUpperLayerSpan(span1, span2) {
        if (span1.attrs.layer > span2.attrs.layer) {
            return span1;
        }
        return span2;
    }

    sortedSpans(s1, s2) {
        let spans = [s1, s2];
        spans.sort((a, b) => a.attrs.layer - b.attrs.layer);
        return spans
    }

    /**
     *
     * @param ss1 Span Array
     * @param ss2 Span Array
     *
     * This methods breaks two SORTED span arrays into non-overlapping fragments
     *
     * In case of overlapping, it surface the one with highest layer index
     */
    doFragment(ss1, ss2) {
        let i1 = 0;
        let i2 = 0;

        let fragments = [];
        while (i1 < ss1.length && i2 < ss2.length) {
            const span1 = ss1[i1];
            const span2 = ss2[i2];

            // 4 scenarios

            // 1. span1 and span2 are equal
            if (span1.rangeEqual(span2)) {
                // such case we choose the one
                // that surface on the upper layer
                const newSpan = this.findUpperLayerSpan(span1, span2).copy();
                newSpan.setAttribute(Attributes.Base, this.sortedSpans(span1, span2));
                fragments.push(newSpan);
                // we consumed both spans
                i1 += 1;
                i2 += 1;
                continue;
            }

            // 2. span1 and span2 are containing one and another, not equal
            const r1 = span1.range;
            const r2 = span2.range;
            if (RangeTree.checkWithin(r1, r2) || RangeTree.checkWithin(r2, r1)) {
                let outSpan = span1;
                let inSpan = span2;
                if (span1.start > span2.start || span1.end < span2.end) {
                    outSpan = span2;
                    inSpan = span1;
                }
                const newSpan = inSpan.copy();
                // but the attributes should inherit from the upperSpan
                const upper = this.findUpperLayerSpan(span1, span2);
                newSpan.attrs = Object.assign({}, upper.attrs);
                newSpan.setAttribute(Attributes.Base, this.sortedSpans(span1, span2));
                fragments.push(newSpan);
                if (inSpan === span1) {
                    i1 += 1;
                } else {
                    i2 += 1;
                }
                continue;
            }

            //3. span1 and span2 are overlapping not containing
            if (RangeTree.checkOverlap(r1, r2)) {
                const overlap = RangeTree.checkOverlap(r1, r2);
                const upper = this.findUpperLayerSpan(span1, span2);
                const [os, oe] = overlap;
                const factory = new SpanFactory(span1.rawText);
                const newSpan = factory.createSpan(os, oe, upper.tag);
                // inherit the attrs from the upper layer
                newSpan.attrs = Object.assign({}, upper.attrs);
                newSpan.setAttribute(Attributes.Base, this.sortedSpans(span1, span2));
                fragments.push(newSpan);
                // advance the pointer for the preceding span
                if (RangeTree.rangeComparator(r1, r2) < 0) {
                    i1 += 1;
                } else {
                    i2 += 1;
                }
            } else {
                //4. span1 and span2 are not overlapping
                if (span1.start < span2.start) {
                    i1 += 1;
                } else {
                    i2 += 1;
                }
            }
        }
        // since two sequence has to be equal, so no need to do copy over

        // now we have obtain all flattened spans
        // decide whether the fragments are head of a span or not
        fragments.forEach((f) => {
            const {start, _} = f;
            const base = f.attrs.base;
            let tags = {};
            base.forEach((s) => {
                // this fragment is the start of a parent layer span
                if (s.start === start) {
                    if (!s.attrs.blank) {
                        tags[s.attrs.layer] = s.tag;
                    }
                }
            });
            f.setAttribute(Attributes.Tags, tags);
        });

        return fragments;
    }
}


export {SpanLayerPlanner}