import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {Card, CardBody, CardHeader, CardFooter, Table, Button} from "reactstrap";
import './EventAnnotator.css'
import '../AnnotatorButton.css'
import {ActiveTriggerSpanColor} from "./EventsAnnotator";
import {EVENT_EMPTY, EVENT_INCOMPLETE} from "../store/EventInstanceDataStore";
import {SvgCrossIcon} from "../svg/SvgCrossIcon";


const EventTriggerStatusSidePanel = observer(
    class TriggerStatusSidePanel extends React.Component {
        constructor(props) {
            super(props);
            this.createInstanceButton = this.createInstanceButton.bind(this);
        }

        createInstanceButton(instanceId) {
            const store = this.props.store;
            const readOnly = store.isReadOnly();
            const activeInstanceId = this.props.v2 ? store.states.activeInstanceIdV2 : store.states.activeInstanceId;
            let color = "secondary";
            if (activeInstanceId === instanceId) {
                color = "primary"
            }
            const idx = instanceId;

            const switchHandler = () => {
                if (!this.props.v2) {
                    store.switchTriggerInstance(idx);
                } else {
                    store.switchTriggerInstanceV2(idx);
                }
            };

            let removeIcon = [];
            if (!this.props.v2 && !readOnly) {
                // v2 doesn't allow removal or anything
                removeIcon.push(
                    <SvgCrossIcon cssStyle="SvgCancelButtonTag"
                                  clickTrigger={() => {
                                      store.removeTriggerInstance(idx);
                                  }}/>
                );
            }

            return <Button className="TagButton InstanceButtonStyle"
                           color={color}
                           size="sm"
                           onClick={switchHandler}>
                {"E" + (instanceId + 1)}
                {removeIcon}
            </Button>
        }

        render() {
            const store = this.props.store;
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const readOnly = store.isReadOnly();

            const activeTriggerSpan = store.states.activeTriggerSpan;
            let triggerType = "";
            let triggerText = undefined;
            let triggerStatus = "";
            let instanceButtons = [];
            if (activeTriggerSpan) {
                triggerType = activeTriggerSpan.tag;
                triggerText = activeTriggerSpan.extent;
                let argumentPropertyStore = store.eventArgumentPropertyStore;
                let activeInstanceId = store.states.activeInstanceId;
                if (this.props.v2) {
                    console.log("pros.v2");
                    argumentPropertyStore = store.eventArgumentPropertyStoreV2;
                    activeInstanceId = store.states.activeInstanceIdV2;
                }
                const statusCode = argumentPropertyStore.getInstanceStatus(activeTriggerSpan,
                    activeInstanceId);
                if (statusCode === EVENT_EMPTY) {
                    triggerStatus = "EMPTY"
                } else if (statusCode === EVENT_INCOMPLETE) {
                    triggerStatus = "INCOMPLETE"
                } else {
                    triggerStatus = "COMPLETE"
                }

                const instanceList = argumentPropertyStore.eventInstanceMap.get(activeTriggerSpan);
                if (instanceList) {
                    instanceList.forEach((instance, idx) => {
                        instanceButtons.push(this.createInstanceButton(idx));
                    })
                }

                // add a button to add instance
                // only when this is not v2
                if (!this.props.v2 && !readOnly) {
                    instanceButtons.push(<Button className="TagButton InstanceButtonStyle"
                                                 size="sm"
                                                 color="success"
                                                 onClick={() => {
                                                     store.addTriggerInstance()
                                                 }}>+
                    </Button>);
                }

            }
            return <div id="EventTriggerStatusSidePanel" style={{"marginBottom": "15px"}}>
                <Card>
                    <CardHeader className="SidePanelHeader">
                        {"TRIGGER " + "(" + this.props.version + ")"}
                    </CardHeader>
                    <CardBody className="SidePanelText">
                        <Table hover>
                            <tbody>
                            <tr>
                                <td>TYPE</td>
                                <td>{triggerType}</td>
                            </tr>
                            <tr>
                                <td>EXTENT</td>
                                <td><span style={{
                                    "background": ActiveTriggerSpanColor,
                                    "paddingTop": "5px",
                                    "paddingBottom": "5px",
                                }}>{triggerText}</span></td>
                            </tr>
                            </tbody>
                        </Table>
                    </CardBody>
                    <CardFooter>
                        {instanceButtons}
                    </CardFooter>
                </Card>
            </div>
        }
    }
);

export {EventTriggerStatusSidePanel}