import {Namespaces} from "../core/Namespaces";
import {UFSchema} from "../core/UFSchema";
import {RangeMap} from "../core/RangeMap";


class RelationAuditor {

    static audit(documentObject, namespace=Namespaces.RELATION_EXTRACTION, entityNamespace=Namespaces.NER) {

        // console.log("RelationRegularizer called");

        if (documentObject[UFSchema.ANNOTATIONS] === undefined
            || documentObject[UFSchema.ANNOTATIONS][namespace] === undefined) {
            // there is no existing coreference annotations
            return documentObject;
        }


        // the relation auditor expects the NER and coreference annotations
        // to be consistent already, so it needs to be called AFTER
        // CoreferenceAuditor.audit is called.

        // Relation audit checking is easy, if any span, be it ARG1 or ARG2, either
        // deleted or modified, the whole relation is deleted, since we dont' have
        // enough information to tell what the new relation would be

        let NERAnnotations = documentObject[UFSchema.ANNOTATIONS][entityNamespace];
        if (NERAnnotations === undefined) {
            NERAnnotations = [];
        }
        const coreferenceAnnotations = documentObject[UFSchema.ANNOTATIONS][Namespaces.COREFERENCE];
        let coreferenceOnlyMentions = [];
        if (coreferenceAnnotations && coreferenceAnnotations["coref_only_mentions"]) {
            coreferenceOnlyMentions = coreferenceAnnotations["coref_only_mentions"];
        }
        const oldRelations = documentObject[UFSchema.ANNOTATIONS][namespace];

        const NERSpanMap = RangeMap.create(NERAnnotations);
        const CoreferenceSpanMap = RangeMap.create(coreferenceOnlyMentions);

        const checkArgConsistent = (arg) => {
            if (arg.tag === "COREF") {
                // coreference span
                if (CoreferenceSpanMap.get(arg)) {
                    return true;
                }
            } else {
                const nerSpan = NERSpanMap.get(arg);
                if (nerSpan && nerSpan.tag === arg.tag) {
                    return true;
                }
            }
            return false;
        };

        const newRelations = [];

        // console.log(oldRelations);

        oldRelations.forEach((r) => {
            let {arg1, arg2} = r;
            if (checkArgConsistent(arg1) && checkArgConsistent(arg2)) {
                newRelations.push(r);
            }
        });

        documentObject[UFSchema.ANNOTATIONS][namespace] = newRelations;

        return documentObject;
    }

    /**
     * audit all possible pairs of entities/relations tasks
     * including:
     * 1. named_entity -> relations
     * 2. event_arguments -> event_relations
     *
     * could be time consuming, need to take care
     **/
    static auditAll(document) {
        let d = document;
        d = RelationAuditor.audit(d, Namespaces.RELATION_EXTRACTION, Namespaces.NER);
        d = RelationAuditor.audit(d, Namespaces.EVENT_RELATIONS, Namespaces.EVENT_ARGUMENTS);
        return d;
    }
}

export {RelationAuditor}