const LayerAttributes = {
    Layer: "layer",
};

const LayerIndex = {
    FRONT: 2,
    MIDDLE: 1,
    BACKGROUND: 0
};

const OverlayMode = {
    OVERLAY: 1,
    SHOW_V1: 2,
    SHOW_V2: 3,
};

const normalizeTagText = (tagText) => {
    if (tagText && tagText.length > 8) {
        tagText = tagText.substr(0, 8);
        tagText += "..";
    }
    return tagText;
};

export {LayerIndex, LayerAttributes, OverlayMode, normalizeTagText}