import {DocumentStateStore} from "./DocumentStateStore";
import {UFSchema} from "annotator/core/UFSchema";
import RangeTree from "annotator/core/RangeTree";
import {SpanFactory} from "annotator/core/Spans";
import {Namespaces} from "annotator/core/Namespaces";
import {RelationRegistry} from "./RelationRegistry";
import {CoreferenceAuditor} from "../audit/CoreferenceAuditor";
import {RelationAuditor} from "../audit/RelationAuditor";

const States = {
    INIT: 0,
    ARG1: 1,
    ARG2: 2,
    FINISHED: 3,
    REJECTED: 4,
};

class RelationStateMachine {
    constructor() {
        this.transitions = new Map();
        this.reset = this.reset.bind(this);
        this.changeStateTo = this.changeStateTo.bind(this);
        this.registerTransition = this.registerTransition.bind(this);
        this.next = this.next.bind(this);
        this.reset();
    }

    reset() {
        this.state = States.INIT;
        this.arg1 = undefined;
        this.arg2 = undefined;
        this.candidateRelations = undefined;
        this.relation = undefined;
    }

    changeStateTo(nextState) {
        this.state = nextState
    }

    registerTransition(state, action) {
        this.transitions.set(state, action);
    }

    next(context) {
        const currentState = this.state;
        const action = this.transitions.get(currentState);
        if (action) {
            const nextState = action(this, context);
            this.changeStateTo(nextState);
            if (nextState === States.REJECTED || nextState === States.FINISHED) {
                // in both cases, advance the state machine to do clean up
                console.log("next state in terminal state");
                if (nextState === States.REJECTED) {
                    console.log("current state -> ", currentState);
                    console.log("state transition get rejected receiving -> ", context);
                }
                this.next();
            }
        } else {
            console.log("no action is registered for state " + this.state);
        }
    }
}

const Attributes = {
    ARG1: "arg1",
    ARG2: "arg2",
    Segment: "segment",
    Relations: "relations",
};

class RelationStateStore extends DocumentStateStore {
    constructor(documentObject,
                relations,
                relationNamespace = Namespaces.RELATION_EXTRACTION,
                entitiesNamespace = Namespaces.NER) {
        super(documentObject);
        this.schemaKey = relationNamespace;
        this.entityNamespace = entitiesNamespace;
        this.updateState = this.updateState.bind(this);
        this.addRelation = this.registerAction(this.addRelation.bind(this));
        this.removeRelation = this.registerAction(this.removeRelation.bind(this));
        this.toggleRelation = this.toggleRelation.bind(this);
        this.prevSegment = this.prevSegment.bind(this);
        this.nextSegment = this.nextSegment.bind(this);
        this.createStateMachine = this.createStateMachine.bind(this);
        this.createRelationRegistry = this.createRelationRegistry.bind(this);

        this.relationRegistry = this.createRelationRegistry(relations);
        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        // we first audit coreference and existing relations
        // annotations to eliminate inconsistency with the up-to-date
        // NER/Coreference results

        // TODO: currently the Relations view only reads from "named_entity" and "coreference" views
        // It is not configurable at this point, need to take care of this later.
        // TODO: call super.expandSpanToHaveMultibyteOffset for the spans here
        CoreferenceAuditor.audit(documentObject);
        RelationAuditor.audit(documentObject, this.schemaKey, this.entityNamespace);
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        const spanFactory = new SpanFactory(rawText);
        this.states.stateMachine = this.createStateMachine();
        this.states.rawText = rawText;
        this.states.spanFactory = spanFactory;
        // the relations are stored separately by segments
        let segments;
        try {
            // HACK for fast deep copy
            // credit to https://stackoverflow.com/a/4591639
            segments = JSON.parse(
                JSON.stringify(documentObject["preprocessing"]["segments"]["sentences"]));
        } catch (e) {
            console.log("Error extracting sentence segments from" +
                " document[\"preprocessing\"][\"segments\"][\"sentences\"] field. Document JSON: " +
                JSON.stringify(documentObject));
            // no preprocessing segments, treat whole document as a single segment
            segments = [{start: 0, end: rawText.length}];
        }
        // sort segments by start
        segments.sort((s1, s2) => {
            return s1.start - s2.start
        });
        this.states.activeSegment = 0;
        this.states.segments = segments;
        // init the activeSegment to be the first one
        // read all the NER annotations and the mentions
        // that are created during coref annotations
        //
        // IMPORTANT: copy its own copy of NER spans before using
        // to avoid undesired side effects
        let NERAnnotations = documentObject[UFSchema.ANNOTATIONS][this.entityNamespace];

        let CorefAnnotations = documentObject[UFSchema.ANNOTATIONS][Namespaces.COREFERENCE];
        NERAnnotations = NERAnnotations ? NERAnnotations : [];
        // Noted we have disabled tagging in some CR tasks
        // 'coref_only_mentions' are not needed in all cases
        CorefAnnotations = CorefAnnotations ? CorefAnnotations : {};
        // we need SpanContainer objects, not plain js objects
        let entities = NERAnnotations.map((ns) => {
            return spanFactory.copySpan(ns);
        });
        let corefMentions = CorefAnnotations["coref_only_mentions"];
        console.log("Coref in Relations", CorefAnnotations);
        if (corefMentions) {
            corefMentions.forEach((m) => {
                entities.push(spanFactory.copySpan(m));
            });
        }
        // need some smart trick here
        // some segments don't have entities, if 2 such segments sit together
        // they will be merged into one blank span
        //
        // in order to break blank spans into segments, we insert
        // segment end as pseudo' spans, with length 0 into the ner spans list
        // to force the break at the boundary of each segment
        segments.forEach((s) => {
            entities.push(spanFactory.createSpan(s.end, s.end));
        });
        this.states.spanTree = RangeTree.createRangeTree(entities);
        // spans has all blank/non-blank text spans
        let spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.spanTree,
            rawText.length,
            (start, end) => {
                return spanFactory.createSpan(start, end)
            }).map((entry) => {
            let [_, __, value] = entry;
            return value;
        });
        // now filter those pseudo spans
        spans = spans.filter((s) => s.end > s.start);
        // now assign those spans to segments;
        let segmentIdx = 0;
        spans.forEach((s) => {
            if (s.start >= segments[segmentIdx].end) {
                // beyond the current segment
                segmentIdx += 1
            } else if (s.end > segments[segmentIdx].end) {
                // use some heuristic here
                // if more 50% of the segment falls in the current segment
                // assign the span to the current segment
                // otherwise assign the span to next segment
                const inSegment = segments[segmentIdx].end - s.start;
                const ratio = inSegment / s.extent.length;
                if (ratio < 0.5) {
                    segmentIdx += 1;
                }
            }
            // console.log("span", s.exportAnnotation(), "segment", segments[segmentIdx], segmentIdx);
            s.setAttribute(Attributes.Segment, segmentIdx);
        });
        // console.log(spans.map((s) => s.exportAnnotation(true)));
        this.states.allSpans = spans;
        // now read from existing relations
        // and create a span -> [relations] mapping
        let spanToRelationsMap = new Map();
        let relationAnnotations = documentObject[UFSchema.ANNOTATIONS][this.schemaKey];
        if (relationAnnotations) {
            relationAnnotations.forEach((r) => {
                const {relation, arg1, arg2} = r;
                const relationType = this.relationRegistry.getRelation(relation);
                if (relationType) {
                    const span1 = spanFactory.copySpan(arg1);
                    const span2 = spanFactory.copySpan(arg2);
                    const relationObj = relationType.create(span1, span2);
                    // the relation is store in a map
                    // keyed by the id of the arg1.attrs.id
                    const span1Id = span1.attrs.id;
                    if (!spanToRelationsMap.has(span1Id)) {
                        spanToRelationsMap.set(span1Id, []);
                    }
                    let rList = spanToRelationsMap.get(span1Id);
                    rList.push(relationObj);
                }
            });
        }
        this.states.spanToRelationsMap = spanToRelationsMap;
        this.states.highlight = undefined;
    }

    createRelationRegistry(relations) {
        const registry = new RelationRegistry();
        relations.forEach((r) => {
            let [name, arg1Types, arg2Types] = r;
            registry.registerRelationTriple(name, arg1Types, arg2Types);
        });
        return registry;
    }

    addRelation(relation) {
        const arg1 = relation.arg1;
        const arg1Id = arg1.attrs.id;
        let relationList = [];
        if (this.states.spanToRelationsMap.has(arg1Id)) {
            relationList = this.states.spanToRelationsMap.get(arg1Id);
        } else {
            this.states.spanToRelationsMap.set(arg1Id, relationList);
        }
        // dedup the relation
        const reId = relation.id;
        let exist = false;
        relationList.forEach((r) => {
            if (r.id === reId) {
                exist = true;
            }
        });
        if (!exist) {
            console.log("relation -> ", relation.exportAnnotation(), "doesn't exist");
            relationList.push(relation);
        } else {
            console.log("find duplicate relation, skip");
        }
    }

    removeRelation(relation) {
        const arg1 = relation.arg1;
        const arg1Id = arg1.attrs.id;
        if (this.states.spanToRelationsMap.has(arg1Id)) {
            const reId = relation.id;
            const relationList = this.states.spanToRelationsMap.get(arg1Id);
            let index = relationList.findIndex((r) => r.id === reId);
            if (index !== -1) {
                // find the element
                relationList.splice(index, 1);
            }
        }
    }

    toggleRelation(relation) {
        // NOTE: highlight and state machine introduced ARG1/ARG2
        // emphasis shouldn't co-exist.
        // this is a placeholder invalid message to reset
        // the state machine

        if (relation !== this.states.highlight) {
            this.updateState({cancel: true});
            const {arg1, arg2} = relation;
            this.updateState({span: arg1});
            this.updateState({span: arg2});
            this.states.highlight = relation;
        } else {
            this.updateState({cancel: true});
        }

        this.notifyViewUpdateChange();
    }

    prevSegment() {
        // since we are still operate at full document
        // level, just using activeSegment to filter
        // the spans that doesn't belong to the current segment
        // for display, the prev/next segment operation is actually
        // pretty simple to implement
        if (this.states.activeSegment > 0) {
            this.states.activeSegment -= 1;
        }
        // this operation doesn't effect the document annotations
        // no need to send documentStateChange signal
        this.notifyViewUpdateChange();
    }

    nextSegment() {
        if (this.states.activeSegment < this.states.segments.length - 1) {
            this.states.activeSegment += 1;
        }
        this.notifyViewUpdateChange();
    }

    get spans() {
        const spanFactory = this.states.spanFactory;
        const activeSegment = this.states.activeSegment;
        const allSpans = this.states.allSpans;
        const spanToRelationsMap = this.states.spanToRelationsMap;
        const stateMachine = this.states.stateMachine;

        let segmentSpans = [];
        allSpans.forEach((s) => {
            if (s.attrs.segment === activeSegment) {
                // copy so no side effects
                const copy = spanFactory.copySpan(s);
                const copyId = copy.attrs.id;
                if (stateMachine.arg1 && copyId === stateMachine.arg1.attrs.id) {
                    // indicate the this span is the current
                    // selected arg1
                    copy.setAttribute(Attributes.ARG1, true);
                }
                if (stateMachine.arg2 && copyId === stateMachine.arg2.attrs.id) {
                    copy.setAttribute(Attributes.ARG2, true);
                }
                // check whether there is existing relations attached
                // the selected spans
                if (spanToRelationsMap.has(copyId)) {
                    const relationList = spanToRelationsMap.get(copyId);
                    if (relationList.length > 0) {
                        copy.setAttribute(Attributes.Relations, relationList);
                    }
                }
                segmentSpans.push(copy);
            }
        });
        return segmentSpans;
    }

    convertStatesToAnnotations() {
        // read the spansToRelations map
        let allRelations = [];
        this.states.spanToRelationsMap.forEach((rList, _, __) => {
            rList.forEach((r) => {
                allRelations.push(r);
            })
        });
        // sorted all relations based on their arg1.start ascending
        allRelations.sort((r1, r2) => r1.arg1.start - r2.arg1.start);
        let relationAnnotations = allRelations.map((r) => r.exportAnnotation());
        let annotationObject = {};
        annotationObject[this.schemaKey] = relationAnnotations;
        return annotationObject
    }

    updateState(update) {
        // NOTE: highlight and state machine introduced ARG1/ARG2
        // emphasis shouldn't co-exist.
        this.states.highlight = undefined;
        this.states.stateMachine.next(update);
    }

    createStateMachine() {
        const machine = new RelationStateMachine();
        // transit from Init to ARG1
        machine.registerTransition(States.INIT, (machine, context) => {
            const {span} = context;
            if (span) {
                machine.arg1 = span;
                // now update the spans because this
                // will change the UI
                //
                // noted since there is no relation added to
                // the document itself by now, so there is
                // no reason to signal documentStateChange
                this.notifyViewUpdateChange();
                return States.ARG1;
            }
            return States.REJECTED;
        });
        // transit from ARG1 to ARG2
        machine.registerTransition(States.ARG1, (machine, context) => {
            const {span} = context;
            if (span && RangeTree.rangeComparator(machine.arg1.range,
                span.range) !== 0) {
                // the second span cannot be the same span as the first one
                // this also means click the same span result in cancellation
                // of the whole workflow
                machine.arg2 = span;
                // based on the tag type of arg1 and arg2
                // suggest the potential relations
                machine.candidateRelations = this.relationRegistry
                    .suggestRelations(machine.arg1.tag, machine.arg2.tag);
                this.notifyViewUpdateChange();
                return States.ARG2
            }
            return States.REJECTED;
        });
        // transit from ARG2 to Finished, in this step we need to
        // select the corresponding relations
        machine.registerTransition(States.ARG2, (machine, context) => {
            const {relationType: relationType} = context;
            if (relationType) {
                // now we should have both arg1, arg2 and relation
                let r = relationType.create(machine.arg1, machine.arg2);
                machine.relation = r;
                // since addRelation is an action,
                // no need to sent viewChange signal
                this.addRelation(r);
                // Finished will reset the whole workflow
                return States.FINISHED;
            }
            // a backup flow
            const {span} = context;
            if (span && span.rangeEqual(machine.arg2)) {
                // click on the same arg2 span
                // go back to ARG1
                console.log("trigger reset arg2");
                machine.arg2 = undefined;
                machine.candidateRelations = undefined;
                this.notifyViewUpdateChange();
                return States.ARG1;
            }
            return States.REJECTED;
        });
        // in case the state is Rejected/Finished
        // we reset the machine back to INIT state
        const cleanUpAction = (machine, context) => {
            console.log("state machine reset");
            machine.reset();
            this.notifyViewUpdateChange();
            return States.INIT;
        };
        machine.registerTransition(States.REJECTED, cleanUpAction);
        machine.registerTransition(States.FINISHED, cleanUpAction);
        return machine;
    }

}

export {RelationStateStore, RelationStateMachine, States}