import React, {ReactElement} from 'react';
import {BaseSpanType} from "../types/CoreTypes";
import {PropertyInstance, PropertyKeyValueType, PropertyTargetType} from "../types/BasePropertyType";
import './Properties.css'
import {KeyValuePropertyEditor} from "./KeyValuePropertyEditor";
import {TextPropertyEditor} from "./TextPropertyEditor";
import {AutoHideContainer} from "./AutoHideContainer";
import {observer} from "mobx-react";
import {PropertyDataStore} from "../store/PropertyDataStore";
import {SSIEAnnotationTypeForProperties} from "./PropertyEditorContainer";

const PropertyEditor = observer((props: {
    target: BaseSpanType | SSIEAnnotationTypeForProperties | "document",
    targetType: PropertyTargetType,
    store: PropertyDataStore,
    // notify outer component for the change
    // use case like, when PropertyDataStore changes, notify outer DocumentDataStore
    // to react to this;
    broadcast?: (store:PropertyDataStore) => void;
}) => {

    const {target, targetType, store, broadcast} = props;
    // subscribe to store updates, this variable is not used
    // but for Mobx to grab/establish the dependency
    const _subscribe = store.updateSubscriber.update;
    let properties: PropertyInstance[];
    let propertyUpdateHandler: any;
    console.log("PropertyEditor rerender");
    if (targetType == PropertyTargetType.Document) {
        properties = store.retrieveDocumentProperty();
        console.log("document properties", properties);
        console.log("document properties", store);
        propertyUpdateHandler = (properties: PropertyKeyValueType[]) => {
            console.log("updateDocumentProperty called", properties);
            store.updateDocumentProperty(properties);
            if (broadcast) {
                // propagate to outer component
                broadcast(store);
            }
        };
    } else if (targetType == PropertyTargetType.TextSpan) {
        // target is now a span
        properties = store.retrieveSpanProperty(target as BaseSpanType);
        console.log("Span properties: " + JSON.stringify(properties))
        propertyUpdateHandler = (properties: PropertyKeyValueType[]) => {
            store.updateSpanProperty(target as BaseSpanType, properties);
            if (broadcast) {
                // propagate to outer component
                broadcast(store);
            }
        };
    } else {
        // Target is now a Semi-structured Block (PropertyTargetType.Block)
        properties = store.retrieveBlockProperty(target as SSIEAnnotationTypeForProperties);
        propertyUpdateHandler = (properties: PropertyKeyValueType[]) => {
            store.updateBlockProperty(target as SSIEAnnotationTypeForProperties, properties);
            if (broadcast) {
                // propagate to outer component
                broadcast(store);
            }
        };
    }

    let propertyEditorComponents: any = [];
    properties.forEach((p: PropertyInstance) => {
        const {property, value} = p;
        const {field_type: fieldType} = property;
        let component: ReactElement | null = null;
        if (fieldType === "KeyValue") {

            component = <KeyValuePropertyEditor property={property}
                                                value={value}
                                                updateHandler={propertyUpdateHandler}/>
        } else if (fieldType == "Text") {
            component = <TextPropertyEditor property={property}
                                            value={value}
                                            updateHandler={propertyUpdateHandler}/>
        }

        if (component !== null) {
            propertyEditorComponents.push(
                <AutoHideContainer title={property.name} key={property.name}>
                    {component}
                </AutoHideContainer>
            )
        }
    });

    return <React.Fragment>
        {propertyEditorComponents}
    </React.Fragment>

});

export {PropertyEditor};