import React from 'react';
import { Button, CardHeader, Collapse } from 'reactstrap';
import { SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import './MetadataSidePanel.css';
import ToggleSwitch from '../../../../core/ToggleSwitch/ToggleSwitch';
import { StringMap } from '../../types/StringMap';
import { DEFAULT_ANNOTATOR_METADATA } from '../../utils/DefaultSsieCollection';

interface Props {
    usingGroundTruth: boolean;
}

/**
 * Component for a side panel in the semi-structured view that shows the document's metadata.
 * Screenshot: https://tiny.amazon.com/zs1pm1aa/drivcorpamazdocudnleCRpng
 */
const MetadataSidePanel: React.FC<Props> = ({ usingGroundTruth }) => {
    const { metadata, annotatorMetadata } = React.useContext(SsieAnnotationStore);

    const [showMetadata, setShowMetadata] = React.useState<boolean>(false);
    const [documentMetadata, setDocumentMetadata] = React.useState<boolean>(true);

    return (
        <div className="card" id="metadata-side-panel__container">
            <CardHeader className="metadata-side-panel__card-header">
                <Button className="TagButton" color="info" onClick={() => setShowMetadata((s) => !s)}>
                    METADATA
                </Button>
                <div id="metadata-side-panel__document-id">{metadata['document_id'] ? metadata['document_id'] : 'Missing Document ID'}</div>
            </CardHeader>
            <Collapse isOpen={showMetadata}>
                <div className="card-body d-flex flex-wrap" id="metadata-side-panel__container">
                    {Object.keys(usingGroundTruth ? annotatorMetadata : DEFAULT_ANNOTATOR_METADATA).length > 0 ? (
                        <ToggleSwitch
                            handleToggle={(e) => setDocumentMetadata(!e.target.checked)}
                            toggleTextUnchecked="DOCUMENT"
                            toggleTextChecked="ANNOTATOR"
                            containerClassname="metadata-side-panel__toggle-switch"
                            toggleColor="#17a2b8"
                        />
                    ) : null}
                    {documentMetadata
                        ? formatMetadata(metadata)
                        : formatMetadata(usingGroundTruth ? annotatorMetadata : DEFAULT_ANNOTATOR_METADATA, true)}
                </div>
            </Collapse>
        </div>
    );
};

/**
 * Return spans of metadata for labeller to view.
 * Viewable keys for document metadata: 'page', 'pages', 'source_ref', 'document_id'
 * @param metadata Key-value object
 * @returns
 */
const formatMetadata = (metadata: StringMap<any>, isAnnotatorMetadata?: boolean) => {
    return (!isAnnotatorMetadata ? ['source_ref', 'document_id', 'page', 'pages'] : Object.keys(metadata)).map((key) => (
        <span key={key} className="metadata-side-panel__item">
            <strong>{formatKey(key)}</strong>: {metadata[key]}
        </span>
    ));
};

const formatKey = (text: string) => {
    return text
        .split('_')
        .map((t) => t[0].toUpperCase() + t.substring(1))
        .join(' ');
};

export default MetadataSidePanel;
