import React, { useContext } from 'react';
import { ConnectTagColor, RedSpanColor, SecondarySpanColor } from '../../../../../../AnnotatorCommons';
import { UNASSIGNED_TAG } from '../../../../../../store/TaggingStateStore';
import {
    Annotation,
    getEntityConnectName,
    multipleRelationsContain,
    relationContains,
    SsieAnnotationStore,
} from '../../../../context/SsieAnnotationStore';

interface Props {
    isSecondary?: boolean;
    annotation: Annotation;
    onMouseDown: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
    onContextMenuClick: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;
    overlappingAnnotations: Annotation[];
    hasConnectionDifference: boolean;
    hasAnnotationDifference: boolean;
}

/**
 * Component for the connection label of a semi-structured annotation where connection label is a shared label between all spans of a discontiguous entity.
 * Screenshot: https://tiny.amazon.com/6jwp5ka7/drivcorpamazdocudnleCRpng
 */
const ConnectionLabel: React.FC<Props> = ({
    isSecondary,
    annotation,
    onMouseDown,
    onContextMenuClick,
    overlappingAnnotations,
    hasConnectionDifference,
    hasAnnotationDifference,
}) => {
    const { ssieAnnotations, stagedRelation, secondarySsieAnnotations, selectedSsieAnnotations } = useContext(SsieAnnotationStore);

    const [adjustLabelForOverlap, setAdjustLabelForOverlap] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (!isSecondary) {
            setAdjustLabelForOverlap(false);
        } else {
            if (overlappingAnnotations.length > 0) {
                const overlappingAnnotationIsStaged =
                    selectedSsieAnnotations.length > 0 &&
                    selectedSsieAnnotations[0].tag !== UNASSIGNED_TAG &&
                    stagedRelation.length > 1 &&
                    relationContains(stagedRelation, overlappingAnnotations[0].id);
                const overlappingAnnotationInConnection =
                    ssieAnnotations.relations.filter((rel) => relationContains(rel, overlappingAnnotations[0].id)).length > 0;
                setAdjustLabelForOverlap(overlappingAnnotationIsStaged || overlappingAnnotationInConnection);
            }
        }
    }, [overlappingAnnotations, ssieAnnotations, stagedRelation, selectedSsieAnnotations, isSecondary]);

    const annotationsToUse = isSecondary ? secondarySsieAnnotations : ssieAnnotations;
    const showEntityConnectLabel = multipleRelationsContain(annotationsToUse.relations, annotation.id) > -1;
    const relationIndex = showEntityConnectLabel
        ? annotationsToUse.relations.findIndex((rel) => new Set(rel).has(annotation.id))
        : stagedRelation.findIndex((id) => id === annotation.id);

    const showStagedConnectionLabel = annotation.tag !== UNASSIGNED_TAG && stagedRelation.length > 1 && relationIndex > -1;

    return showEntityConnectLabel ? (
        <span
            className={`selection-boundary-span-tag ConnectTagStyle NoSelect`}
            onMouseDown={onMouseDown}
            onContextMenu={onContextMenuClick}
            style={{
                color: `${hasConnectionDifference || hasAnnotationDifference ? RedSpanColor : isSecondary ? SecondarySpanColor : ConnectTagColor}`,
                bottom: `${adjustLabelForOverlap ? '-30px' : '-15px'}`,
            }}
        >
            {getEntityConnectName(relationIndex)}
        </span>
    ) : showStagedConnectionLabel ? (
        <span
            className={`selection-boundary-span-tag ConnectTagStyle NoSelect`}
            onMouseDown={onMouseDown}
            onContextMenu={onContextMenuClick}
            style={{ color: ConnectTagColor }}
        >
            {`ARG_${relationIndex}`}
        </span>
    ) : null;
};

export default ConnectionLabel;
