import React from 'react';
import { Namespaces } from '../../../../core/Namespaces';
import { ContextMenuSelector } from '../../../../menu/ContextMenuSelector';
import { TAGGING_ANNOTATOR_MENU_ID } from '../../../TaggingAnnotator';

import {
    Annotation,
    SsieAnnotationStore,
    isSsieAnnotationRelationSelected,
    SsieAnnotations,
    relationContains,
} from '../../context/SsieAnnotationStore';
import { copyAnnotation, getAnnotationText, getOverlappingAnnotations, showConnectedButton } from '../../utils/SsieUtils';

interface Props {
    store: any;
    updateSelectedSsieAnnotationsProperties: (
        tag: string,
        {
            selectedSsieAnnotations,
            ssieAnnotations,
            setSsieAnnotationStore,
        }: { selectedSsieAnnotations: Annotation[]; ssieAnnotations: SsieAnnotations; setSsieAnnotationStore: any }
    ) => void;
    removeActiveSsieAnnotations: (selectedSsieAnnotations: Annotation[], ssieAnnotations: SsieAnnotations, setSsieAnnotationStore: any) => void;
    handleAutoTagClick: (autoTagSsieToken: () => void) => void;
    handleConnectButtonClick: (
        setSsieAnnotationStore: any,
        selectedSsieAnnotations: Annotation[],
        ssieAnnotations: SsieAnnotations,
        stagedRelation: string[]
    ) => void;
}

/**
 * Component for semi-structured annotation context menu. Uses ContextMenuSelector component from tagging workflow.
 * Screenshot: https://tiny.amazon.com/17a09rwee/drivcorpamazdocudnleCRpng
 */
const SemiStructuredContextMenuSelector: React.FC<Props> = ({
    store,
    updateSelectedSsieAnnotationsProperties,
    removeActiveSsieAnnotations,
    handleAutoTagClick,
    handleConnectButtonClick,
}) => {
    const {
        pdfBlocks,
        selectedSsieAnnotations,
        ssieAnnotations,
        setSsieAnnotationStore,
        stagedRelation,
        autoTagSsieToken,
        selectedSecondaryAnnotations,
        secondarySsieAnnotations,
    } = React.useContext(SsieAnnotationStore);

    const [secondaryAnnotationClicked, setSecondaryAnnotationClicked] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (store.schemaKey === Namespaces.SSIE && selectedSecondaryAnnotations.length > 0) {
            setSecondaryAnnotationClicked(true);
        } else {
            setSecondaryAnnotationClicked(false);
        }
    }, [store, selectedSecondaryAnnotations]);

    const handleReplacePrimaryAnnotation = React.useCallback(() => {
        const relationsToRemove =
            selectedSsieAnnotations.length > 0
                ? ssieAnnotations.relations.filter((rel) => new Set(rel).has(selectedSsieAnnotations[0].id)).map((rel) => rel[0])
                : [];
        const secondaryRelationToAdd = secondarySsieAnnotations.relations.find((rel) => relationContains(rel, selectedSecondaryAnnotations[0].id));
        const annotationsToAdd = selectedSecondaryAnnotations.map((anno) => copyAnnotation(anno));

        const annotationsToRemove: Annotation[] = [];
        for (const annotation of annotationsToAdd) {
            Array.prototype.push.apply(annotationsToRemove, getOverlappingAnnotations(annotation, ssieAnnotations.annotations));
        }
        setSsieAnnotationStore({
            selectedSsieAnnotations: [],
            ssieAnnotations: ssieAnnotations
                .deleteRelations(relationsToRemove)
                .deleteAnnotations(annotationsToRemove)
                .withNewRelation(secondaryRelationToAdd ? secondaryRelationToAdd : [])
                .withNewAnnotations(annotationsToAdd),
            selectedSecondaryAnnotations: undefined,
        });
    }, [ssieAnnotations, selectedSsieAnnotations, selectedSecondaryAnnotations, setSsieAnnotationStore, secondarySsieAnnotations]);

    const getReplacePrimaryAnnotationText = React.useCallback(() => {
        const replaceFromText = selectedSsieAnnotations.map((selAnno) => getAnnotationText(selAnno, pdfBlocks));
        const replaceToText = selectedSecondaryAnnotations.map((selAnno) => getAnnotationText(selAnno, pdfBlocks));
        return (
            'REPLACE "' +
            replaceFromText.map((text) => text.join(' ')).join(' ') +
            '" with "' +
            replaceToText.map((text) => text.join(' ')).join(' ') +
            '"'
        );
    }, [selectedSsieAnnotations, selectedSecondaryAnnotations, pdfBlocks]);

    return (
        <ContextMenuSelector
            menuId={TAGGING_ANNOTATOR_MENU_ID}
            numItem={4}
            tags={store.tagGroupStore.tags}
            items={store.tagGroupStore.tags}
            clickHandler={(tag: string) => {
                store.updateActiveTag(tag);
                updateSelectedSsieAnnotationsProperties(tag, {
                    selectedSsieAnnotations,
                    ssieAnnotations,
                    setSsieAnnotationStore,
                });
            }}
            untag={{
                untagHandler: () => {
                    store.removeActiveSpan();
                    removeActiveSsieAnnotations(selectedSsieAnnotations, ssieAnnotations, setSsieAnnotationStore);
                },
                untagText: 'REMOVE',
            }}
            autoTag={{
                autoTagHandler: () => handleAutoTagClick(autoTagSsieToken),
                autoTagText: 'AUTO TAG',
            }}
            connect={
                showConnectedButton(selectedSsieAnnotations)
                    ? {
                          connectHandler: () =>
                              handleConnectButtonClick(setSsieAnnotationStore, selectedSsieAnnotations, ssieAnnotations, stagedRelation),
                          connectText: isSsieAnnotationRelationSelected(selectedSsieAnnotations[0].id, ssieAnnotations.relations)
                              ? 'DISCONNECT'
                              : 'CONNECT',
                      }
                    : {}
            }
            replacePrimaryAnnotation={
                secondaryAnnotationClicked
                    ? {
                          replacePrimaryAnnotationHandler: handleReplacePrimaryAnnotation,
                          replacePrimaryAnnotationText: getReplacePrimaryAnnotationText(),
                      }
                    : {}
            }
        />
    );
};

export default SemiStructuredContextMenuSelector;
