import {extendObservable} from 'mobx'
import {GlobalViewConfig} from "editor/config/ViewMetadataConfig";
import {Checkpointtable} from "editor/checkpoint/Checkpointtable";

class ViewStateStore extends Checkpointtable {
    constructor() {
        super();
        extendObservable(this, {
            editorStateUpdated: 1
        });
        this.currentTask = GlobalViewConfig.NER;
        this.defaultViewWidth = 1000;
        this.setTask = this.setTask.bind(this);
        this.getTaskDisplayOptions = this.getTaskDisplayOptions.bind(this);
    }

    setTask(task) {
        this.currentTask = task;
        this.editorStateUpdated += 1;
        console.log(task);
    }

    getTaskDisplayOptions() {
        const displayWidth = this.currentTask.displayWidth;
        if (!displayWidth) {
            return {width: displayWidth};
        }
        return {width: displayWidth};
    }

    checkpoint() {
        return {
            currentTaskId: this.currentTask.id
        }
    }

    resume(checkpoint) {
        const {currentTaskId} = checkpoint;
        const task = GlobalViewConfig.getTaskById(currentTaskId);
        this.setTask(task)
    }
}

export {ViewStateStore}