import {UFSchema} from "../../annotator/core/UFSchema";
import {GlobalViewConfig} from "../config/ViewMetadataConfig";
import {deepCopy} from "../../annotator/utils/DeepCopy";
import {RangeOverlapChecker} from "../../annotator/core/RangeOverlapChecker";
import {RangeMap} from "../../annotator/core/RangeMap";
import {get,sum} from 'lodash'


function combineEventArgumentTriggerAnnotations(currentDocument,
                                                injectNamespace = GlobalViewConfig.EVENT_ARGUMENTS_TRIGGERS.namespace) {
    // this is a specific method to combine events arguments/triggers annotations
    // all together in a unified way, and import to event_arguments_triggers

    // NOTED, currently this feels like a HACK, but no other place to put
    // it as well, need to reconsider in the future

    if (currentDocument[UFSchema.ANNOTATIONS] === undefined) {
        // Need to have an annotations object in the first place
        currentDocument[UFSchema.ANNOTATIONS] = {}
    }

    let triggerAnnotations = currentDocument[UFSchema.ANNOTATIONS][GlobalViewConfig.EVENT_TRIGGERS.namespace];
    let argumentsAnnotations = currentDocument[UFSchema.ANNOTATIONS][GlobalViewConfig.EVENT_ARGUMENTS.namespace];

    if (triggerAnnotations === undefined) {
        triggerAnnotations = [];
    } else {
        triggerAnnotations = deepCopy(triggerAnnotations);
    }

    if (argumentsAnnotations === undefined) {
        argumentsAnnotations = [];
    } else {
        argumentsAnnotations = deepCopy(argumentsAnnotations);
    }

    // if arguments overlaps with triggers, delete arguments
    // we assume trigger is more important in this case
    const triggerSpanOverlapChecker = new RangeOverlapChecker(triggerAnnotations);

    const triggerArgumentsAnnotations = [];

    triggerAnnotations.forEach((s) => {
        triggerArgumentsAnnotations.push(s);
    });
    argumentsAnnotations.forEach((s) => {
        if (triggerSpanOverlapChecker.overlap(s) === -1) {
            triggerArgumentsAnnotations.push(s);
        }
    });

    // modify/overwrite the events_arguments_triggers namespace
    currentDocument[UFSchema.ANNOTATIONS][injectNamespace] =
        triggerArgumentsAnnotations;
}


function separateEventArgumentTriggerAnnotations(annotations, schemaConfig,
                                                 injectNamespace = GlobalViewConfig.EVENT_ARGUMENTS_TRIGGERS.namespace) {
    const triggerTagSet = new Set(schemaConfig.EventTriggersTagSet);
    const triggerAnnotations = [];
    const argumentAnnotations = [];

    annotations[injectNamespace].forEach((s) => {
        const tag = s.tag;
        if (triggerTagSet.has(tag)) {
            triggerAnnotations.push(deepCopy(s));
        } else {
            argumentAnnotations.push(deepCopy(s));
        }
    });

    annotations[GlobalViewConfig.EVENT_ARGUMENTS.namespace] = argumentAnnotations;
    annotations[GlobalViewConfig.EVENT_TRIGGERS.namespace] = triggerAnnotations;

    return annotations;
}

function extractEventAnnotationSpans(documentObject, existingLockedSpanSet = new RangeMap()) {
    if (documentObject[UFSchema.ANNOTATIONS] === undefined) {
        // Need to have an annotations object in the first place
        documentObject[UFSchema.ANNOTATIONS] = {}
    }

    const annotations = documentObject[UFSchema.ANNOTATIONS];
    const spanLockReasons = [];
    const triggerAnnotations = annotations[GlobalViewConfig.EVENT_TRIGGERS.namespace];
    if (annotations[GlobalViewConfig.EVENTS.namespace] !== undefined) {
        const eventAnnotations = annotations[GlobalViewConfig.EVENTS.namespace];
        eventAnnotations.forEach((eventObject) => {
            const trigger = eventObject["trigger"];
            const eventArguments = eventObject["arguments"];
            // For locked triggers, indicate which arguments are associated with the trigger
            // Value is the count of associated arguments to this trigger
            const count = sum(eventArguments.map(arg => arg['values'].length))
            spanLockReasons.push({
                span: deepCopy(trigger),
                reason: `Trigger "${trigger.extent}" has ${count} associated argument(s)`
            });
            const triggerIndex = triggerAnnotations.findIndex(span => span.start === trigger.start && span.end === trigger.end);
            eventArguments.forEach((arg) => {
                const argValues = arg["values"];
                argValues.forEach((argV) => {
                    spanLockReasons.push({
                        span: deepCopy(argV),
                        reason: `Argument "${argV.extent}" is associated with trigger T${triggerIndex+1} (${trigger.extent})`
                    });
                })
            })
        });
    }
    spanLockReasons.forEach(spanLockReason => {
        const { span, reason } = spanLockReason;
        const lockReasons = get(existingLockedSpanSet.get(span),"lockReasons") || {};
        if (!(GlobalViewConfig.EVENTS.displayName in lockReasons)) {
            lockReasons[GlobalViewConfig.EVENTS.displayName] = [];
        }
        lockReasons[GlobalViewConfig.EVENTS.displayName].push(reason);
        existingLockedSpanSet.add(span, {...span, lockReasons});
    });
    return existingLockedSpanSet;
}

function extractCorefAnnotationSpans(documentObject, existingLockedSpanSet = new RangeMap()) {
    if (documentObject[UFSchema.ANNOTATIONS] === undefined) {
        // Need to have an annotations object in the first place
        documentObject[UFSchema.ANNOTATIONS] = {}
    }

    const annotations = documentObject[UFSchema.ANNOTATIONS];

    let triggerSpanLockReasons = getCorefLockReasons(annotations[GlobalViewConfig.EVENT_TRIGGERS_GROUPS.namespace]);
    let argumentSpanLockReasons = getCorefLockReasons(annotations[GlobalViewConfig.EVENT_ARGUMENTS_GROUPS.namespace]);

    triggerSpanLockReasons.forEach((spanLockReason) => {
        const { span, reason } = spanLockReason;
        const lockReasons = get(existingLockedSpanSet.get(span),"lockReasons") || {};
        if (!(GlobalViewConfig.EVENT_TRIGGERS_GROUPS.displayName in lockReasons)) {
            lockReasons[GlobalViewConfig.EVENT_TRIGGERS_GROUPS.displayName] = [];
        }
        lockReasons[GlobalViewConfig.EVENT_TRIGGERS_GROUPS.displayName].push(reason);
        existingLockedSpanSet.add(span, {...span, lockReasons});
    });
    argumentSpanLockReasons.forEach((spanLockReason) => {
        const { span, reason } = spanLockReason;
        const lockReasons = get(existingLockedSpanSet.get(span),"lockReasons") || {};
        if (!(GlobalViewConfig.EVENT_ARGUMENTS_GROUPS.displayName in lockReasons)) {
            lockReasons[GlobalViewConfig.EVENT_ARGUMENTS_GROUPS.displayName] = [];
        }
        lockReasons[GlobalViewConfig.EVENT_ARGUMENTS_GROUPS.displayName].push(reason);
        existingLockedSpanSet.add(span, {...span, lockReasons});
    });

    return existingLockedSpanSet;
}


function getCorefLockReasons(corefAnnotations) {
    if (!corefAnnotations) {
        return [];
    }
    const spanLockReasons = [];
    corefAnnotations["groups"].forEach((group,groupIndex) => {
        group["mentions"].forEach(span => {
            // Add to existing locked span
            spanLockReasons.push({
                span: deepCopy(span),
                reason: `Span "${span.extent}" is a member of group G${groupIndex}`
            })
        });
    });
    return spanLockReasons;
}


function extractAnnotationsV2AsSeparateDocument(document) {
    // document might comes with 2 version annotations
    // e.g. for arbitration purpose

    // this function extracts the v2 annotations, make it as if
    // it is a separate document with its own annotations
    const documentV2 = deepCopy(document);
    // overwrite documentV2's annotations field
    documentV2[UFSchema.ANNOTATIONS] = {};
    if (documentV2[UFSchema.ANNOTATIONS_V2] !== undefined) {
        documentV2[UFSchema.ANNOTATIONS] = documentV2[UFSchema.ANNOTATIONS_V2];
    } else if (documentV2[UFSchema.CONFLICTED_ANNOTATIONS_LIST] !== undefined) {
        // This list currently will have only 1 element
        documentV2[UFSchema.ANNOTATIONS] = documentV2[UFSchema.CONFLICTED_ANNOTATIONS_LIST][0];
    }
    // now delete the annotations_v2 field from DocumentV2
    delete documentV2[UFSchema.ANNOTATIONS_V2];
    delete documentV2[UFSchema.CONFLICTED_ANNOTATIONS_LIST];

    return documentV2;
}


export {
    combineEventArgumentTriggerAnnotations,
    separateEventArgumentTriggerAnnotations,
    extractEventAnnotationSpans,
    extractCorefAnnotationSpans,
    extractAnnotationsV2AsSeparateDocument
}
