import React, {Component} from 'react';
import {CardText, Col, Row} from 'reactstrap';
import {observer} from 'mobx-react'
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import {SingleLayerSpan} from "annotator/span/SingleLayerSpan";
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import {LightSelectTagColor, UnselectTagColor} from "../AnnotatorCommons";
import {SPAN_TYPE, TRIGGER_SPAN_TYPE} from "../store/EventsStateStore";
import {EventTriggerStatusSidePanel} from './EventTriggerStatusSidePanel'
import {EventArgumentSidePanel} from './EventArgumentSidePanel'
import {EventContextMenu} from './EventContextMenu'
import {EventCandidateArgumentsBar} from './EventCandidateArgumentsBar'
import {EventTriggerSelectionBar} from './EventTriggerSelectionBar'

const TriggerSpanColor = "#e4c4e6";
const V1ArgumentColor = "#a4dce6";
const V2ArgumentColor = "#ffb407";
const V1V2ArgumentColor = "#94ff26";
const ActiveTriggerSpanColor = "#ff9774";
const ActiveTriggerSpanBackground = "#4d3e69";
const ActiveTriggerSpanBackgroundV2 = "#761428";

const EVENT_ANNOTATOR_MENU_ID = "EventAnnotatorMenu";

const EventsAnnotator = observer(
    class EventsAnnotator extends Component {

        constructor(props) {
            super(props);
            this.getTagColor = this.getTagColor.bind(this);
            this.getArgumentMarkUp = this.getArgumentMarkUp.bind(this);
            this.annotatorTextAreaRef = React.createRef();
            this.state = {
                contextMenuSpan: null
            }
        }

        getTagColor(span) {
            const spanType = span.getAttribute(SPAN_TYPE);
            let color = UnselectTagColor;
            if (spanType === TRIGGER_SPAN_TYPE) {
                {
                    if (span.rangeEqual(this.props.store.states.activeTriggerSpan)) {
                        color = ActiveTriggerSpanColor;
                    } else {
                        color = TriggerSpanColor;
                    }
                }
            } else {
                if (span.rangeEqual(this.props.store.states.activeArgumentSpan)) {
                    color = LightSelectTagColor;
                } else if (span.attrs.v1 && !span.attrs.v2) {
                    // V1 only color
                    // this is an argument under current selected trigger
                    color = V1ArgumentColor;
                } else if (span.attrs.v1 && span.attrs.v2) {
                    // V1 and V2 color
                    color = V1V2ArgumentColor;
                } else if (!span.attrs.v1 && span.attrs.v2) {
                    color = V2ArgumentColor;
                }
                else {
                    color = UnselectTagColor;
                }
            }
            return color
        }

        getArgumentMarkUp(span) {
            if (span.attrs.blank) {
                return span.extent;
            }
            let markers = [];
            if (span.attrs.v1) {
                markers.push(<mark className="RichInnerTagStyle NoSelect"
                                   style={{
                                       "color": "white",
                                       "background": ActiveTriggerSpanBackground
                                   }}>
                        {"A" + span.attrs.idx}
                    </mark>
                )
            }
            if (span.attrs.v2) {
                markers.push(<mark className="RichInnerTagStyle NoSelect"
                                   style={{
                                       "color": "white",
                                       "background": ActiveTriggerSpanBackgroundV2,
                                   }}>
                        {"B" + span.attrs.idx_v2}
                    </mark>
                )

            }
            // this is one of the argument of the current event/trigger
            return <span>{span.extent}
                {markers}
            </span>

        }

        componentDidUpdate() {
            this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
        }

        componentDidMount() {
            this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
        }

        render() {
            console.log("event slot filling annotator rerender");
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const readOnly = this.props.store.isReadOnly();
            const frameWrapper = this.props.frameWrapper;
            const spans = this.props.store.spans;
            let tagSpans = [];

            for (let i = 0; i < spans.length; i++) {
                let spanObj = spans[i];
                let key = "span_" + i;
                let hoverOver = undefined;
                if (!spanObj.attrs.blank) {
                    hoverOver = <span className="HoverText">{spanObj.tag}</span>
                }
                const color = this.getTagColor(spanObj);
                const markup = this.getArgumentMarkUp(spanObj);
                tagSpans.push(<SingleLayerSpan key={key}
                                               text={markup}
                                               tag={spanObj.tag}
                                               color={color}
                                               start={spanObj.start}
                                               end={spanObj.end}
                                               blank={spanObj.attrs.blank}
                                               selectable={false}
                                               transparent={false}
                                               store={this.props.store}
                                               cssStyle={"HighlightSpan"}
                                               clickTrigger={() => {
                                                    this.props.store.toggleActiveSpan(spanObj);
                                               }}
                                               onMouseDown={
                                                   () => {
                                                       if (!spanObj.attrs.blank) {
                                                           this.setState({
                                                               contextMenuSpan: spanObj
                                                           });
                                                       }
                                                   }}
                                               rightClickMenuId={readOnly ? null : EVENT_ANNOTATOR_MENU_ID}
                                               hoverOver={hoverOver}/>);
            }
            let annotatorBody = <div id="EventsAnnotatorSelectionPanel">
                <EventTriggerSelectionBar store={this.props.store}
                                          activeTrigger={this.props.store.states.activeTriggerSpan}/>
                <EventCandidateArgumentsBar store={this.props.store}
                                            candidates={this.props.store.getCandidateArguments()}/>
                <div 
                    className="AnnotatorTextView card-body"
                    ref={this.annotatorTextAreaRef}
                    onScroll={
                        (e) => {
                            this.props.globalViewStateStore.setScrollPercentageFromElement(e.target);
                        }
                    }
                >
                    <AnnotatorTextArea textDirection={this.props.textDirection}
                                       textAlignment={this.props.textAlignment}>
                        <CardText className="AnnotatorText"
                                  markable="parent"
                                  id="tag-container">
                            {tagSpans}
                        </CardText>
                    </AnnotatorTextArea>
                </div>
                <EventContextMenu menuId={EVENT_ANNOTATOR_MENU_ID} store={this.props.store} span={this.state.contextMenuSpan} />
            </div>;
            // add frame around it
            if (frameWrapper) {
                annotatorBody = frameWrapper(annotatorBody);
            }

            return <div id="EventsAnnotatorBody">
                <Row>
                    <Col xl="6" id="EventsAnnotatorSelectionColumn">
                        {annotatorBody}
                    </Col>
                    <Col xl="3" id="EventsAnnotatorSideColumn">
                        <EventTriggerStatusSidePanel store={this.props.store} version={"A"}/>
                        <EventArgumentSidePanel store={this.props.store}/>
                    </Col>
                    <Col xl="3" id="EventsAnnotatorSideColumn"
                         style={{"display": this.props.store.showV2 ? "one" : "none"}}>
                        <EventTriggerStatusSidePanel store={this.props.store} v2={true} version={"B"}/>
                        <EventArgumentSidePanel store={this.props.store} v2={true}/>
                    </Col>

                </Row>
            </div>
        }
    }
);

export {EventsAnnotator, ActiveTriggerSpanColor}