import React from 'react';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';

class ErrorDisplayPanel extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log("init error -> ", this.props);
        //this.state.modal = true;
        const action = () => {
            this.props.callback();
        };
        return (
            <Modal isOpen={this.props.store.showError} toggle={action} style={{"minWidth":"600px"}}>
                <ModalHeader toggle={action} tag={"h3"}><b>Fatal Error: Invalid Document</b></ModalHeader>
                <ModalHeader tag={"h4"}>Why are we here</ModalHeader>
                <ModalBody>
                    <p>
                        If you are seeing this message, there are some possible reasons:
                        <ul>
                            <li>Illegal formatted JSON</li>
                            <li>Critical fields missing in the document, e.g raw_text, document_id...etc</li>
                            <li>Browser related incompatibilities</li>
                            <li>Internal errors</li>
                        </ul>
                        Please record <b>error message </b> with the corresponding <b>document json</b>, as well as your
                        current <b>OS/Browser(including versions)/ACEditor versions</b>,
                        and submit to the Comprehend Team for investigation
                    </p>
                </ModalBody>
                <ModalHeader tag={"h4"}>Error Message</ModalHeader>
                <ModalBody>
                    {this.props.error.message}
                </ModalBody>
                <ModalHeader tag={"h4"}>Document JSON</ModalHeader>
                <ModalBody>
                    <div style={{"overflowWrap": "break-word"}}>{JSON.stringify(this.props.document)} </div>
                </ModalBody>
                <ModalFooter>
                    <span>Press <b>Confirm</b> to save the current progress and <b>correct</b> the error in document, then <b>reload</b> ACEditor.</span>
                    <Button color="primary" onClick={action}>Confirm</Button>
                </ModalFooter>
            </Modal>
        );
    }
}

export default ErrorDisplayPanel;
