import React, {useCallback, useMemo} from 'react';
import {observer} from 'mobx-react';
import {ContextMenuSelector} from "../menu/ContextMenuSelector";
import uuid from 'uuid';
import {EventsStateStore} from "../store/EventsStateStore";
import {Span} from "../core/UFSchema";

const CONTEXT_MENU_REMOVE_FROM_ALL_TRIGGERS = 'REMOVE_FROM_ALL_TRIGGERS';
const CONTEXT_MENU_REMOVE_FROM_TRIGGER = 'REMOVE_FROM_TRIGGER';
const CONTEXT_MENU_NO_ACTION = 'NO_ACTION';


interface EventContextMenuProps {
    menuId: string;
    store: EventsStateStore;
    span: Span;
}

interface TriggerWithArgumentName {
    triggerSpan: Span;
    argumentName: string;
}

interface TriggerWithArgumentNameAndIndex extends TriggerWithArgumentName {
    triggerIndex: number;
}


interface EventMenuItemData {
    action: string;
    connectedTriggersWithArgumentName?: TriggerWithArgumentName[];
}

const EventContextMenu = observer((props: EventContextMenuProps) => {
    const { menuId, store, span } = props;
    // eslint-disable-next-line no-unused-vars
    // @ts-ignore
    const viewUpdateChange = store.viewUpdateChange; // Required for mobx state updates

    const {
        footerTags,
        footerItems,
        numItems
    } = useMemo(() => {
        const footerTags: JSX.Element[] = [];
        const footerItems: EventMenuItemData[] = [];
        let numItems = 0;
        if (span && span.attrs.spanType === "argument") {

            // @ts-ignore
            const triggerSpans: Span[] = store.states.triggerSpans;
            console.log("This state's trigger spans: ", triggerSpans);

            const connectedTriggersWithArgumentName: TriggerWithArgumentName[] = store.getTriggerSpansForArguments(span);

            if (!connectedTriggersWithArgumentName || connectedTriggersWithArgumentName.length === 0) {
                footerTags.push(<span key={uuid.v4()}>No Associated Trigger</span>);
                footerItems.push({action: CONTEXT_MENU_NO_ACTION});
                numItems++;

            } else {
                const connectedTriggersWithArgumentNameAndIndex: TriggerWithArgumentNameAndIndex[] = [];

                // Identify Trigger Index to show T1, T2 etc in UI
                connectedTriggersWithArgumentName.forEach((triggerAndArgumentName: TriggerWithArgumentName) => {
                    const triggerIndex = triggerSpans.findIndex(
                        (triggerSpan) => (
                            triggerAndArgumentName.triggerSpan.start === triggerSpan.start
                            && triggerAndArgumentName.triggerSpan.end === triggerSpan.end)
                    );
                    if (triggerIndex !== -1) {
                        connectedTriggersWithArgumentNameAndIndex.push({...triggerAndArgumentName, triggerIndex: triggerIndex});
                    }
                });

                connectedTriggersWithArgumentNameAndIndex.forEach((connectedTriggerAndArgumentName) => {
                    footerTags.push(<span
                        key={uuid.v4()}>Remove From Trigger T{connectedTriggerAndArgumentName.triggerIndex + 1} , {connectedTriggerAndArgumentName.argumentName}</span>);
                    footerItems.push({action: CONTEXT_MENU_REMOVE_FROM_TRIGGER, connectedTriggersWithArgumentName: [connectedTriggerAndArgumentName]});
                    numItems++;
                });

                footerTags.push(<span
                    key={uuid.v4()}>Remove From All Connected Triggers</span>);
                footerItems.push({action: CONTEXT_MENU_REMOVE_FROM_ALL_TRIGGERS, connectedTriggersWithArgumentName: connectedTriggersWithArgumentName});
                numItems++;
            }

        } else {
            footerTags.push(<span key={uuid.v4()}>No Action Available on Trigger Span</span>);
            footerItems.push({action: CONTEXT_MENU_NO_ACTION});
            numItems++;
        }

        return {
            footerTags,
            footerItems,
            numItems
        };
    }, [span]);

    /**
     * Moves a span to a new group via context menu / right click 
     */
    const contextMenuClickHandler = useCallback((item: EventMenuItemData) => {
        if (!span || !item) {
            return;
        }
        if (item.action === CONTEXT_MENU_NO_ACTION || !item.connectedTriggersWithArgumentName) {
            return;
        }

        if (item.action === CONTEXT_MENU_REMOVE_FROM_ALL_TRIGGERS) {
            // Add span to a new group
            item.connectedTriggersWithArgumentName.forEach((connectedTriggerAndArgumentName) => {
                store.removeEventArgumentFromTrigger(connectedTriggerAndArgumentName.argumentName, span, connectedTriggerAndArgumentName.triggerSpan)
            });
            return;
        }

        // Remove argument from a particular trigger
        const connectedTriggerWithArgumentName: TriggerWithArgumentName = item.connectedTriggersWithArgumentName[0];
        store.removeEventArgumentFromTrigger(connectedTriggerWithArgumentName.argumentName, span, connectedTriggerWithArgumentName.triggerSpan)


    }, [store, span]);

    return (<ContextMenuSelector 
        menuId={menuId}
        numItem={numItems}
        tags={[]}
        items={[]}
        footerTags={footerTags}
        footerItems={footerItems}
        clickHandler={contextMenuClickHandler}
    />);
});

export {EventContextMenu};