import {Validator, Violation} from "./Validator";
import {Namespaces} from "../core/Namespaces";
import {UFSchema} from "../core/UFSchema";

class EventsTriggerCoreferenceConsistencyValidator extends Validator {
    constructor() {
        super(Namespaces.EVENT_TRIGGERS_GROUPS);
    }

    validate(document) {
        if (!Validator.checkNamespaceExists(document, Namespaces.EVENT_TRIGGERS_GROUPS)) {
            // doesn't have trigger coreference annotation
            return [];
        }

        const triggerGroupAnnotations = document[UFSchema.ANNOTATIONS][Namespaces.EVENT_TRIGGERS_GROUPS];
        const violations = [];
        if (triggerGroupAnnotations["groups"] !== undefined) {
            triggerGroupAnnotations["groups"].forEach((g, groupIdx) => {
                const groupTagSet = new Set();
                g["mentions"].forEach((m) => {
                    groupTagSet.add(m.tag);
                });
                if (groupTagSet.size > 1) {
                    const groupTagSetJson = JSON.stringify(Array.from(groupTagSet));
                    violations.push(new Violation(document, this.namespace,
                        `Trigger Coreference group G${groupIdx} has multiple different trigger types ${groupTagSetJson}`))
                }
            })
        }
        return violations;
    }

}

export {EventsTriggerCoreferenceConsistencyValidator}