import React from 'react';
import './Customization.css'
import {observer} from "mobx-react";
import uuid from 'uuid'
import {TableRowSelectionStore} from "../table/TableRowSelectionStore";
import {TableRowSelector} from "../table/TableRowSelector";
import {
    Button,
    ButtonDropdown,
    Card, CardBody,
    CardFooter,
    CardHeader, CustomInput, DropdownItem,
    DropdownMenu,
    DropdownToggle, FormGroup, Input, Label,
} from "reactstrap";
import classnames from "classnames";
import {EntityGroupSelectionBar} from "./EntityGroupSelectionBar";
import {EntityGroupSelectionStore} from "./EntityGroupSelectioStore";
import {TextSubmitBar} from "./TextSubmitBar";
import {deepCopy} from "../../annotator/utils/DeepCopy";


const EventCustomizationPanel = observer(
    class EventCustomizationPanel extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                dropdownOpen: false,
                selected: undefined,
                optional: false,
                multiValues: false,
            };
            this.tableSelection = new TableRowSelectionStore();
            this.argumentSelectionStore = new EntityGroupSelectionStore();

            this.toggle = this.toggle.bind(this);
            this.getTriggerList = this.getTriggerList.bind(this);
            this.getCheckboxButton = this.getCheckboxButton.bind(this);
            this.submitNewEventArgument = this.submitNewEventArgument.bind(this);
            this.removeSelectedEventArguments = this.removeSelectedEventArguments.bind(this);
        }

        toggle() {
            this.setState({
                dropdownOpen: !this.state.dropdownOpen
            });
        }

        getTriggerList(schemaConfig) {
            const triggers = schemaConfig.EventTriggersTagSet;
            let triggerItems = [];
            triggers.forEach((t) => {
                triggerItems.push(<DropdownItem
                    onClick={(e) => {
                        this.setState({selected: t});
                        e.preventDefault();
                    }}>
                    <span className="EventTag">
                        {t}
                    </span>
                </DropdownItem>)
            });
            return triggerItems;
        };

        getCheckboxButton(label, onChangeHandler) {
            return <Button className="EventTag"
                           color="secondary">
                <CustomInput type="checkbox"
                             id={"exampleCustomCheckbox_" + uuid.v4()}
                             label={label}
                             onChange={onChangeHandler}/>

            </Button>
        }

        submitNewEventArgument(argumentName) {
            const schemaConfig = this.props.store.schemaConfig;
            const trigger = this.state.selected;
            const selectedArgumentTypes = deepCopy(this.argumentSelectionStore.entities);
            const optional = this.state.optional;
            const multiValues = this.state.multiValues;

            if (!this.state.selected) {
                this.props.store.alertStore.sendAlert("Invalid Operation",
                    "A event trigger must be selected before you can add argument to it. Got "
                    + this.state.selected + " instead");
            } else if (selectedArgumentTypes.length === 0) {
                this.props.store.alertStore.sendAlert("Invalid Event Argument",
                    "Argument types can't be empty.");

            } else {
                // we have a valid event argument to submit
                schemaConfig.addEventDefinitionArgument(trigger,
                    argumentName,
                    selectedArgumentTypes,
                    optional,
                    multiValues);
            }

            this.argumentSelectionStore.clear();
        }

        removeSelectedEventArguments(selectedTableIndices) {
            // convert Set to Array
            selectedTableIndices = Array.from(selectedTableIndices);
            const rootStore = this.props.store;
            const schemaConfig = rootStore.schemaConfig;
            const trigger = this.state.selected;
            const eventDef = schemaConfig.EventDefinitionsSet.get(trigger);
            if (eventDef) {
                const argumentList = eventDef.argumentList;
                const toDeleteArgumentList = selectedTableIndices.map((si) => {
                    return argumentList[si].argumentName;
                });
                schemaConfig.removeEventDefinitionArgument(trigger, toDeleteArgumentList);
            }
        }

        render() {
            const rootStore = this.props.store;
            const schemaConfig = rootStore.schemaConfig;
            // update this component when schemaConfig changes
            const subscribeSchemaConfigUpdate = schemaConfig.configUpdated;

            const triggerItems = this.getTriggerList(schemaConfig);
            let selectedTrigger = this.state.selected;

            if (!selectedTrigger && schemaConfig.EventTriggersTagSet.length > 0) {
                selectedTrigger = schemaConfig.EventTriggersTagSet[0];
                this.setState({selected: selectedTrigger});
            }

            const dropdownText = selectedTrigger === undefined ? "NONE" : selectedTrigger;

            const eventDefinition = schemaConfig.EventDefinitionsSet.get(selectedTrigger);

            let rows = [];

            if (eventDefinition) {
                const argumentList = eventDefinition.argumentList;
                argumentList.forEach((argument) => {
                    const {argumentName, argumentTypes, optional, multiValues} = argument;
                    let cols = [];
                    // order: name, types, optional, multiArgument
                    cols.push(argumentName);
                    let argTypesCol = [];
                    argumentTypes.forEach((t) => {
                        argTypesCol.push(<span>{t}</span>);
                        argTypesCol.push(<br/>);
                    });
                    cols.push(<p>{argTypesCol}</p>);
                    cols.push(optional.toString());
                    cols.push(multiValues.toString());
                    rows.push(cols);
                })
            }

            const forceReconstructTextSubmitBarKey = uuid.v4();
            return <div id="EventCustomizationPanel">
                <Card>
                    <CardHeader>
                        <Button className={classnames("TagButton")}
                                color="primary"
                                disabled
                                style={{
                                    "marginRight": "5px"
                                }}>
                            TRIGGERS
                        </Button>

                        <ButtonDropdown isOpen={this.state.dropdownOpen}
                                        toggle={this.toggle}>
                            <DropdownToggle caret
                                            className={classnames("TagButton")}
                                            color="info">
                                {dropdownText}
                            </DropdownToggle>
                            <DropdownMenu>
                                {triggerItems}
                            </DropdownMenu>
                        </ButtonDropdown>
                    </CardHeader>

                    <CardHeader>
                        <EntityGroupSelectionBar header={"ARGUMENT TYPES"}
                                                 entities={schemaConfig.EventArgumentsTagSet}
                                                 store={this.argumentSelectionStore}/>
                    </CardHeader>
                    <CardHeader>
                        {this.getCheckboxButton("OPTIONAL", () => {
                            this.setState({
                                optional: !this.state.optional
                            });
                        })}
                        {this.getCheckboxButton("MULTI VALUES", () => {
                            this.setState({
                                multiValues: !this.state.multiValues
                            });
                        })}
                    </CardHeader>

                    <CardHeader>
                        <TextSubmitBar store={rootStore}
                                       key={forceReconstructTextSubmitBarKey}
                                       default={"NEW_ARGUMENT_NAME"}
                                       onSubmit={this.submitNewEventArgument}/>
                    </CardHeader>


                    <CardBody className="TableContainer">
                        <TableRowSelector store={this.tableSelection}
                                          headers={["Argument Name", "Argument Types", "Optional", "Multi Values"]}
                                          rows={rows}/>
                    </CardBody>

                    <CardFooter>
                        <Button className="TagButton"
                                color="danger"
                                onClick={() => {
                                    rootStore.alertStore.sendAlert("Confirmation",
                                        "Are you sure to remove the selected event arguments ?",
                                        () => {
                                            this.removeSelectedEventArguments(this.tableSelection.selectedRowIds);
                                            this.tableSelection.clear();
                                        }, true);
                                }}>
                            REMOVE ARGUMENT
                        </Button>
                    </CardFooter>


                </Card>

            </div>
        }
    }
);

export {EventCustomizationPanel}