import React, {Component} from 'react';
import {Table} from "reactstrap";

class AnnotationViolationDisplayPanel extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const violations = this.props.violations;

        let tableHeaders = (<thead>
        <tr>
            <th>Document Id</th>
            <th>View</th>
            <th>Cause</th>
        </tr>
        </thead>);

        let tableRows = [];

        violations.forEach((v) => {
            const cell = v.cell;
            const {documentId, namespace, cause} = cell;
            tableRows.push(<tr>
                <td>{documentId}</td>
                <td>{namespace}</td>
                <td>{cause}</td>
            </tr>)
        });

        return <div id="DocumentViolationDisplayPanelArea"
                    style={{
                        "overflow": "scroll",
                        "maxHeight": "600px"
                    }}>
            <p style={{
                "fontSize": "20px"
            }}>Detected <b>{violations.length}</b> Violations
            </p>
            <Table bordered style={{"minWidth": "600px"}}>
                {tableHeaders}
                <tbody>
                {tableRows}
                </tbody>
            </Table>
        </div>
    }
}

export {AnnotationViolationDisplayPanel}