import React from 'react';
import {Button, Input, InputGroup, InputGroupAddon} from "reactstrap";
import 'annotator/AnnotatorButton.css'
import uuid from 'uuid';

class TextSubmitBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            text: 0,
        };
        this.submit = this.submit.bind(this);
    }

    submit() {
        try {
            if (this.state.text.length > 0) {
                if (this.state.text.indexOf(' ') > 0
                    || this.state.text.indexOf('\t') > 0
                    || this.state.text.indexOf('\n') > 0) {
                    this.props.store.alertStore.sendAlert("Invalid Tag", <p>
                        Contain whitespaces or empty. Please do not include special characters in tag name.
                    </p>);
                } else {
                    this.props.onSubmit(this.state.text);
                }
            }
        } finally {
            console.log("in finally forceUpdate");
            this.forceUpdate();
        }
    }

    render() {
        console.log("forced update");
        let width = this.props.width;
        let size = this.props.size;
        let buttonText = this.props.buttonText || "ADD";
        // fill in the defaults
        if (!width) {
            width = "350px";
        }
        if (!size) {
            size = "16px";
        }
        return <div id="TextSubmitBar"
                    style={{"maxWidth": width}}>
            <InputGroup>
                <Input placeholder={this.props.default}
                       onChange={(e) => {
                           this.setState({text: e.target.value})
                       }}/>
                <InputGroupAddon addonType="append">
                    <Button color="primary"
                            onClick={() => this.submit()}
                            style={{
                                "fontFamily": "Oswald",
                                "fontSize": size,
                                "fontWeight": "bold"
                            }}>{buttonText}</Button>
                </InputGroupAddon>
            </InputGroup>
        </div>
    }

}

export {TextSubmitBar}