import React, {Component} from "react";
import {States} from "annotator/store/RelationStateStore";
import {Button, ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";
import './RelationAnnotator.css'
import uuid from "uuid";

class RelationSelector extends Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            dropdownOpen: false
        };
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }


    render() {
        const {selectionState, store} = this.props;
        let relationButtons = [];
        let displaySelection = "none";
        const cancelHandler = (e) => {
            store.updateState({});
        };
        if (selectionState === States.ARG2) {
            // now two arguments are both ready
            const candidates = store.states.stateMachine.candidateRelations;
            if (candidates) {
                candidates.forEach((r) => {
                    const clickHandler = (e) => {
                        store.updateState({relationType: r});
                    };
                    const button = <DropdownItem id={"RelationSelector_" + uuid.v4()}
                                                 onClick={clickHandler}>
                        <span className="RelationSelectorDropdownText">{r.name}</span>
                    </DropdownItem>;
                    relationButtons.push(button);
                });
            } else {
                relationButtons.push(<DropdownItem id={"RelationSelector_Cancel"}
                                                   onClick={cancelHandler}>
                    <span className="RelationSelectorDropdownText">NO MATCH</span>
                </DropdownItem>)
            }
            displaySelection = "";
        }
        return <div id="RelationProgressPanel">
            <Button className="TagButton"
                    color="primary"
                    disabled>
                CANDIDATES
            </Button>
            <ButtonDropdown isOpen={this.state.dropdownOpen}
                            toggle={this.toggle}
                            style={{"display": displaySelection}}>
                <DropdownToggle caret
                                className="TagButton"
                                color="info">
                    {"SELECT"}
                </DropdownToggle>
                <DropdownMenu>
                    {relationButtons}
                </DropdownMenu>
            </ButtonDropdown>
            <Button id={"RelationSelector_Cancel"}
                    style={{"display": displaySelection}}
                    className="TagButton"
                    color="danger"
                    onClick={cancelHandler}>
                CANCEL
            </Button>
        </div>
    }
}

export {RelationSelector}