import React, {useState} from 'react';
import {Button} from 'reactstrap';
import ConfirmModal from '../notification/ConfirmModal';

const GroundTruthNavigationFooter = (props: {
    rootStore: any
}) => {
    const {rootStore} = props;
    const [showModal, setShowModal] = useState(false);
    const toggleConfirmationModal = () => setShowModal(!showModal);
    const submit = () => {
        rootStore.submitGroundTruth();
        toggleConfirmationModal();
    }
    return (
        <React.Fragment>
            <Button color="info" size="lg" className="FooterButton" onClick={toggleConfirmationModal}>SUBMIT</Button>
            <ConfirmModal 
                title="Are you sure you?" 
                body="Once you submit this document, you will no longer be able to make edits to to it. Do you want to continue?"
                showModal={showModal}
                onConfirm={submit}
                toggle={toggleConfirmationModal}
            />
        </React.Fragment>
    );
}

export {GroundTruthNavigationFooter};