import React, {Component} from 'react';
import {observer} from 'mobx-react'
import {FullEditorAnnotatorFactory} from "./AnnotatorFactory";
import {untracked} from "mobx";
import ErrorDisplayPanel from "./ErrorDisplayPanel";
import {FileIoManager} from "editor/utils/FileIoManager";
import uuid from "uuid";

const AnnotatorRouter = observer(
    class AnnotatorRouter extends Component {
        constructor(props) {
            super(props);
        }

        render() {
            // this updateSignal is every increasing number
            // it is here to force the container to update
            console.log("selector activated");
            const rootStore = this.props.store;
            // 1. subscribe to CollectionStateStore update signals
            // trigger when user navigate to different documents
            const subscribeCollectionUpdate = rootStore.collectionStore.updated;
            // 2. subscribe to EditorStateStore update signal
            // trigger when user switch views
            const subscribeEditorUpdate = rootStore.editorStateStore.editorStateUpdated;
            // 3. subscribe to SchemaConfig update signal
            // trigger when user switch views
            const subscribeSchemaConfigUpdate = rootStore.schemaConfig.configUpdated;

            const document = rootStore.collectionStore.getActiveDocument();

            // this is a debug hack
            window.component = this;

            //try {
            let component = null;
            untracked(() => {
                /*
                IMPORTANT NOTE:
                the untracked block here is VERY important
                many bugs with MobX happen because unexpected collection of dependencies
                In this particular case, we don't want AnnotatorContainer to react to any
                of the annotators' internal states, because if the internal state changes,
                AND the AnnotatorContainer reacts to it, it will effectively reset the Annotator itself
                which results VERY unintuitive bugs, and very hard to find.

                To solve this problem, untracked the annotator creation block, so there is no
                further need to worry about unexpected dependency collection
                */
                try {
                    component = FullEditorAnnotatorFactory.createAnnotator(rootStore, document);
                } catch (e) {
                    const failFastCallback = () => {
                        this.props.store.errorStore.showError = false;
                        const contents = this.props.store.saveProjectFile(true);
                        FileIoManager.downloadAsFile(contents, "save_your_current_project_status_" + uuid.v4() + ".json");
                    };
                    console.error("Error in AnnotatorRouter");
                    console.error(e);
                    this.props.store.errorStore.showError = true;
                    component = <ErrorDisplayPanel error={e}
                                                   store={this.props.store.errorStore}
                                                   callback={failFastCallback}
                                                   document={document}/>
                }
            });

            const {width} = rootStore.editorStateStore.getTaskDisplayOptions();

            return <div id="AnnotatorComponentArea"
                        className="AnnotatePageArea"
                        style={{"width": width}}>
                {component}
            </div>;
        }

    });

export {AnnotatorRouter}