import React, { Component } from 'react'
import { CollectionIndexHeader } from './CollectionIndexHeader'
import { CollectionNavigationFooter } from './CollectionNavigationFooter'
import { Button, Card, CardFooter, Collapse } from 'reactstrap'
import { CommentBox } from './CommentBox'
import { DocumentPropertyEditor } from './DocumentPropertyEditor'
import { GroundTruthNavigationFooter } from './GroundTruthNavigationFooter'
import styled from 'styled-components'

const HeaderActions = styled.div`
    display: flex;
    flex-direction: row-reverse;
    > span:last-child {
        margin-right: 20px;
    }
`

const FooterActions = styled.div`
    display: flex;
    flex-direction: row;
    > span:first-child {
        margin-right: 20px;
    }
`

class AnnotatorFrame extends Component {
    constructor(props) {
        super(props)
        this.state = {
            toggle: false,
            toggleProperty: false,
        }
        this.toggle = this.toggle.bind(this)
        this.toggleProperty = this.toggleProperty.bind(this)
    }

    toggle() {
        this.setState({ toggle: !this.state.toggle })
    }

    toggleProperty() {
        this.setState({ toggleProperty: !this.state.toggleProperty })
    }

    render() {
        const {
            index,
            total,
            rootStore,
            prev,
            next,
            reject,
            rejectStatus,
            comment,
            commentHandler,
            readOnly,
            taskName,
            propertyDataStore,
            propertyHandler: propertyHandler,
            footer = true,
        } = this.props

        const actions = rootStore.getUsingGroundTruth() ? (
            <GroundTruthNavigationFooter rootStore={rootStore} />
        ) : (
            <CollectionNavigationFooter
                onToggleComments={() => this.toggle()}
                prev={prev}
                next={next}
                reject={reject}
            />
        )

        console.log('AnnotatorFrame comment: ' + comment)
        return (
            <div id="AnnotatorFrame">
                <Card>
                    <CollectionIndexHeader
                        index={index}
                        total={total}
                        rootStore={rootStore}
                        rejectStatus={rejectStatus}
                        readOnly={readOnly}
                        taskName={taskName}
                    >
                        {!footer ? <HeaderActions>{actions}</HeaderActions> : null}
                    </CollectionIndexHeader>
                    {this.props.children}
                    <Collapse isOpen={this.state.toggle}>
                        <CommentBox comment={comment} commentHandler={commentHandler} />
                    </Collapse>
                    <DocumentPropertyEditor
                        open={this.state.toggleProperty}
                        store={propertyDataStore}
                        updateHandler={propertyHandler}
                    />

                    {footer ? (
                        <CardFooter>
                            <FooterActions>{actions}</FooterActions>{' '}
                            <Button
                                color="info"
                                size="lg"
                                className="FooterButton"
                                style={{ display: 'none' }}
                                onClick={() => {
                                    this.toggleProperty()
                                }}
                            >
                                PROPERTY
                            </Button>
                        </CardFooter>
                    ) : null}
                </Card>
            </div>
        )
    }
}

export { AnnotatorFrame }
