import React from 'react';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';
import {observer} from 'mobx-react';
import uuid from 'uuid';

const AlertModal = observer(
    class AlertModal extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            // subscribe to alertStore
            const susbscribeAlertStore = this.props.store.alertStore.open;

            const proceedCallback = () => {
                // close it first
                this.props.store.alertStore.open = false;
                this.props.store.alertStore.callback();
            };
            const shutdownCallback = () => {
                this.props.store.alertStore.open = false;
            };
            let buttons = [];
            buttons.push(<Button color="primary"
                                 key={"AlertModal_" + uuid.v4()}
                                 onClick={proceedCallback}>
                Confirm
            </Button>);
            if (this.props.store.alertStore.cancellable) {
                buttons.push(<Button color="secondary"
                                     key={"AlertModal_" + uuid.v4()}
                                     onClick={shutdownCallback}>Cancel</Button>)
            }
            return (
                <div id="AlertModal">
                    <Modal isOpen={this.props.store.alertStore.open}
                           toggle={shutdownCallback}
                           style={{"minWidth": "800px"}}
                           backdrop='static'>
                        <ModalHeader>
                            <span style={{
                                "fontWeight": 700,
                                "fontFamily": "Roboto",
                                "fontSize": "20px"
                            }}>{this.props.store.alertStore.title}</span>
                        </ModalHeader>
                        <ModalBody>
                            <div style={{
                                "fontFamily": "Roboto",
                                "fontSize": "18px"
                            }}>
                                {this.props.store.alertStore.message}
                            </div>
                        </ModalBody>
                        <ModalFooter>
                            {buttons}
                        </ModalFooter>
                    </Modal>
                </div>
            );
        }
    });

export default AlertModal