import React from "react";
import {Input, InputGroup, InputGroupAddon} from "reactstrap";
import {observer} from "mobx-react";

const AttributeKeyValueTextInput = observer(
    class AttributeKeyValueTextInput extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const subscribeParentChange = this.props.parent.parentChange;
            const parent = this.props.parent;
            const name = this.props.name;
            return <div style={{"maxWidth": "700px"}}>
                <InputGroup size="lg">
                    <InputGroupAddon addonType="prepend"
                                     style={{"fontFamily": "Roboto"}}>
                        {name.toUpperCase()}
                    </InputGroupAddon>
                    <Input value={parent.attributesContainer[name]}
                           onChange={(e) => {
                               parent.attributesContainer[name] = e.target.value;
                               parent.parentChange += 1;
                           }}/>
                </InputGroup>
            </div>
        }
    }
);

export {AttributeKeyValueTextInput}