import React, { Component } from 'react'
import 'annotator/AnnotatorButton.css'
import uuid from 'uuid'
import { Button, CardHeader } from 'reactstrap'
import TaskSelector from 'editor/sidepanel/TaskSelector'
import PageButtons from '../../annotator/tagging/pdf/components/PageButtons/PageButtons'

class CollectionIndexHeader extends Component {
    constructor(props) {
        super(props)
    }

    createDisplayButton(tag, index, total) {
        // assuming index is zero based
        const currentPos = index + 1 + '/' + total
        let prefix = tag.toUpperCase()
        const displayInfo = prefix + ' ' + currentPos
        return (
            <Button
                className="TagButton"
                key={'ProgressHeaderButton_' + uuid.v4()}
                size="sm"
                disabled
                color="success"
            >
                {displayInfo}
            </Button>
        )
    }

    render() {
        const { index, total, rootStore, rejectStatus, readOnly, taskName, children } = this.props
        let displayButtonGroup = []
        const documentProgressButton = this.createDisplayButton('document', index, total)
        displayButtonGroup.push(documentProgressButton)
        if (rejectStatus) {
            // add rejected status header
            displayButtonGroup.push(
                <Button
                    className="TagButton"
                    key={'ProgressHeaderButton_' + uuid.v4()}
                    size="sm"
                    disabled
                    style={{ marginLeft: '5px' }}
                    color="danger"
                >
                    REJECTED
                </Button>
            )
        }

        // If we're using groundtruth, display the labeling task changer here
        // If we're not using groundtruth, display the document number here
        return (
            <div id="ProgressHeader">
                <CardHeader className="d-flex flex-direction-row align-items-center justify-content-between">
                    {rootStore.getUsingGroundTruth() ? (
                        <div className="d-flex flex-direction-row align-items-center">
                            <span className="mr-2">Labeling Task: </span>
                            <TaskSelector store={rootStore} />
                        </div>
                    ) : (
                        <div>{displayButtonGroup}</div>
                    )}
                    <div>
                        {readOnly ? (
                            <div className="ml-2 text-lg text-danger font-weight-bold">
                                {taskName ? `"${taskName}"` : null} annotations are read-only
                            </div>
                        ) : null}
                        {children}
                    </div>
                    <PageButtons namespace={rootStore.editorStateStore.currentTask.namespace} />
                </CardHeader>
            </div>
        )
    }
}

export { CollectionIndexHeader }
