import React, {Component} from "react";
import './RelationAnnotator.css'
import 'annotator/AnnotatorButton.css'
import uuid from 'uuid';
import {SvgCrossIcon} from "annotator/svg/SvgCrossIcon";
import {SingleLayerSpan} from "annotator/span/SingleLayerSpan";
import {createRelationSpan} from "./RelationAnnotator";
import {SelectTagColor, UnselectTagColor, ActiveSpanColor} from "annotator/AnnotatorCommons";
import {RelationConnector} from "./RelationConnector";

class RelationVisualization extends Component {
    constructor(props) {
        super(props);
        this.getRelationArgSpan = this.getRelationArgSpan.bind(this);
    }

    getRelationArgSpan(spanObj, color, relation) {
        const spanMarkup = createRelationSpan(spanObj.extent, spanObj.tag);
        return <SingleLayerSpan key={"RelationDisplaySpan_TagSpan_" + uuid.v4()}
                                text={spanMarkup}
                                tag={spanObj.tag}
                                color={color}
                                start={spanObj.start}
                                end={spanObj.end}
                                blank={false}
                                selectable={false}
                                transparent={true}
                                cssStyle={"RelationHighlightSpan NoSelect"}
                                clickTrigger={() => {
                                    this.props.store.toggleRelation(relation);
                                }}/>;
    }

    render() {
        const relation = this.props.relation;
        const chooseArgColor = (arg) => {
            if (this.props.arg1 && this.props.arg1.rangeEqual(arg)) {
                return SelectTagColor;
            } else if (this.props.arg2 && this.props.arg2.rangeEqual(arg)) {
                return ActiveSpanColor;
            } else {
                return UnselectTagColor;
            }
        };
        let arg1Color = chooseArgColor(relation.arg1);
        let arg2Color = chooseArgColor(relation.arg2);

        const highlight = this.props.store.states.highlight;
        if (highlight !== undefined && highlight !== relation) {
            // highlight mode, only highlight one relation
            arg1Color = UnselectTagColor;
            arg2Color = UnselectTagColor;
        }
        return <div className="RelationVisualizationBox">
            <span>{this.props.id}.</span>
            {this.getRelationArgSpan(relation.arg1, arg1Color, relation)}
            <RelationConnector name={relation.relationType.name}/>
            {this.getRelationArgSpan(relation.arg2, arg2Color, relation)}
            <SvgCrossIcon clickTrigger={() => {
                this.props.store.removeRelation(relation);
            }}/>
        </div>

    }
}

export {RelationVisualization}