import {Checkpointtable} from "../checkpoint/Checkpointtable";
import {deepCopy} from "../../annotator/utils/DeepCopy";

class EventPropertiesConfig extends Checkpointtable {
    constructor() {
        super();

        this.properties = new Map();

        this.addEventProperty = this.addEventProperty.bind(this);
        this.addEventPropertyValue = this.addEventPropertyValue.bind(this);
        this.removeEventProperty = this.removeEventProperty.bind(this);
        this.removeEventPropertyValue = this.removeEventPropertyValue.bind(this);
    }

    addEventProperty(name) {
        if (this.properties.has(name)) {
            // already registered
            return
        }
        const values = [];
        this.properties.set(name, values);
    }

    addEventPropertyValue(name, value) {
        if (this.properties.has(name)) {
            const values = this.properties.get(name);
            if (values.indexOf(value) === -1) {
                // not found in the list, then add
                values.push(value);
            }
        }
    }

    removeEventProperty(name) {
        this.properties.delete(name);
    }

    removeEventPropertyValue(name, value) {
        if (this.properties.has(name)) {
            const values = this.properties.get(name);
            const idx = values.indexOf(value);
            if (idx >= 0) {
                values.splice(idx, 1);
            }
        }
    }

    checkpoint() {
        return {
            "properties": this.propertyList
        }
    }

    resume(checkpoint) {
        this.properties.clear();
        const propertyList = checkpoint["properties"];
        propertyList.forEach((property) => {
            const {name, values} = property;
            this.properties.set(name, values);
        });
    }

    get propertyList() {
        const propertyList = [];
        this.properties.forEach((values, name) => {
            propertyList.push({
                "name": name,
                "values": deepCopy(values)
            });
        });
        return propertyList;
    }
}

export {EventPropertiesConfig}