import React from 'react';
import './Customization.css'
import 'annotator/AnnotatorButton.css'
import {Button, Card, CardBody, CardFooter, CardHeader, Table} from "reactstrap";
import {observer} from "mobx-react";
import {TableRowSelectionStore} from "../table/TableRowSelectionStore";
import {TextSubmitBar} from "./TextSubmitBar";
import {TableRowSelector} from "../table/TableRowSelector";
import {EntityGroupSelectionBar} from "./EntityGroupSelectionBar";
import {EntityGroupSelectionStore} from "./EntityGroupSelectioStore";
import {deepCopy} from "annotator/utils/DeepCopy";
import uuid from 'uuid';

const RelationCustomizationPanel = observer(
    class RelationCustomizationPanel extends React.Component {
        constructor(props) {
            super(props);
            this.tableSelection = new TableRowSelectionStore();
            this.arg1SelectionGroup = new EntityGroupSelectionStore();
            this.arg2SelectionGroup = new EntityGroupSelectionStore();
            this.submitNewRelation = this.submitNewRelation.bind(this);
        }

        submitNewRelation(relationName) {
            const arg1Types = deepCopy(this.arg1SelectionGroup.entities);
            const arg2Types = deepCopy(this.arg2SelectionGroup.entities);
            if (arg1Types.length === 0 || arg2Types.length === 0) {
                this.props.store.alertStore.sendAlert("Invalid Relation",
                    "ARG1 or ARG2 entities cannot be empty")
            } else {
                this.props.store.schemaConfig.addRelation(relationName, arg1Types, arg2Types,
                    this.props.namespace);
                //reset panel
                this.arg1SelectionGroup.clear();
                this.arg2SelectionGroup.clear();
            }

        }

        render() {
            const rootStore = this.props.store;
            const namespace = this.props.namespace;
            const entityNamespace = this.props.entityNamespace;
            const schemaConfig = rootStore.schemaConfig;
            // trigger when schemaConfig changes
            const subscribeSchemaConfigUpdate = schemaConfig.configUpdated;

            const relationRows = [];
            const getArgTypeCol = (argTypes) => {
                if (typeof argTypes === typeof "") {
                    // only one argument entity
                    return <p><span>{argTypes}</span></p>;
                } else {
                    // list of arguments
                    let args = [];
                    argTypes.forEach((arg) => {
                        if (arg !== "ANY") {
                            // ANY is added as placeholder for fuzz match
                            // they are not meant to display to end users
                            args.push(<span>{arg}</span>);
                            args.push(<br/>)
                        }
                    });
                    args.pop();
                    return <p>{args}</p>
                }
            };

            const relationSetKey = schemaConfig.getTagSetKey(namespace);
            const entityTagSetKey = schemaConfig.getTagSetKey(entityNamespace);
            schemaConfig[relationSetKey].forEach((r) => {
                let [name, arg1Types, arg2Types] = r;
                const cols = [];
                cols.push(name);
                cols.push(getArgTypeCol(arg1Types));
                cols.push(getArgTypeCol(arg2Types));
                relationRows.push(cols);
            });

            // Give a new key to refresh TextSubmitBar after click
            const forceReconstructTextSubmitBarKey = uuid.v4();

            return <div id="NERCustomizationPanel">
                <Card>
                    <CardHeader>
                        <EntityGroupSelectionBar header={"ARG1"}
                                                 entities={schemaConfig[entityTagSetKey]}
                                                 store={this.arg1SelectionGroup}/>
                    </CardHeader>
                    <CardHeader>
                        <EntityGroupSelectionBar header={"ARG2"}
                                                 entities={schemaConfig[entityTagSetKey]}
                                                 store={this.arg2SelectionGroup}/>
                    </CardHeader>
                    <CardHeader>
                        <TextSubmitBar store={rootStore}
                                       key={forceReconstructTextSubmitBarKey}
                                       default={"NEW_RELATION"}
                                       onSubmit={this.submitNewRelation}/>
                    </CardHeader>
                    <CardBody className="TableContainer">
                        <TableRowSelector store={this.tableSelection}
                                          headers={["Relation Name", "ARG1 Entities", "ARG2 Entities"]}
                                          rows={relationRows}/>
                    </CardBody>
                    <CardFooter>
                        <Button className="TagButton"
                                color="danger" onClick={() => {
                            schemaConfig.batchRemoveRelations(this.tableSelection.selectedRowIds,
                                this.props.namespace);
                            this.tableSelection.clear();
                        }}>REMOVE</Button>
                    </CardFooter>
                </Card>
            </div>
        }
    }
);

export {RelationCustomizationPanel}