import React, {Component} from 'react';
import {Progress} from "reactstrap";
import {observer} from 'mobx-react'
import 'editor/sidepanel/EditorSidePanel.css';


const ProgressPanel = observer(
    class ProgressPanel extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const updated = this.props.store.progressStore.updated;
            const progressStore = this.props.store.progressStore;
            const total = progressStore.totalDoc;
            const maxRead = progressStore.maxRead.size;
            // console.log("ProgressPanel rendered ->", maxRead);
            let ratio = 1.0;
            if (total === 0) {
                ratio = 0.0;
            } else {
                ratio = Math.round((maxRead.toFixed(5) / total) * 100);
            }

            const cursor = maxRead + "/" + total;

            return <div>
                <Progress color="warning" value={ratio}/>
                <div className="TaskLabelContainer"
                     style={{"marginTop": "5px"}}>
                    <div className="TaskLabel">COMPLETED</div>
                    {cursor}
                </div>
            </div>
        }
    });

export default ProgressPanel