import React, {Component} from 'react';
import {Button, FormGroup, Input, InputGroup, InputGroupAddon, Label} from "reactstrap";
import {NavigationChoicePanel} from "./NavigationChoicePanel";


class NavigatePanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            navDoc: 0,
            mode: 0
        };

        this.navigate = this.navigate.bind(this);
    }

    navigate() {
        let id = this.state.navDoc;
        const navBySeqIdx = () => {
            let numId = parseInt(id);
            if (!isNaN(id)) {
                // we need to subtract 1 here, since the document id
                // shown here starts from 1
                this.props.store.navigateToDocumentBySequenceIdx(numId - 1);
            }
        };
        const navByDocumentId = () => {
            this.props.store.navigateToDocumentByDocumentId(id);
        };
        // this is hack, to have the internal state expose to external component
        const externalStore = {"mode": 0};
        this.props.store.sendAlert(
            <span><b>{"Please Confirm".toUpperCase()}</b></span>,
            <div id="NavigationChoicePanel">
                <NavigationChoicePanel store={externalStore}/>
            </div>,
            () => {
                console.log("externalStore mode", externalStore.mode);
                if (externalStore.mode === 0) {
                    navBySeqIdx();
                } else {
                    navByDocumentId();
                }
            },
            false
        )
    }

    render() {
        return <div>
            <InputGroup>
                <Input placeholder="Document Id"
                       onChange={(e) => {
                           //console.log(typeof e.target.value);
                           this.setState({navDoc: e.target.value})
                       }}
                       onKeyPress={(e) => {
                           if (e.key === 'Enter') {
                               this.navigate()
                           }
                       }}/>
                <InputGroupAddon addonType="append">
                    <Button color="warning"
                            onClick={this.navigate}>Go</Button>
                </InputGroupAddon>
            </InputGroup>
        </div>

    }
}

export default NavigatePanel;