import React, {Component} from 'react';
import {Button, CardBody, CardHeader, CardText} from 'reactstrap';
import {observer} from 'mobx-react'
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import SentimentTagGroup from "./SentimentTagGroup";
import {SentimentTagColor} from "./SentimentTagConfig";
import SentimentTagSpan from "./SentimentTagSpan";
import {AnnotatorTextArea} from "../AnnotatorTextArea";

const SentimentAnnotator = observer(
    class SentimentAnnotator extends Component {

        constructor(props) {
            super(props);
        }

        render() {
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const spans = this.props.store.spans;
            const selectedTag = this.props.store.tagGroupStore.selectedTag;

            let tagSpans = [];
            let i = 0;
            console.log("in sentiment annotator");
            console.log(spans);
            spans.forEach((s) => {
                //console.log("s");
                //console.log(s);
                tagSpans.push(<SentimentTagSpan idx={i}
                                                toggle={false}
                                                color={SentimentTagColor[s.tag]}
                                                start={s.start}
                                                end={s.end}
                                                tag={s.tag}
                                                sarcastic={s.attrs.sarcastic}
                                                blank={s.attrs.blank}
                                                extent={s.extent}
                                                active={this.props.store.states.activeSpan}
                                                store={this.props.store}
                                                key={i}/>);
                i += 1;
            });
            return (
                <div id="SentimentAnnotatorBody">
                    <CardBody className="AnnotatorTextView">
                        <AnnotatorTextArea textDirection={this.props.textDirection}
                                           textAlignment={this.props.textAlignment}>
                            <CardText className="AnnotatorText"
                                      markable="parent"
                                      id="tag-container"
                                      style={{"lineHeight": "2.8", display: 'inline-block'}}
                                      tag='div'>
                                {tagSpans}
                            </CardText>
                        </AnnotatorTextArea>
                    </CardBody>
                    <CardHeader className="FlexHeader">
                        <Button disabled
                                color="warning"
                                size="lg"
                                style={{
                                    "fontFamily": "'Oswald'",
                                    "fontWeight": "700",
                                    "fontSize": "18px",
                                    "marginRight": "50px"
                                }}>
                            FULL DOC EVALUATION: {selectedTag}
                        </Button>

                        <SentimentTagGroup className="TagGroupSection"
                                           store={this.props.store}/>

                    </CardHeader>

                </div>
            );
        }
    }
);

export {SentimentAnnotator}