import {extendObservable} from 'mobx'

class TagGroupStore {
    constructor(tags, ssieProps) {
        this.tags = tags;
        this.numTags = tags.length;
        this.tagMap = new Map();
        // init a tag set
        let idx = 0;
        tags.forEach((t) => {
            // k is index, v is actual tag
            // reverse
            this.tagMap.set(t, idx);
            idx += 1;
        });
        this.selectedTagIndex = 0;
        this.selectedTag = this.tags[0];
        this.updateSelectTag = this.updateSelectTag.bind(this);
        this.updateSelectTagByIndex = this.updateSelectTagByIndex.bind(this);
        this.markAsNoTagSelected = this.markAsNoTagSelected.bind(this);

        extendObservable(this, {
            selectedTag: this.selectedTag,
            selectedTagIndex: this.selectedTagIndex
        });
    }

    updateSelectTagByIndex(tagIndex) {
        if (tagIndex >= 0 && tagIndex < this.numTags) {
            this.selectedTagIndex = tagIndex;
            this.selectedTag = this.tags[tagIndex.toString()];

            if (this.ssieProps) {
                this.ssieProps.setSelectedTag(this.selectedTag);
            }
        }
    }

    updateSelectTag(tag) {
        if (this.tagMap.has(tag)) {
            const tagIdx = this.tagMap.get(tag);
            this.updateSelectTagByIndex(tagIdx);
        }
    }

    getTagName(tagIndex) {
        if (tagIndex >= 0 && tagIndex < this.numTags) {
            return this.tags[tagIndex.toString()];
        }
    }

    markAsNoTagSelected() {
        this.selectedTagIndex = -1;
        this.selectedTag = undefined;

        if (this.ssieProps) {
            this.ssieProps.setSelectedTag(this.selectedTag);
        }
    }

    setSsieProps(ssieProps) {
        this.ssieProps = ssieProps;
    }
}

export {TagGroupStore}