import React, {useEffect, useCallback, useState, useRef, useMemo} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFileUpload } from '@fortawesome/free-solid-svg-icons'
import {EntityLinkingStateStore} from "../store/EntityLinkingStateStore";
import {DatabaseCandidateRecordType, EntityLinkingSpanType} from "../types/EntityLinkingTypes";
import {SpanContainer} from "../core/Spans";
import {
    Button, DropdownToggle, CardHeader, DropdownItem,
    DropdownMenu,  InputGroup, InputGroupAddon, Input, InputGroupButtonDropdown, Label
} from "reactstrap";

import {observer} from 'mobx-react';
import styled from 'styled-components';
import { DatabaseCandidateStore } from '../store/DatabaseCandidateStore';
import DatabaseUploader from './DatabaseUploader';
import DatabaseCandidateGrid from './DatabaseCandidateGrid';

import { debounce } from 'lodash';
import 'react-virtualized/styles.css';

const TABLE_HEADER_HEIGHT = 38;
const TABLE_PAGINATION_HEIGHT = TABLE_HEADER_HEIGHT;

const DatabaseCandidateRecordSelectorContainer = styled.div`
    width:100%;
    height:100%;
    .card {
        height:100%;
        .card-header {
            display:flex;
            flex-direction:row;
            justify-content:space-between;
        }
    }
`;

const UploadButtonContainer = styled.div`
    position: relative;
    overflow: hidden;
    display: inline-block;
    button {
        border-top-right-radius:0;
        border-bottom-right-radius:0;
    }

    input[type=file] {
        display: inline-block;
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0;
        width:38px;
        height:38px;
        filter: alpha(opacity=0);
        cursor: pointer;
    }
`;

const FilterComponent = observer((props: { 
    filterText: string;
    onFilter: (s: string) => void;
    onClear: () => void;
    dbCandidateStore: DatabaseCandidateStore;
    searchAllCandidates: boolean;
    setSearchAllCandidates: (value: boolean) => void;
}) => {
    const {
        searchAllCandidates, setSearchAllCandidates, filterText, onFilter, onClear, dbCandidateStore
    } = props;

    const [localFilterText, setLocalFilterText] = useState(filterText);
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const debouncedOnFilter = useRef(debounce(onFilter, 500)).current;
    useEffect(() => {
        if (filterText === localFilterText) {
            return;
        }
        setLocalFilterText(filterText);
    }, [filterText]);

    const setText = useCallback((value) => {
        setLocalFilterText(value);
        debouncedOnFilter(value);
    }, []);

    const toggleDropDown = () => setDropdownOpen(!dropdownOpen);
    const uploadDatabaseCandidates = (e: React.ChangeEvent<HTMLInputElement>) => {
        const files = e.target.files;
        if (!files || files?.length === 0) {
            return;
        }
        try {
            dbCandidateStore.loadCandidatesFromFile(files[0]);
        } catch (err) {
            alert(err);
        }
    }
    return (
        <div className="d-flex align-items-center">
            <InputGroup>
                <UploadButtonContainer>
                    <Button color="info" onClick={onClear} id="DBUploadTooltip" title="Upload database candidates .jsonl file"><FontAwesomeIcon icon={faFileUpload} /></Button>
                    <input type="file" name="dbfile" title="Upload database candidates .jsonl file" onChange={uploadDatabaseCandidates} />
                </UploadButtonContainer>
                <InputGroupButtonDropdown addonType="prepend" isOpen={dropdownOpen} toggle={toggleDropDown}>
                    <DropdownToggle color="primary" caret>
                        {
                            searchAllCandidates ? 'All' : 'Recommended'
                        }
                    </DropdownToggle>
                    <DropdownMenu>
                        <DropdownItem onClick={() => setSearchAllCandidates(false)}>Search Recommended Candidates</DropdownItem>
                        <DropdownItem onClick={() => setSearchAllCandidates(true)}>Search All Candidates</DropdownItem>
                    </DropdownMenu>
                </InputGroupButtonDropdown>
                <Input placeholder="Filter candidates" value={localFilterText} onChange={(e) => {
                    setText(e.currentTarget.value);
                }} />
                <InputGroupAddon addonType="append"><Button color="secondary" onClick={onClear}>X</Button></InputGroupAddon>
            </InputGroup>
        </div>
    );
});


const NoDataComponent = (props: {
    annotations: EntityLinkingSpanType | null;
    filename: string;
}) => {
    const { annotations, filename } = props;
    // If there are database records but still no data in the table
    // show a message depending on if the user has a span selected or not.
    if (annotations) {
        return  (<div className="p-3">
            No candidates found
        </div>);
    }
    return (
        <div className="p-3 d-flex flex-column align-items-center">
            <div className="mb-3 text-muted">  
                Using database "{filename}"
            </div>
            <div>
                No Selected Span
            </div>
        </div>
    );
};

const DatabaseCandidateRecordSelector = observer((props: {
    activeSpan: SpanContainer | undefined | null,
    store: EntityLinkingStateStore,
    dbCandidateStore: DatabaseCandidateStore
}) => {
    const {store, dbCandidateStore, activeSpan} = props;
    const containerRef = useRef<HTMLDivElement>(null);
    const [width, setWidth] = useState(0);
    const [height, setHeight] = useState(0);
    const [filterText, setFilterText] = useState('');
    const [searchAllCandidates, setSearchAllCandidates] = useState(false);

    // @ts-ignore
    const viewUpdateChange = store.viewUpdateChange;

    const allRecords: DatabaseCandidateRecordType[] = dbCandidateStore.retrieveCandidates();
    const recommendedRecords = activeSpan ? 
        dbCandidateStore.retrieveCandidates(store.getSpanDatabaseCandidateIds(activeSpan)) : [];

    // Reset text filter if the active span changes
    useEffect(() => {
        setFilterText('');
    },[activeSpan]);

    useEffect(() => {
        if (!containerRef || !containerRef.current) {
            return;
        }
        const el = containerRef.current;
        const updateWindowDimensions = () => {
            setWidth(el.offsetWidth);
            setHeight(el.offsetHeight);
        }
        updateWindowDimensions();
        window.addEventListener("resize", updateWindowDimensions);

        return () => window.removeEventListener("resize", updateWindowDimensions) 

    }, [containerRef, store, activeSpan]);

    const annotations = activeSpan ? store.getSpanAnnotations(activeSpan) || null : null;

    const selectedRecordId = annotations ? annotations.gold_id : undefined;
    
    const columns: string[] = useMemo(() => {
        let columns: string[] = [];
        if (!activeSpan || !annotations) {
            return columns;
        }
        if (allRecords.length > 0) {
            const sample = allRecords[0];
            const {attributes} = sample;
            columns = Object.keys(attributes);
            columns.push('ID');
        }
        return columns;
    }, [activeSpan, annotations]);

    // Local span attributes
    let notSure = false;
    let noRelated = false;
    let goldId: string | undefined = undefined;
    if (activeSpan && annotations) {
        const {gold_id, properties} = annotations;
        notSure = properties!.not_sure;
        noRelated = properties!.no_related;
        goldId = gold_id;
    }

    const data: DatabaseCandidateRecordType[] = useMemo(() => {
        const localAnnotations = activeSpan ? store.getSpanAnnotations(activeSpan) || null : null;

        if (!activeSpan || !localAnnotations) {
            return [];
        }
        let records = searchAllCandidates ? allRecords : recommendedRecords;
        const localData: DatabaseCandidateRecordType[] = [];
        
        records.forEach((r) => {
            // Filter by text 
            if (filterText) {
                // Primitive search: concatinates all values in our data row together and 
                // check to see if the filterText is an exact match anywhere in the combined text string
                const text = [r.id, ...Object.values(r.attributes)].join(' ');
                if (!text.toLowerCase().includes(filterText.toLowerCase())) {
                    return;
                }
            }
            localData.push(r);
        });
        return localData;
    }, [searchAllCandidates, allRecords, recommendedRecords, activeSpan, filterText, store, goldId]);

    const onSelectRecord = (recordId: string | undefined) => {
        if (!activeSpan) {
            return;
        }
        store.updateSpanAnnotations(activeSpan, {
            not_sure: false,
            no_related: false,
            gold_id: recordId
        });
    }


    return <DatabaseCandidateRecordSelectorContainer>
        <div className="card">
            <CardHeader>
                <div className="d-flex flex-direction-row">
                    <Button color="primary" disabled={true} className="TagButton" size="sm">
                        {data.length > 0 ? data.length : 'NO'} CANDIDATES
                    </Button>
                    <div style={{
                        "paddingLeft": "20px", "fontSize": "1em", "fontWeight": "bold",
                        "alignItems": "center",
                        "display": activeSpan ? "inherit" : "none"
                    }}>
                        <Label check style={{"marginRight": "20px"}}>
                            <input type="checkbox"
                                checked={noRelated} onChange={(e) => {
                                store.updateSpanAnnotations(activeSpan!, {
                                    no_related: !noRelated,
                                    not_sure: false,
                                    gold_id: undefined
                                })
                            }
                            }/>{' '}
                            No Related
                        </Label>

                        <Label check>
                            <input type="checkbox" checked={notSure} onChange={(e) => {
                                store.updateSpanAnnotations(activeSpan!, {
                                    not_sure: !notSure,
                                    no_related: false,
                                    gold_id: undefined
                                })
                            }}/>{' '}
                            Not Sure
                        </Label>

                    </div>
                </div>
                <div className="d-flex align-items-center">
                    <FilterComponent
                        filterText={filterText}
                        onClear={() => setFilterText('')}
                        onFilter={setFilterText}
                        dbCandidateStore={dbCandidateStore}
                        searchAllCandidates={searchAllCandidates}
                        setSearchAllCandidates={setSearchAllCandidates}
                    />
                </div>
            </CardHeader>
            <div className="card-body p-0 d-flex justify-content-center " ref={containerRef}>
                {
                    allRecords.length === 0 ? (
                        <DatabaseUploader height={height} dbCandidateStore={dbCandidateStore}/>
                    ) : ( data.length === 0 ? (
                        <NoDataComponent annotations={annotations} filename={dbCandidateStore.getFilename()} />
                    ) : (
                        <DatabaseCandidateGrid 
                            columns={columns}
                            rows={data}
                            height={height}
                            width={width}
                            columnWidth={300}
                            rowHeight={32}
                            onSelectRecord={onSelectRecord}
                            selectedRecordId={selectedRecordId}
                        />
                    ))
                }

            </div>
        </div>
    </DatabaseCandidateRecordSelectorContainer>
});

export {DatabaseCandidateRecordSelector};