import React from 'react';
import { Button } from 'reactstrap';
import { observer } from 'mobx-react';
import 'annotator/AnnotatorButton.css';
import 'annotator/Annotator.css';
import { Namespaces } from '../core/Namespaces';

const NERTagGroup = observer(
    class NERTagGroup extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            let buttons = [];
            let { store, disabled } = this.props;
            let numberTags = store.tagGroupStore.numTags;
            for (let i = 0; i < numberTags; i++) {
                let tagName = store.tagGroupStore.getTagName(i);
                // pass a closure to capture
                // the button's local change
                let onClickEvent = function () {
                    this.props.store.updateActiveTag(tagName);
                    if (this.props.store.schemaKey === Namespaces.SSIE) {
                        this.props.updateSelectedSsieAnnotationsProperties(tagName);
                    }
                };
                if (this.props.selectedTag === tagName) {
                    buttons.push(
                        <Button
                            key={'button_' + i}
                            className="TagButton"
                            color="primary"
                            size="sm"
                            disabled={disabled}
                            style={{ margin: '2px' }}
                            onClick={onClickEvent.bind(this)}
                        >
                            {tagName}
                        </Button>
                    );
                } else {
                    buttons.push(
                        <Button
                            key={'button_' + i}
                            className="TagButton"
                            color="info"
                            size="sm"
                            disabled={disabled}
                            style={{ margin: '2px' }}
                            onClick={onClickEvent.bind(this)}
                        >
                            {tagName}
                        </Button>
                    );
                }
            }

            return <div className="TagGroup">{buttons}</div>;
        }
    }
);

export default NERTagGroup;
