import React from 'react';
import {PropertiesConfig} from "./PropertiesConfig";
import {RemovableTag} from "./RemovableTag";
import {TextSubmitBar} from "../customization/TextSubmitBar";
import {BasePropertyType} from "../../annotator/types/BasePropertyType";
import {randomElementKey} from "../../annotator/utils/Utils";


type PropType = {
    property: BasePropertyType,
    namespace: string
    propertiesConfig: PropertiesConfig
}


class ValuesEditComponent extends React.Component<PropType> {

    constructor(props: PropType) {
        super(props);
    }

    render() {
        const {property, namespace, propertiesConfig} = this.props;
        const values = property.values!;

        const currentValues: any = [];
        values.forEach((v: string) => {
            const removeValueHandler = () => {
                const sp = propertiesConfig.copySpanProperty(property);
                sp.values = sp.values!.filter((v2) => v2 !== v);
                propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
            };
            currentValues.push(<RemovableTag text={v}
                                             color={"primary"}
                                             removeHandler={removeValueHandler}/>)
        });

        const addValueHandler = (v: any) => {
            const sp = propertiesConfig.copySpanProperty(property);
            if (sp.values!.indexOf(v) === -1) {
                // v doesn't exist in this property yet
                sp.values!.push(v);
                propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
            }
        };

        const forceReconstructTextSubmitBarKey = randomElementKey();

        const addNewValueBar = <TextSubmitBar key={forceReconstructTextSubmitBarKey}
                                              width="150px"
                                              size="14px"
                                              onSubmit={addValueHandler}/>;

        return <div style={{"display": "inline-block"}}>
            <div>
                {currentValues}
            </div>
            <div>
                {addNewValueBar}
            </div>
        </div>
    }
}

export {ValuesEditComponent}