import React, { useState} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSpinner } from '@fortawesome/free-solid-svg-icons'
import {observer} from 'mobx-react';
import {useDropzone} from 'react-dropzone'
import styled from 'styled-components';
import {lighten} from 'polished';

import { AppTheme } from '../../styles/theme';
import { DatabaseCandidateStore } from '../store/DatabaseCandidateStore';

interface DatabaseUploaderContainerProps {
    height: number;
    isDragActive: boolean;
    hasError: boolean;
    theme: AppTheme;
}

const DatabaseUploaderContainer = styled.div`
    align-items:center; 
    justify-content:center;
    display:flex;
    width:100%;
    height: calc(${(p: DatabaseUploaderContainerProps) => p.height}px - 32px);
    margin: 16px;
    padding: 16px;
    border-radius:5px;
    border 1px dashed ${(p: DatabaseUploaderContainerProps) => p.isDragActive ? lighten(.15,p.theme.success) : lighten(.25,p.theme.secondary)};
    background: ${ 
        (p: DatabaseUploaderContainerProps) => 
            p.hasError ? lighten(.375,p.theme.danger) : (
                p.isDragActive ? lighten(.55,p.theme.success) : ( 'inherit' )
            )
    };
    color: #aaa;
`;

const DatabaseUploader = observer((props: {
    height: number;
    dbCandidateStore: DatabaseCandidateStore;
}) => {
    const {height, dbCandidateStore} = props;
    const [ dropErrorMessage, setDropErrorMessage ] = useState('');
    const isLoading = dbCandidateStore.getIsLoading();
    const filename = dbCandidateStore.getFilename();
    const fileParsingErrorMessage = dbCandidateStore.getErrorMessage();

    const onDrop = (acceptedFiles:File[]) => {
        setDropErrorMessage('');
        if (acceptedFiles.length === 0) {
            return;
        }
        try {
            dbCandidateStore.loadCandidatesFromFile(acceptedFiles[0]);
        } catch (err) {
            setDropErrorMessage(err);
        }
    }
    const { isDragAccept, isDragReject, getRootProps, getInputProps, isDragActive} = useDropzone({
        onDrop
    });
    return (
        <DatabaseUploaderContainer 
            height={height} {...getRootProps()} 
            isDragActive={isDragActive} 
            hasError={dropErrorMessage.length > 0}
        >
            <input {...getInputProps()} />
            {
                fileParsingErrorMessage ? (<span className="text-danger">{fileParsingErrorMessage}</span>) : (
                    dropErrorMessage ? <span className="text-danger">{dropErrorMessage}</span> : (
                        <span className={
                            isDragAccept || isLoading ? 'text-success' : ''
                        }>{
                            (isLoading) ? <span><FontAwesomeIcon icon={faSpinner} spin /> Parsing file "{filename}"...</span> : 'Drag or click here to upload a .JSONL database candidates file'
                        }
                        </span>
                    )
                )
            }    
        </DatabaseUploaderContainer>
    );
});

export default DatabaseUploader;