import React, {useState} from 'react';
import {Button, Card, CardHeader} from "reactstrap";
import {PropertyEditor} from "./PropertyEditor";
import {BaseSpanType} from "../types/CoreTypes";
import {PropertyDataStore} from "../store/PropertyDataStore";
import {PropertyTargetType} from "../types/BasePropertyType";


export const PropertyEditorContainer = (props: {
    target: BaseSpanType | SSIEAnnotationTypeForProperties | "document",
    targetType: PropertyTargetType,
    store: PropertyDataStore,
    // notify outer component for the change
    // use case like, when PropertyDataStore changes, notify outer DocumentDataStore
    // to react to this;
    broadcast?: (store: PropertyDataStore) => void;
}) => {
    const {target, targetType, store, broadcast} = props;
    const [showSpanOrBlock, setShowSpanOrBlock] = useState(true);
    let banner = showSpanOrBlock ? (targetType == PropertyTargetType.TextSpan ? "SPAN" : "BLOCK") : "DOCUMENT";
    let updatedTargetType: PropertyTargetType = targetType; // Need this additional variable because for document level properties, external variable is not passed in to the PropertyEditorContainer
    const toggle = () => {
        setShowSpanOrBlock(!showSpanOrBlock);
        if (!showSpanOrBlock) {
            updatedTargetType = PropertyTargetType.Document;
        } else {
            updatedTargetType = targetType;
        }
    }

    let editorTarget: BaseSpanType | SSIEAnnotationTypeForProperties | "document" =
        showSpanOrBlock ? target : "document";
    return <Card>
        <CardHeader>
            <Button className="FunctionalButton"
                    disabled
                    color="info"
                    size="sm">
                {banner}
            </Button>
            <Button className="FunctionalButton"
                    style={{"float": "right"}}
                    color="success"
                    size="sm"
                    onClick={toggle}>
                {"SHOW " + (showSpanOrBlock ? "DOCUMENT" : (targetType == PropertyTargetType.TextSpan ? "SPAN" : "BLOCK"))}
            </Button>
        </CardHeader>

        <PropertyEditor target={editorTarget}
                        targetType={updatedTargetType}
                        store={store}
                        broadcast={broadcast}/>
    </Card>
};

export interface SSIEAnnotationTypeForProperties {
    id: string,
    tag: string
}

