import { BaseConfig } from './BaseConfig';

class ViewMetadataConfig extends BaseConfig {
    constructor() {
        super();
        this.id2Config = new Map();
        this.values = [];
        const makeConfig = (id, displayName, namespace, attributes = undefined) => {
            const config = {
                id: id,
                displayName: displayName,
                namespace: namespace,
            };
            if (attributes) {
                // assign additional attributes to config if provided
                Object.assign(config, attributes);
            }
            this.id2Config.set(id, config);
            this.values.push(config);
            return config;
        };

        this.createConfig('NER', makeConfig(1, 'NER', 'named_entity'));
        this.createConfig('KPE', makeConfig(2, 'KPE', 'key_phrase'));
        this.createConfig(
            'NER_ARBITRATION',
            makeConfig(3, 'NER Arbitration', 'named_entity_arbitration')
        );
        this.createConfig(
            'KPE_ARBITRATION',
            makeConfig(4, 'KPE Arbitration', 'key_phrase_arbitration')
        );
        this.createConfig('SENTIMENT', makeConfig(5, 'Sentiment', 'sentiment'));
        this.createConfig(
            'COREFERENCE',
            makeConfig(6, 'Coreference', 'coreference', { entityNamespace: 'named_entity' })
        );
        this.createConfig(
            'RELATION_EXTRACTION',
            makeConfig(7, 'Relation (Alpha)', 'relations', { entityNamespace: 'named_entity' })
        );
        this.createConfig('TAGGING_DIFF_VIEWER', makeConfig(8, 'Tagging Diff', 'tagging_diff'));
        this.createConfig(
            'EVENT_ARGUMENTS',
            makeConfig(9, 'EVT Entities', 'event_arguments', { displayWidth: 1200 })
        );
        this.createConfig(
            'EVENT_TRIGGERS',
            makeConfig(10, 'EVT Triggers', 'event_triggers', { displayWidth: 1200 })
        );
        this.createConfig(
            'EVENT_ARGUMENTS_TRIGGERS',
            makeConfig(11, 'Evt Entities+Triggers', 'event_arguments_triggers', {
                displayWidth: 1200,
                disableExport: true,
            })
        );
        this.createConfig(
            'EVENT_RELATIONS',
            makeConfig(12, 'EVT Relations', 'event_relations', {
                entityNamespace: 'event_arguments',
                displayWidth: 1200,
            })
        );
        this.createConfig(
            'EVENT_ARGUMENTS_GROUPS',
            makeConfig(13, 'EVT Entities Group', 'event_arguments_groups', {
                entityNamespace: 'event_arguments',
            })
        );
        this.createConfig(
            'EVENT_TRIGGERS_GROUPS',
            makeConfig(14, 'EVT Triggers Group', 'event_triggers_groups', {
                entityNamespace: 'event_triggers',
            })
        );
        this.createConfig('EVENTS', makeConfig(15, 'Events', 'events', { displayWidth: 1600 }));
        this.createConfig(
            'EVENTS_P1_ARBITRATION',
            makeConfig(16, 'Evt Entities+Triggers', 'events_p1_arbitration', {
                displayWidth: 1200,
                disableExport: true,
            })
        );
        this.createConfig('ENTITY_LINKING', makeConfig(17, 'Entity Linking', 'entity_linking'));
        this.createConfig('SSIE', makeConfig(17, 'SSIE', 'semi_structured_information_extraction'));
        this.createConfig('DEVELOPMENT_MODE', makeConfig(99, 'Dev View', 'dev_only'));
        this.getTaskById = this.getTaskById.bind(this);
        this.filterByProfile = this.filterByProfile.bind(this);
    }

    getTaskById(idx) {
        return this.id2Config.get(idx);
    }

    filterByProfile(viewProfile) {
        // filter the view list given a subset of views
        return this.values.filter((v) => viewProfile.has(v.namespace));
    }
}

const GlobalViewConfig = new ViewMetadataConfig();
const DefaultViewProfile = new Set(GlobalViewConfig.values.map((v) => v.namespace));
// PrimeViewProfile includes everything
// except dev view and events related profile
const PrimeViewProfile = new Set(DefaultViewProfile);
PrimeViewProfile.delete(GlobalViewConfig.DEVELOPMENT_MODE.namespace);

// GlobalViewConfig.values.filter(v => v.namespace.startsWith("event")).forEach((v) => {
//    PrimeViewProfile.delete(v.namespace);
// });

// VendorViewProfile excludes dev/and arbitration views
const VendorViewProfile = new Set(PrimeViewProfile);
VendorViewProfile.delete(GlobalViewConfig.NER_ARBITRATION.namespace);
VendorViewProfile.delete(GlobalViewConfig.KPE_ARBITRATION.namespace);
VendorViewProfile.delete(GlobalViewConfig.TAGGING_DIFF_VIEWER.namespace);

export { GlobalViewConfig, VendorViewProfile, PrimeViewProfile };
