import React, {useState} from 'react';
import {BasePropertyType, PropertyKeyValueType} from "../types/BasePropertyType";
import {deepCopy} from "../utils/DeepCopy";
import {Button, ButtonDropdown, CardBody, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";

import './Properties.css'

const KeyValuePropertyEditor = (props: {
    property: BasePropertyType,
    value: string[],
    updateHandler: { (properties: PropertyKeyValueType[]): void }
}) => {


    const {property, value: currentValue, updateHandler} = props;
    const {name: propertyName, values: propertyValues, options} = property;
    const allowMultipleValues = options!.multi_values === true;

    const addItemHandler = (val: string) => {
        let v2 = deepCopy(currentValue);
        if (!allowMultipleValues) {
            // if not allow multiple values overwrite
            v2 = [val,]
        } else if (currentValue.indexOf(val) === -1) {
            v2.push(val);
        }
        updateHandler([{
            name: propertyName,
            value: v2
        }])
    };

    const removeItemHandler = (val: string) => {
        const v2 = currentValue.filter((v) => v !== val);
        updateHandler([{
            name: propertyName,
            value: v2
        }]);
    };

    const Body = (props: {
        items: string[],
        currentValue: string[],
    }) => {
        const [dropdownOpen, setOpen] = useState(false);

        const toggle = () => setOpen(!dropdownOpen);

        const {items, currentValue} = props;
        const selectItems = items.map((i) => {
            return (
                <DropdownItem key={i} onClick={() => addItemHandler(i)}>
                    {i.toUpperCase()}
                </DropdownItem>
            )
        });

        let valueTags: JSX.Element[] = [];

        if (currentValue.length > 0) {
            currentValue.forEach((v) => {
                valueTags.push(<Button className="PropertyValueTag PropertyValueTagName"
                                       color="primary"
                                       key={v.toUpperCase()}>
                    {v.toUpperCase()} <span style={{"fontSize": "14px", "padding": "2px"}}
                                            onClick={() => removeItemHandler(v)}> 
                    </span>
                </Button>)
            })
        } else {
            valueTags.push(<Button key={'NOT_SPECIFIED'} disabled className="PropertyValueTag PropertyValueTagName">
                NOT SPECIFIED
            </Button>)
        }

        return <React.Fragment>
            <ButtonDropdown isOpen={dropdownOpen} toggle={toggle}>
                <DropdownToggle caret
                                outline
                                className="PropertyValueTag">
                    SELECT
                </DropdownToggle>
                <DropdownMenu>
                    {selectItems}
                </DropdownMenu>
            </ButtonDropdown>
            {valueTags}
        </React.Fragment>;

    };


    return <React.Fragment>

        <CardBody>
            <Body items={propertyValues!}
                  currentValue={currentValue}/>
        </CardBody>

    </React.Fragment>

};

export {KeyValuePropertyEditor}