import {BaseConfig} from "./BaseConfig";
import {GlobalViewConfig} from "./ViewMetadataConfig";

class ActiveViewConfig extends BaseConfig {
    constructor() {
        super();
        this.activeViews = new Set();
        this.allViews = new Set();
        // by default include all views
        GlobalViewConfig.values.forEach((v) => {
            this.activeViews.add(v.namespace);
            this.allViews.add(v.namespace)
        });

        this.setViews = this.setViews.bind(this);
    }

    setViews(viewSet) {
        this.activeViews = new Set();
        viewSet.forEach((v) => {
            if (this.allViews.has(v)) {
                this.activeViews.add(v);
            }
        });
        this.configUpdated += 1;
    }

    resume(checkpoint) {
        const views = checkpoint["views"];
        if (views.length > 0) {
            this.setViews(views);
        }
        this.configUpdated += 1;
    }

    checkpoint() {
        const viewList = [];
        this.activeViews.forEach((v) => {
            viewList.push(v);
        });
        return {
            "views": viewList
        }
    }
}

export {ActiveViewConfig}