class RollingBackupManager {
    constructor(prefix = "aceditor_checkpoint_backup_", maxBackupToKeep = 5) {
        this.prefix = prefix;
        this.maxBackupToKeep = maxBackupToKeep;

        this.push = this.push.bind(this);
        this.pop = this.pop.bind(this);
        this.listKeys = this.listKeys.bind(this);
        this.decodeKey = this.decodeKey.bind(this);
        this.listMetadata = this.listMetadata.bind(this);
    }

    get backups() {
        const backupKeys = this.listKeys();
        const backups = [];
        backupKeys.forEach(k => {
            try {
                // in case where people happen to open multiple
                // ACEditors instance, there is a race condition
                // to take care of
                const item = window.localStorage.getItem(k);
                if (item) {
                    backups.push({key: k, data: item});
                }
            } catch (e) {
                // skip
            }
        });

        return backups
    }

    push(item) {
        const {metadata, data} = item;
        // key scheme
        // {PREFIX}_{UTC_TIMESTAMP}_{JSON_STRING_METADATA}
        // reason to have the metadata encoded in the key
        // is for easy retrieval, since we don't want to decompress
        // all items in LocalStorage before we can check the metadata
        const key = this.prefix + new Date().getTime() + "_" + JSON.stringify(metadata);
        // first check whether we are reaching the max limit
        // of backup to save or not
        if (this.listKeys().length === this.maxBackupToKeep) {
            this.pop();
        }
        // still we might run into issue with no sufficient
        // space to save the backup.
        let spaceNotSufficient = false;
        let backupSuccess = false;
        // a backup measure for debugging
        // and busy loop break
        let count = 0;
        while (true) {
            count += 1;
            if (backupSuccess) break;
            if (spaceNotSufficient && this.listKeys().length === 0) {
                // space is not sufficient
                // but our underlying storage has nothing to remove
                // meaning the whole collection is too big
                backupSuccess = false;
                break;
            }

            if (count > this.maxBackupToKeep + 1) {
                // it should only pop this.maxBackupToKeep
                // time before it breaks;
                console.warn("something is wrong");
                console.warn("backupSuccess", backupSuccess);
                console.warn("spaceNotSufficient", spaceNotSufficient);
                console.warn("this.listKeys()", this.listKeys());
                break;
            }

            try {
                window.localStorage.setItem(key, data);
                backupSuccess = true;
            } catch (e) {
                // do special check here
                if (e instanceof DOMException) {
                    if (e.code === 22 || e.code === 1014) {
                        // 22 is the code for Chrome
                        // while 1014 is the code for firefox
                        // other browsers are at your own risk.

                        // pop one more then retry
                        console.log("Exceeds max quota for localStorage, expire one item");
                        this.pop();
                        backupSuccess = false;
                        spaceNotSufficient = true;
                    } else {
                        // non-recoverable DOMException
                        console.error("Can't recover DOMException from push the checkpoint, error: ", e.message);
                        break;
                    }
                } else {
                    // non-recoverable error
                    console.error("Can't recover Exception from push the checkpoint, error: ", e.message);
                    backupSuccess = false;
                    break;
                }
            }
        }
        return backupSuccess;
    }

    pop() {
        const keys = this.listKeys();
        if (keys.length > 0) {
            const oldestItem = keys[0];
            try {
                window.localStorage.removeItem(oldestItem);
                console.log("Expire oldest entry ", oldestItem);
            } catch (e) {
                console.warn("Fail to delete backup key " + oldestItem)
            }
        }
    }

    listKeys() {
        let prefix = this.prefix;
        const backupKeys = Object.keys(window.localStorage).filter(k => k.startsWith(prefix));
        // the key scheme is prefix + utc timestamp
        // so should come naturally with order
        backupKeys.sort((k1, k2) => {
            if (k1 < k2) {
                return -1;
            }
            if (k1 > k2) {
                return 1;
            }
            return 0;
        });
        return backupKeys;
    }

    decodeKey(key) {
        // first strip prefix
        const strippedPrefix = key.substring(this.prefix.length);
        // strip time stamp
        const tsLength = (new Date().getTime() + "").length;
        // noted that we have a '_' after TS
        const stringMetadata = strippedPrefix.substring(tsLength + 1);
        // console.log(stringMetadata);
        return JSON.parse(stringMetadata);
    }

    listMetadata() {
        const metadata = [];
        this.listKeys().forEach((k) => {
            const m = this.decodeKey(k);
            metadata.push({
                key: k,
                metadata: m
            });
        });
        return metadata;
    }

}

export {RollingBackupManager}