import RangeTree from "./RangeTree";

class RangeOverlapChecker {
    constructor(spans) {
        this.tree = RangeTree.createRedBlackRangeTree();
        spans.forEach((s) => {
            let {start, end} = s;
            let r = [start, end];
            this.tree = this.tree.insert(r, s);
        });
        this.overlap = this.overlap.bind(this);
    }

    overlap(span) {
        // check whether a span is overlapping with
        // initialized spans.
        // Return 3 values:
        // 1. Not overlap: -1
        // 2. Contains: 0
        // 3. Overlap: 1.
        const {start, end} = span;
        const range = [start, end];
        if (this.tree.get(range) !== undefined) {
            // contains
            return 0;
        }
        const prevNode = this.tree.lt(range);
        const nextNode = this.tree.gt(range);
        //console.log(prevNode.key);
        //console.log(nextNode.key);

        if (prevNode.valid && RangeTree.checkOverlap(prevNode.key, range)) {
            // overlap with previous node
            return 1;
        }

        if (nextNode.valid && RangeTree.checkOverlap(nextNode.key, range)) {
            // overlap with previous node
            return 1;
        }

        // didn't find overlap
        return -1;
    }
}

export {RangeOverlapChecker}