import React from 'react';
import {
    Card, Col, Nav, NavItem,
    NavLink, Row
} from "reactstrap";
import './Pages.css'
import classnames from 'classnames';
import {EventCustomizationPanel} from "../customization/EventCustomizationPanel";
import {NERCustomizationPanel} from "../customization/NERCustomizationPanel";
import {RelationCustomizationPanel} from "../customization/RelationCustomizationPanel";
import {EventPropertyCustomizationPanel} from "../customization/EventPropertyCustomizationPanel";
import {GlobalViewConfig} from "../config/ViewMetadataConfig";
import {PropertiesCustomizationPanel} from "../customization/PropertiesCustomizationPanel";

class ConfigPage extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: 1
        };
    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab
            });
        }
    }

    render() {
        const rootStore = this.props.store;

        let panel = "";
        switch (this.state.activeTab) {
            case 1:
                panel = <NERCustomizationPanel store={this.props.store}
                                               namespace={GlobalViewConfig.NER.namespace}/>;
                break;
            case 2:
                panel = <PropertiesCustomizationPanel store={this.props.store}/>;
                break;
            case 3:
                panel = <RelationCustomizationPanel store={this.props.store}
                                                    namespace={GlobalViewConfig.RELATION_EXTRACTION.namespace}
                                                    entityNamespace={GlobalViewConfig.NER.namespace}/>;
                break;
            case 4:
                panel = <NERCustomizationPanel store={this.props.store}
                                               namespace={GlobalViewConfig.EVENT_ARGUMENTS.namespace}/>;
                break;
            case 5:
                panel = <NERCustomizationPanel store={this.props.store}
                                               namespace={GlobalViewConfig.EVENT_TRIGGERS.namespace}/>;
                break;
            case 6:
                panel = <EventCustomizationPanel store={this.props.store}/>;
                break;
            case 7:
                panel = <EventPropertyCustomizationPanel store={this.props.store}/>;
                break;
            case 8:
                panel = <RelationCustomizationPanel store={this.props.store}
                                                    namespace={GlobalViewConfig.EVENT_RELATIONS.namespace}
                                                    entityNamespace={GlobalViewConfig.EVENT_ARGUMENTS.namespace}/>

        }

        const createNavItem = (idx, title, namespace) => {
            // lexical scope
            let i = idx;

            if (rootStore.activeViewConfig.activeViews.has(namespace)) {
                return <NavItem>
                    <NavLink
                        className={classnames("ConfigNavItemTitle", {active: this.state.activeTab === i})}
                        onClick={() => {
                            this.toggle(i);
                        }}>
                        {title}
                    </NavLink>
                </NavItem>
            }

        };

        let navItems = [
            createNavItem(1, "NER", GlobalViewConfig.NER.namespace),
            createNavItem(2, "PROPERTIES", GlobalViewConfig.NER.namespace),
            createNavItem(3, "RELATION", GlobalViewConfig.RELATION_EXTRACTION.namespace),
            createNavItem(4, "EVENT ARGUMENTS", GlobalViewConfig.EVENT_ARGUMENTS.namespace),
            createNavItem(5, "EVENT TRIGGERS", GlobalViewConfig.EVENT_TRIGGERS.namespace),
            createNavItem(6, "EVENT DEFINITIONS", GlobalViewConfig.EVENTS.namespace),
            createNavItem(7, "EVENT PROPERTIES", GlobalViewConfig.EVENTS.namespace),
            createNavItem(8, "EVENT RELATIONS", GlobalViewConfig.EVENTS.namespace),
        ];

        // filter all the non-display configuration panel pages
        navItems = navItems.filter(ni => ni !== undefined);

        return <div id="ConfigPage" className="ConfigPageArea">
            <Nav tabs>
                {navItems}
            </Nav>
            <div>
                <Row>
                    <Col sm="12">
                        <Card>
                            {panel}
                        </Card>
                    </Col>
                </Row>
            </div>

        </div>
    }
}

export {ConfigPage}