import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {Button, Card, CardBody, CardHeader} from "reactstrap";
import './EventAnnotator.css'


const EventCandidateArgumentsBar = observer(
    class EventCandidateArgumentsBar extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const store = this.props.store;
            const candidates = this.props.candidates;
            const readOnly = store.isReadOnly();
            let candidateArguments = [];

            candidates.forEach((arg) => {
                const clickHandler = () => {
                    store.addSelectedEventArgument(arg);
                };
                candidateArguments.push(<Button className="TagButton"
                                                color="info"
                                                size="sm"
                                                disabled={readOnly}
                                                style={{
                                                    "margin": "2px",
                                                    "fontSize": "18px"
                                                }}
                                                onClick={clickHandler}>
                    {arg}
                </Button>)
            });

            return <div id="EventCandidateArgumentsBar">
                <Card>
                    <CardHeader>
                        <Button
                            className="TagButton"
                            color="secondary"
                            size="sm"
                            style={{
                                "margin": "2px",
                                "fontSize": "18px"
                            }}
                            disabled={true}>
                            ARGUMENTS
                        </Button>
                        {candidateArguments}
                    </CardHeader>
                </Card>
            </div>
        }
    }
);

export {EventCandidateArgumentsBar}