import React, {Component} from 'react';
import {observer} from 'mobx-react'
import './EditorSidePanel.css'
import { ProjectImportExportPanel } from './ProjectImportExportPanel';
import ProgressPanel from "editor/sidepanel/ProgressPanel";
import TaskSelector from "editor/sidepanel/TaskSelector";
import NavigatePanel from "editor/sidepanel/NavigatePanel";
import AlertModal from "editor/notification/AlertModal";
import DocumentMetadataPanel from "editor/sidepanel/ProjectMetadataPanel";
import {CheckpointPanel} from "./CheckpointPanel";
import {SchemaImportExportPanel} from "./SchemaImportExportPanel";
import { SsieAnnotationStore } from '../../annotator/tagging/pdf/context/SsieAnnotationStore';

const EditorSidePanel = observer(
    class EditorSidePanel extends React.Component {

        constructor(props) {
            super(props);
        }


        render() {
            return <div className="SideBarMain">
                <div className="PanelSection TaskPanelText"
                     style={{"marginTop": 0, "paddingTop": 0}}>
                    VIEWS
                </div>
                <TaskSelector store={this.props.store}/>


                <div className="PanelSection TaskPanelText">
                    PROJECT
                </div>
                <SsieAnnotationStore.Consumer>
                    {({ getOutputObject }) => (
                        <ProjectImportExportPanel store={this.props.store} ssieProps={{ getOutputObject }} />
                    )}
                </SsieAnnotationStore.Consumer>

                <div className="PanelSection TaskPanelText">
                    PROGRESS
                </div>

                <ProgressPanel store={this.props.store}/>



                <div className="PanelSection TaskPanelText">
                    CHECKPOINT
                </div>
                <CheckpointPanel store={this.props.store}/>

                <div className="PanelSection TaskPanelText">
                    SCHEMA
                </div>
                <SchemaImportExportPanel store={this.props.store}/>

                <div className="PanelSection TaskPanelText">
                    NAVIGATE
                </div>
                <NavigatePanel store={this.props.store}/>


                <div className="PanelSection TaskPanelText">
                    METADATA
                </div>
                <DocumentMetadataPanel store={this.props.store}/>
                <AlertModal store={this.props.store}/>
            </div>
        }
    }
);

export default EditorSidePanel;