import React from 'react';
import HotKeyProvider from './HotKeyProvider';
import PDFStoreProvider from './PDFStoreProvider';
import SsieAnnotationStoreProvider from './SsieAnnotationStoreProvider';

interface Props {}

const SsieContextProviders: React.FC<Props> = ({ children, ...ssieProps }) => {
    return (
        <HotKeyProvider>
            <PDFStoreProvider {...ssieProps}>
                <SsieAnnotationStoreProvider {...ssieProps}>
                    {children}
                </SsieAnnotationStoreProvider>
            </PDFStoreProvider>
        </HotKeyProvider>
    );
}

export default SsieContextProviders;