class TaggingSpanSelector {

    constructor() {
        this.findSelectableParent = this.findSelectableParent.bind(this);
    }

    findSelectableParent(node) {
        let n = node;
        while (n !== document.body) {
            // bubble up all the way to the body
            let nodeAttributes = n.attributes;
            if (nodeAttributes) {
                const selectableAttribute = nodeAttributes.getNamedItem("data-selectable");
                if (selectableAttribute) {
                    // this is the a Selectable component
                    let start = nodeAttributes.getNamedItem("data-start").value;
                    let end = nodeAttributes.getNamedItem("data-end").value;
                    start = parseInt(start);
                    end = parseInt(end);
                    return {start: start, end: end, nodeRef: n, selectable: selectableAttribute.value == "true"}
                }
            }
            n = n.parentNode;
        }
    }

    retrieveNodesUnderRange(range) {
        /*
        This method iterate through the node originalSpanTree to find the all the children
        that covers by the range
         */
        const ancestor = range.commonAncestorContainer;
        const startContainer = range.startContainer;
        const endContainer = range.endContainer;

        let nodeIterator = document.createNodeIterator(
            ancestor,
            NodeFilter.SHOW_ALL, // pre-filter
            {
                // custom filter
                acceptNode: function (node) {
                    return NodeFilter.FILTER_ACCEPT;
                }
            }
        );

        let selectedNodes = [];
        while (nodeIterator.nextNode()) {
            if (selectedNodes.length === 0 && nodeIterator.referenceNode !== startContainer) {
                continue;
            }
            selectedNodes.push(nodeIterator.referenceNode);
            if (nodeIterator.referenceNode === endContainer) {
                break;
            }
        }
        return selectedNodes;
    }

    convertSelectionToTextRange(selection) {
        console.log("selection -> ", selection);
        if (selection && selection.rangeCount > 0) {
            // we get a selection
            // nail down the range
            let r = selection.getRangeAt(0);
            console.log("range ->", r);
            console.log("start offset ->", r.startOffset);
            console.log("end offset ->", r.endOffset);

            // easy case it is in single container
            if (!r.collapsed) {
                if (r.startContainer === r.endContainer && r.startContainer.nodeType === 3) {
                    console.log("single container, fast path");
                    // nodeType 3 indicates a text node
                    const container = r.startContainer;
                    let parent = this.findSelectableParent(container);
                    if (parent === undefined || !parent.selectable) {
                        return;
                    }
                    console.log("parent", parent);
                    // NOTE: the startOffset and endOffset
                    // all refers to the offsets within the selected container
                    let text = container.textContent;
                    let selectStart = r.startOffset;
                    let selectEnd = r.endOffset;
                    let selectedText = text.substring(selectStart, selectEnd);
                    // adding some trimming logic to
                    if (parent && selectedText.length > 0 && selectedText.trim().length > 0) {
                        let trimmed = selectedText.trim();
                        let trimStart = selectedText.indexOf(trimmed);
                        let trimEnd = trimStart + trimmed.length;
                        // adjust the offsets to match
                        // the length of the text after trimming
                        selectStart += trimStart;
                        selectEnd = selectEnd - (selectedText.length - trimEnd);
                        let {start: parentOffset} = parent;
                        let globalStart = parentOffset + selectStart;
                        let globalEnd = parentOffset + selectEnd;
                        return {start: globalStart, end: globalEnd, rangeNodes: [parent]}
                    }
                } else {
                    // selected multiple elements
                    console.log("multiple containers");
                    const nodes = this.retrieveNodesUnderRange(r);
                    const parentSelectables = [];
                    for (let i = 0; i < nodes.length; i++) {
                        const pn = this.findSelectableParent(nodes[i]);
                        if (pn === undefined || !pn.selectable) {
                            // one of the parent node is not selectable. skip
                            console.log("detect range includes unselectable parent container -> ", pn);
                            return
                        }
                        const length = parentSelectables.length;
                        if (length === 0 || parentSelectables[length - 1].nodeRef !== pn.nodeRef) {
                            parentSelectables.push(pn);
                        }
                    }
                    console.log("parent containers", parentSelectables);
                    let start = parentSelectables[0].start + r.startOffset;
                    // need to adjust this based on node text
                    console.log("All Nodes -> ", nodes);
                    nodes.forEach((n) => {
                        console.log(n.parentNode.nodeName);
                        console.log(n.parentNode.className);
                        console.log(n.textContent);
                    });
                    // assume the last select text block is not a Tag
                    let end = parentSelectables[parentSelectables.length - 1].start + r.endOffset;
                    const lastNode = nodes[nodes.length - 1];
                    if (lastNode.attributes
                        && lastNode.attributes.getNamedItem("data-tag")) {
                        // node itself is a tag span
                        // assume we select the whole last block
                        end = parentSelectables[parentSelectables.length - 1].end;
                    } else {
                        let lastParentNodeAttributes = nodes[nodes.length - 1].parentNode.attributes;
                        if ((lastParentNodeAttributes
                            && lastParentNodeAttributes.getNamedItem("data-tag"))) {
                            // we select into a tag's text content, e.g. "LOC"/"PER"
                            // here we actually selected the whole last block
                            // set the end accordingly
                            end = parentSelectables[parentSelectables.length - 1].end;
                        }
                    }
                    return {start: start, end: end, rangeNodes: parentSelectables}
                }
            }
        }
    }
}

export default TaggingSpanSelector;