import React, {Component} from "react";
import {RelationVisualization} from "./RelationVisualization";
import {States} from "../store/RelationStateStore";

class RelationDisplayPanel extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const store = this.props.store;
        const stateMachine = this.props.stateMachine;
        let relations = this.props.relations;
        /**
        // filter relations based on current state
        if (stateMachine.state === States.ARG1 || stateMachine.state === States.ARG2) {
            const arg1 = stateMachine.arg1;
            console.log("here arg1", arg1, relations);
            // filter relations
            // Date: 2019/01/02: Per date team's request, we want to
            // highlight relations that has either ARG1 or ARG2 in question
            relations = relations.filter((r) => arg1.rangeEqual(r.arg1) ||
                arg1.rangeEqual(r.arg2));
            console.log("filtered after arg1", relations);
            if (stateMachine.state === States.ARG2) {
                const arg2 = stateMachine.arg2;
                relations = relations.filter((r) => arg2.rangeEqual(r.arg2) ||
                    arg2.rangeEqual(r.arg1));
            }
        }**/
        let relationVisualizations = [];
        let i = 1;
        relations.forEach((r) => {
            relationVisualizations.push(<RelationVisualization store={store}
                                                               id={i}
                                                               relation={r}
                                                               arg1={stateMachine.arg1}
                                                               arg2={stateMachine.arg2}
                                                               key={i}>
            </RelationVisualization>);
            i += 1;
        });

        return <div id="RelationDisplayPanel" style={{"lineHeight": 3.0}}>
            {relationVisualizations}
        </div>
    }
}

export {RelationDisplayPanel}