import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {Button, Card, CardBody, CardHeader} from "reactstrap";
import './EventAnnotator.css'
import {EVENT_EMPTY, EVENT_INCOMPLETE} from "../store/EventInstanceDataStore";


const EventTriggerSelectionBar = observer(
    class EventTriggerSelectionBar extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const store = this.props.store;
            const triggers = store.states.triggerSpans;
            const activeTrigger = this.props.activeTrigger;

            let triggerButtons = [];

            triggers.forEach((span, idx) => {
                const clickHandler = () => {
                    store.toggleActiveSpan(span);
                };
                let color = "secondary";
                if (span.rangeEqual(activeTrigger)) {
                    color = "primary"
                }
                triggerButtons.push(<Button className="TagButton"
                                            color={color}
                                            size="sm"
                                            style={{
                                                "marginRight": "2px",
                                                "fontSize": "18px"
                                            }}
                                            onClick={clickHandler}
                                            key={idx}>
                    {"T" + (idx + 1)}
                </Button>)
            });

            return <div id="EventTriggerSelectionBar">
                <Card>
                    <CardHeader>
                        <Button
                            className="TagButton"
                            color="info"
                            size="sm"
                            style={{
                                "margin": "2px",
                                "fontSize": "18px"
                            }}
                            disabled={true}>
                            TRIGGERS
                        </Button>
                        {triggerButtons}
                    </CardHeader>
                </Card>
            </div>
        }
    }
);

export {EventTriggerSelectionBar}