import {extendObservable, reaction} from 'mobx'
import {UFSchema} from "../core/UFSchema";
import GraphemeSplitter from "grapheme-splitter";

class DocumentStateStore {
    constructor(documentObject, labelingTask = undefined) {
        this.documentObject = documentObject;
        this.rawText = documentObject[UFSchema.RAW_TEXT];
        // define signals that could be subscribed by views
        // NOTE: DO NOT OVERRIDE unless you know what you are doing
        extendObservable(this, {
            viewUpdateChange: 0, // notify the component view needs to update
            documentStateChange: 0, // notify downstream receiver of document state update
            customEventNotify: 0 // notify downstream listeners about custom events arrival
        });
        // create states
        this.states = {};
        this._actions = {};
        this._viewActions = {};
        // notifications
        this._pendingNotifications = [];
        this.notificationCallbacks = [];
        this.arbitrationAnnotationObject = undefined;
        this.hasV2InnerObject = false;
        // Assumption - both annotation_v2 and conflicted_annotations_list are not present together in document
        if (documentObject[UFSchema.ANNOTATIONS_V2]) {
            this.arbitrationAnnotationObject = documentObject[UFSchema.ANNOTATIONS_V2];
        } else if (documentObject[UFSchema.CONFLICTED_ANNOTATIONS_LIST]) {
            this.arbitrationAnnotationObject = documentObject[UFSchema.CONFLICTED_ANNOTATIONS_LIST][0]
        }
        // Labeling Task
        this.labelingTask = labelingTask;
        this.splitter = new GraphemeSplitter();

        // binding all methods
        this.initStatesFromDocument = this.initStatesFromDocument.bind(this);
        this.convertStatesToAnnotations = this.convertStatesToAnnotations.bind(this);
        this.collectDocumentStatistics = this.collectDocumentStatistics.bind(this);
        this.notifyDocumentStateChange = this.notifyDocumentStateChange.bind(this);
        this.notifyViewUpdateChange = this.notifyViewUpdateChange.bind(this);
        this.sendCustomNotification = this.sendCustomNotification.bind(this);
        this.onDocumentStateChange = this.onDocumentStateChange.bind(this);
        this.registerAction = this.registerAction.bind(this);
        this.registerViewAction = this.registerViewAction.bind(this);
        this.registerNotification = this.registerNotification.bind(this);
        this.activateNotification = this.activateNotification.bind(this);
        this.isReadOnly = this.isReadOnly.bind(this);
        this.expandSpanToHaveMultibyteOffset = this.expandSpanToHaveMultibyteOffset.bind(this);

        // activate notification bus
        this.activateNotification();
    }

    /**
     * Determines if a document is read-only based on the current labelingTask and the labeling_tasks defined in
     * the document metadata
     */
    isReadOnly() {
        const metadata = this.documentObject[UFSchema.METADATA] || {};

        // If there's no labeling task associated with this store, or if the document doesn't have a labeling task 
        // associated with it, don't make the document read-only
        console.log("Labeling Task: " + this.labelingTask);
        if (!this.labelingTask || !(UFSchema.LABELING_TASKS in metadata)){
            return false;
        }
        const task = metadata[UFSchema.LABELING_TASKS].find(
            (task) => task[UFSchema.LABELING_TASK_NAME] === this.labelingTask
        );
        return task ? task[UFSchema.LABELING_TASK_READ_ONLY] : true;
    }

    onDocumentStateChange(callback) {
        /**
         * The callback take in a single argument, that is the current
         * annotations of the document, then deal with it.
         */
        reaction(
            () => [this.documentStateChange],
            (_) => {
                console.log("onDocumentStateChange fired");
                const annotations = this.convertStatesToAnnotations();
                callback(annotations);
            }
        )
    }

    activateNotification() {
        // this is the ONE entry point where the notification
        // callbacks are executed
        reaction(
            () => [this.customEventNotify],
            (_) => {
                console.log("Executing pending notifications");
                // nasty dirty broadcast
                this.notificationCallbacks.forEach((callback) => {
                    this._pendingNotifications.forEach((notification) => {
                        callback(notification);
                    })
                });
                // clean up notifications after each execution;
                this._pendingNotifications = [];
            }
        )
    }

    sendCustomNotification(key, notification) {
        let notificationContext = {};
        notificationContext["key"] = key;
        notificationContext["body"] = notification;
        this._pendingNotifications.push(notificationContext);
        this.customEventNotify += 1;
    }

    initStatesFromDocument(documentObject) {
        if (!("annotations" in documentObject)) {
            documentObject["annotations"] = {};
            console.log("init empty annotations field")
        }
    }


    convertStatesToAnnotations() {
        throw new Error("convertStatesToAnnotations not implemented");
    }

    // This method expands imported and saved spans so that characters that have multiple bytes, will have
    // offsets that reflect this. This is necessary for ACEditor to display the characters correctly.
    // example characters are emojis, etc
    expandSpanToHaveMultibyteOffset(span, runes) {
        let startWithMultiByteOffset = 0;

        // TODO: This method can be optimized by maintaining state of UTF16 offsets of prior spans
        // example implementation - https://code.amazon.com/packages/CosmodromeWebComponentsStaticWebsite/blobs/b0fa55fb0d089a84ca8e77cbfdb4d18c2e5527b9/--/src/components/crowd-selectable-text/crowd-span-selection.tsx#L333-L362
        // current runtime: O(N^2), can cause issues for PII where docs can be very large
        for (let i = 0; i < span.start; i++) {
            startWithMultiByteOffset = startWithMultiByteOffset + runes[i].length;
        }

        let endWithMultibyteOffset = startWithMultiByteOffset;
        for (let j = span.start; j < span.end; j++) {
            endWithMultibyteOffset = endWithMultibyteOffset + runes[j].length;
        }

        span.start = startWithMultiByteOffset;
        span.end = endWithMultibyteOffset;
        span.extent = this.rawText.substring(span.start, span.end);
    }

    collectDocumentStatistics() {

    }

    notifyDocumentStateChange() {
        this.documentStateChange += 1;
    }

    notifyViewUpdateChange() {
        this.viewUpdateChange += 1;
    }

    registerAction(action) {
        // keep track of all actions
        this._actions[action.name] = action;
        return (...args) => {
            const result = action(...args);
            this.notifyDocumentStateChange();
            this.notifyViewUpdateChange();
            return result;
        }
    }

    registerViewAction(action) {
        // keep track of all actions
        this._viewActions[action.name] = action;
        return (...args) => {
            const result = action(...args);
            this.notifyViewUpdateChange();
            return result;
        }
    }

    registerNotification(key, callback) {
        // for notifications, they are executed all in one call
        // and the events are cleared once that is done

        // Each callback accepts one single argument that is the notification object
        const wrapped = (notificationContext) => {
            const {key: k, body} = notificationContext;
            if (key === k) {
                callback(body);
            }
        };
        this.notificationCallbacks.push(wrapped)
    }

    get annotations() {
        return this.convertStatesToAnnotations();
    }

    get showV2() {
        return this.hasV2InnerObject;
    }
}

export {DocumentStateStore}