import React, {Component} from 'react';
import {Card, CardFooter, CardHeader, CardText, Col, Row, Button, UncontrolledTooltip} from 'reactstrap';
import {observer} from 'mobx-react'
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import {SingleLayerSpan} from "annotator/span/SingleLayerSpan";
import {CoreferenceGroupPanel} from "./CoreferenceGroupPanel";
import {
    SelectTagColor, TagColorScheme,
    UnselectTagColor, ActiveSpanColor, LightSelectTagColor
} from "annotator/AnnotatorCommons";
import {CoreferenceMentionsDisplay} from "./CoreferenceMentionsDisplay";
import TaggingSpanSelector from "annotator/tagging/TaggingSpanSelector";
import KeyPressStore from 'annotator/utils/KeyPressStore';
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import {GroupColors} from "../AnnotatorCommons";
import {CoreferenceContextMenu} from 'annotator/coreference/CoreferenceContextMenu';
import uuid from 'uuid';

const ONE_COLUMN_LAYOUT = "one_column";
const TWO_COLUMN_LATYOUT = "two_column";
const COREFERENCE_ANNOTATOR_MENU_ID = "CoreferenceAnnotatorMenu";

const CoreferenceAnnotator = observer(
    class CoreferenceAnnotator extends Component {

        constructor(props) {
            super(props);
            this.selector = new TaggingSpanSelector();
            
            // Keep references to the span objects and to the annotator panel to allow us to scroll to spans
            // when selected in the group mentions side panel
            this.spanRefs = {};
            this.autoTagButtonRef = React.createRef();
            this.autoGroupButtonRef = React.createRef();
            this.annotatorTextAreaRef = React.createRef();
            this.state = {
                contextMenuSpan: null
            }
        }

        handleSelection(event) {

            if (!KeyPressStore.altKeyPressed()) {
                // by press alt key, we give user the ability to select text
                event.stopPropagation();
                let selection = window.getSelection();
                const selectedTextRange = this.selector.convertSelectionToTextRange(selection);
                if (selectedTextRange) {
                    const {start, end, rangeNodes} = selectedTextRange;
                    if (rangeNodes.length === 1) {
                        // for regular annotator, we only consider annotation that happens
                        // within a single span container as valid
                        // if user select multiple parent spans, we do nothing
                        this.props.store.insertSpan(start, end);
                    }
                }
                selection.removeAllRanges();
            }
        }


        getSpanMarkup(spanObj) {
            let background = TagColorScheme[spanObj.tag];
            if (!background) {
                background = UnselectTagColor
            }
            if (spanObj.tag) {
                let groupMarkUp = "";
                if (spanObj.attrs.groupId >= 0) {
                    let colorIdx = spanObj.attrs.groupId % GroupColors.length;
                    groupMarkUp = <mark className="RichInnerTagStyle NoSelect"
                                        style={{
                                            "background": GroupColors[colorIdx],
                                            "color": "#fff",
                                            "fontSize": "16px"
                                        }}>
                        {"G" + spanObj.attrs.groupId}</mark>
                }
                return <span> {spanObj.extent}
                    {groupMarkUp}
                </span>;
            } else {
                return spanObj.extent;
            }
        }
        
        componentDidUpdate(prevProps) {
            // Scroll to a given span if set
            if (this.props.store.states.scrollToSpan) {
                const spanId = this.props.store.states.scrollToSpan.attrs.id;
                if (!this.spanRefs[spanId]) {
                    return;
                }
                if (!this.spanRefs[spanId].current) {
                    return;
                }
                if (!(this.annotatorTextAreaRef && this.annotatorTextAreaRef.current)) {
                    return;
                }
                this.annotatorTextAreaRef.current.scroll({top:this.spanRefs[spanId].current.offsetTop - 10, behavior: 'smooth'});

                // Unset the selected span after a single scroll to operation
                this.props.store.clearScrollToSpan();
            }
            this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
        }

        componentDidMount() {
            this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
        }

        render() {
            // eslint-disable-next-line no-unused-vars
            const viewUpdateChange = this.props.store.viewUpdateChange; // Required for mobx state updates
            const spans = this.props.store.spans;
            const activeSpan = this.props.store.states.activeSpan;
            const readOnly = this.props.store.isReadOnly();
            const alertStore = this.props.alertStore;
            const layout = this.props.layout === undefined ? ONE_COLUMN_LAYOUT : this.props.layout;
            const frameWrapper = this.props.frameWrapper;
            let hoverOver = undefined;
            const tagSpans = [];

            for (let i = 0; i < spans.length; i++) {
                let spanObj = spans[i];
                let key = "span_" + i;
                let markUp = this.getSpanMarkup(spanObj);
                let clickTrigger = () => {
                    console.log("in click trigger -> ", spanObj);
                    if (!spanObj.attrs.blank) {
                        this.props.store.toggleMentionState(spanObj);
                    }   
                };
                let color = UnselectTagColor;
                const isActive = activeSpan && activeSpan.rangeEqual(spanObj);

                if (!spanObj.attrs.blank) {
                    if (spanObj.attrs.groupId >= 0
                        && spanObj.attrs.groupId === this.props.store.states.focusGroup) {
                        color = LightSelectTagColor;
                    }
                    if (isActive) {
                        // this is the activeSpan
                        color = ActiveSpanColor;
                    }
                    hoverOver = <span className="HoverText">{spanObj.tag}</span>;
                }
                if (!(spanObj.attrs.id in this.spanRefs)) {
                    this.spanRefs[spanObj.attrs.id] = React.createRef();
                }
                tagSpans.push(
                    <SingleLayerSpan key={key}
                        innerRef={this.spanRefs[spanObj.attrs.id]}
                        hoverOver={hoverOver}
                        text={markUp}
                        tag={spanObj.tag}
                        color={color}
                        start={spanObj.start}
                        end={spanObj.end}
                        blank={spanObj.attrs.blank}
                        selectable={false}
                        transparent={spanObj.tag === "COREF"}
                        store={this.props.store}
                        cssStyle={"HighlightSpan"}
                        clickTrigger={clickTrigger}
                        onMouseDown={
                            () => {
                                if (!spanObj.attrs.blank) {
                                    this.setState({
                                        contextMenuSpan: spanObj
                                    });
                                }
                            }}
                        rightClickMenuId={readOnly ? null : COREFERENCE_ANNOTATOR_MENU_ID}
                    />
                );
            }
            
            const autoTagButtonDisabled = !(this.props.store.states.activeSpan && this.props.store.states.activeSpan.attrs.groupId >= 0);

            let autoTagTooltipText = undefined;
            if (autoTagButtonDisabled) {
                autoTagTooltipText = (<div>
                    <p>Select an span that is assigned to a group before auto-tagging.</p>
                    <div>Auto-tag finds all spans with the same text as the selected span and adds them to the selected span's group</div>
                </div>);
            } else {
                autoTagTooltipText = (
                    <span>
                        Adds all spans with the text "{this.props.store.states.activeSpan.getExtent()}" to group "G{this.props.store.states.activeSpan.attrs.groupId}"
                    </span>
                );
            }

            const autoGroupTooltipText = 'Automatically create groups of proper noun spans that are not currently assigned to a group';
            
            let annotatorBody = <div id="CoreferenceAnnotatorCorePanel">
                <CardHeader>
                    <CoreferenceGroupPanel store={this.props.store}
                                           selected={this.props.store.states.focusGroup}
                                           groups={this.props.store.groupList}/>
                </CardHeader>
                <div 
                    className="AnnotatorTextView card-body"
                    ref={this.annotatorTextAreaRef}
                    onScroll={
                        (e) => {
                            this.props.globalViewStateStore.setScrollPercentageFromElement(e.target);
                        }
                    }
                >
                    <AnnotatorTextArea textDirection={this.props.textDirection}
                                       textAlignment={this.props.textAlignment}>
                        <CardText 
                            className="AnnotatorText card-text"
                            markable="parent"
                            id="tag-container"
                            onMouseUp={this.handleSelection.bind(this)}
                        >
                            {tagSpans}
                        </CardText>
                    </AnnotatorTextArea>
                </div>
                <CardFooter className="d-flex justify-content-between">
                    <div className="d-inline-block text-white" ref={this.autoTagButtonRef}>
                        <Button
                            tag="a"
                            className="FunctionalButton"
                            size="lg"
                            disabled={readOnly || autoTagButtonDisabled}
                            onClick={() => {
                                const activeSpan = this.props.store.states.activeSpan;
                                if (!activeSpan) {
                                    return;
                                }
                                this.props.store.addSimilarSpansToGroup(activeSpan);
                            }}>
                            AUTO TAG
                        </Button>
                    </div>
                    <div className="d-inline-block text-white" ref={this.autoGroupButtonRef}>
                        <Button
                            tag="a"
                            className="FunctionalButton"
                            size="lg"
                            disabled={readOnly}
                            onClick={() => {
                                alertStore.sendAlert('Confirm','This action will auto-assign all proper noun spans with the same text value to new groups. Do you want to continue?',() => {
                                    this.props.store.autoGroupSimilarSpans(); 
                                }, true);
                            }}>
                            AUTO GROUP
                        </Button>
                    </div>
                    <UncontrolledTooltip target={this.autoTagButtonRef}>{autoTagTooltipText}</UncontrolledTooltip>
                    <UncontrolledTooltip target={this.autoGroupButtonRef}>{autoGroupTooltipText}</UncontrolledTooltip>
                    
                </CardFooter>
                <CoreferenceContextMenu menuId={COREFERENCE_ANNOTATOR_MENU_ID} store={this.props.store} span={this.state.contextMenuSpan} />
            </div>;

            if (frameWrapper) {
                annotatorBody = frameWrapper(annotatorBody);
            }

            if (layout === TWO_COLUMN_LATYOUT) {
                const leftWidth = this.props.store.showV2 ? "6" : "8";
                const rightWidth = this.props.store.showV2 ? "6" : "4";
                return <div id="CoreferenceAnnotatorTwoColumn">
                    <Row>
                        <Col xl={leftWidth} id="CoreferenceAnnotatorColumn">
                            {annotatorBody}
                        </Col>
                        <Col xl={rightWidth} id="CoreferenceMentionDisplayColumn">
                            <Card>
                                <CoreferenceMentionsDisplay 
                                    groups={this.props.store.groupList} 
                                    groupsV2={this.props.store.showV2 ? this.props.store.groupListV2 : null}
                                    store={this.props.store}
                                    readOnly={readOnly}
                                />
                            </Card>
                        </Col>
                    </Row>
                </div>
            }

            return (
                <div id="CoreferenceAnnotatorBody">
                    {annotatorBody}
                    <CoreferenceMentionsDisplay 
                        groups={this.props.store.groupList} 
                        groupsV2={this.props.store.showV2 ? this.props.store.groupListV2 : null}
                        store={this.props.store}
                        readOnly={readOnly}
                    />
                </div>
            );
        }
    }
);

export {CoreferenceAnnotator}