import {Validator, Violation} from "./Validator";
import {UFSchema} from "../core/UFSchema";

class SingletonCoreferenceValidator extends Validator {
    constructor(namespace) {
        super(namespace);
    }

    validate(document) {
        if (!Validator.checkNamespaceExists(document, this.namespace)) {
            // doesn't have coreference annotation
            return [];
        }

        const coreferenceAnnotations = document[UFSchema.ANNOTATIONS][this.namespace];
        const violations = [];
        if (coreferenceAnnotations["groups"] !== undefined) {
            coreferenceAnnotations["groups"].forEach((g, groupIdx) => {
                const mentions = g["mentions"];
                if (mentions.length === 1) {
                    violations.push(new Violation(document, this.namespace,
                        `Coreference Group G${groupIdx} has only one member`))
                }
            })
        }
        return violations;
    }

}

export {SingletonCoreferenceValidator}
