import React from 'react';
import {CollectionStateStore} from "editor/store/CollectionStateStore";
import {ProgressStateStore} from "editor/store/ProgressStateStore";
import {ViewStateStore} from "./editor/store/ViewStateStore";
import {AlertManager} from "editor/notification/AlertManager";
import ErrorStore from "editor/utils/ErroManager";
import {DefaultCollection} from "annotator/default/DefaultCollection";
import {Checkpointtable} from "./editor/checkpoint/Checkpointtable";
import {CheckpointManager} from "./editor/checkpoint/CheckpointManager";
import {SchemaConfig} from "./editor/config/SchemaConfig";
import {Pages, PageStateStore} from "./editor/store/PageStateStore";
import {GlobalViewStateStore} from "annotator/store/GlobalViewStateStore";
import {VersionConfig} from './editor/config/EditorVersionConfig'
import Presets from 'presets'
import {ActiveViewConfig} from "./editor/config/ActiveViewConfig";
import {
    GlobalViewConfig,
    PrimeViewProfile,
    VendorViewProfile
} from "./editor/config/ViewMetadataConfig";
import {GroupValidator} from "./annotator/validation/Validator";
import {EventsTriggerCoreferenceConsistencyValidator} from "./annotator/validation/EventsValidator";
import {SingletonCoreferenceValidator} from "./annotator/validation/CoreferenceValidator";

class RootStore extends Checkpointtable {
    constructor() {
        super();
        console.log("RootStore Constructed");
        this.editorStateStore = new ViewStateStore();
        this.collectionStore = new CollectionStateStore(this, []);
        this.progressStore = new ProgressStateStore([]);
        this.alertStore = new AlertManager();
        this.errorStore = new ErrorStore();
        this.checkpointManager = new CheckpointManager(this);
        this.schemaConfig = new SchemaConfig();
        this.activeViewConfig = new ActiveViewConfig();
        this.pageStateStore = new PageStateStore();
        this.globalViewStateStore = new GlobalViewStateStore();
        // session information, like annotator id, etc
        this.sessionInfo = {};

        this.nextDocument = this.nextDocument.bind(this);
        this.nextUnreadDocument = this.nextUnreadDocument.bind(this);
        this.navigateToDocumentBySequenceIdx = this.navigateToDocumentBySequenceIdx.bind(this);
        this.navigateToDocumentByDocumentId = this.navigateToDocumentByDocumentId.bind(this);
        this.prevDocument = this.prevDocument.bind(this);
        this.loadProjectFile = this.loadProjectFile.bind(this);
        this.saveProjectFile = this.saveProjectFile.bind(this);
        this.checkCollectionViolations = this.checkCollectionViolations.bind(this);
        this.showRootStoreStatusError = this.showRootStoreStatusError.bind(this);
        this.setToDefaultState = this.setToDefaultState.bind(this);
        this.checkStoreReady = this.checkStoreReady.bind(this);

        this.setToDefaultState(Presets);
    };


    loadProjectFile(jsons, path = undefined, error = undefined) {
        // checkpoint before load
        this.checkpointManager.checkpointEditorState("before load");
        try {
            if (error !== undefined) {
                // re-throw old error
                throw error;
            }
            if (!jsons || jsons.length === 0) {
                // error occurred, abort
                throw new Error("inputs are empty or not valid JSONs. Expected document JSON per line format");
            }
            let documents = jsons;
            let hasProgress = false;
            if (jsons && jsons.length > 0 && jsons[0]["type"] === "progressSnapshot") {
                // first object progress snapshot
                console.log("find progress snapshot");
                documents = documents.slice(1, documents.length);
                hasProgress = true;
            }
            // zero documents
            if (documents.length === 0) {
                throw new Error("No documents found in the project file");
            }
            // now validate every document format
            for (let i = 0; i < documents.length; i++) {
                const d = documents[i];
                if (d["raw_text"] === undefined) {
                    throw new Error("Document " + (i + 1) + " doesn't have raw text field");
                }
                if (d["metadata"]["document_id"] === undefined) {
                    throw new Error("Document " + (i + 1) + " doesn't have document_id in metadata");
                }
            }
            const collectionSnapshot = {
                documents: documents,
                path: path
            };
            this.collectionStore.resume(collectionSnapshot);
            this.progressStore.reset(documents);
            // restore snapshot
            if (hasProgress) {
                try {
                    const progressCheckpoint = jsons[0];
                    const lastRead = this.progressStore.resume(progressCheckpoint);
                    this.collectionStore.setActiveDocument(lastRead);
                } catch (e) {
                    // Failed to resume the progress
                    // fallback to default state
                    this.progressStore.reset(documents);
                    this.collectionStore.setActiveDocument(0);
                    console.error("Failed to resume progress store", e)
                }
            }
            //force update
            console.log("collection updated");
            this.collectionStore.updated += 1;
            // jump to annotate page by default
            this.pageStateStore.jumpToPage(Pages.EDIT);
        } catch (e) {
            console.error("in loadProjectFile error", e.message);
            this.showRootStoreStatusError(e);
        }
    }

    saveProjectFile(exportProgress = true) {
        // before loading first checkpoint to save current status
        this.checkpointManager.checkpointEditorState();
        let timestamp = parseInt(new Date().getTime() / 1000);
        try {
            let documents = this.collectionStore.collection;
            documents.forEach((d) => {
                let annotatorId = this.sessionInfo["annotator_id"];
                if (annotatorId === undefined) {
                    annotatorId = "unknown"
                }
                d["metadata"]["timestamp"] = timestamp;
                d["metadata"]["annotator_id"] = annotatorId;
                d["metadata"]["schema_version"] = this.schemaConfig.version;
            });

            let jsonlDocuments = CollectionStateStore.doConvertToJsonPerLine(documents);
            let contents = [];
            if (exportProgress) {
                const snapshot = JSON.stringify(this.progressStore.checkpoint());
                contents = [snapshot, '\n', ...jsonlDocuments];
            } else {
                contents = [...jsonlDocuments];
            }
            return contents;
        } catch (e) {
            console.error("in saveProjectFile error", e.message);
            this.showRootStoreStatusError(e);
        }
    }

    checkCollectionViolations() {
        const documents = this.collectionStore.collection;
        const groupValidator = this.groupValidator;

        const collectionViolations = [];
        documents.forEach((d) => {
            const vios = groupValidator.validateAll(d);
            collectionViolations.push(...vios);
        });

        return collectionViolations;
    }

    nextDocument() {
        console.log("next");
        try {
            const docIdx = this.collectionStore.activeDocument;
            this.globalViewStateStore.resetScroll();
            this.collectionStore.setActiveDocument(docIdx + 1);
            this.progressStore.markAsComplete(docIdx);
        } catch (e) {
            console.error("in nextDocument error", e.message);
            this.showRootStoreStatusError(e);
        }
    }

    prevDocument() {
        console.log("prev");
        try {
            const docIdx = this.collectionStore.activeDocument;
            this.globalViewStateStore.resetScroll();
            this.collectionStore.setActiveDocument(docIdx - 1);
        } catch (e) {
            console.error("in prevDocument error", e.message);
            this.showRootStoreStatusError(e);
        }
    }

    navigateToDocumentBySequenceIdx(docIdx) {
        console.log("nav ", docIdx);
        try {
            let id = parseInt(docIdx);
            // we don't allow navigate in sentiment task, since each
            // task needs to be complete prior
            if (!isNaN(id) && id < this.progressStore.totalDoc && id >= 0) {
                const currentDocIdx = this.collectionStore.activeDocument;
                this.collectionStore.setActiveDocument(id);
                this.progressStore.rangeUpdateCompleteDocuments(id);
            }

        } catch (e) {
            console.error("in navigateToDocument error", e.message);
            this.showRootStoreStatusError(e);
        }
    }

    navigateToDocumentByDocumentId(documentId) {
        let trimmedDocumentId = documentId.trim();
        let docIdx = -1;
        this.collectionStore.collection.forEach((d, idx) => {
            if (d["metadata"]["document_id"] === trimmedDocumentId) {
                if (docIdx === -1) {
                    docIdx = idx;
                }
            }
        });
        if (docIdx >= 0) {
            this.navigateToDocumentBySequenceIdx(docIdx);
        } else {
            console.log("not found");
        }
    }

    nextUnreadDocument() {
        console.log("next unread");
        const nextUnread = this.progressStore.findFirstUnreadDocument();
        this.navigateToDocumentBySequenceIdx(nextUnread);
    }

    sendAlert(title, message, callback, cancellable = false) {
        this.alertStore.sendAlert(title, message, callback, cancellable)
    }

    checkpoint() {
        console.log("saving rootStore checkpoint");
        const collectionCheckpoint = this.collectionStore.checkpoint();
        const progressCheckpoint = this.progressStore.checkpoint();
        const editorCheckpoint = this.editorStateStore.checkpoint();
        const projectCheckpoint = {
            collectionCheckpoint: collectionCheckpoint,
            progressCheckpoint: progressCheckpoint,
            editorCheckpoint: editorCheckpoint,
        };

        return projectCheckpoint;
    }

    resume(checkpoint) {
        console.log("resume rootStore checkpoint");
        // first save the current state of editor
        // so we can potentially rollback
        this.checkpointManager.checkpointEditorState("before resume");
        try {
            const {
                collectionCheckpoint,
                progressCheckpoint,
                editorCheckpoint
            } = checkpoint;
            this.editorStateStore.resume(editorCheckpoint);
            this.collectionStore.resume(collectionCheckpoint);
            this.progressStore.resume(progressCheckpoint);
            const lastRead = this.progressStore.findFirstUnreadDocument();
            this.collectionStore.setActiveDocument(lastRead);
            this.pageStateStore.jumpToPage(Pages.EDIT);
        } catch (e) {
            this.showRootStoreStatusError(e);
        }
    }

    showRootStoreStatusError(error) {
        this.alertStore.sendAlert("RootStore Error", <div>
            <p> Error Message: {error.message} </p>
            <p> Resume from last checkpoint/backups, or reload ACEditor</p>
        </div>)
    }

    checkStoreReady({ successCallback = () => {}, failureCallback = () => {} }) {
        if (this.sessionInfo["annotator_id"] === undefined) {
            this.sendAlert(<span style={{ color: 'red' }}>Unknown Annotator</span>,
                <p><b>Warning:</b> Annotator ID not specified </p>,
                failureCallback
            );
        } else {
            this.sendAlert("Welcome " + this.sessionInfo["annotator_id"] + " !",
                <p>Enjoy working with ACEditor!</p>,
                successCallback
            );
        }
    }

    get groupValidator() {
        const groupValidator = new GroupValidator();
        groupValidator.register(new EventsTriggerCoreferenceConsistencyValidator())
            .register(new SingletonCoreferenceValidator(GlobalViewConfig.COREFERENCE.namespace))
            .register(new SingletonCoreferenceValidator(GlobalViewConfig.EVENT_ARGUMENTS_GROUPS.namespace))
            .register(new SingletonCoreferenceValidator(GlobalViewConfig.EVENT_TRIGGERS_GROUPS.namespace));
        return groupValidator;
    }


    setToDefaultState(presets) {
        if (process.env.NODE_ENV === "development") {
            // go to dev view directly
            this.editorStateStore.resume({currentTaskId: 99});
        } else {
            this.editorStateStore.resume({currentTaskId: 1})
        }
        this.collectionStore.resume({documents: DefaultCollection, path: ""});
        this.progressStore.reset(DefaultCollection);

        // now set default view list
        if (process.env.REACT_APP_VERSION === "vendor") {
            // if this is vendor version;
            this.activeViewConfig.setViews(VendorViewProfile);
        } else if (process.env.NODE_ENV !== "development") {
            // delete dev view
            this.activeViewConfig.setViews(PrimeViewProfile);
        }

        if (presets) {
            if (presets["schema_config"] !== undefined) {
                this.schemaConfig.resume(presets["schema_config"])
            }
            if (presets["view_config"] !== undefined) {
                if (process.env.NODE_ENV !== "development") {
                    const presetsViewConfig = presets["view_config"];
                    if (presetsViewConfig !== undefined && presetsViewConfig["views"] !== undefined) {
                        const viewList = presetsViewConfig["views"].map(v => v["namespace"]);
                        this.activeViewConfig.resume({
                            "views": viewList
                        });
                    }
                    // redirect the task to something filtered first one
                    const selectedViews = GlobalViewConfig.filterByProfile(this.activeViewConfig.activeViews);
                    this.editorStateStore.setTask(selectedViews[0]);
                }
            }
            if (presets["version"] !== undefined && presets["compiled_time"] !== undefined) {
                VersionConfig["version"] = presets["version"];
                VersionConfig["utcTime"] = presets["compiled_time"]
            }
        }

        if (this.getUsingGroundTruth()) {
            // Set GroundTruth schema
            // 1. Check whether runtimeSchemaConfig exists
            if (window.GROUNDTRUTH.runtimeSchemaConfig) {
                this.schemaConfig.resume(window.GROUNDTRUTH.runtimeSchemaConfig);
            } else {
                this.schemaConfig.resume(window.GROUNDTRUTH.initialSchemaConfig);
            }

            // Set labeling task views config
            const buildConfig = window.GROUNDTRUTH.runtimeBuildConfig? window.GROUNDTRUTH.runtimeBuildConfig:
                window.GROUNDTRUTH.initialBuildConfig;

            const viewList = buildConfig["views"].map(v => v["namespace"]);
            this.activeViewConfig.resume({
                "views": viewList
            });
            const selectedViews = GlobalViewConfig.filterByProfile(this.activeViewConfig.activeViews);
            this.editorStateStore.setTask(selectedViews[0]);
        }
    }

    submitGroundTruth() {
        if (!window.GROUNDTRUTH) {
            return;
        }
        document.querySelector('crowd-form').submit();
    }

    getUsingGroundTruth() {
        /** Returns true if this interface is using sagemaker groundtruth */
        if (window.GROUNDTRUTH) {
            return true;
        }
        return false;
    }

}


export {RootStore};