import {DatabaseCandidateRecordType} from '../types/EntityLinkingTypes'
import {observable, action, runInAction} from 'mobx';

class DatabaseCandidateStore {
    @observable
    private _candidates: DatabaseCandidateRecordType[] = [];

    @observable
    private _id2CandidatesMap = new Map<string, DatabaseCandidateRecordType>();

    @observable
    private static instance: DatabaseCandidateStore;

    @observable
    private isLoading: boolean = false;

    @observable
    private filename: string = '';

    @observable
    private errorMessage: string = '';

    private constructor() {
    }

    @observable
    public static getInstance(): DatabaseCandidateStore {
        if (!DatabaseCandidateStore.instance) {
            DatabaseCandidateStore.instance = new DatabaseCandidateStore();
        }
        return DatabaseCandidateStore.instance;
    }
    
    @action
    public loadCandidatesFromFile(file: File) {
        const filename = file.name;
        const parts = filename.split('.');
        const extension = parts.pop()?.toLowerCase() || '';
        if (extension !== 'jsonl') {
            throw `Invalid database candidates file "${file.name}" - must have .jsonl extension`;
        }
        this.isLoading = true;
        this.errorMessage = '';
        this.filename = file.name;
        this._id2CandidatesMap.clear();
        this._candidates.splice(0,this._candidates.length);
        const reader = new FileReader();
        // Make this async operation an action so it atomically updates the _id2CandidatesMap
        reader.onload = ((e: ProgressEvent<FileReader>) => {
            const result = e.target?.result;
            const candidates: DatabaseCandidateRecordType[] = [];
            if (!result) {
                return;
            }
            const textResult = result as string;
            runInAction(() => {
                this.isLoading = false;
            });
            const lines = textResult.split(/\r?\n/);
            for(var i = 0; i < lines.length; i++) {
                const line = lines[i];
                if (!line.trim()) {
                    continue;
                }
                let row:any = {};
                try {
                    row = JSON.parse(line);
                } catch {
                    runInAction(() => {
                        this.errorMessage = `Error reading line ${i+1} in database candidates file "${this.filename}". Was expecting a single JSON line per row in JSONL input.`;
                        this._id2CandidatesMap.clear();
                    });
                    return;
                }
                
                if (!row.id || !row.attributes) {
                    runInAction(() => {
                        this.errorMessage = `Error reading line ${i+1} in database candidates file "${this.filename}". Each object should have an "id" and "attributes" property`;
                        this._id2CandidatesMap.clear();
                    });
                    return;
                }
                candidates.push(row as DatabaseCandidateRecordType);
            }
            this.setCandidates(candidates);
        });
        reader.readAsText(file);
    }

    @observable
    public getIsLoading() {
        return this.isLoading;
    }

    @observable
    public getFilename() {
        return this.filename;
    }

    @observable
    public getErrorMessage() {
        return this.errorMessage;
    }

    @action
    public setCandidates(candidates: DatabaseCandidateRecordType[]) {
        this._id2CandidatesMap.clear();
        this._candidates.splice(0,this._candidates.length);
        candidates.forEach((r) => {
            this._id2CandidatesMap.set(r.id, r);
            this._candidates.push(r);
        })
    }

    @observable
    public retrieveCandidates(candidateIds: string[] | undefined = undefined): DatabaseCandidateRecordType[] {
        if (!candidateIds) {
            return this._candidates;
        }
        const candidates: DatabaseCandidateRecordType[] = [];
        
        for (let each of candidateIds) {
            if (this._id2CandidatesMap.has(each)) {
                const candidate = this._id2CandidatesMap.get(each)!;
                candidates.push(candidate);
            }
        }

        return candidates;
    }
}

export {DatabaseCandidateStore}