import React, {Component} from 'react'
import {observer} from 'mobx-react'
import 'annotator/AnnotatorTagSpan.css'
import uuid from "uuid";
import {UncontrolledTooltip} from "reactstrap";
import {LayerIndex, normalizeTagText, OverlayMode} from "./Constants";

const Layer0Color = "inherit";
const Layer1Color = "#b3f0ff";
const Layer2Color = "#ffe184";
const MismatchColor = "#ffa69f";
// const FPColor = "#957ddc"; // False Positive
// const FNColor = "#c38db8"; // False Negative


const LayerColors = [Layer0Color, Layer1Color, Layer2Color];
const DoubleLayerSpan = observer(
    class DoubleLayerSpan extends Component {
        constructor(props) {
            super(props);
            // used for floating tooltip
            this.tagSpanUUID = "target-" + uuid.v4();
            this.computeColorDisplayPlan = this.computeColorDisplayPlan.bind(this);
            this.computeTagDisplayPlan = this.computeTagDisplayPlan.bind(this);
        }

        computeColorDisplayPlan(layers, mode) {
            // each layer is a span
            const layerIndices = layers.map((s) => {
                return s.attrs.layer;
            });
            // background layer
            const outerLayerIdx = Math.min(...layerIndices);
            // inner layer
            const innerLayerIdx = Math.max(...layerIndices);
            // assuming by default there is no span attached
            let outerColor = LayerColors[0];
            let innerColor = LayerColors[0];
            if (innerLayerIdx === 2 && outerLayerIdx === 1) {
                // case 1, overlay span
                outerColor = LayerColors[1];
                // now decide whether the inner/outer tags matches
                innerColor = Layer2Color;
                if (layers[0].tag !== layers[1].tag) {
                    outerColor = MismatchColor;
                }
            } else if (innerLayerIdx === 2 && outerLayerIdx === 0) {
                // case 2, only inner span
                // mark this as false positive color
                innerColor = Layer2Color;
            } else if (innerLayerIdx === 1) {
                // case 3, only outer span
                // mark it as false negative color
                innerColor = Layer1Color;
                outerColor = Layer1Color;
            }
            // overrides with mode
            if (mode === OverlayMode.SHOW_V1) {
                innerColor = Layer0Color;
                outerColor = Layer0Color;
                if (innerLayerIdx === LayerIndex.FRONT) {
                    // has v2 in the span
                    innerColor = Layer2Color;
                    outerColor = Layer0Color;
                }

            } else if (mode === OverlayMode.SHOW_V2) {
                // first reset all colors
                innerColor = Layer0Color;
                outerColor = Layer0Color;
                // show only V1
                if (outerLayerIdx === 1 || innerLayerIdx === 1) {
                    // contains layer 1
                    innerColor = Layer1Color;
                    outerColor = Layer1Color;
                }
            }

            return {
                innerLayer: innerLayerIdx,
                outerLayer: outerLayerIdx,
                innerColor: innerColor,
                outerColor: outerColor
            }
        }

        computeTagDisplayPlan(tags, mode) {
            let displayInnerTag = "none";
            let displayOuterTag = "none";

            if (tags[2] !== undefined && mode !== OverlayMode.SHOW_V2) {
                displayInnerTag = "";
            }
            if (tags[1] !== undefined && mode !== OverlayMode.SHOW_V1) {
                displayOuterTag = "";
            }
            return {
                displayInnerTag: displayInnerTag,
                displayOuterTag: displayOuterTag
            }
        }

        render() {
            const tags = this.props.tags;
            const mode = this.props.mode;
            const disableHover = !!this.props.disableHover;
            const {innerLayer, outerLayer, innerColor, outerColor} = this.computeColorDisplayPlan(this.props.layers, mode);
            const {displayInnerTag, displayOuterTag} = this.computeTagDisplayPlan(tags, mode);

            const createHoverContent = (layer) => {
                const span = this.props.layers.filter(s => s.attrs.layer === layer)[0];
                const v1Or2 = (layer === 2) ? "V1" : "V2";
                const property = this.props.property
                const document_properties =  this.props.document_properties
                console.log(document_properties)
                return <ul style={{
                    "padding": 0,
                    "margin": 0,
                    "textAlign": "left"
                }}>
                    <li>{v1Or2}: {span.tag}</li>
                    {property.length > 0 &&  <li> {v1Or2} Properties: </li>}
                    {property.map(d => (<li> {d.property.name} : {JSON.stringify(d.value)}</li>))} 
                    {document_properties.length > 0 &&  <li> {v1Or2} Document Properties: </li>}
                    {document_properties.map(d => (<li> {d.property.name} : {JSON.stringify(d.value)}</li>))} 
                </ul>
            };
            let hover = "";
            let outerHover = "";
            if (displayInnerTag !== "none" && !disableHover) {
                // For V1 Tag
                hover = <UncontrolledTooltip className="NoSelect"
                                             style={{
                                                 "background": "rgba(0,0,0,0.9)"
                                             }}
                                             placement="top-start"
                                             delay={20}
                                             target={this.tagSpanUUID + "_inner"}>

                    {createHoverContent(2)}
                </UncontrolledTooltip>

            }
            if (displayOuterTag !== "none" && !disableHover) {
                outerHover = <UncontrolledTooltip className="NoSelect"
                                                  style={{
                                                      "background": "rgba(0,0,0,0.9)"
                                                  }}
                                                  placement="top-start"
                                                  delay={20}
                                                  target={this.tagSpanUUID + "_outer"}>
                    {createHoverContent(1)}
                </UncontrolledTooltip>
            }

            if (innerLayer > 0 || outerLayer > 0) {
                // at least one tag is associated the current
                // span, we don't do display the newline display
                return <span className="OuterHighlightSpan"
                             id={this.tagSpanUUID + "_outer"}
                             style={{"background": outerColor}}>
                    <span className="InnerHighlightSpan"
                          id={this.tagSpanUUID + "_inner"}
                          style={{"background": innerColor}}>
                    {this.props.text}
                        <span className="SmallTagStyle NoSelect"
                              style={{"display": displayInnerTag, "zIndex": 10}}
                              data-tag>{normalizeTagText(tags[2])}
                          </span>
                    </span>
                    <span className="SmallTagStyle NoSelect"
                          style={{"display": displayOuterTag}}
                          data-tag>{normalizeTagText(tags[1])}
                          </span>
                    {hover}
                    {outerHover}</span>
            } else {
                // no tag is associated with this span, do the break
                let lines = this.props.text.split('\n');
                let segments = [];
                // unlike the SingleLayerSpan class, here we don't need
                // to track start and end
                for (let i = 0; i < lines.length; i++) {
                    segments.push(<span className="OuterHighlightSpan"
                                        id={this.tagSpanUUID + "_outer"}
                                        style={{"background": outerColor}}
                                        key={this.tagSpanUUID + "_outer"}>
                        <span className="InnerHighlightSpan"
                              id={this.tagSpanUUID + "_inner"}
                              style={{
                                  "background": innerColor,
                                  "fontWeight": 300
                              }}>
                            {lines[i]}
                        </span>
                    </span>);
                    if (i !== lines.length - 1) {
                        // not the last segment
                        segments.push(<br/>)
                    }
                }
                return segments;
            }

        }
    }
    )
;

export {
    DoubleLayerSpan
}