import {DocumentStateStore} from "./DocumentStateStore";
import {RangeMap} from "annotator/core/RangeMap";
import RangeTree from "annotator/core/RangeTree";
import {UFSchema} from "annotator/core/UFSchema";
import {Attributes, SpanFactory} from "annotator/core/Spans";
import {TagGroupStore} from "./TagGroupStore";
import {DefaultRateLimiter, ZeroRateLimiter} from "annotator/store/SpanRateLimiter";
import {TaggingStateStore, TaggingStoreUndoMangerFactory} from "./TaggingStateStore";
import {Namespaces} from "annotator/core/Namespaces";
import {ComprehendCommonNERTagSet} from "../default/DefaultSchema";
import {EntityAttributesStore} from "./EntityAttributesStore";

const Role = {
    Annotator: 1,
    Arbitrator: 2
};

class TaggingArbitrationStateStore extends DocumentStateStore {
    constructor(documentObject,
                namespace = Namespaces.NER_ARBITRATION,
                tagSet = ComprehendCommonNERTagSet,
                enableRateLimit = false,
                attributesToEntityConfig = undefined) {
        super(documentObject);
        // noted the schema key is the schema key of
        // the original task, like 'named_entity' and 'key_phrase'
        this.schemaKey = "";
        this.namespace = namespace;
        if (namespace === Namespaces.NER_ARBITRATION) {
            this.schemaKey = Namespaces.NER
        } else if (namespace === Namespaces.KPE_ARBITRATION) {
            this.schemaKey = Namespaces.KPE;
        } else {
            throw new Error("TaggingArbitrationStateStore finds unsupported task -> " + namespace);
        }
        this.tagGroupStore = new TagGroupStore(tagSet);
        this.entityAttributesStore = new EntityAttributesStore(attributesToEntityConfig);
        this.rateLimiter = enableRateLimit ? DefaultRateLimiter() : ZeroRateLimiter();
        this.undoManager = TaggingStoreUndoMangerFactory(this);
        this.toggleOriginalSpan = this.registerAction(this.toggleOriginalSpan.bind(this));
        this.removeSpan = this.registerAction(this.removeSpan.bind(this));
        this.insertTagSpan = this.registerAction(this.insertTagSpan.bind(this));
        this.updateActiveTag = this.registerAction(this.updateActiveTag.bind(this));
        this.updateFocusSpanAttributes = this.registerAction(this.updateFocusSpanAttributes.bind(this));
        this.toggleActiveSpan = this.registerViewAction(this.toggleActiveSpan.bind(this));
        this.toggleFocusSpan = this.registerViewAction(this.toggleFocusSpan.bind(this));
        this.switchMode = this.registerViewAction(this.switchMode.bind(this));
        this.getFocusSpanAttributes = this.getFocusSpanAttributes.bind(this);
        this.insertSpan = this.insertSpan.bind(this);
        // init all states
        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        let annotations = documentObject[UFSchema.ANNOTATIONS][this.schemaKey];
        if (!annotations) {
            annotations = [];
        }
        let corrections = documentObject[UFSchema.ANNOTATIONS][this.namespace];
        if (!corrections) {
            corrections = {};
        }
        let added = corrections["added"] ? corrections["added"] : [];
        let removed = corrections["removed"] ? corrections["removed"] : [];

        this.states.rawText = rawText;
        this.states.activeSpan = undefined;
        this.states.focusSpan = undefined;
        this.states.focusMode = false;
        this.states.spanFactory = new SpanFactory(rawText);
        const runes = this.splitter.splitGraphemes(documentObject[UFSchema.RAW_TEXT]);
        // track whether a span has been removed by arbitrator or not
        let rangeMap = new RangeMap();
        removed.forEach((s1) => {
            let s2 = this.states.spanFactory.copySpan(s1);
            super.expandSpanToHaveMultibyteOffset(s2, runes);
            s2.setAttribute(Attributes.Removed, true);
            //a["attrs"]["removed"] = true;
            rangeMap.add(s2, s2);
        });
        this.states.originalSpanStateMap = rangeMap;
        // create by original annotator
        // can be overwrite by user (arbitrator)
        let originalSpans = annotations.map((s1) => {
            const s2 = this.states.spanFactory.copySpan(s1);
            super.expandSpanToHaveMultibyteOffset(s2, runes);
            s2.setAttribute(Attributes.Selectable, true);
            return s2;
        });
        this.states.originalSpanTree = RangeTree.createRangeTree(originalSpans);
        //create by arbitrator
        added = added.map((s1) => {
            // the arbitrator added annotations are mutable, so
            // they can be reselected without removing
            let s2 = this.states.spanFactory.copySpan(s1);
            super.expandSpanToHaveMultibyteOffset(s2, runes);
            s2.setAttribute(Attributes.Selectable, true);
            return s2;
        });
        this.states.spanTree = RangeTree.createRangeTree(added);
    }

    get spans() {
        console.log("merge two trees");
        const rawText = this.states.rawText;
        const spanFactory = this.states.spanFactory;
        // this will yield all no-overlapping spans
        let ranges = RangeTree.retrieveAllNonOverlappingRanges(this.states.originalSpanTree, rawText.length);
        // console.log("non-overlapping ranges");
        // console.log(ranges);
        // next we mark everything that is removed
        let originalAndBlankSpans = [];
        ranges.forEach((r) => {
            let [start, end, value] = r;
            if (!value) {
                // a blank span
                let s1 = spanFactory.createSpan(start, end);
                s1.setAttribute(Attributes.Selectable, true);
                originalAndBlankSpans.push(s1);
            } else {
                let oldSpan = spanFactory.createSpan(start, end, value.tag, value.attrs);
                oldSpan.setAttribute(Attributes.Removed, false)
                    .setAttribute(Attributes.Selectable, true)
                    .setAttribute(Attributes.Role, Role.Annotator);
                if (this.states.originalSpanStateMap.get(oldSpan)) {
                    // this span is removed
                    oldSpan.setAttribute(Attributes.Removed, true);
                }
                originalAndBlankSpans.push(oldSpan);
            }
        });
        // extract all added ranges
        let addedRanges = RangeTree.retrieveAllInsertedRanges(this.states.spanTree);
        let addedSpans = [];
        addedRanges.forEach((r) => {
            let [start, end, value] = r;
            // user added span can't be further selected
            let newSpan = spanFactory.createSpan(start, end, value.tag);
            newSpan.setAttribute(Attributes.Role, Role.Arbitrator)
                .setAttribute(Attributes.Selectable, false);
            addedSpans.push(newSpan);
        });
        console.log("added spans -> ", addedSpans);
        // ALERT: Mega scale complex logic...much more documentation is needed
        let mergedFlatSpans = [];
        let oIdx = 0; // for original spans
        let aIdx = 0; // for added spans
        while (oIdx < originalAndBlankSpans.length && aIdx < addedSpans.length) {
            // orgSpan means either it is a original annotated span, or a blank span
            let orgSpan = originalAndBlankSpans[oIdx];
            // added span means the arbitrator added span
            let addSpan = addedSpans[aIdx];

            let intersection = RangeTree.checkOverlap(orgSpan.range, addSpan.range);
            if (!intersection) {
                // no intersection
                if (orgSpan.start < addSpan.start) {
                    mergedFlatSpans.push(orgSpan);
                    oIdx += 1;
                } else {
                    mergedFlatSpans.push(addSpan);
                    aIdx += 1;
                }
            } else {
                // there is intersection
                // 3 scenarios

                // oSpan within aSpan
                if (RangeTree.checkWithin(addSpan.range, orgSpan.range)) {
                    // the original Span is eclipsed by the the added Span
                    // [aaaaaa[oooo]aaaaaa]

                    // treat it as if it is removed
                    if (!orgSpan.getAttribute(Attributes.BLANK)) {
                        orgSpan.setAttribute(Attributes.Removed, true);
                        // we need a deep copy here because some changes
                        // happened inplace.
                        this.states.originalSpanStateMap.add(orgSpan, spanFactory.copySpan(orgSpan));
                    }
                    oIdx += 1;
                } else if (RangeTree.checkWithin(orgSpan.range, addSpan.range)) {
                    // aSpan within oSpan
                    // [ooooo[aaaa]ooooo]
                    // break into 3 pieces
                    console.log("trigger within");

                    // it is equal as removed
                    if (!orgSpan.getAttribute(Attributes.BLANK)) {
                        orgSpan.setAttribute(Attributes.Removed, true);
                        this.states.originalSpanStateMap.add(orgSpan, spanFactory.copySpan(orgSpan));
                    }
                    let [intersectionStart, intersectionEnd] = intersection;
                    //console.log("intersection -> ", intersectionStart, intersectionEnd);
                    if (orgSpan.start < intersectionStart) {
                        // push the head
                        let prev = spanFactory.copySpan(orgSpan);
                        prev.end = intersectionStart;
                        // console.log("prev ->", prev);
                        mergedFlatSpans.push(prev)
                    }
                    // push the middle aSpan
                    mergedFlatSpans.push(addSpan);
                    aIdx += 1;

                    if (intersectionEnd < orgSpan.end) {
                        // NOTED we can't push right now
                        // the tail part of the original span can be inserted too
                        orgSpan.start = intersectionEnd;
                    } else {
                        oIdx += 1;
                    }

                } else {
                    // intersect but not within
                    if (!orgSpan.getAttribute(Attributes.BLANK)) {
                        orgSpan.setAttribute(Attributes.Removed, true);
                        this.states.originalSpanStateMap.add(orgSpan, spanFactory.copySpan(orgSpan));
                    }
                    let [intersectionStart, intersectionEnd] = intersection;
                    // adjust oSpan in place
                    if (addSpan.start < orgSpan.start) {
                        // aSpan is ahead of oSpan
                        // [aaaaa[aaaa]ooooo]

                        //update oSpan in place
                        orgSpan.start = addSpan.end;
                        mergedFlatSpans.push(addSpan);
                        aIdx += 1;
                    } else {
                        // aSpan is after of oSpan
                        // [ooooo[aaaa]aaaaaa]

                        // truncate the original span
                        orgSpan.end = intersectionStart;
                        mergedFlatSpans.push(orgSpan);
                        oIdx += 1;
                    }
                }
            }
        }

        // append what is left
        while (oIdx < originalAndBlankSpans.length) {
            mergedFlatSpans.push(originalAndBlankSpans[oIdx]);
            oIdx += 1;
        }
        while (aIdx < addedSpans.length) {
            mergedFlatSpans.push(addedSpans[aIdx]);
            aIdx += 1;
        }

        // here we need to do copy, to force generate
        // new text extents, since some of the span have changed
        // their range.
        return mergedFlatSpans.map((s) => {
            return this.states.spanFactory.copySpan(s);
        });
    }

    convertStatesToAnnotations() {
        console.log("export corrections annotations");
        const schemaKey = this.schemaKey;
        const spans = this.spans;
        let correctedAnnotations = [];
        // this is the annotations after corrections
        spans.forEach((s) => {
            if (!s.attrs.blank && !s.attrs.removed) {
                // const {start, end, tag, extent} = s;
                correctedAnnotations.push(s.exportAnnotation(false));
            }
        });
        let annotationObject = {};
        annotationObject[schemaKey + "_corrected"] = correctedAnnotations;
        let corrections = {
            "added": [],
            "removed": []
        };
        this.states.originalSpanStateMap.map.forEach(
            (value, key, m) => {
                corrections["removed"].push(
                    value.exportAnnotation(false)
                );
                // console.log(value, key)
            }
        );
        let addedSpans = RangeTree.retrieveAllInsertedRanges(this.states.spanTree);
        addedSpans.map((e) => {
            let [_s, _e, value] = e;
            corrections["added"].push(value.exportAnnotation());
        });
        corrections["added"].sort((s1, s2) => s1.start - s2.end);
        corrections["removed"].sort((s1, s2) => s1.start - s2.end);
        annotationObject = Object.assign(annotationObject, corrections);
        let updatedAnnotations = {};
        updatedAnnotations[this.namespace] = annotationObject;
        return updatedAnnotations;
    }

    toggleOriginalSpan(start, end) {
        console.log(RangeTree.retrieveAllInsertedRanges(this.states.originalSpanTree));
        console.log("find result ->", this.states.originalSpanTree.find([start, end]));
        if (this.states.originalSpanTree.find([start, end]).valid) {
            console.log("find span");
            // the span is find
            // save the span for undo operation
            const oldSpan = this.states.originalSpanTree.get([start, end]);
            if (this.states.originalSpanStateMap.get(oldSpan)) {
                // if find remove it
                this.states.originalSpanStateMap.remove(oldSpan);
            } else {
                // if not find
                this.states.originalSpanStateMap.add(oldSpan, oldSpan)
            }
            console.log(this.states.originalSpanStateMap);
            return true;
        }
        return false;
    }

    insertTagSpan(start, end, tag, track = true) {
        const newSpan = TaggingStateStore.doInsertTagSpan(this, start, end, tag, track);
        return newSpan;
    }

    removeSpan(start, end, track = true) {
        const oldSpan = TaggingStateStore.doRemoveSpan(this, start, end, track);
        return oldSpan;
    }

    updateActiveTag(tag) {
        return TaggingStateStore.doUpdateActiveTag(this, tag);
    }

    insertSpan(start, end) {
        const newSpan = this.insertTagSpan(start, end, this.tagGroupStore.selectedTag, true);
        this.states.activeSpan = undefined;
        return newSpan;
    }

    toggleActiveSpan(span) {
        return TaggingStateStore.doToggleSpanSelection(this, span);
    }

    removeActiveSpan() {
        return TaggingStateStore.doRemoveActiveSpan(this);
    }

    updateFocusSpanAttributes(attributes) {
        return TaggingStateStore.doUpdateSelectSpanAttributes(this,
            this.states.focusSpan, attributes);
    }

    getFocusSpanAttributes() {
        const activeSpan = this.states.focusSpan;
        if (activeSpan) {
            return this.entityAttributesStore.getSpanEntityAttributes(activeSpan);
        }
    }

    toggleFocusSpan(span) {
        const focusSpan = this.states.focusSpan;
        if (focusSpan === undefined) {
            // set it anyway
            this.states.focusSpan = span;
        } else {
            if (focusSpan === span) {
                // de-selected the already selected span
                this.states.focusSpan = undefined;
            } else {
                this.states.focusSpan = span;
            }
        }
    }

    switchMode() {
        this.states.focusMode = !this.states.focusMode;
        console.log("this.states.focusMode", this.states.focusMode);
    }

}

export {TaggingArbitrationStateStore, Role}