import React, {Component} from 'react';
import {Button, Input, InputGroup, InputGroupAddon} from "reactstrap";


class TextInputBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            text: undefined,
            buttonText: undefined
        };
    }

    componentDidMount() {
        this.setState({ buttonText: this.props.buttonText ? this.props.buttonText : "Set" });
    }

    render() {
        return <div id="SchemaVersionInputBox">
            <InputGroup style={{"maxWidth": "350px"}}>
                <Input placeholder={"unknown"}
                       onChange={(e) => {
                           this.setState({text: e.target.value});
                       }}/>
                <InputGroupAddon addonType="append">
                    <Button color={this.state.buttonText === 'SUCCESS!' ? 'success' : 'primary'}
                            onClick={() => {
                                this.props.submit(this.state.text);
                                
                                if (this.state.text !== undefined) {
                                    this.setState({ buttonText: 'SUCCESS!' });
                                    setTimeout(() => {
                                        this.setState({ buttonText: this.props.buttonText ? this.props.buttonText : "Set" });
                                    }, 1000);
                                }
                            }}>
                        {this.state.buttonText}
                    </Button>
                </InputGroupAddon>
            </InputGroup>
        </div>
    }
}

export {TextInputBox}