import {Checkpointtable} from "../checkpoint/Checkpointtable";
import {deepCopy} from "../../annotator/utils/DeepCopy";
import {BasePropertyType, TargetProperties} from "../../annotator/types/BasePropertyType";

type SpanPropertyRecordType = BasePropertyType


const copyProperty = (sp: SpanPropertyRecordType): SpanPropertyRecordType => {
    return {
        name: sp.name,
        level: sp.level,
        export_key: sp.export_key,
        field_type: sp.field_type,
        targets: sp.targets,
        values: sp.values,
        options: sp.options,
    };
};

class TaskSpanPropertiesConfig extends Checkpointtable {
    namespace: string;
    spanProperties: SpanPropertyRecordType[];

    constructor(namespace: string) {
        super();
        this.namespace = namespace;
        this.spanProperties = [];
    }


    public resume(taskSchema: any) {
        console.log("Resume properties config")
        if (taskSchema !== undefined) {
            let annotationTask = taskSchema["annotation_task"];
            if (annotationTask === "NER") {
                // only NER tasks can have span properties registered
                const properties: SpanPropertyRecordType[] = taskSchema["properties"];
                if (properties !== undefined) {
                    properties.forEach((p) => {
                        this.rectifySpanProperty(p);
                        let {name, level, export_key, targets, field_type, values, options} = p;
                        this.addSpanProperty({
                            name: name,
                            level: level,
                            export_key: export_key,
                            targets: targets,
                            field_type: field_type,
                            values: values,
                            options: options
                        });
                    })
                }
            }
        }
    }

    public checkpoint(): SpanPropertyRecordType[] {
        return this.spanProperties.map((sp) => {
            return deepCopy(sp);
        });
    }

    public rectifySpanProperty(spanProperty: SpanPropertyRecordType) {
        if (spanProperty.level === "document") {
            // make sure targets are assigned before use
            spanProperty.targets = [];
        }
        spanProperty.export_key = spanProperty.export_key || spanProperty.name;
        return spanProperty;
    }

    public addSpanProperty(spanProperty: SpanPropertyRecordType) {
        this.rectifySpanProperty(spanProperty);
        const spanPropertyName = spanProperty.name;
        const propertyIdx = this.spanProperties.findIndex((sp) => sp.name === spanPropertyName);
        if (propertyIdx === -1) {
            // Do not registered the same property twice
            this.spanProperties.push(spanProperty);
        }
    }

    public removeSpanProperty(spanProperty: SpanPropertyRecordType) {
        this.removeSpanPropertyByName(spanProperty.name);
    }

    public removeSpanPropertyByName(spanPropertyName: string) {
        const propertyIdx = this.spanProperties.findIndex((sp) => sp.name == spanPropertyName);
        if (propertyIdx > -1) {
            // property found, remove it
            this.spanProperties.splice(propertyIdx, 1);
        }
    }

    public updateSpanProperty(spanProperty: SpanPropertyRecordType) {
        this.rectifySpanProperty(spanProperty);
        const propertyIdx = this.spanProperties.findIndex((sp) => sp.name == spanProperty.name);
        if (propertyIdx > -1) {
            // property found, only update when type matches
            if (this.spanProperties[propertyIdx]["field_type"] === spanProperty["field_type"]) {
                this.spanProperties[propertyIdx] = spanProperty;
            }
        }
    }

    public fixInconsistencies(tagSet: string[]) {
        // look into each property to see whether it contains already deleted tags
        // this will be useful upon unloading the schema, to make things consistent

        const checkTags = new Set(tagSet);

        let toUpdate: SpanPropertyRecordType[] = [];
        let toRemove: SpanPropertyRecordType[] = [];

        this.spanProperties.forEach((sp) => {
            const tagsToKeep = sp.targets.filter((t) => checkTags.has(t));
            if (tagsToKeep.length === 0) {
                // all the tags that this property predicated on is removed
                if (sp.level === "tag") {
                    // only if it is at "tag" level, document level will always
                    // have an empty targets field
                    toRemove.push(sp);
                }
            } else {
                sp.targets = tagsToKeep;
                toUpdate.push(sp);
            }
        });

        // update all properties
        toUpdate.forEach((sp) => {
            this.updateSpanProperty(sp);
        });
        toRemove.forEach((sp) => {
            this.removeSpanProperty(sp);
        })
    }

    get targetProperties(): TargetProperties {
        let _targetProperties: TargetProperties = {
            tags: {},
            document: []
        };

        this.spanProperties.forEach((sp) => {
            const sp2 = copyProperty(sp);
            if (sp2.level === "document") {
                _targetProperties.document.push(sp2);
            } else {
                // this is a tag level property
                const tags = sp2.targets!;
                tags.forEach((t) => {
                    if (!_targetProperties.tags.hasOwnProperty(t)) {
                        // first time see this property
                        _targetProperties.tags[t] = [];
                    }
                    _targetProperties.tags[t].push(sp2);
                });
            }
        });

        return _targetProperties;
    }

    public clear() {
        // clear already registered properties
        this.spanProperties = [];
    }

}

class PropertiesConfig {
    private schemaConfig: any;
    private taskPropertiesConfig: Map<string, TaskSpanPropertiesConfig>;

    constructor(schemaConfig: any) {
        this.schemaConfig = schemaConfig;
        this.taskPropertiesConfig = new Map();

        this.createTaskConfig = this.notifyChange(this.createTaskConfig.bind(this));
        this.addTaskSpanPropertyConfig = this.notifyChange(this.addTaskSpanPropertyConfig.bind(this));
        this.removeTaskSpanPropertyConfig = this.notifyChange(this.removeTaskSpanPropertyConfig.bind(this));
        this.removeTaskSpanPropertyConfigByName = this.notifyChange(this.removeTaskSpanPropertyConfigByName.bind(this));
        this.updateTaskSpanPropertyConfig = this.notifyChange(this.updateTaskSpanPropertyConfig.bind(this));
        this.resetAll = this.notifyChange(this.resetAll.bind(this));
    }

    public notifyChange(method: (...args: any) => any) {
        return (...args: any) => {
            const result = method(...args);
            if (this.schemaConfig !== undefined) {
                // could be undefined
                this.schemaConfig.configUpdated += 1;
            }
            return result;
        }
    }

    public copySpanProperty(spanProperty: SpanPropertyRecordType): SpanPropertyRecordType {
        return copyProperty(spanProperty);
    }

    public createTaskConfig(namespace: string, taskSchema?: any) {
        const taskConfig = new TaskSpanPropertiesConfig(namespace);
        taskConfig.resume(taskSchema);
        this.taskPropertiesConfig.set(namespace, taskConfig);
    }

    public addTaskSpanPropertyConfig(namespace: string, spanProperty: SpanPropertyRecordType) {
        const taskConfig = this.getTaskConfig(namespace);
        if (taskConfig !== undefined) {
            taskConfig.addSpanProperty(spanProperty);
        }
    }

    public removeTaskSpanPropertyConfig(namespace: string, spanProperty: SpanPropertyRecordType) {
        const taskConfig = this.getTaskConfig(namespace);
        if (taskConfig !== undefined) {
            taskConfig.removeSpanProperty(spanProperty);
        }
    }

    public removeTaskSpanPropertyConfigByName(namespace: string, spanPropertyName: string) {
        const taskConfig = this.getTaskConfig(namespace);
        if (taskConfig !== undefined) {
            taskConfig.removeSpanPropertyByName(spanPropertyName);
        }
    }

    public updateTaskSpanPropertyConfig(namespace: string, spanProperty: SpanPropertyRecordType) {
        const taskConfig = this.getTaskConfig(namespace);
        if (taskConfig !== undefined) {
            taskConfig.updateSpanProperty(spanProperty);
        }
    }

    public getTaskSpanPropertyTemplateByType(name: string, fieldType: "KeyValue" | "Text"): SpanPropertyRecordType {
        let defaultSpanPropertyTemplate: SpanPropertyRecordType = {
            name: name,
            level: "tag",
            export_key: name,
            targets: [],
            field_type: fieldType,
            // add required as a default property
            options: {
                required: false
            },
        };

        if (fieldType === "KeyValue") {
            // need to set empty values field
            Object.assign(defaultSpanPropertyTemplate, {values: []});
            defaultSpanPropertyTemplate.options!["multi_values"] = false;
        } else {
            defaultSpanPropertyTemplate.options!["multi_instances"] = false;

        }

        return defaultSpanPropertyTemplate
    }

    public resetAll() {
        this.taskPropertiesConfig.forEach((v, _) => {
            // clear each instance independently
            v.clear();
        })
    }

    public getTaskConfig(namespace: string) {
        return this.taskPropertiesConfig.get(namespace);
    }

    get tasks(): string[] {
        let tasks = Array.from(this.taskPropertiesConfig.keys());
        tasks.sort();
        return tasks;
    }
}


export {TaskSpanPropertiesConfig, PropertiesConfig}