const randomElementKey = () => {
    // copied from https://stackoverflow.com/questions/1349404/generate-random-string-characters-in-javascript
    return Math.random().toString(36).substring(2);
};

/**
 * Copied from:
 * https://stackoverflow.com/questions/273789/is-there-a-version-of-javascripts-string-indexof-that-allows-for-regular-expr/274094
 * @param str 
 * @param regex 
 * @param startpos 
 */
const regexIndexOf = (str: string, regex: RegExp, startpos: number) => {
    var indexOf = str.substring(startpos || 0).search(regex);
    return (indexOf >= 0) ? (indexOf + (startpos || 0)) : indexOf;
}

const fetchContent = async (uri: string) => {
    return fetch(uri).then(
        res => res.text().then(body => {
            console.log(body);
            return body;
        }
    ),
    () => 'not_able_to_fetch'
  );
}

export {randomElementKey, regexIndexOf, fetchContent}
