import React from "react";
import {Button} from "reactstrap";
import './Properties.css'


type TagPropType = {
    text: string,
    color: string,
    removeHandler: any
}

class RemovableTag extends React.Component<TagPropType> {
    constructor(props: TagPropType) {
        super(props);
    }

    render() {
        const {text, color, removeHandler} = this.props;
        return <div className="PropertyTagContainer">
                <span className="PropertyTag">
                    {text}
                    <Button color={color}
                            size="sm"
                            className="RemoveButton"
                            onClick={removeHandler}>
                        X
                    </Button>
                </span>
        </div>
    }
}

export {RemovableTag}