class UndoEditLogManager {
    constructor() {
        this.edits = [];
        this.undoActionMap = new Map();
        this.recordEdit = this.recordEdit.bind(this);
        this.undoEdit = this.undoEdit.bind(this);
        this.registerUndoAction = this.registerUndoAction.bind(this);
    }

    registerUndoAction(action, undoCallback) {
        this.undoActionMap[action] = undoCallback;
    }

    recordEdit(action, context) {
        this.edits.push({action: action, context: context});
    }

    undoEdit() {
        if (this.edits.length > 0) {
            const {action, context} = this.edits.pop();
            const undoCallback = this.undoActionMap[action];
            undoCallback(context);
        }
    }
}

export {UndoEditLogManager}