import {
    BasePropertyType,
    PropertyInstance,
    PropertyKeyValueType,
    PropertyValueType
} from "../types/BasePropertyType";
import {ViolationType} from "../types/CoreTypes";

class PropertyInstanceDataStore {
    propertyDefinition: Map<string, BasePropertyType>;
    instanceDataStore: Map<string, PropertyValueType>;

    constructor(propertyList: BasePropertyType[]) {
        this.propertyDefinition = new Map<string, BasePropertyType>();
        propertyList.forEach((p) => {
            this.propertyDefinition.set(p.name, p);
        });
        this.instanceDataStore = new Map<string, PropertyValueType>();
    }

    public retrieveProperty(): PropertyInstance[] {
        const properties: PropertyInstance[] = [];
        for (const [k, v] of Array.from(this.propertyDefinition.entries())) {
            let instance: PropertyInstance = {
                property: v,
                value: []
            };
            if (this.instanceDataStore.has(v.export_key!)) {
                instance.value = this.instanceDataStore.get(v.export_key!)!;
            }
            properties.push(instance)
        }
        return properties;
    }


    public updateProperty(instances: PropertyKeyValueType[]) {
        instances.forEach((i) => {
            const {name, value} = i;
            console.log(`Updating property with name: ${name} , value ${value}`)
            const propertyDef = this.propertyDefinition.get(name);

            if (value !== undefined && propertyDef) {
                // values are valid and property is allowed
                console.log("Export key: " + propertyDef.export_key)
                this.instanceDataStore.set(propertyDef.export_key!, value);
            }
        })
    }

    public removeProperty(name: string) {
        const propertyDef = this.propertyDefinition.get(name);
        if (propertyDef) {
            console.log(`Deleting property for name: ${name} , export key ${propertyDef.export_key}`)
            this.instanceDataStore.delete(propertyDef.export_key!);
        }
    }

    public load(properties: { [key: string]: string | string[] }) {
        console.log("Properties load called :" + JSON.stringify(properties));
        for (const [k, v] of Object.entries(properties)) {
            const relevantPropertyDef = Array.from(this.propertyDefinition.values()).find(propertyDef => propertyDef.export_key === k)
            if (relevantPropertyDef) {
                // property definition exists
                if (Array.isArray(v)) {
                    // value is already an array
                    this.instanceDataStore.set(k, v);
                }
                if (typeof v === 'string') {
                    // single string type
                    // perform automatic boxing
                    this.instanceDataStore.set(k, [v]);
                }
            }
        }
    }

    public dump() {
        console.log("Properties Dump called");
        const properties: { [key: string]: string | string[] } = {};
        for (const [k, v] of Array.from(this.instanceDataStore.entries())) {
            // NOTE: More than 1 propertyDef can have the same export key.
            // So if we define 2 propertyDef with same export key but different behavior
            // (field type/multi_value/multi_instance) we can get unexpected results
            const relevantPropertyDef = Array.from(this.propertyDefinition.values()).find(propertyDef => propertyDef.export_key === k)
            if (relevantPropertyDef !== undefined) {
                // valid property
                if (v.length > 0) {
                    // contains value at all
                    if (relevantPropertyDef.field_type === "KeyValue" &&
                        relevantPropertyDef.options!.multi_values !== true) {
                        // single value KeyValue field
                        properties[k] = v[0]
                    } else if (relevantPropertyDef.field_type === "Text"
                        && relevantPropertyDef.options!.multi_instances !== true) {
                        properties[k] = v[0];
                    } else {
                        properties[k] = v;
                    }
                }
            }
        }
        console.log("Dumped properties: " + JSON.stringify(properties));
        return properties;
    };

    get empty() {
        if (this.instanceDataStore.size === 0) {
            return true;
        }
        for (const [_, v] of Array.from(this.instanceDataStore.entries())) {
            if (v.length !== 0) {
                // actually not empty
                return false;
            }
        }
        return true;
    };

    checkIntegrity(): ViolationType[] {
        const violations: ViolationType[] = [];
        for (const [k, v] of Array.from(this.propertyDefinition.entries())) {
            const options = v.options;
            if (options !== null && options !== undefined) {
                const required = options.required;
                if (required) {
                    // if this property is required
                    // check whether instance data store has it
                    let filled = true;
                    if (this.instanceDataStore.get(v.export_key!) === undefined) {
                        filled = false;
                    } else {
                        const values = this.instanceDataStore.get(v.export_key!);
                        if (values === undefined || values.length === 0) {
                            // not specified
                            filled = false;
                        }
                    }
                    if (!filled) {
                        violations.push({
                                cause: `property '${k}' is required, can't be empty`
                            }
                        )
                    }
                }
            }
        }
        return violations;
    }
}

export {PropertyInstanceDataStore}