class KeyPressStore {
    constructor() {
        this.keyPressEvent = undefined;
        this.handleKeyPressEvent = this.handleKeyPressEvent.bind(this);
        this.handleKeyUpEvent = this.handleKeyUpEvent.bind(this);
        this.altKeyPressed = this.altKeyPressed.bind(this);
        // register both events
        document.addEventListener('keydown', (event) => {
            this.handleKeyPressEvent(event);
        });
        document.addEventListener('keyup', (event) => {
            this.handleKeyUpEvent(event);
        })
    }

    handleKeyPressEvent(e) {
        this.keyPressEvent = e;
        console.log(e);
    }

    handleKeyUpEvent(e) {
        this.keyPressEvent = undefined;
        console.log(e);
    }

    altKeyPressed() {
        if (this.keyPressEvent) {
            if (this.keyPressEvent.altKey) {
                return true
            }
        }
        return false
    }
}

// make it a singleton
export default new KeyPressStore();