import {BaseConfig} from "./BaseConfig";
import {
    ComprehendCommonEventArgumentSTagSet, ComprehendCommonEventDefinitionsSet, ComprehendCommonEventTriggersTagSet,
    ComprehendCommonKPETagSet, ComprehendCommonNERTagSet,
    ComprehendCommonRelationSet, ComprehendEventsProperties, ComprehendLinkingAttributes
} from "../../annotator/default/DefaultSchema";
import {deepCopy} from "annotator/utils/DeepCopy";
import {GlobalViewConfig} from "./ViewMetadataConfig";
import {EventDefinitionConfig} from "./EventDefinitionConfig";
import {Namespaces} from "../../annotator/core/Namespaces";
import {EventPropertiesConfig} from "./EventPropertiesConfig";
import {PropertiesConfig} from "../properties/PropertiesConfig";


const NERTagSetKey = "NERTagSet";
const KPETagSetKey = "KPETagSet";
const EventArgumentsTagSetKey = "EventArgumentsTagSet";
const EventTriggersTagSetKey = "EventTriggersTagSet";
const EventDefinitionsSetKey = "EventDefinitionsSet";
const EventPropertiesSetKey = "EventPropertiesSet";
const EventRelationsSetKey = "EventRelationsSet";
const RelationSetKey = "RelationSet";
const LinkingAttributeSetKey = "LinkingAttributeSet";

const NAMESPACES_TO_KEY_MAP = {
    "named_entity": NERTagSetKey,
    "event_arguments": EventArgumentsTagSetKey,
    "event_triggers": EventTriggersTagSetKey,
    "relations": RelationSetKey,
    "events": EventDefinitionsSetKey,
    "event_relations": EventRelationsSetKey,
};

const DEFAULT_SCHEMA_VERSION = "unknown";

class SchemaConfig extends BaseConfig {
    constructor() {
        super();
        // we do a deep copy every time
        const nerTagSet = deepCopy(ComprehendCommonNERTagSet);
        const kpeTagSet = deepCopy(ComprehendCommonKPETagSet);
        const relationSet = deepCopy(ComprehendCommonRelationSet);
        const linkingAttributesSet = deepCopy(ComprehendLinkingAttributes);

        // KPE shouldn't be changed
        this.createConfig(KPETagSetKey, kpeTagSet);
        this.createConfig(NERTagSetKey, nerTagSet);
        this.createConfig(RelationSetKey, relationSet);
        this.createConfig(EventRelationsSetKey, []);
        this.createConfig(LinkingAttributeSetKey, linkingAttributesSet);
        // add Events related config
        this.createConfig(EventArgumentsTagSetKey, deepCopy(ComprehendCommonEventArgumentSTagSet));
        this.createConfig(EventTriggersTagSetKey, deepCopy(ComprehendCommonEventTriggersTagSet));
        const defaultDefinitionSet = new Map();
        const sampleEventDef = new EventDefinitionConfig();
        sampleEventDef.trigger = ComprehendCommonEventDefinitionsSet[0].trigger;
        sampleEventDef.argumentList = ComprehendCommonEventDefinitionsSet[0].argumentList;
        defaultDefinitionSet.set(sampleEventDef.trigger, sampleEventDef);
        this.createConfig(EventDefinitionsSetKey, defaultDefinitionSet);
        const eventPropertiesConfig = new EventPropertiesConfig();
        eventPropertiesConfig.resume(ComprehendEventsProperties);

        this.createConfig(EventPropertiesSetKey, eventPropertiesConfig);

        this.propertiesConfig = new PropertiesConfig(this);
        this.propertiesConfig.createTaskConfig(GlobalViewConfig.NER.namespace);
        this.propertiesConfig.createTaskConfig(GlobalViewConfig.EVENT_ARGUMENTS.namespace);
        this.propertiesConfig.createTaskConfig(GlobalViewConfig.EVENT_TRIGGERS.namespace);

        if (process.env.NODE_ENV === "development") {
            this.propertiesConfig.addTaskSpanPropertyConfig(GlobalViewConfig.NER.namespace,
                {
                    name: "Sentiment",
                    level: "document",
                    targets: [],
                    field_type: "KeyValue",
                    values: ["Positive", "Negative", "NoSentiment"],
                    options: {
                        multi_values: false,
                        required: false,
                    }
                });
            this.propertiesConfig.addTaskSpanPropertyConfig(GlobalViewConfig.NER.namespace,
                {
                    name: "Reason",
                    level: "tag",
                    targets: ["PER", "LOC", "ORG"],
                    field_type: "Text",
                    options: {
                        multi_instances: false,
                        required: false,
                    }
                })
        }

        this.addNERTag = this.addNERTag.bind(this);
        this.batchRemoveNERTags = this.batchRemoveNERTags.bind(this);
        this.getTagSetKey = this.getTagSetKey.bind(this);
        this.getTagSet = this.getTagSet.bind(this);
        this.addRelation = this.addRelation.bind(this);
        this.batchRemoveRelations = this.batchRemoveRelations.bind(this);
        this.addLinkingAttribute = this.addLinkingAttribute.bind(this);
        this.batchRemoveLinkingAttributes = this.batchRemoveLinkingAttributes.bind(this);
        this.addEventDefinitionArgument = this.addEventDefinitionArgument.bind(this);
        this.removeEventDefinitionArgument = this.removeEventDefinitionArgument.bind(this);
        this.addEventProperty = this.addEventProperty.bind(this);
        this.removeEventProperty = this.removeEventProperty.bind(this);
        this.addEventPropertyValue = this.addEventPropertyValue.bind(this);
        this.removeEventPropertyValue = this.removeEventPropertyValue.bind(this);
        this.dumpNERTaskSchema = this.dumpNERTaskSchema.bind(this);
        this.loadNERTaskSchema = this.loadNERTaskSchema.bind(this);
        this.dumpRETaskSchema = this.dumpRETaskSchema.bind(this);
        this.loadRETaskSchema = this.loadRETaskSchema.bind(this);
        this.dumpEVENTSTaskSchema = this.dumpEVENTSTaskSchema.bind(this);
        this.loadEVENTSTaskSchema = this.loadEVENTSTaskSchema.bind(this);
        this.loadTaskSchema = this.loadTaskSchema.bind(this);
        this.removeEventDefinition = this.removeEventDefinition.bind(this);
        this.setVersion = this.setVersion.bind(this);
        this.resume = this.resume.bind(this);
        this.checkpoint = this.checkpoint.bind(this);

        this.version = DEFAULT_SCHEMA_VERSION;
    }


    getTagSetKey(namespace) {
        const config = NAMESPACES_TO_KEY_MAP[namespace];
        if (!config) {
            console.log("Current namespace config: " + this.namespaceToConfig);
            throw new Error("Unknown namespace is given " + namespace)
        }
        return config
    }

    getTagSet(namespace) {
        return this[this.getTagSetKey(namespace)];
    }


    addNERTag(tag, namespace = GlobalViewConfig.NER.namespace) {
        const key = this.getTagSetKey(namespace);
        // Noted that this[key] could have been mutated by other operations
        // so ALWAYS access them through property
        this[key].push(tag);
        this.configUpdated += 1;
    }


    batchRemoveNERTags(tags, namespace = GlobalViewConfig.NER.namespace) {
        const selectedTags = new Set(tags);
        const key = this.getTagSetKey(namespace);
        const tagSet = this[key];
        let newTags = [];
        let removedTags = new Set();
        for (let i = 0; i < tagSet.length; i++) {
            if (!selectedTags.has(tagSet[i])) {
                newTags.push(tagSet[i]);
            } else {
                removedTags.add(tagSet[i])
            }
        }

        if (namespace === GlobalViewConfig.EVENT_TRIGGERS.namespace) {
            // for trigger namespace, if a trigger is removed
            // it needs to be removed from corresponding eventDefinitionMap as well
            removedTags.forEach((tag) => {
                this[EventDefinitionsSetKey].delete(tag);
            })
        }

        if (namespace === GlobalViewConfig.EVENT_ARGUMENTS.namespace) {
            // for event arguments removal, we need to check whether
            // the removed argument is used in any of those argumentTypes
            // if remove the event argument tag affect the event definition
            // we throw an error
            const values = [...this[EventDefinitionsSetKey].values()];
            for (let i = 0; i < values.length; i++) {
                const v = values[i];
                if (!v.checkConsistent(newTags)) {
                    throw new Error("Event " + v.trigger + " relies on one of the argument removed by this operation. " +
                        "You need to fix/delete this event definition before you can delete this argument type");
                }
            }
        }

        this.mutate(key, newTags);

        if (namespace === GlobalViewConfig.EVENT_ARGUMENTS.namespace) {
            // now we have verified that the arguments can be removed without
            // consistency violation, we can go and modify the event definition
            // to remove the argument from the event argumentList
            const values = [...this[EventDefinitionsSetKey].values()];
            for (let i = 0; i < values.length; i++) {
                const v = values[i];
                v.adaptToSchemaChange(this[EventArgumentsTagSetKey]);
            }
        }

        if (namespace === GlobalViewConfig.NER.namespace) {
            // NOTE: currently only consider cascading corrections under the default NER namespace
            // Because Events related views currently don't yet have the corresponding Relation views
            // Need to revisit whether this is the best solution or not later

            // make the schema consistent
            // we need to remove all relations that
            // depends on the removed tags as well
            //
            // Criteria:
            // 1. If a relation has a single argument depends on removed tag, remove
            // 2. If a relation has an argument group included removed tag, remove the tag from the group
            // 3. Else keep as is.
            let newRelations = [];
            this.RelationSet.forEach((r) => {
                let [name, arg1Types, arg2Types] = r;
                arg1Types = !Array.isArray(arg1Types) ? [arg1Types] : arg1Types;
                arg2Types = !Array.isArray(arg2Types) ? [arg2Types] : arg2Types;

                // ANY is a placeholder tag, that shouldn't be considered
                arg1Types = arg1Types.filter((t) => t !== "ANY");
                arg2Types = arg2Types.filter((t) => t !== "ANY");

                const newArg1Types = arg1Types.filter((t) => !removedTags.has(t));
                const newArg2Types = arg2Types.filter((t) => !removedTags.has(t));

                if (newArg1Types.length > 0 && newArg2Types.length > 0) {
                    newRelations.push([name, newArg1Types, newArg2Types]);
                }
            });


            this.mutate(RelationSetKey, newRelations);

            // for linking attributes, we simply remove
            // the the tags from each KV groups

            let newLinkingAttributes = [];
            this.LinkingAttributeSet.forEach((kv) => {
                const {key, value} = kv;
                const newValues = value.filter((t) => !removedTags.has(t));
                newLinkingAttributes.push({
                    key: key,
                    value: newValues
                })
            });


            // console.log("LinkingAttributeSet", newLinkingAttributes);
            this.mutate(LinkingAttributeSetKey, newLinkingAttributes);
        }

    }

    addRelation(name, arg1Types, arg2Types, namespace = Namespaces.RELATION_EXTRACTION) {
        const key = NAMESPACES_TO_KEY_MAP[namespace];
        this[key].push([name, arg1Types, arg2Types]);
        this.configUpdated += 1;
    }


    batchRemoveRelations(indices, namespace = Namespaces.RELATION_EXTRACTION) {
        const key = NAMESPACES_TO_KEY_MAP[namespace];
        let relations = [];
        for (let i = 0; i < this[key].length; i++) {
            if (!indices.has(i)) {
                relations.push(this[key][i]);
            }
        }
        this.mutate(key, relations);
    }

    addLinkingAttribute(name, entityGroup) {
        this.LinkingAttributeSet.push({
            key: name,
            value: entityGroup
        });
        this.configUpdated += 1;
    }

    batchRemoveLinkingAttributes(indices) {
        let newLinkingAttributes = [];
        for (let i = 0; i < this.LinkingAttributeSet.length; i++) {
            if (!indices.has(i)) {
                newLinkingAttributes.push(this.LinkingAttributeSet[i]);
            }
        }
        this.mutate(LinkingAttributeSetKey, newLinkingAttributes);
    }

    addEventDefinitionArgument(trigger, argumentName, argumentTypes, optional, multiValues) {
        if (!this[EventDefinitionsSetKey].has(trigger)) {
            // if the said event hasn't been created
            // add it to the map
            this[EventDefinitionsSetKey].set(trigger, new EventDefinitionConfig(trigger));
        }
        const eventDefinition = this[EventDefinitionsSetKey].get(trigger);
        eventDefinition.addEventArgument(argumentName,
            argumentTypes, optional, multiValues);
        this.configUpdated += 1;
    }

    removeEventDefinitionArgument(trigger, argumentNames) {
        if (this[EventDefinitionsSetKey].has(trigger)) {
            const eventDefinition = this[EventDefinitionsSetKey].get(trigger);
            argumentNames.forEach((arg) => {
                eventDefinition.removeEventArgument(arg);
            });
            if (eventDefinition.argumentList.length === 0) {
                // no argument is left after deletion
                this.removeEventDefinition(trigger);
            }
        }
        this.configUpdated += 1;
    }

    removeEventDefinition(trigger) {
        this[EventDefinitionsSetKey].delete(trigger);
        this.configUpdated += 1;
    }

    addEventProperty(name) {
        this[EventPropertiesSetKey].addEventProperty(name);
        this.configUpdated += 1;
    }

    removeEventProperty(name) {
        this[EventPropertiesSetKey].removeEventProperty(name);
        this.configUpdated += 1;

    }

    addEventPropertyValue(name, value) {
        this[EventPropertiesSetKey].addEventPropertyValue(name, value);
        this.configUpdated += 1;
    }

    removeEventPropertyValue(name, value) {
        this[EventPropertiesSetKey].removeEventPropertyValue(name, value);
        this.configUpdated += 1;
    }

    dumpNERTaskSchema(namespace, tags) {
        const schema = {};
        schema[namespace] = {"annotation_task": "NER"};
        schema[namespace]["tags"] = deepCopy(tags);
        // handle the span level properties config
        const taskPropertiesConfig = this.propertiesConfig.getTaskConfig(namespace);
        if (taskPropertiesConfig !== undefined) {
            taskPropertiesConfig.fixInconsistencies(tags);
            schema[namespace]["properties"] = taskPropertiesConfig.checkpoint();
        }
        return schema;
    }

    loadNERTaskSchema(namespace, taskSchema) {
        const tags = taskSchema["tags"];
        const tagSetKey = NAMESPACES_TO_KEY_MAP[namespace];
        if (tagSetKey) {
            // only
            this.mutate(tagSetKey, tags);
        }
        this.propertiesConfig.createTaskConfig(namespace, taskSchema);
    }

    dumpRETaskSchema(namespace, relations) {
        const schema = {};
        schema[namespace] = {"annotation_task": "RE"};
        let trimmedRelations = [];
        relations.forEach((r) => {
            let [name, arg1, arg2] = r;
            let newArg1 = !Array.isArray(arg1) ? [arg1,] : deepCopy(arg1);
            let newArg2 = !Array.isArray(arg2) ? [arg2,] : deepCopy(arg2);
            newArg1 = newArg1.filter((t) => t !== "ANY");
            newArg2 = newArg2.filter((t) => t !== "ANY");
            trimmedRelations.push({
                "name": name,
                "arg1_value_types": newArg1,
                "arg2_value_types": newArg2
            })
        });
        schema[namespace]["relations"] = trimmedRelations;
        return schema
    }

    loadRETaskSchema(namespace, taskSchema) {
        const relations = taskSchema["relations"];
        const relationTriplets = [];
        relations.forEach((r) => {
            const name = r["name"];
            const arg1ValueTypes = r["arg1_value_types"];
            const arg2ValueTypes = r["arg2_value_types"];
            relationTriplets.push(
                [name, arg1ValueTypes, arg2ValueTypes]
            )
        });
        const relationSetKey = NAMESPACES_TO_KEY_MAP[namespace];
        if (relationSetKey) {
            this.mutate(relationSetKey, relationTriplets)
        }
    }

    dumpEVENTSTaskSchema(namespace) {
        const schema = {};
        schema[namespace] = {"annotation_task": "EVENTS"};
        const eventDefs = [...this.EventDefinitionsSet.values()];
        schema[namespace]["types"] = eventDefs.map((e) => {
            return e.checkpoint();
        });
        // TODO: add properties handler later
        Object.assign(schema[namespace], this.EventPropertiesSet.checkpoint());
        return schema
    }

    loadEVENTSTaskSchema(namespace, taskSchema) {
        const eventDefObjects = taskSchema["types"].map((e) => {
            const e1 = new EventDefinitionConfig();
            e1.resume(e);
            return e1;
        });
        const eventDefMap = new Map();
        eventDefObjects.forEach((eventDef) => {
            eventDefMap.set(eventDef.trigger, eventDef);
        });
        const eventDefinitionSetKey = NAMESPACES_TO_KEY_MAP[namespace];
        if (eventDefinitionSetKey) {
            this.mutate(EventDefinitionsSetKey, eventDefMap);
        }
        // now resume events property config
        const propertyCheckpoint = {
            "properties": taskSchema["properties"]
        };
        const eventPropertiesConfig = new EventPropertiesConfig();
        eventPropertiesConfig.resume(propertyCheckpoint);
        this.mutate(EventPropertiesSetKey, eventPropertiesConfig);
    }

    loadTaskSchema(namespace, taskSchema) {
        const task = taskSchema["annotation_task"];
        if (task === "NER") {
            this.loadNERTaskSchema(namespace, taskSchema);
        } else if (task === "RE") {
            this.loadRETaskSchema(namespace, taskSchema);
        } else if (task === "EVENTS") {
            this.loadEVENTSTaskSchema(namespace, taskSchema);
        } else {
            console.warn("Invalid taskSchema: ", taskSchema)
        }
    }

    resume(checkpoint) {
        // load a JSON schema
        // clean first
        this.propertiesConfig.resetAll();
        try {
            const schemas = checkpoint["schemas"];
            for (const [namespace, taskSchema] of Object.entries(schemas)) {
                this.loadTaskSchema(namespace, taskSchema)
            }

            if (checkpoint["version"] !== undefined) {
                this.version = checkpoint["version"];
            }
        } catch (e) {
            console.log(`Failed to load schemaCheckpoint: ${JSON.stringify(checkpoint,
                null, 2)}`)
        }
    }

    setVersion(version) {
        this.version = version;
        this.configUpdated += 1;
    }

    checkpoint() {
        // dumps as a json
        let schemas = {};
        Object.assign(schemas, this.dumpNERTaskSchema(Namespaces.NER, this.NERTagSet));
        Object.assign(schemas, this.dumpNERTaskSchema(Namespaces.EVENT_TRIGGERS, this.EventTriggersTagSet));
        Object.assign(schemas, this.dumpNERTaskSchema(Namespaces.EVENT_ARGUMENTS, this.EventArgumentsTagSet));
        Object.assign(schemas, this.dumpRETaskSchema(Namespaces.RELATION_EXTRACTION, this.RelationSet));
        Object.assign(schemas, this.dumpRETaskSchema(Namespaces.EVENT_RELATIONS, this.EventRelationsSet));
        Object.assign(schemas, this.dumpEVENTSTaskSchema(Namespaces.EVENTS));
        return {
            "version": this.version,
            "schemas": schemas
        };
    }
}

export {SchemaConfig}