import React from 'react';
import uuid from 'uuid'
import {Button, ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";
import 'annotator/AnnotatorButton.css'
import {observer} from "mobx-react";

const EntityGroupSelectionBar = observer(
    class EntityGroupSelectionBar extends React.Component {
        constructor(props) {
            super(props);
            this.state = {
                dropdownOpen: false
            };

            this.toggle = this.toggle.bind(this);
        }

        toggle() {
            this.setState({
                dropdownOpen: !this.state.dropdownOpen
            });
        }


        render() {
            const entities = this.props.entities;
            const subscribeUpdated = this.props.store.updated;

            const entityList = [];

            entities.forEach((e) => {
                entityList.push(<DropdownItem onClick={() => this.props.store.add(e)}>
                <span style={{
                    "fontFamily": "Oswald",
                    "fontSize": "18px",
                    "fontWeight": 700
                }}>{e}</span>
                </DropdownItem>)
            });


            const displayEntities = [];

            this.props.store.entities.forEach((e) => {
                displayEntities.push(<Button className="TagButton"
                                             color="info"
                                             disabled>{e}</Button>);
            });

            return <div id={"EntityGroupSelectionBar_" + uuid.v4()}>
                <ButtonDropdown isOpen={this.state.dropdownOpen}
                                toggle={this.toggle}>
                    <DropdownToggle caret
                                    className="TagButton"
                                    color="info">
                        {this.props.header}
                    </DropdownToggle>
                    <DropdownMenu>
                        {entityList}
                    </DropdownMenu>
                </ButtonDropdown>
                {displayEntities}
                <Button className="TagButton"
                        color="danger"
                        onClick={() => {
                            this.props.store.pop();
                        }}>UNDO</Button>
            </div>
        }
    });

export {EntityGroupSelectionBar}