import React from 'react';
import LZString from 'lz-string';
import uuid from 'uuid';
import {extendObservable} from "mobx";
import {RollingBackupManager} from "./RollingBackupManager";
import {CollectionStateStore} from "../store/CollectionStateStore";

const LocalStorageCheckpointKey = "aceditor_project_checkpoint";
const LocalStorageTimestampKey = "aceditor_checkpoint_timestamp";
const LocalStorageBackupPrefix = "aceditor_checkpoint_backup_";
const MaxBackupToKeep = 10;
const TestMessage = "";
const CheckpointMetadataV1 = "CheckpointMetadataV1";

class CheckpointManager {
    constructor(rootStore, autoSaveInterval = 60 * 1000) {
        this.rootStore = rootStore;
        this.enableAutoSave = true;
        this.autoSaveInterval = autoSaveInterval;
        this.backupManager = new RollingBackupManager(LocalStorageBackupPrefix,
            MaxBackupToKeep);
        extendObservable(this, {
            checkpointManagerUpdated: 0,
            lastSaveTimestamp: undefined
        });
        this.localStorageEnabled = false;
        // unique identifier to prevent multiple
        // concurrent ACEditor tabs override each other
        this.session_id = uuid.v4();
        window.LZString = LZString;
        try {
            window.localStorage.setItem("check_test", "yes");
            const result = window.localStorage.getItem("check_test");
            window.localStorage.removeItem("check_test");
            this.localStorageEnabled = result === "yes";
        } catch (e) {
            console.warn("localStorage test failed");
        }
        if (this.localStorageEnabled) {
            console.log("localStorage enabled");
        } else {
            console.warn("localStorage disabled");
        }
        this.readLastTimestamp = this.readLastTimestamp.bind(this);
        this.checkpointEditorState = this.checkpointEditorState.bind(this);
        this.resumeFromCheckpoint = this.resumeFromCheckpoint.bind(this);
        this.compressedCheckpointReady = this.compressedCheckpointReady.bind(this);
        this.sendNotEnabledAlert = this.sendNotEnabledAlert.bind(this);
        this.resumeFromCompressedCheckpoint = this.resumeFromCompressedCheckpoint.bind(this);
        this.convertCheckpointToLines = this.convertCheckpointToLines.bind(this);
        this.setEnableAutoSave = this.setEnableAutoSave.bind(this);
        this.startAutoSave = this.startAutoSave.bind(this);

        // register handler to global context
        window.notifyCompressionDone = (data) => {
            this.compressedCheckpointReady(data);
        };
        if (!this.localStorageEnabled ||
            window.Worker === undefined ||
            window.compressWorker === undefined) {
            this.sendNotEnabledAlert();
        }
        // another layer of verification
        // we send a dummy message to check whether it can be decoded
        // correctly from the compression worker
        this.verified = false;

        if (window.Worker !== undefined && window.compressWorker !== undefined) {
            window.compressWorker.postMessage({
                metadata: {
                    test: true
                },
                raw: TestMessage
            })
        }

        // wait for 2s to verify the output is decoded
        // correctly
        setTimeout(() => {
            console.log("delayed checkpointing verification");
            if (!this.verified) {
                this.sendNotEnabledAlert();
            } else {
                console.log("checkpointing is verified")
            }
        }, 2 * 1000);

        // try resume checkpoint first
        this.lastSaveTimestamp = this.readLastTimestamp();

        this.startAutoSave();
    }

    startAutoSave() {
        setInterval(() => {
            console.log("enableAutoSave: ", this.enableAutoSave);
            if (this.enableAutoSave) {
                console.log("auto-save checkpoint");
                this.checkpointEditorState();
            }
        }, this.autoSaveInterval);
    }

    static checkIsDefaultCollection(checkpoint) {
        try {
            return checkpoint.collectionCheckpoint.documents[0]
                .metadata["please_no_checkpoint"] === true;
        } catch (e) {
            // skip
        }
        return false;
    }


    checkpointEditorState(trigger = "auto") {
        try {
            if (this.localStorageEnabled) {
                const rootStoreCheckpoint = this.rootStore.checkpoint();
                if (CheckpointManager.checkIsDefaultCollection(rootStoreCheckpoint)) {
                    // this is the DefaultCollection
                    // DO NOT override past user-checkpoints!
                    console.warn("Default collection detected. Skip checkpointing");
                    return;
                }
                this.lastSaveTimestamp = new Date().getTime();
                const jsonCheckpoint = JSON.stringify(rootStoreCheckpoint);
                window.localStorage.setItem(LocalStorageCheckpointKey,
                    jsonCheckpoint);
                // save timestamp as well
                window.localStorage.setItem(LocalStorageTimestampKey, JSON.stringify(
                    {timestamp: this.lastSaveTimestamp}
                ));
                const checkpointType = trigger === undefined ? "auto" : trigger;
                if (window.compressWorker !== undefined) {
                    window.compressWorker.postMessage({
                        metadata: {
                            timestamp: this.lastSaveTimestamp,
                            session_id: this.session_id,
                            filename: this.rootStore.collectionStore.collectionPath,
                            type: checkpointType,
                            version: CheckpointMetadataV1,
                        },
                        raw: jsonCheckpoint
                    })
                }
                // send update signal
                this.checkpointManagerUpdated += 1;
                return rootStoreCheckpoint;
            }
        } catch (e) {
            console.error("Failed to checkpoint: ", e.message);
        }
    }

    compressedCheckpointReady(data) {
        try {
            if (!this.verified && data.metadata["test"]) {
                // we need to verify first
                const uncompressed = LZString.decompress(data.data);
                if (uncompressed !== TestMessage) {
                    this.sendNotEnabledAlert();
                }
                // verify only once
                this.verified = true;
                return;
            }

            // compute the data size
            const kbSize = Math.ceil(new Blob([data.data]).size / 1000);
            data.metadata["kB"] = kbSize;
            this.backupManager.push(data);
            // send update signal
            this.checkpointManagerUpdated += 1;
        } catch (e) {
            console.error("Fail to push ready compressed checkpoint", e.message)
        }
    }

    resumeFromCheckpoint() {
        try {
            if (this.localStorageEnabled) {
                const checkpointString = window.localStorage.getItem(LocalStorageCheckpointKey);
                const checkpoint = JSON.parse(checkpointString);
                this.rootStore.resume(checkpoint);
                // read
                this.lastSaveTimestamp = this.readLastTimestamp();
            }
        } catch (e) {
            console.error("Fail to resume from last checkpoint", e.message)
        }
    }

    readLastTimestamp() {
        try {
            if (this.localStorageEnabled) {
                let timestampObject = window.localStorage.getItem(LocalStorageTimestampKey);
                if (timestampObject) {
                    timestampObject = JSON.parse(timestampObject);
                    return timestampObject.timestamp;
                }
            }
        } catch (e) {
            console.error("Fail to read last timestamp from localStorage", e.message);
        }
    }

    decodeCompressedCheckpoint(key) {
        try {
            const compressed = window.localStorage.getItem(key);
            if (compressed === null) {
                // invalid or already expired
                return;
            }
            const decompressed = LZString.decompress(compressed);
            const checkpoint = JSON.parse(decompressed);
            return checkpoint;
        } catch (e) {
            console.error("Fail to decode a compressed checkpoint", key, e.message);
        }
    }

    resumeFromCompressedCheckpoint(key) {
        try {
            if (this.localStorageEnabled) {
                const checkpoint = this.decodeCompressedCheckpoint(key);
                if (checkpoint) {
                    this.rootStore.resume(checkpoint);
                    const metadata = this.backupManager.decodeKey(key);
                    this.lastSaveTimestamp = metadata.timestamp;
                }
            }
        } catch (e) {
            console.error("Fail to resume from a compressed checkpoint", e.message)
        }
    }

    convertCheckpointToLines(key) {
        try {
            if (this.localStorageEnabled) {
                const checkpoint = this.decodeCompressedCheckpoint(key);
                if (checkpoint) {
                    const documents = checkpoint.collectionCheckpoint.documents;
                    return CollectionStateStore.doConvertToJsonPerLine(documents);
                } else {
                    return ["Checkpoint has expired, this is now empty", "\n"]
                }
            }
        } catch (e) {
            console.error("Fail to convert from a compressed checkpoint", e.message);
            return [e.message, "\n"]
        }

    }

    setEnableAutoSave(value) {
        this.enableAutoSave = value;
    }

    sendNotEnabledAlert() {
        this.rootStore.alertStore.sendAlert(<b>Warning: Checkpointing can't be enabled</b>,
            <p>
                <b>ACEditor</b> has detected that checkpointing can't be enabled in your current browser setting. <br/>
                If you are working in a production environment, you are subject to the risk of losing your work,
                in case of unexpected accidents, like crashing/irregular document format. <br/>
                Please try to open ACEditor in the <b>latest Chrome browser</b> available on your platform and try
                again.
            </p>)
    }
}

export {CheckpointManager}