import React from 'react';
import {observer} from "mobx-react";
import './Pages.css'
import {TableRowSelectionStore} from "../table/TableRowSelectionStore";
import {Button, Card, CardBody, CardFooter, CardHeader} from "reactstrap";
import {TableRowSelector} from "../table/TableRowSelector";
import uuid from 'uuid';
import {FileIoManager} from "../utils/FileIoManager";

const BackupPage = observer(
    class BackupPage extends React.Component {
        constructor(props) {
            super(props);
            this.tableSelectorStore = new TableRowSelectionStore(true);
            this.currentBackUpKeys = [];
            this.resumeSelected = this.resumeSelected.bind(this);
            this.copyAsText = this.copyAsText.bind(this);
            this.exportAsFile = this.exportAsFile.bind(this);
        }

        resumeSelected() {
            const selected = this.tableSelectorStore.selectedRowIds;
            if (selected.size > 0) {
                // because this is a exclusive selected table
                // there will be only one entry in the set
                const idx = [...selected.keys()][0];
                const selectKey = this.currentBackUpKeys[idx];
                console.log("selectKey", selectKey);
                console.log(this.currentBackUpKeys);
                this.props.store.checkpointManager.resumeFromCompressedCheckpoint(selectKey);
            }
            this.tableSelectorStore.clear();
        }

        copyAsText() {
            const selected = this.tableSelectorStore.selectedRowIds;
            if (selected.size > 0) {
                // because this is a exclusive selected table
                // there will be only one entry in the set
                const idx = [...selected.keys()][0];
                const selectKey = this.currentBackUpKeys[idx];
                console.log(this.currentBackUpKeys);
                const lines = this.props.store.checkpointManager.convertCheckpointToLines(selectKey);
                const content = lines.filter(l => l !== '\n').join('\n');
                this.textArea.value = content;
                this.textArea.select();
                document.execCommand('copy');
            }
            // this.tableSelectorStore.clear();
        }

        exportAsFile() {
            const selected = this.tableSelectorStore.selectedRowIds;
            if (selected.size > 0) {
                // because this is a exclusive selected table
                // there will be only one entry in the set
                const idx = [...selected.keys()][0];
                const selectKey = this.currentBackUpKeys[idx];
                console.log(this.currentBackUpKeys);
                const lines = this.props.store.checkpointManager.convertCheckpointToLines(selectKey);
                FileIoManager.downloadAsFile(lines, "checkpoint_dump.json")
            }
            // this.tableSelectorStore.clear();
        }

        render() {
            const subscribeToCheckpointManagerUpdate = this.props.store.checkpointManager.checkpointManagerUpdated;
            const headers = ["Checkpoint Saved Time", "Source File Name", "Trigger", "Size"];
            // clear internal
            this.tableSelectorStore.set.clear();
            const checkpointManager = this.props.store.checkpointManager;
            // 'this' bug again, be careful
            let displayData = [];
            checkpointManager.backupManager.listMetadata().forEach((item) => {
                const {timestamp, filename, type, kB} = item.metadata;
                const dateTime = new Date(0);
                dateTime.setUTCMilliseconds(timestamp);
                const displayTime = <b>{dateTime.toLocaleString()}</b>;
                const displayFilename = <p style={{
                    "maxWidth": "500px",
                    "overflowWrap": "anywhere"
                }}>{filename}</p>;
                const displaySize = kB + "KB";
                displayData.push({
                    key: item.key,
                    row: [displayTime, displayFilename, type , displaySize]
                });
            });
            // put most update-to-date checkpoint at top
            displayData.reverse();
            this.currentBackUpKeys = displayData.map(d => d.key);
            const rows = displayData.map(d => d.row);
            return <div id="BackupPage" className="AnnotatePageArea">
                <Card>
                    <CardHeader>
                        <Button className="TagButton"
                                size="lg"
                                disabled
                                color="success">BACKUP</Button>
                    </CardHeader>
                    <CardBody style={{
                        "overflow": "scroll",
                        "maxHeight" : "600px"
                    }}>
                        <TableRowSelector store={this.tableSelectorStore}
                                          key={uuid.v4()}
                                          headers={headers}
                                          rows={rows}/>
                    </CardBody>
                    <CardHeader>
                        <Button className="TagButton"
                                size="lg"
                                disabled
                                color="success">TEXT</Button>
                    </CardHeader>
                    <CardBody>
                        <textarea
                            style={{"width": "950px", "height": "150px"}}
                            ref={(textarea) => this.textArea = textarea}
                            value='Text will show here'/>
                    </CardBody>
                    <CardFooter>
                        <Button className="TagButton"
                                size="lg"
                                color="info"
                                onClick={this.resumeSelected}>RESUME</Button>
                        <Button className="TagButton"
                                size="lg"
                                color="info"
                                onClick={this.exportAsFile}>EXPORT AS FILE</Button>
                        <Button className="TagButton"
                                size="lg"
                                color="info"
                                onClick={this.copyAsText}>COPY AS TEXT</Button>
                    </CardFooter>
                    <CardFooter>
                        <Button className="TagButton"
                                size="lg"
                                color="danger"
                                onClick={() => {
                                    this.props.store.alertStore.sendAlert(
                                        "Warning: Clear All Previous Backups",
                                        <p>This action will delete all your past backups.
                                            Click <b>Confirm</b> to proceed. <br/> Are you sure to proceed? </p>,
                                        () => {
                                            window.localStorage.clear();
                                            checkpointManager.checkpointManagerUpdated += 1;
                                            checkpointManager.lastSaveTimestamp = undefined;
                                        },
                                        true
                                    )
                                }}>CLEAR ALL</Button>
                    </CardFooter>
                </Card>
            </div>
        }
    });

export {BackupPage}