import {RangeMap} from "annotator/core/RangeMap";

class SpanRateLimiter {
    constructor(threshold) {
        this.threshold = threshold;
        this.lastInsertionTime = new RangeMap();
        this.record = this.record.bind(this);
        this.accept = this.accept.bind(this);
    }

    getUnixTimestamp() {
        return new Date().getTime();
    }

    record(span) {
        const ts = this.getUnixTimestamp();
        this.lastInsertionTime.add(span, ts);
    }

    accept(span) {
        const lastInsert = this.lastInsertionTime.get(span);
        const now = this.getUnixTimestamp();
        if (lastInsert && now - lastInsert < this.threshold) {
            // this span has been inserted very recently
            // no new operation is allowed
            return false;
        }
        return true;
    }
}

const DefaultRateLimiter = () => {
    return new SpanRateLimiter(100);
};

const ZeroRateLimiter = () => {
    return new SpanRateLimiter(0);
};

export {SpanRateLimiter, DefaultRateLimiter, ZeroRateLimiter}