import React, {Component} from 'react';
import {observer} from 'mobx-react'
import 'annotator/AnnotatorTagSpan.css'
import {UncontrolledTooltip} from "reactstrap";
import uuid from "uuid";
import {ContextMenuTrigger} from 'react-contextmenu';
import {normalizeTagText} from "./Constants";


const SingleLayerSpan = observer(
    class SingleLayerSpan extends Component {
        constructor(props) {
            super(props);
            // to use ToolTip, we need to initialize the id
            // in the constructor, not at the rendering time
            // to avoid error
            // see: https://github.com/reactstrap/reactstrap/issues/773
            this.tagSpanUUID = "target-" + uuid.v4();
        }


        onClickEvent(event) {
            event.stopPropagation();
            console.log('In SingleLayerSpan.onClickEvent. props=', this.props);
            this.props.clickTrigger(this.props);
        };

        render() {
            let tagOpacity = this.props.transparent ? 0.01 : 1.0;
            let rightClickMenuId = this.props.rightClickMenuId;

            const spanUuid = this.tagSpanUUID;
            let hoverToolTip = "";
            if (this.props.hoverOver) {
                hoverToolTip = <UncontrolledTooltip className="NoSelect"
                                                    style={{"background": "rgba(0,0,0,0.9)"}}
                                                    placement="auto-end"
                                                    delay={100}
                                                    target={spanUuid}>
                    {this.props.hoverOver}
                </UncontrolledTooltip>

            }

            if (!this.props.blank) {
                // the span has tag
                const textDecoration = this.props.textDecoration ? this.props.textDecoration : "";
                let displayTagText = normalizeTagText(this.props.tag);

                let contextMenuAction = () => {
                };
                if (rightClickMenuId) {
                    if (this.props.contextMenuAction) {
                        contextMenuAction = this.props.contextMenuAction;
                    }
                }

                const span = <span id={spanUuid}
                                   ref={this.props.innerRef}
                                   data-start={this.props.start}
                                   data-end={this.props.end}
                                   data-selectable={this.props.selectable}
                                   onClick={this.onClickEvent.bind(this)}
                                   onMouseDown={this.props.onMouseDown}
                                   onContextMenu={contextMenuAction}
                                   className='single-layer-span'
                                   >

                    <mark className={this.props.cssStyle}
                          style={{
                              "background": this.props.color,
                              "textDecoration": textDecoration
                          }}>
                        {this.props.text}
                        <span className="TagStyle NoSelect"
                              data-tag
                              style={{"opacity": tagOpacity}}>{displayTagText}
                         </span>
                     </mark>
                    {hoverToolTip}
                 </span>;

                if (rightClickMenuId) {
                    return <ContextMenuTrigger renderTag='span'
                                               name={spanUuid}
                                               id={rightClickMenuId}
                                               holdToDisplay={this.props.holdToDisplay !== undefined ? this.props.holdToDisplay : 1000}
                                               >
                        {span}
                    </ContextMenuTrigger>
                } else {
                    return span;
                }


            } else {
                // find newlines in text
                let segmentedSpans = [];
                let lines = this.props.text.split('\n');
                if (lines.length > 1) {
                    let cur = 0;

                    for (let i = 0; i < lines.length; i++) {
                        const segStart = this.props.start + this.props.text.indexOf(lines[i], cur);
                        const segEnd = segStart + lines[i].length;
                        cur += lines[i].length;
                        segmentedSpans.push(<span key={`${i}`}data-start={segStart}
                                                  data-end={segEnd}
                                                  data-selectable={true}>
                            <mark className={"BlankSpan"}>{lines[i]}</mark></span>);
                        if (i !== lines.length - 1) {
                            // not the last line
                            segmentedSpans.push(<br key={`br-${i}`}/>)
                        }
                    }
                } else {
                    segmentedSpans.push(<span key={'0'} data-start={this.props.start}
                                              data-end={this.props.end}
                                              data-selectable={true}>
                    <mark className={"BlankSpan"}>{this.props.text}</mark>
                   </span>)
                }

                return (segmentedSpans)
            }
        }
    }
);

export {SingleLayerSpan}