import React, {useState} from 'react';
import {Button, CardHeader, Collapse} from "reactstrap";
import './Properties.css'

type PropsType = {
    title: string
}

const AutoHideContainer: React.FC<PropsType> = (props) => {
    const [isOpen, setIsOpen] = useState(true);

    const toggle = () => setIsOpen(!isOpen);


    return <React.Fragment>

        <CardHeader>
            <span className="PropertyTitle">{props.title.toUpperCase()}</span>
            <Button color="info"
                    className="SmallButton"
                    onClick={toggle}
                    size="sm"> {isOpen ? "HIDE" : "SHOW"}
            </Button>
        </CardHeader>

        <Collapse isOpen={isOpen}>{props.children}</Collapse>
    </React.Fragment>
};

export {AutoHideContainer}
