import createRBTree from 'functional-red-black-tree'

class RangeTree {
    static createRangeTree(ranges) {

        // create a range based BST
        let tree = RangeTree.createRedBlackRangeTree();
        if (ranges) {
            ranges.forEach((r) => {
                const {start, end} = r;
                tree = tree.insert([start, end], r);
            });
        }
        return tree;
    }

    static retrieveAllNonOverlappingRanges(tree, maxLength, blankSpanCreator = (start, end) => undefined) {
        // this method return ALL ranges
        // including those empty ones in between two consecutive nodes
        console.log("RangeTree all ranges iterator");
        let spans = [];
        let cursor = 0;
        let iterator = tree.begin;
        if (iterator.valid) {
            while (1) {
                let key = iterator.key;
                let value = iterator.value;

                let [v_begin, v_end] = key;

                // first check whether there is a blank span here
                if (cursor < v_begin) {
                    // Note, v_begin is inclusive.
                    spans.push([cursor, v_begin, blankSpanCreator(cursor, v_begin)])
                }
                // move cursor to the end
                // NOTED, in case there is nested spans in the tree
                // which could be the case in the Relations view
                // we need to take the max instead of assuming cursor
                // will monotonically increase
                // In that case the nested span will be returned
                // But the parent span will not be split
                // TODO: ADD a test for this?
                cursor = Math.max(v_end, cursor);
                // now deal the annotated span
                spans.push([v_begin, v_end, value]);
                if (iterator.hasNext) {
                    iterator.next()
                } else {
                    break;
                }
            }
            // last span
            if (cursor < maxLength) {
                spans.push([cursor, maxLength, blankSpanCreator(cursor, maxLength)])
            }
        } else {
            // indicate the originalSpanTree is empty
            spans.push([0, maxLength, blankSpanCreator(0, maxLength)])
        }
        return spans;
    }

    static retrieveAllInsertedRanges(tree) {
        // console.log("retrieve all inserted ranges");
        let spans = [];
        let iterator = tree.begin;
        if (iterator.valid) {
            while (1) {
                let key = iterator.key;
                let value = iterator.value;

                let [v_begin, v_end] = key;
                spans.push([v_begin, v_end, value]);
                if (iterator.hasNext) {
                    iterator.next()
                } else {
                    break;
                }
            }
        }
        return spans;
    }

    static checkOverlap(r1, r2) {
        //console.log("r1 -> ", r1);
        //console.log("r2 -> ", r2);
        let [s1, e1] = r1;
        let [s2, e2] = r2;
        let maxStart = Math.max(s1, s2);
        let minEnd = Math.min(e1, e2);
        // in case maxStart == minEnd, the spans
        // are neighboring each other, but not overlapping
        if (maxStart < minEnd) {
            return [maxStart, minEnd]
        } else {
            return
        }
    }

    static checkWithin(r1, r2) {
        // check whether r2 in r1
        let [s1, e1] = r1;
        let [s2, e2] = r2;
        if (s2 >= s1 && e2 <= e1) {
            return true;
        }
        return false;
    }

    static rangeComparator(a, b) {
        let [a1, a2] = a;
        let [b1, b2] = b;

        let startDiff = a1 - b1;
        if (startDiff !== 0) {
            return startDiff < 0 ? -1 : 1;
        }
        let endDiff = a2 - b2;
        if (endDiff !== 0) {
            return endDiff < 0 ? -1 : 1;
        }
        // truly equal
        return 0;
    }

    static exists(tree, range) {
        return tree.find(range).valid;
    }

    static createRedBlackRangeTree() {
        return createRBTree(RangeTree.rangeComparator);
    }

}

export default RangeTree;