const expand = (a, b) => [].concat(...a.map(d => b.map(e => [].concat(d, e))));
const cartesian = (a, b, ...c) => (b ? cartesian(expand(a, b), ...c) : a);
const ANY_ENTITY_TYPE = "ANY";

class Relation {
    constructor(span1, span2, relationType) {
        // expect arg1, arg2 to be SpanContainers
        this.arg1 = span1;
        this.arg2 = span2;
        this.relationType = relationType;
        // lexical scope, enforce copy
        let seq = Relation.Counter;
        this.seq = seq;
        Relation.Counter += 1;
    }

    get id() {
        return this.arg1.attrs.id + "_" +
            this.relationType.name + "_" +
            this.arg2.attrs.id;
    }

    exportAnnotation() {
        return {
            arg1: this.arg1.exportAnnotation(false),
            arg2: this.arg2.exportAnnotation(false),
            relation: this.relationType.name
        }
    }

}

// define a monotonic increasing relation sequence id
Relation.Counter = 0;

class RelationType {
    constructor(name, arg1Types, arg2Types) {
        this.name = name;
        this.arg1Types = Array.isArray(arg1Types) ? arg1Types : [arg1Types,];
        this.arg2Types = Array.isArray(arg2Types) ? arg2Types : [arg2Types,];
        // since we have COREF type of entities
        // from Coreference module, we need to support partial signature matching
        if (arg1Types.indexOf(ANY_ENTITY_TYPE) === -1) {
            this.arg1Types.push(ANY_ENTITY_TYPE);
        }
        if (arg2Types.indexOf(ANY_ENTITY_TYPE) === -1) {
            this.arg2Types.push(ANY_ENTITY_TYPE);
        }
    }

    create(span1, span2) {
        return new Relation(span1, span2, this);
    }

    get signatures() {
        return cartesian(this.arg1Types, this.arg2Types);
    }
}

class RelationRegistry {
    constructor() {
        this._nameToRelation = new Map();
        this._typeToRelations = new Map();
        this._register = this._register.bind(this);
        this.registerRelationTriple = this.registerRelationTriple.bind(this);
        this.getRelation = this.getRelation.bind(this);
        this.suggestRelations = this.suggestRelations.bind(this);
    }

    _getTypeSignature(arg1Type, arg2Type) {
        return arg1Type + "/" + arg2Type;
    }

    _register(relation) {
        // register by name
        this._nameToRelation.set(relation.name, relation);
        relation.signatures.forEach((sig) => {
            let [arg1, arg2] = sig;
            const signatureKey = this._getTypeSignature(arg1, arg2);
            let relationList = this._typeToRelations.get(signatureKey);
            if (relationList === undefined) {
                relationList = [];
                this._typeToRelations.set(signatureKey, relationList);
            }
            relationList.push(relation);
        });
    }

    registerRelationTriple(name, arg1Types, arg2Types) {
        const relation = new RelationType(name, arg1Types, arg2Types);
        this._register(relation);
    }

    getRelation(name) {
        return this._nameToRelation.get(name);
    }

    suggestRelations(arg1Type, arg2Type) {
        if (arg1Type === "COREF") {
            // in case it is a coref mention
            // we don't know its type, assume it is an ANY type
            arg1Type = ANY_ENTITY_TYPE
        }
        if (arg2Type === "COREF") {
            arg2Type = ANY_ENTITY_TYPE;
        }
        const sk = this._getTypeSignature(arg1Type, arg2Type);
        // console.log("sk ->", sk);
        // console.log("_typeToRelations", this._typeToRelations.keys());
        return this._typeToRelations.get(sk);
    }
}

export {RelationRegistry}