import {debounce} from 'lodash';

// Scroll debounce update time in milliseconds 
const SCROLL_DEBOUNCE_MS = 500;

class GlobalViewStateStore {
    private scrollPercent: number = 0;
    private debouncedSetScrollPercent: (percent: number) => void;

    constructor() {
        // Debounced set scroll method to ensure we're only setting the scroll percent once at the end
        // of a scrolling action
        this.debouncedSetScrollPercent = debounce(
            (percent:number) => this.setScrollPercent(percent),
            SCROLL_DEBOUNCE_MS
        );
    }
    
    /**
     * Returns the scroll percent of the current document
     */
    public getScrollPercent() {
        return this.scrollPercent;
    }

    /**
     * Resets current document scroll to 0%
     */
    public resetScroll() {
        this.scrollPercent = 0;
    }

    /**
     * Indicates how far the current document is scrolled, so when we switch editor views we can scroll
     * to the same point
     * @param {number} element dom element to calculate scroll percent of
     */
    public setScrollPercentageFromElement(el: HTMLElement, debounce: boolean = true) {
        const percent = el.scrollTop / el.scrollHeight;
        if (debounce) {
            this.debouncedSetScrollPercent(percent);
        } else {
            this.setScrollPercent(percent);
        }
    }

    public setScrollPercent(percent: number) {
        this.scrollPercent = percent;
    }

    /**
     * Scrolls the passed in element to the store's current scroll position
     * @param {HTMLElement | undefined} element
     */
    public updateElementScroll(element: HTMLElement | undefined) {
        // Handle undefined element case here to avoid undefined check every time in 
        // componentDidUpdate / componentDidMount
        if (!element) {
            return;
        }
        const scrollTop = Math.round(element.scrollHeight * this.scrollPercent);
        if (scrollTop !== element.scrollTop) {
            element.scroll({top:scrollTop});
        }
    }
}

export {GlobalViewStateStore}
