import React, {Component} from 'react';
import {observer} from 'mobx-react';
import {Button, Card, CardBody, CardHeader, CardText, Table} from "reactstrap";
import './EventAnnotator.css'

const MissingArgumentBackground = "#b6b9c3";


const EventArgumentSidePanel = observer(
    class EventArgumentSidePanel extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const store = this.props.store;
            const viewUpdateChange = this.props.store.viewUpdateChange;
            let argumentValues;
            if (this.props.v2) {
                argumentValues = store.getActiveTriggerInstanceArgumentV2();
            } else {
                argumentValues = store.getActiveTriggerInstanceArgument();
            }
            let argumentCells = [];
            if (argumentValues) {
                argumentValues.forEach((val, idx) => {
                    const {argumentName, span} = val;
                    // span could be undefined, when the argument is not specified
                    const removeHandler = () => {
                        if (span) {
                            store.removeSelectedEventArgument(argumentName, span);
                        }
                    };
                    const spanClickHandler = () => {
                        if (span) {
                            store.states.activeArgumentSpan = span;
                            store.notifyViewUpdateChange();
                        }
                    };
                    const text = span ? span.extent : <span style={{
                        "background": MissingArgumentBackground,
                        "padding": "5px"
                    }}>MISSING</span>;
                    const removeColor = !this.props.v2 ? "danger" : "secondary";
                    argumentCells.push(
                        <tr>
                            <td style={{"maxWidth": "20px"}}>
                                {idx + 1}
                            </td>
                            <td style={{
                                "maxWidth":
                                    "100px"
                            }}>
                                {argumentName}
                            </td>
                            <td style={{
                                "maxWidth":
                                    "100px"
                            }}>
                                <span className="SideArgumentHover"
                                      onClick={spanClickHandler}>
                                    {text}
                                </span>
                            </td>
                            <td>
                                <Button color={removeColor}
                                        style={{"fontSize": "12px"}}
                                        size="sm"
                                        disabled={this.props.v2 || this.props.store.isReadOnly()}
                                        onClick={removeHandler}>x
                                </Button>
                            </td>
                        </tr>
                    )
                })
            }

            return <div id="EventArgumentSidePanel" style={{"marginBottom": "15px"}}>
                <Card>
                    <CardHeader className="SidePanelHeader">
                        ARGUMENTS
                    </CardHeader>
                    <CardBody className="SidePanelText">
                        <Table hover>
                            {argumentCells}
                        </Table>
                    </CardBody>
                </Card>
            </div>
        }
    }
);

export {EventArgumentSidePanel}