import React from 'react';

import {TargetsEditComponent} from "./TargetsEditComponent";
import {OptionsEditComponent} from "./OptionsEditComponent";
import {ExportKeyEditComponent} from "./ExportKeyEditComponent";
import {CellPropType} from "./CellPropertyTypes";


class CommonPropertyConfigCell extends React.Component<CellPropType> {

    constructor(props: CellPropType) {
        super(props);

    }

    render() {
        const property = this.props.property;
        const namespace = this.props.namespace;
        const tagSet = this.props.schemaConfig.getTagSet(namespace)!;
        let propertiesConfig = this.props.schemaConfig.propertiesConfig;
        // ! operator suppress the strictNullCheck error
        const {name, field_type: fieldType} = property;
        const export_keyEdit = <ExportKeyEditComponent property={property}
                                                       namespace={namespace}
                                                       propertiesConfig={propertiesConfig}/>
        // component to allow conditioned tag config
        const tagEdit = <TargetsEditComponent property={property}
                                              namespace={namespace}
                                              tagSet={tagSet}
                                              propertiesConfig={propertiesConfig}/>;

        // component to allow specify different options
        const optionEdit = <OptionsEditComponent property={property}
                                                 namespace={namespace}
                                                 propertiesConfig={propertiesConfig}/>;

        return <React.Fragment>
            <td style={{"width": "100px"}}><b>{name}</b></td>
            <td style={{"maxWidth": "100px"}}>{fieldType}</td>
            <td style={{"minWidth": "300px"}}>{export_keyEdit}</td>
            <td style={{"minWidth": "500px"}}>{tagEdit}</td>
            <td style={{"maxWidth": "300px"}}>{optionEdit}</td>
        </React.Fragment>
    }
}

export {CommonPropertyConfigCell}