import React, {Component} from 'react';
import {Button, CardFooter} from "reactstrap";
import './Annotation.css'

class CollectionNavigationFooter extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {onToggleComments, prev, next, reject} = this.props;

        return (
            <React.Fragment>
                <span>
                    <Button color="info"
                            size="lg"
                            className="FooterButton"
                            onClick={prev}>
                        PREV
                    </Button>
                    <Button color="info"
                            size="lg"
                            className="FooterButton ml-1"
                            onClick={next}>
                        NEXT
                    </Button>
                </span>
                <span>
                    <Button color="danger"
                            size="lg"
                            className="FooterButton"
                            onClick={reject}>
                        REJECT
                    </Button>
                    <Button color="info"
                            size="lg"
                            className="FooterButton ml-1"
                            onClick={onToggleComments}>
                        COMMENT
                    </Button>
                </span>
            </React.Fragment>
        )
    }
}

export {CollectionNavigationFooter}