import React from "react";
import {observer} from "mobx-react";
import {Button, CardBody} from "reactstrap";
import {AttributeKeyValueTextInput} from "./AttributeKeyValueTextInput";
import uuid from 'uuid';
import 'annotator/AnnotatorButton.css'
import {extendObservable} from "mobx";

const AttributesEditor = observer(
    class AttributesEditor extends React.Component {
        constructor(props) {
            console.log("AttributesEditor constructor called");
            super(props);
            this.attributesContainer = Object.assign({}, this.props.attributes);
            extendObservable(this, {
                parentChange: 0
            });
        }

        render() {
            console.log("AttributesEditor render called");
            if (this.attributesContainer === undefined) {
                return <div/>;
            }
            let rows = [];
            Object.keys(this.attributesContainer).forEach((k) => {
                rows.push(<AttributeKeyValueTextInput name={k}
                                                      parent={this}/>);
                rows.push(<br/>)
            });
            return <div className="AttributesEditor" id={uuid.v4()}>
                <CardBody>
                    {rows}
                    <Button className="FunctionalButton"
                            color="info"
                            style={{"maxWidth": "200px", "fontSize": "18px"}}
                            onClick={() => this.props.onSubmit(this.attributesContainer)}>
                        CONFIRM CHANGE
                    </Button>
                </CardBody>
            </div>
        }
    }
);

export {AttributesEditor}