import React, {Component} from 'react';
import uuid from 'uuid';
import {Button} from "reactstrap";

class FileImportButton extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const inputId = uuid.v4();
        return <Button className="FileContainer"
                       color="warning"
                       onClick={() => document.getElementById(inputId).click()}>
            {this.props.text}
            <input id={inputId} type="file" onChange={this.props.onClick}/>
        </Button>

    }
}

export {FileImportButton}