import React, {Component} from "react";
import uuid from 'uuid';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import cx from 'classnames';

import {CoreferenceContextMenu} from 'annotator/coreference/CoreferenceContextMenu';
import {SingleLayerSpan} from "annotator/span/SingleLayerSpan";
import {SelectTagColor} from "annotator/AnnotatorCommons";
import 'annotator/AnnotatorTagSpan.css'
import 'annotator/AnnotatorButton.css'
import './CoreferenceMentionsDisplay.css';

const DisplayColor = "#1591d0";

// Clicking on an active span
const ActiveColor = '#00C851'

const COREFERENCE_ANNOTATOR_MENU_ID_PREFIX = "CoreferenceAnnotatorMenu";

/**
 * A corefernece group with the group's label and all items
 */
class CoreferenceGroup extends Component {
    constructor(props){
        super(props);
        this.state = {
            isDragging: false
        };
    }
    toggleGroup = () => {
        const v2 = this.props.name==='v2';
        const currentFocusGroup = this.props.store.getFocusGroup(v2);
        if (currentFocusGroup && currentFocusGroup.groupId === this.props.group.groupId) {
            // Unselect
            this.props.store.setFocusGroup(-1, v2)
        } else {
            // Select
            this.props.store.setFocusGroup(this.props.group.groupId, v2);
        }
    }
    toggleSpan = (span) => {
        // Pull the span from the state because the span in the group store can be out of date
        const spanFromState = this.props.store.states.spanTree.get([span.start, span.end]);
        if (!spanFromState) {
            return;
        }
        this.props.store.toggleMentionState(spanFromState)
        this.props.store.scrollToActiveSpan();
    }
    render() {
        const {
            group, index, columnName, highlightedSpans, activeSpan, setContextMenuSpan,
            focusGroup, isReadOnly, spanHighlightColor, groupSelectedColor, contextMenuId,

        } = this.props;
        const tagSpans = [];
        const isInFocus = focusGroup === group.groupId;
        tagSpans.push(
            <SingleLayerSpan 
                key={uuid.v4()}
                text={"G" + group.groupId.toString()}
                tag={""}
                color={SelectTagColor}
                start={0}
                end={0}
                blank={false}
                selectable={false}
                transparent={true}
                cssStyle={"CoreferenceDisplayHighlightSpan " +
                "CoreferenceDisplayGroupHeader"}
                clickTrigger={() => {}}
            />
        );
        const spans = group.values.sort((a, b) => a.start - b.start);
        spans.forEach((spanObj,index) => {
            const spanKey = `${spanObj.start}_${spanObj.end}`;
            // Act
            const isHighlighted = highlightedSpans.has(spanKey);
            const isActive = activeSpan && (activeSpan.attrs.id === spanObj.attrs.id);
            const color = isActive ? ActiveColor : (isHighlighted ? spanHighlightColor : DisplayColor );
            tagSpans.push(
                <Draggable 
                    draggableId={`span-${columnName}-${group.groupId}-${spanObj.start}_${spanObj.end}`} 
                    index={index}
                    key={`span-${columnName}-${group.groupId}-${spanObj.start}_${spanObj.end}`} 
                    isDragDisabled={isReadOnly}
                >
                    {(provided) => (
                        <span 
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            {...provided.dragHandleProps}
                        >
                            <SingleLayerSpan 
                                text={spanObj.extent}
                                tag={""}
                                color={color}
                                start={spanObj.start}
                                end={spanObj.end}
                                blank={false}
                                
                                transparent={true}
                                cssStyle={"CoreferenceDisplayHighlightSpan"}
                                clickTrigger={() => this.toggleSpan(spanObj)}
                                onMouseDown={() => setContextMenuSpan(spanObj)}
                                rightClickMenuId={contextMenuId}
                                holdToDisplay={-1}
                            />
                        </span>
                    )}
                </Draggable>
            );
        });
        //selectable={false}
        return (
            <Draggable draggableId={`group-${group.groupId}`} index={index}>
                {(draggableProvided) => (
                    <Droppable droppableId={`group-${columnName}-${group.groupId}`} type="coref-group" direction="horizontal">
                        {(droppableProvided, snapshot) => (
                            <div 
                                ref={draggableProvided.innerRef}
                                {...draggableProvided.draggableProps}
                                className={cx([
                                    'coref-group','mb-2', 
                                    snapshot.isDraggingOver ? 'dragging' : ''
                                ])}
                                style={{
                                    ...(isInFocus ? {
                                        backgroundColor: groupSelectedColor
                                    } : null ),
                                    ...draggableProvided.draggableProps.style,
                                }}
                                onClick={this.toggleGroup}
                            >
                                <div className="handle" {...draggableProvided.dragHandleProps}></div>
                                <div className="flex-grow-1">
                                    <div 
                                        ref={droppableProvided.innerRef}
                                        {...droppableProvided.droppableProps}
                                        className={cx(
                                            "d-flex flex-direction-row flex-wrap align-items-center", {
                                            dragging: snapshot.isDraggingOver
                                        })}
                                    >
                                        {tagSpans}
                                        {droppableProvided.placeholder}
                                    </div>
                                </div>
                            </div>
                        )}
                    </Droppable>
                )}
            </Draggable>
        );
    }
}

class CoreferenceGroupsColumn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            draggingGroupId: null,
            contextMenuSpan: null
        };
    }

    setContextMenuSpan = (span) => {
        this.setState({
            contextMenuSpan: span
        });
    }
    
    onDragEnd = (result) => {
        const { destination, source, draggableId } = result;
        if (!destination) {
            return;
        }
        if (
            destination.droppableId === source.droppableId &&
            destination.index === source.index
        ) {
            return;
        }

        if (draggableId.startsWith('span') && destination.droppableId.startsWith('group')) {
            // Only allow spans to drop in different destinations
            const parts = draggableId.split('-');
            const [start,end] = parts[3].split('_').map(v => Number(v));
            const groupId = Number(destination.droppableId.split('-')[2]);
            const span = this.props.store.states.spanTree.get([start, end]);
            this.props.store.removeMentionFromGroup(span);
            this.props.store.assignMentionToGroup(start,end,groupId);
            
        }
        if (draggableId.startsWith('group')) {
           const groupId = Number(draggableId.split('-')[1]);
           const newGroupOrder = [...this.props.groupOrder];
           newGroupOrder.splice(source.index, 1);
           newGroupOrder.splice(destination.index, 0, groupId);
           this.props.setGroupOrder(newGroupOrder);
        }
    }

    render() {
        const { groups, groupOrder, name, activeSpan, store, highlightedSpans, focusGroup, isReadOnly, spanHighlightColor, groupSelectedColor } = this.props;
        if (!groups || !groupOrder) {
            return null;
        }
        const contextMenuId = `${COREFERENCE_ANNOTATOR_MENU_ID_PREFIX}_${name}`;
        const groupComponents = groupOrder.map(groupId => groups.find(g => g.groupId === groupId))
            .filter(group => group !== undefined)
            .map((group, index) => (
                <CoreferenceGroup 
                    group={group}
                    key={group.groupId}
                    columnName={name}
                    index={index}
                    activeSpan={activeSpan}
                    focusGroup={focusGroup}
                    store={store}
                    highlightedSpans={highlightedSpans}
                    name={name}
                    isReadOnly={isReadOnly}
                    spanHighlightColor={spanHighlightColor}
                    groupSelectedColor={groupSelectedColor}
                    contextMenuId={contextMenuId}
                    setContextMenuSpan={this.setContextMenuSpan}
                />
            )
        );

        return (
            <DragDropContext onDragEnd={this.onDragEnd}>
                <Droppable droppableId={name} type="groups-list">
                    {(provided) => (
                        <div>
                            <div 
                                ref={provided.innerRef}
                                {...provided.droppableProps}
                                className="coref-group-column"
                            >
                                {groupComponents}
                                {provided.placeholder}
                            </div>
                            {
                                !isReadOnly ? (
                                    <CoreferenceContextMenu menuId={contextMenuId} store={store} span={this.state.contextMenuSpan} />
                                ) : null
                            }
                        </div>
                    )}
                </Droppable>
            </DragDropContext>
        )
    }
    componentDidUpdate(prevProps) {
        // Check if the user added a new group 
        // Need to do this in order to maintain sort order
        if (prevProps.groups !== this.props.groups) {
            const prevGroupIds = new Set(this.props.groupOrder);
            const groupIds = this.props.groups.map(g => g.groupId);
            
            // These are all the groups that were added
            const newGroupIds = groupIds.filter(groupId => !prevGroupIds.has(groupId));
            if (newGroupIds.length > 0) {
                // Append new groups to the end of the groupOrder
                this.props.setGroupOrder([...this.props.groupOrder, ...newGroupIds]);
            }
        }
    }
}

export {CoreferenceGroupsColumn};
