import React, { useState } from 'react';
import {
    LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER,
    LABELING_TASK_EVENTS_RE_SLOT_FILLING,
    LABELING_TASK_COREF_GROUPING,
    LABELING_TASK_NAMED_ENTITY_RECOGNITION,
    UFDocument,
} from '../../annotator/core/UFSchema';
import { ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap';
import { GlobalViewConfig } from '../config/ViewMetadataConfig';
import uuid from 'uuid';
import { Pages } from '../store/PageStateStore';
import { RootStore } from '../../RootStore';
import { Namespaces } from '../../annotator/core/Namespaces';

const TASK_MAPPING = {
    [LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER]: new Set([
        'event_arguments',
        'event_triggers',
        'event_arguments_triggers',
    ]),
    [LABELING_TASK_EVENTS_RE_SLOT_FILLING]: new Set(['events']),
    [LABELING_TASK_COREF_GROUPING]: new Set([
        'event_triggers_groups',
        'event_arguments_groups',
        'coreference',
    ]),
    [LABELING_TASK_NAMED_ENTITY_RECOGNITION]: new Set(['named_entity']),
};

const TaskSelector = (props: { store: RootStore }) => {
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggle = () => setDropdownOpen(!dropdownOpen);
    const { store } = props;

    const editorStore = store.editorStateStore;

    // If no labeling_tasks are specified in the UF metadata, everything is editable
    const readOnlyNamespaces = new Set<string>();
    const activeDocument = store.collectionStore.getActiveDocument() as UFDocument;
    if (
        activeDocument &&
        activeDocument.metadata.labeling_tasks &&
        activeDocument.metadata.labeling_tasks.length > 0
    ) {
        // Labeling tasks are specified, meaning only tasks marked as readOnly:false are editable
        Object.values(TASK_MAPPING)
            .map((s) => Array.from(s.values()))
            .flat()
            .forEach((namespace) => readOnlyNamespaces.add(namespace));
        // If a task is read_only === false (editable), remove it from the readOnlyNamesapces
        activeDocument.metadata.labeling_tasks.forEach((labelingTask) => {
            if (labelingTask.read_only) {
                return;
            }
            TASK_MAPPING[labelingTask.name].forEach((namespace) =>
                readOnlyNamespaces.delete(namespace)
            );
        });
    }

    let views = GlobalViewConfig.filterByProfile(store.activeViewConfig.activeViews);
    let viewDropdownItems: JSX.Element[] = [];
    for (let i = 0; i < views.length; i++) {
        const displayName = getDisplayName(views[i].displayName, store);
        viewDropdownItems.push(
            <DropdownItem
                style={{ fontSize: '14px' }}
                key={'DropdownItem_' + uuid.v4()}
                onClick={() => {
                    editorStore.setTask(views[i]);
                    // page jumps back to Edit page
                    store.pageStateStore.jumpToPage(Pages.EDIT);
                }}
            >
                {readOnlyNamespaces.has(views[i].namespace) ? (
                    <React.Fragment>
                        <div className="text-muted">{displayName}</div>
                        <div className="DropdownWarning">Read-only</div>
                    </React.Fragment>
                ) : (
                    <React.Fragment>{displayName}</React.Fragment>
                )}
            </DropdownItem>
        );
    }

    const displayName = getDisplayName(editorStore.currentTask.displayName, store);
    return (
        <ButtonDropdown isOpen={dropdownOpen} toggle={toggle}>
            <DropdownToggle caret color="warning" style={{ fontSize: '14px' }}>
                {readOnlyNamespaces.has(editorStore.currentTask.namespace) ? (
                    <div style={{ display: 'inline-block', textAlign: 'left' }} className="mr-1">
                        <div className="text-muted">{displayName}</div>
                        <div className="DropdownWarning">Read-only</div>
                    </div>
                ) : (
                    <React.Fragment>{displayName}</React.Fragment>
                )}
            </DropdownToggle>
            <DropdownMenu>{viewDropdownItems}</DropdownMenu>
        </ButtonDropdown>
    );
};

const getDisplayName = (name: string, store: RootStore) => {
    if (
        store.getUsingGroundTruth() &&
        store.editorStateStore.currentTask.namespace === Namespaces.SSIE
    ) {
        return 'NER';
    }
    return name.toUpperCase();
};

export default TaskSelector;
