import React, {Component} from "react";
import {Button, CardBody, CardHeader, Collapse, Row, Col} from "reactstrap";

import {CoreferenceGroupsColumn} from 'annotator/coreference/CoreferenceGroupsColumn';
import 'annotator/AnnotatorTagSpan.css'
import 'annotator/AnnotatorButton.css'
import './CoreferenceMentionsDisplay.css';

// Highlight color col1
const HighlightPanelColor = '#ffecb3';
const HighlightSpanColor = '#ffa000';

class CoreferenceMentionsDisplay extends Component {
    constructor(props) {
        super(props);
        const allGroupOrders = this.sortGroupsByContent();
        this.state = {
            toggle: true,
            groupOrder: {
                [props.store.states.documentId]: allGroupOrders[0]
            }, // documentId => [0,1,2,3],
            groupOrderV2: {
                [props.store.states.documentId]: allGroupOrders[1]
            }  // documentId => [0,1,2,3],
        };
        this.toggle = this.toggle.bind(this);
    }


    /**
     * Pairs groups from annotator 1 and annotator 2 by closest matching content using a rough heuristic.
     * 
     * Returns two arrays of groupIds representing sorted groupV1 and groupV2. Doesn't change actual internal group order representation.
     */
    sortGroupsByContent() {
        if (!this.props.store.showV2) {
            return [
                this.props.groups.map(g => g.groupId),
                []
            ]
        }
        const groupList = [...this.props.groups].reverse();
        const groupListV2 = this.props.groupsV2;
        const group1MentionSets = {};
        const group2MentionSets = {};
        const group2Set = new Set(groupListV2.map(g => g.groupId));
        
        // Object that maps groupId to an array of objects with shape:
        // {
        //     score: number, /* number between 0 and 1 inclusive
        //     groupId: number /* integer groupId number */
        // }
        const group1Matches = {}; 
        groupList.forEach(group => {
            group1MentionSets[group.groupId] = new Set(group.getMentions().map(m => m.attrs.id));
            
        });
        groupListV2.forEach(group => {
            group2MentionSets[group.groupId] = new Set(group.getMentions().map(m => m.attrs.id));
        });
        Object.keys(group1MentionSets).forEach(group1Id => {
            const group1MentionsSet = group1MentionSets[group1Id];
            group1Matches[group1Id] = [];
            Object.keys(group2MentionSets).forEach(group2Id => {
                const group2MentionsSet = group2MentionSets[group2Id];
                const group1MentionArray = [...group1MentionsSet];
                const intersection = group1MentionArray.filter(id => group2MentionsSet.has(id));
                if (intersection.length === 0) {
                    return;
                }
                group1Matches[group1Id].push({
                    score: intersection.length / group1MentionArray.length,
                    groupId: group2Id
                });
            });
            group1Matches[group1Id].sort((a,b) => b.score - a.score);
        });

        const group1Order = [];
        const group2Order = [];
        groupList.forEach(group => {
            const group1Id = group.groupId;
            var group2Id = null;
            for(var i = 0; i < group1Matches[group1Id].length; i++) {
                const match = group1Matches[group1Id][i];
                
                if (!group2Set.has(Number(match.groupId))) {
                    continue;
                }
                group2Id = Number(match.groupId);
                group2Set.delete(group2Id);
                break;
            }
            // If ann1's group1 didn't have a good match from ann2, add group2 to the end of the grouplist
            if (group2Id === null || group2Id === undefined) {
                group1Order.push(group1Id);
                return;
            }

            group1Order.unshift(group1Id);
            group2Order.unshift(group2Id);
        });

        // At this point all of annotator #1's groups are in group1Order, but annotator #2 may still have items left in the tree.  
        // Add remaining annotator 2 groups to group2Order array
        [...group2Set].forEach((key) => {
            group2Order.push(key);
        });
        return [group1Order,group2Order];
    }


    toggle() {
        this.setState({toggle: !this.state.toggle});
    }

    componentDidUpdate(prevProps) {
        // Update group order if we are looking at a document for the first time
        if ((prevProps.store.states.documentId !== this.props.store.states.documentId) && (this.state.groupOrder[this.props.store.states.documentId] === undefined )) {
            const documentId = this.props.store.states.documentId;
            const allGroupOrders = this.sortGroupsByContent();
            this.setState({
                groupOrder: {
                    ...this.state.groupOrder,
                    [documentId]: allGroupOrders[0]
                },
                groupOrderV2: {
                    ...this.state.groupOrderV2,
                    [documentId]: allGroupOrders[1]
                }
            });
        }
    }

    setGroupOrder(documentId, groupOrder) {
        this.setState({
            groupOrder: {
                ...this.state.groupOrder,
                [documentId]: groupOrder
            }
        });
    }

    setGroupOrderV2(documentId, groupOrder) {
        this.setState({
            groupOrderV2: {
                ...this.state.groupOrderV2,
                [documentId]: groupOrder
            }
        });
    }

    render() {
        const { groups, groupsV2, store, readOnly} = this.props;
        const { activeSpan, documentId } = store.states;
        const groupOrder = this.state.groupOrder[documentId] || [];
        const groupOrderV2 = this.state.groupOrderV2[documentId] || [];
        let groupColumns = null;
        if (groupsV2) {
            const v1FocusGroup = store.getFocusGroup();
            const v1SpanMentions = v1FocusGroup ? new Set(v1FocusGroup.getMentions().map(m => `${m.start}_${m.end}`)) : new Set();
            const v2FocusGroup = store.getFocusGroup(true);
            const v2SpanMentions = v2FocusGroup ? new Set(v2FocusGroup.getMentions().map(m => `${m.start}_${m.end}`)) : new Set();
            
            groupColumns = (
                <React.Fragment>
                    <Row className="m-0">
                        <Col xs="6" className="pt-3 pb-2 pr-4 pl-4">
                            <h5>Annotator #1 / Arbitrator</h5>
                            <p className="text-muted">Rearrange groups and make final edits here</p>
                        </Col>
                        <Col xs="6" className="p-3">
                            <h5>Annotator #2</h5>
                            <p className="text-muted">Rearrange columns <span className="text-danger">(content is read-only)</span></p>
                        </Col>
                    </Row>
                    <Row className="m-0 p-0 mb-2">
                        <Col xs="6">
                            <CoreferenceGroupsColumn 
                                groups={groups} 
                                name="v1" 
                                store={store} 
                                highlightedSpans={v2SpanMentions} 
                                focusGroup={v1FocusGroup ? v1FocusGroup.groupId : -1 }
                                activeSpan={activeSpan}
                                isReadOnly={readOnly}
                                spanHighlightColor={HighlightSpanColor}
                                groupSelectedColor={HighlightPanelColor}
                                groupOrder={groupOrder}
                                setGroupOrder={groupOrder => this.setGroupOrder(documentId, groupOrder)}
                            />
                        </Col>
                        <Col xs="6">
                            <CoreferenceGroupsColumn 
                                groups={groupsV2} 
                                name="v2" 
                                store={store} 
                                highlightedSpans={v1SpanMentions}
                                focusGroup={v2FocusGroup ? v2FocusGroup.groupId : -1 }
                                activeSpan={activeSpan}
                                isReadOnly={true}
                                spanHighlightColor={HighlightSpanColor}
                                groupSelectedColor={HighlightPanelColor}
                                groupOrder={groupOrderV2}
                                setGroupOrder={groupOrder => this.setGroupOrderV2(documentId, groupOrder)}
                            />
                        </Col>
                    </Row>
                </React.Fragment>
            )
        } else {
            const focusGroup = store.getFocusGroup();
            groupColumns = (
                <React.Fragment>
                    <Row className="m-0">
                        <Col className="pt-3 pb-2 pr-4 pl-4">
                            <p className="text-muted">Rearrange groups and make edits here</p>
                        </Col>
                    </Row>
                    <Row className="m-0 p-0 mb-2">
                        <Col>
                            <CoreferenceGroupsColumn 
                                groups={groups} 
                                name="v1" 
                                store={store} 
                                highlightedSpans={new Set()}
                                focusGroup={focusGroup ? focusGroup.groupId : -1}
                                groupSelectedColor={HighlightPanelColor}
                                activeSpan={activeSpan}
                                isReadOnly={readOnly}
                                groupOrder={groupOrder}
                                setGroupOrder={groupOrder => this.setGroupOrder(documentId, groupOrder)}
                            />
                        </Col>
                    </Row>
                </React.Fragment>
            );
        }
        const buttonText = this.state.toggle ? "HIDE GROUP MENTIONS" : "SHOW GROUP MENTIONS";

        return <div className="coref-groups-display" id="CoreferenceMentionsDisplayDiv">
            <CardHeader>
                <Button className="TagButton"
                        color="success"
                        onClick={this.toggle}>
                    {buttonText}
                </Button>
            </CardHeader>
            <Collapse isOpen={this.state.toggle}>
                <CardBody className="p-0">
                    {groupColumns}
                </CardBody>
            </Collapse>
        </div>
    }
}

export {CoreferenceMentionsDisplay}
