import {DocumentStateStore} from "./DocumentStateStore";
import {UFSchema, LABELING_TASK_COREF_GROUPING} from "annotator/core/UFSchema";
import {RangeMap} from "annotator/core/RangeMap";
import RangeTree from "annotator/core/RangeTree";
import {SpanFactory} from "annotator/core/Spans";
import {Namespaces} from "annotator/core/Namespaces";
import {CoreferenceAuditor} from "../audit/CoreferenceAuditor";
import {get} from 'lodash'

const CorefPlaceHolderTag = "COREF";
const Attributes = {
    GroupId: "groupId"
};
const EmptyCoreferenceAnnotations = () => {
    return {
        groups: [],
        coref_only_mentions: []
    }
};
const InvalidGroupId = -15213;

class CoreferenceMentionGroup {

    constructor(groupId, group, spanFactory) {
        this.groupId = groupId;
        this._map = new RangeMap();
        if (group && group.mentions) {
            group.mentions.forEach((span) => {
                // make span a SpanContainer instance
                const newSpan = spanFactory.copySpan(span);
                // Update the span with a groupId
                newSpan.setAttribute(Attributes.GroupId, groupId);
                this._map.add(newSpan, newSpan);
            });
        }

        this.addMention = this.addMention.bind(this);
        this.hasMention = this.hasMention.bind(this);
        this.removeMention = this.removeMention.bind(this);
        this.getMentions = this.getMentions.bind(this);
        this.exportAnnotation = this.exportAnnotation.bind(this);
    }

    static create(id, group, spanFactory) {
        return new CoreferenceMentionGroup(id, group, spanFactory);
    }

    addMention(mention) {
        // expecting mention to be a SpanContainer object
        return this._map.add(mention, mention);
    }

    removeMention(mention) {
        return this._map.remove(mention);
    }

    getMentions() {
        return this._map.values();
    }

    hasMention(m) {
        return this._map.get(m);
    }

    get size() {
        return this._map.size;
    }

    get values() {
        return this._map.values();
    }

    exportAnnotation() {
        let annotations = {
            mentions: [],
        };
        let mentions = this._map.values();
        // sort the mentions;
        mentions.sort((m1, m2) => {
            let r1 = [m1.start, m1.end];
            let r2 = [m2.start, m2.end];
            return RangeTree.rangeComparator(r1, r2);
        });
        annotations.mentions = mentions.map((m) => {
            return m.exportAnnotation(false);
        });
        return annotations;
    }
}

class CoreferenceStateStore extends DocumentStateStore {
    constructor(documentObject,
                namespace = Namespaces.COREFERENCE,
                entityNamespace = Namespaces.NER,
                allowTagging = true) {
        super(documentObject, LABELING_TASK_COREF_GROUPING);
        this.schemaKey = namespace;
        this.entityNamespace = entityNamespace;
        this.allowTagging = allowTagging;
        this.groupSequnceId = 0;
        this.groupSequnceIdV2 = 0;
        this.createGroup = this.registerAction(this.createGroup.bind(this));
        this.removeGroup = this.registerAction(this.removeGroup.bind(this));
        this.selectGroup = this.registerAction(this.selectGroup.bind(this));
        this.getFocusGroup = this.getFocusGroup.bind(this);
        this.setFocusGroup = this.registerViewAction(this.setFocusGroup.bind(this));
        this.assignMentionToGroup = this.registerAction(this.assignMentionToGroup.bind(this));
        this.removeMentionFromGroup = this.registerAction(this.removeMentionFromGroup.bind(this));
        this.toggleMentionState = this.registerViewAction(this.toggleMentionState.bind(this));
        this.addSimilarSpansToGroup = this.registerViewAction(this.addSimilarSpansToGroup.bind(this));
        this.autoGroupSimilarSpans = this.registerViewAction(this.autoGroupSimilarSpans.bind(this));
        this.removeActiveSpanFromGroup = this.registerAction(this.removeActiveSpanFromGroup.bind(this));
        this.insertSpan = this.registerAction(this.insertSpan.bind(this));
        this.removeActiveSpan = this.registerAction(this.removeActiveSpan.bind(this));
        this.buildGroups = this.buildGroups.bind(this);
        this.scrollToActiveSpan = this.registerAction(this.scrollToActiveSpan.bind(this));
        this.clearScrollToSpan = this.registerViewAction(this.clearScrollToSpan.bind(this));

        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        // we first audit coreference annotations to eliminate inconsistency
        // with the up-to-date NER results
        CoreferenceAuditor.audit(documentObject, this.schemaKey, this.entityNamespace);
        super.initStatesFromDocument(documentObject);
        this.states.rawText = documentObject[UFSchema.RAW_TEXT];
        this.states.spanFactory = new SpanFactory(this.states.rawText);
        this.states.activeSpan = undefined;
        this.states.scrollToSpan = undefined;
        this.states.documentId = documentObject.metadata[UFSchema.DOCUMENT_ID];

        this.buildGroups(documentObject[UFSchema.ANNOTATIONS], this.states.rawText);

        this.hasV2InnerObject = get(this.arbitrationAnnotationObject, [this.schemaKey, 'groups', '0']) !== undefined;
        if (this.hasV2InnerObject) {
            this.buildGroups(this.arbitrationAnnotationObject, this.states.rawText, true);
        }
        
        // focus group is for highlighting only
        // the minimum groupId as the active group
        let minGroupId = 100000000;
        Array.from(this.states.groups.values()).forEach((g) => {
            if (g.groupId < minGroupId) {
                minGroupId = g.groupId;
            }
        });
        this.states.focusGroup = minGroupId;
    }
    
    /**
     * Creates states.groups and and states.spanFactory representations based on 
     * the passed in document
     * 
     * @param {*} annotationObject Annotation document
     * @param {string} rawText of the document
     * @param {boolean} arbitration Set to true to build groups for annotator #2
     */
    buildGroups(annotationObject, rawText, arbitration=false) {
        const groupsKey = arbitration ? 'groupsV2' : 'groups';
        const spanTreeKey = arbitration ? 'spanTreeV2' : 'spanTree';

        // coreference requires named_entity annotations being provided
        let NERAnnotations = annotationObject[this.entityNamespace];
        NERAnnotations = NERAnnotations ? NERAnnotations : [];
        // we need SpanContainer objects, not plain js objects
        // IMPORTANT: copy its own copy of NER spans before using
        // to avoid undesired side effects
        const runes = this.splitter.splitGraphemes(rawText)
        const nerSpans = NERAnnotations.map((ns) => {
            const span = this.states.spanFactory.copySpan(ns);
            super.expandSpanToHaveMultibyteOffset(span, runes);
            return span
        });
        this.states[spanTreeKey] = RangeTree.createRangeTree(nerSpans);
        // retrain a copy of original ner span
        // create all spans
        
        let coreferenceAnnotations = annotationObject[this.schemaKey];
        coreferenceAnnotations = coreferenceAnnotations ? coreferenceAnnotations : EmptyCoreferenceAnnotations();
        this.states[groupsKey] = new Map();
        // init group map
        coreferenceAnnotations.groups.forEach((g) => {
            this.createGroup(g, arbitration);
        });
        // in case there is COREF tag spans created
        // by user, we need to insert it into the spanTree as well
        coreferenceAnnotations.coref_only_mentions.forEach((m) => {
            const span = this.states.spanFactory.copySpan(m);
            super.expandSpanToHaveMultibyteOffset(span, runes);
            const {start, end} = span;
            this.states[spanTreeKey] = this.states[spanTreeKey].insert([start, end], span);
        });
        // no groups is created at first
        // create the first group
        if (this.states[groupsKey].size === 0) {
            this.createGroup({}, arbitration);
        }
        this._updateSpanGroups();
    }

    createGroup(group, arbitration=false) {
        const groupsKey = arbitration ? 'groupsV2' : 'groups';
        const groupSequenceIdKey = arbitration ? 'groupSequnceId' : 'groupSequnceIdV2';
        group = (group === undefined || group.mentions === undefined) ? {} : group;
        let newMentionGroup = CoreferenceMentionGroup.create(this[groupSequenceIdKey], group,
            this.states.spanFactory);
        this[groupSequenceIdKey] += 1;
        this.states[groupsKey].set(newMentionGroup.groupId, newMentionGroup);
        // this.selectGroup(newMentionGroup.groupId);
        // ideally createGroup shouldn't have any effect on spans
        return newMentionGroup;
    }

    removeGroup(groupId) {
        // Delete group
        this.states.groups.delete(groupId);
        // Update span mentions
        this._updateSpanGroups();
        if (groupId === this.states.focusGroup) {
            // user remove the activeGroup
            // we don't have an activeGroup now
            // make it -1
            this.states.focusGroup = InvalidGroupId;
        }
    }

    /**
     * Selects a coreference group by id and adds any selected spans to that group
     * 
     * @param {number} groupId Group ID to select
     */
    selectGroup(groupId) {
        if (this.states.groups.has(groupId)) {
            // Unselect v2 focus group if it was selected
            this.states.focusGroupV2 = undefined;
            this.states.focusGroup = groupId;
            // if there is an outstanding active span
            if (this.states.activeSpan) {
                // remove from previous group
                // the number of groups are unlikely to be a lot
                // use list should be fine
                this.groupList.forEach((g) => {
                    g.removeMention(this.states.activeSpan);
                });
                const {start, end} = this.states.activeSpan;
                this.assignMentionToGroup(start, end, groupId);
                // release the selection of the current span
                this.states.activeSpan = undefined;
            }
        }
    }

    getFocusGroup(arbitration=false) {
        const groupsKey = arbitration ? 'groupsV2' : 'groups';
        const focusGroupKey = arbitration ? 'focusGroupV2' : 'focusGroup';
        if (!this.states[groupsKey]) {
            return null;
        }
        return this.states[groupsKey].get(this.states[focusGroupKey]);
    }

    /**
     * Selects a coreference group by id
     * 
     * TODO: Eventually move all arbitration /'v2' state management out of this class.
     * A better solution might be to have another store (CorefGroupStore?) to manage v1/v2 arbitration states
     * 
     * @param {number} groupId Group ID to select
     * @param {boolean} v2 Set to true to true to select a group from annotator #2
     */
    setFocusGroup(groupId, v2=false) {
        const focusGroupKey = v2 ? 'focusGroupV2' : 'focusGroup';
        this.states.focusGroup = undefined;
        this.states.focusGroupV2 = undefined;
        this.states[focusGroupKey] = groupId;
    }
    
    /**
     * Finds spans with the same text extent and entity type and 
     * adds them to the specified span's current group.
     * 
     * @param {object} span
     */
    addSimilarSpansToGroup(span) {
        // Do nothing if the specified span doesn't have a group
        if (!(span.attrs.groupId >= 0)) {
            return;
        }
        const groupId = span.attrs.groupId;
        const extent = span.getExtent();
        const tag = span.tag;
        const similarAnnotations = this.states.spanTree.values.filter(span => {
            return (
                span.getExtent().toLowerCase() === extent.toLowerCase() &&
                span.tag === tag &&
                span.attrs.groupId < 0
            );
        });
        const group = this.states.groups.get(groupId);
        similarAnnotations.forEach(span => {
            this.assignMentionToGroup(span.start, span.end, group.groupId);
        });
        
        return group;
    }
    
    /**
     * Finds all spans with the same text extent and entity types and adds them to new groups
     * 
     * @param {boolean} properNounsOnly set to true to only group proper nouns (capitalized words)
     */
    autoGroupSimilarSpans(properNounsOnly=true) {
        const spans = this.states.spanTree.values;
        // Groupby span extent
        const spanGroups = spans.reduce((groups, currentSpan) => {
            // Only consider spans not assigned to a group
            if (currentSpan.getAttribute(Attributes.GroupId) >= 0) {
                return groups;
            }
            const extent = currentSpan.extent;
            const tag = currentSpan.tag;
            // Only consider proper nouns (first letter is capitalized)
            if (extent.length === 0 || (
                properNounsOnly && (extent[0] !== extent[0].toUpperCase())
            )) {
                return groups;
            }
            // Only group spans together if they have the same tag (entity type)
            const key = `${extent}-${tag}`
            if (!(key in groups)) {
                groups[key] = [];
            }
            groups[key].push(currentSpan);
            return groups;
        }, {});
        // Ensure all groups have at least two items
        Object.keys(spanGroups).forEach(key => {
            if (spanGroups[key].length < 2) {
                delete spanGroups[key];
            }
        });
        // Add final spans to new groups
        Object.keys(spanGroups).forEach(key => {
            const group = this.createGroup();
            spanGroups[key].forEach(span => {
                span.setAttribute(Attributes.GroupId, group.groupId);
                group.addMention(span);
            });
        });
    }

    assignMentionToGroup(start, end, groupId) {
        /**
         * This method does two things:
         * 1. If no previous assigned group, add the mention to the current active group
         * 2. If previous assigned already, remove it first, then do the assigning
         */
        let span = this.states.spanTree.get([start, end]);
        if (span) {
            // the span needs to be one of the original spans
            const selectGroup = this.states.groups.get(groupId);
            if (selectGroup) {
                // need to make sure active group is valid
                span.setAttribute(Attributes.GroupId, groupId);
                selectGroup.addMention(span);
            }
        }
    }

    removeMentionFromGroup(span) {
        if (span) {
            const {start, end} = span;
            const groupId = span.getAttribute(Attributes.GroupId);
            const group = this.states.groups.get(groupId);
            if (group) {
                let span = this.states.spanTree.get([start, end]);
                if (span) {
                    span.setAttribute(Attributes.GroupId, -1);
                    group.removeMention(span);
                }
            }
        }
    }

    removeActiveSpanFromGroup() {
        if (this.states.activeSpan && this.states.activeSpan.attrs.groupId >= 0) {
            this.removeMentionFromGroup(this.states.activeSpan);
            // reset activeSpan
            this.states.activeSpan = undefined;
        }
    }

    toggleMentionState(span) {
        // check whether a span is in a group or not
        if (!this.states.activeSpan) {
            // no active span
            this.states.activeSpan = span;
            return;
        }
        if (span.rangeEqual(this.states.activeSpan)) {
            // equal to the actual span
            this.states.activeSpan = undefined;
        } else {
            // different than actual span
            this.states.activeSpan = span;
        }
    }

    get spans() {
        let spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.spanTree,
            this.states.rawText.length,
            (start, end) => {
                return this.states.spanFactory.createSpan(start, end)
            }).map((entry) => {
            let [_, __, value] = entry;
            return value;
        });
        return spans;
    }

    get groupList() {
        // return group in a list;
        return Array.from(this.states.groups.values()).sort((g1, g2) => {
            return g1.groupId - g2.groupId;
        });
    }

    get groupListV2() {
        // return groupsV2 in a list;
        if (!('groupsV2' in this.states)) {
            return [];
        }
        return Array.from(this.states.groupsV2.values()).sort((g1, g2) => {
            return g1.groupId - g2.groupId;
        });
    }

    insertSpan(start, end) {
        if (!this.allowTagging) return;
        if (!this.states.spanTree.find([start, end]).valid) {
            // only if not already inserted
            let newSpan = this.states.spanFactory.createSpan(start, end, CorefPlaceHolderTag);
            newSpan.setAttribute(Attributes.GroupId, -1);
            this.states.spanTree = this.states.spanTree.insert([start, end], newSpan);
            this.states.activeSpan = newSpan;
            return newSpan;
        }
    }

    removeActiveSpan() {
        if (this.states.activeSpan) {
            if (this.states.activeSpan.tag === CorefPlaceHolderTag) {
                // only if this span is a NoTag span,
                // we will remove it, otherwise do not change it
                const {start, end} = this.states.activeSpan;

                // remove span from group assignment
                this.removeActiveSpanFromGroup();
                // remove span from spanTree
                this.states.spanTree = this.states.spanTree.remove([start, end]);
                // cancel active span selection
                this.states.activeSpan = undefined;
            }
        }
    }

    convertStatesToAnnotations() {
        let groupAnnotations = [];
        let sortedGroups = Array.from(this.states.groups.values()).sort((g1, g2) => {
            return g1.groupId - g2.groupId;
        });
        sortedGroups.forEach((sg) => {
            let gAnnotations = sg.exportAnnotation();
            if (gAnnotations.mentions.length > 0) {
                // ignore empty groups
                groupAnnotations.push(gAnnotations);
            }
        });
        let annotations = EmptyCoreferenceAnnotations();
        annotations.groups = groupAnnotations;
        RangeTree.retrieveAllInsertedRanges(this.states.spanTree).forEach((e) => {
            let [_, __, span] = e;
            if (span.tag === CorefPlaceHolderTag) {
                annotations.coref_only_mentions.push(span.exportAnnotation(false))
            }
        });
        let wrappedAnnotations = {};
        wrappedAnnotations[this.schemaKey] = annotations;
        return wrappedAnnotations;
    }

    scrollToActiveSpan() {
        this.states.scrollToSpan = this.states.activeSpan;
    }

    clearScrollToSpan() {
        this.states.scrollToSpan = undefined;
    }

    /**
     * Updates all spans internal attrs.groupId reference
     * @param {*} spans 
     */
    _updateSpanGroups() {
        let globalMentionMap = new RangeMap();
        Array.from(this.states.groups.values()).forEach((g) => {
            g.getMentions().forEach((m) => {
                globalMentionMap.add(m, g.groupId);
            })
        });
        // obtain all spans
        this.spans.forEach((s) => {
            // now check the membership for each span;
            if (!s.attrs.blank) {
                let groupId = globalMentionMap.get(s);
                groupId = groupId !== undefined ? groupId : -1;
                // set groupId accordingly

                s.setAttribute(Attributes.GroupId, groupId);
            }
        });
    }
}

export {CoreferenceMentionGroup, CoreferenceStateStore};