import React from 'react';

import {CellPropType} from "./CellPropertyTypes";
import {CommonPropertyConfigCell} from "./CommonPropertyConfigCell";
import {ValuesEditComponent} from "./ValuesEditComponent";

class KeyValuePropertyConfigCell extends React.Component<CellPropType> {

    constructor(props: CellPropType) {
        super(props);
    }


    render() {
        const property = this.props.property;
        const namespace = this.props.namespace;
        let propertiesConfig = this.props.schemaConfig.propertiesConfig;

        const valueEditComponent = <ValuesEditComponent property={property}
                                                        namespace={namespace}
                                                        propertiesConfig={propertiesConfig}/>;

        return <React.Fragment>
            <CommonPropertyConfigCell namespace={namespace}
                                      property={property}
                                      schemaConfig={this.props.schemaConfig}/>
            <td style={{"minWidth": "200px"}}>{valueEditComponent}</td>
        </React.Fragment>
    }
}

export {KeyValuePropertyConfigCell}