import React, {Component} from "react";
import {observer} from 'mobx-react'
import {CardBody, CardHeader, CardTitle, CardText, Button, CardFooter} from "reactstrap";
import {RelationSelector} from "./RelationSelector";
import {SelectTagColor, TagColorScheme, UnselectTagColor} from "annotator/AnnotatorCommons";
import 'annotator/AnnotatorTagSpan.css'
import './RelationAnnotator.css'
import {SingleLayerSpan} from "annotator/span/SingleLayerSpan";
import {RelationDisplayPanel} from "./RelationDisplayPanel";
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import {RangeMap} from "../core/RangeMap";

const ArgBackground = "#a70500";

const createRelationSpan = (extent, tag, args = undefined, displayHint = false) => {
    let extraStyle = {};
    let displayTag = "";
    let argMarks = [];
    if (args) {
        console.log("Args", args, extent);
        let mark = <mark className="RichInnerTagStyle"
                         style={{
                             "background": ArgBackground,
                             "borderStyle": "none",
                             "color": "#fff"
                         }}>{args}</mark>;
        argMarks.push(mark);
        // make argument span larger
        extraStyle = {
            "fontSize": "20px",
            "fontWeight": "700px",
        }
    }
    if (tag === "COREF") {
        // coreference mention
        displayTag = "none";
    }

    if (displayHint) {
        // add underline if a span/mention
        // is already grouped in a relation
        extraStyle["textDecorationLine"] = "underline";

    }

    return <span style={extraStyle}>
        {extent}
        <mark className="TagStyle NoSelect"
              style={{
                  "display": displayTag,
                  "top": "-22px"
              }}>
            {tag}
            </mark>
        {argMarks}
        </span>
};

const RelationAnnotator = observer(
    class RelationAnnotator extends Component {
        constructor(props) {
            super(props);
        }

        render() {
            // console.log("relation rerender");
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const {store} = this.props;
            const stateMachine = store.states.stateMachine;
            const spans = store.spans;
            let tagSpans = [];

            let relationToDisplay = [];
            let spanAlreadyMarked = new RangeMap();
            spans.forEach((s) => {
                if (s.attrs.relations) {
                    s.attrs.relations.map((r) => {
                        console.log("r -> ", r);
                        relationToDisplay.push(r);
                        // track spans that are already marked
                        spanAlreadyMarked.add(r.arg1, r.arg1);
                        spanAlreadyMarked.add(r.arg2, r.arg2);
                    });
                }
            });

            relationToDisplay.sort((r1, r2) => {
                return r1.seq - r2.seq;
            });

            for (let i = 0; i < spans.length; i++) {
                let spanObj = spans[i];
                let key = "span_" + i;
                let tagColor = UnselectTagColor;
                // default span markup
                let spanMarkup = spanObj.extent;
                let clickTrigger = () => {
                };
                let displayHint = false;
                if (spanAlreadyMarked.get(spanObj)) {
                    displayHint = true;
                }
                if (!spanObj.attrs.blank) {
                    // non-blank span can update
                    // the store state
                    clickTrigger = () => {
                        // pass itself to the documentStore
                        store.updateState({span: spanObj});
                    };
                    // this is an arg1 and arg2 span
                    if (spanObj.attrs.arg1 || spanObj.attrs.arg2) {
                        tagColor = SelectTagColor;
                        let arg = "ARG1";
                        if (spanObj.attrs.arg2) {
                            arg = "ARG2";
                        }
                        spanMarkup = createRelationSpan(spanObj.extent, spanObj.tag, arg, displayHint);
                    } else {
                        spanMarkup = createRelationSpan(spanObj.extent, spanObj.tag, undefined, displayHint);
                    }
                }
                tagSpans.push(<SingleLayerSpan key={key}
                                               text={spanMarkup}
                                               tag={spanObj.tag}
                                               color={tagColor}
                                               start={spanObj.start}
                                               end={spanObj.end}
                                               blank={spanObj.attrs.blank}
                                               selectable={false}
                                               transparent={true}
                                               store={this.props.store}
                                               cssStyle={"HighlightSpan"}
                                               clickTrigger={clickTrigger}/>);
            }

            const activeSegment = store.states.activeSegment + 1;
            const totalSegments = store.states.segments.length;
            const sentenceIdxTitle = "SENTENCE " + activeSegment.toString() + "/" + totalSegments.toString();
            return (
                <div id="RelationAnnotatorBody">
                    <CardBody className="AnnotatorTextView">
                        <CardTitle>
                            <Button
                                className="TagButton"
                                disabled
                                color="info"
                                style={{
                                    "userSelect": "none",
                                    "fontSize": "18px",
                                    "marginBottom": "15px"
                                }}>{sentenceIdxTitle} </Button>
                        </CardTitle>
                        <AnnotatorTextArea textDirection={this.props.textDirection}
                                           textAlignment={this.props.textAlignment}>
                            <CardText className="AnnotatorText"
                                      markable="parent"
                                      id="tag-container">
                                {tagSpans}
                            </CardText>
                        </AnnotatorTextArea>
                    </CardBody>
                    <CardHeader>
                        <RelationSelector selectionState={stateMachine.state}
                                          store={store}/>
                    </CardHeader>
                    <CardBody style={{"fontSize": 18}}>
                        <RelationDisplayPanel store={store}
                                              stateMachine={stateMachine}
                                              relations={relationToDisplay}/>
                    </CardBody>
                    <CardFooter>
                        <Button className="FunctionalButton"
                                style={{
                                    "marginRight": "5px",
                                    "fontSize": "18px",
                                    "maxWidth": "200px"
                                }}
                                onClick={store.prevSegment}>PREV SENT</Button>
                        <Button className="FunctionalButton"
                                style={{
                                    "marginLeft": "auto",
                                    "fontSize": "18px",
                                    "maxWidth": "200px"
                                }}
                                onClick={store.nextSegment}>NEXT SENT</Button>
                    </CardFooter>
                </div>
            )

        }
    }
);

export {createRelationSpan, RelationAnnotator}
