import React from 'react';
import {extendObservable} from 'mobx'

class EntityGroupSelectionStore extends React.Component {
    constructor(props) {
        super(props);
        extendObservable(this, {
            updated: 0
        });
        this.entities = [];

        this.add = this.add.bind(this);
        this.pop = this.pop.bind(this);
        this.clear = this.clear.bind(this);
    }

    add(e) {
        if (this.entities.indexOf(e) === -1) {
            this.entities.push(e);
            this.updated += 1;
        }
    }

    pop() {
        this.entities.pop();
        this.updated += 1;
    }

    clear() {
        this.entities = [];
        this.updated += 1;
    }
}

export {EntityGroupSelectionStore}