import React, { useRef, useEffect, useState, useContext, useCallback } from 'react';
import './Page.css';
import PDFPageInfo, { doOverlap } from '../../types/PDFPageInfo';
import styled from 'styled-components';
import PDFPageRenderer from '../../utils/PDFPageRenderer';
import { Bounds } from '../../types/Bounds';
import { Annotation, SsieAnnotationStore, SsieAnnotations } from '../../context/SsieAnnotationStore';
import { getElementPosition, Optional } from '../../utils/SsieUtils';
import { Selection, SelectionBoundary, SelectionTokens } from '../Selection/Selection';
import { UNASSIGNED_TAG } from '../../../../store/TaggingStateStore';
import { ActiveSpanColor, RedSpanColor, SecondarySpanColor, SelectTagColor, UnassignedSpanColor } from '../../../../AnnotatorCommons';
import { isRightClick } from '../../utils/SsieUtils';
import { PDFStore } from '../../context/PDFStore';
import { Block } from '../../types/Block';

interface PageProps {
    pageInfo: PDFPageInfo;
}

/**
 * Component that renders the PDF page's data, annotation creation actions and composed components.
 * Screenshot: https://tiny.amazon.com/d53rp3vh/drivcorpamazdocudnleCRpng
 */
export const Page: React.FC<PageProps> = ({ pageInfo }) => {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const [isVisible, setIsVisible] = useState<boolean>(false);
    const [scale, setScale] = useState<number>(1);

    const {
        selectedSsieAnnotations,
        ssieAnnotations,
        setSsieAnnotationStore,
        selectedTag = UNASSIGNED_TAG,
        secondarySsieAnnotations,
        showSecondaryAnnotations,
        pdfBlocks,
    } = useContext(SsieAnnotationStore);
    const { currPdfPage, pdfPage, onError } = useContext(PDFStore);

    const containerRef = useRef<HTMLDivElement>(null);
    const [selection, setSelection] = useState<Bounds>();

    useEffect(() => {
        try {
            const determinePageVisiblity = () => {
                if (canvasRef.current !== null) {
                    const windowTop = 0;
                    const windowBottom = window.innerHeight;
                    const rect = canvasRef.current.getBoundingClientRect();
                    setIsVisible(
                        // Top is in within window
                        (windowTop < rect.top && rect.top < windowBottom) ||
                            // Bottom is in within window
                            (windowTop < rect.bottom && rect.bottom < windowBottom) ||
                            // top is negative and bottom is +ve
                            (rect.top < windowTop && rect.bottom > windowTop)
                    );
                }
            };

            if (canvasRef.current === null) {
                onError(new Error('No canvas element'));
                return;
            }

            pageInfo.bounds = getPageBoundsFromCanvas(canvasRef.current);

            const renderer = new PDFPageRenderer(pageInfo.pdfPageProxy, canvasRef.current, onError);
            renderer.render(pageInfo.scale);

            determinePageVisiblity();

            const handleResize = () => {
                if (canvasRef.current === null) {
                    onError(new Error('No canvas element'));
                    return;
                }
                pageInfo.bounds = getPageBoundsFromCanvas(canvasRef.current);
                renderer.rescaleAndRender(pageInfo.scale);

                setScale(pageInfo.scale);
                determinePageVisiblity();
            };
            window.addEventListener('resize', handleResize);
            window.addEventListener('scroll', determinePageVisiblity);
            return () => {
                window.removeEventListener('resize', handleResize);
                window.removeEventListener('scroll', determinePageVisiblity);
            };
        } catch (e) {
            onError(e);
        }
    }, [onError, pageInfo, canvasRef]); // We deliberately only run this once.

    const onMouseDown = useCallback(
        (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            if (event.shiftKey || isRightClick(event)) {
                return;
            }

            if (containerRef.current === null) {
                throw new Error('No Container');
            }

            if (!selection) {
                const { x: containerX, y: containerY } = getElementPosition(containerRef.current);
                const left = event.pageX - containerX;
                const top = event.pageY - containerY;
                const newSelection = {
                    left,
                    top,
                    right: left,
                    bottom: top,
                };

                // check for intersecting annotations and select if existing
                const intersectingAnnotations = getAnnotationsFromPdf(ssieAnnotations, pageInfo).filter((anno) =>
                    intersectsAnnotation(pageInfo, anno, newSelection)
                );
                if (intersectingAnnotations.length > 0) {
                    setSsieAnnotationStore({
                        selectedSsieAnnotations: intersectingAnnotations,
                        stagedRelation: [],
                    });
                } else {
                    setSsieAnnotationStore({
                        selectedSsieAnnotations: [],
                        stagedRelation: [],
                    });
                    setSelection(newSelection);
                }
            }

            event.stopPropagation();
        },
        [selection, containerRef, pageInfo, ssieAnnotations, setSsieAnnotationStore]
    );

    const onMouseMove = React.useCallback(
        (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            if (event.shiftKey || isRightClick(event)) {
                return;
            }

            if (selection) {
                if (containerRef.current === null) {
                    throw new Error('No Container');
                }

                const { x: containerX, y: containerY } = getElementPosition(containerRef.current);
                const right = event.pageX - containerX;
                const bottom = event.pageY - containerY;
                const newSelection = {
                    ...selection,
                    right,
                    bottom,
                };
                setSelection(newSelection);
            }

            event.stopPropagation();
        },
        [selection, containerRef]
    );

    const onMouseUp = React.useCallback(
        (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
            if (event.shiftKey || isRightClick(event)) {
                return;
            }

            if (selection) {
                const newAnnotation = getNewAnnotation(
                    pageInfo,
                    selection,
                    UNASSIGNED_TAG,
                    pdfBlocks,
                    getAnnotationTokenIds(getAnnotationsFromPdf(ssieAnnotations, pageInfo))
                );

                if (newAnnotation) {
                    // add the new annotation and set it as selected
                    setSsieAnnotationStore({
                        ssieAnnotations: ssieAnnotations.withNewAnnotations([newAnnotation]),
                        selectedSsieAnnotations: [newAnnotation],
                        stagedRelation: [newAnnotation.id],
                    });
                }

                // remove bounding selection
                setSelection(undefined);
            }

            event.stopPropagation();
        },
        [selection, pageInfo, ssieAnnotations, setSsieAnnotationStore, pdfBlocks]
    );

    const isAnnotationPage = currPdfPage === parseInt(pdfPage);

    return (
        <PageAnnotationsContainer
            className={`page-annotations-container ${isAnnotationPage ? '' : 'not-allowed-cursor'}`}
            ref={containerRef}
            onMouseDown={onMouseDown}
            onMouseMove={onMouseMove}
            onMouseUp={onMouseUp}
        >
            <div className={`${!isAnnotationPage ? 'page__unannotatable' : ''}`} />
            <PageCanvas ref={canvasRef} />
            {
                // We only render the tokens if the page is visible, as rendering them all makes the
                // page slow and/or crash.
                isAnnotationPage &&
                    scale &&
                    isVisible &&
                    secondarySsieAnnotations &&
                    showSecondaryAnnotations &&
                    getAnnotationsFromPdf(secondarySsieAnnotations, pageInfo).map((annotation) => {
                        return <Selection pageInfo={pageInfo} annotation={annotation} key={annotation.id} color={SecondarySpanColor} isSecondary />;
                    })
            }
            {
                // We only render the tokens if the page is visible, as rendering them all makes the
                // page slow and/or crash.
                isAnnotationPage &&
                    scale &&
                    isVisible &&
                    getAnnotationsFromPdf(ssieAnnotations, pageInfo).map((annotation) => {
                        const isSelected = selectedSsieAnnotations.includes(annotation);
                        const color = getAnnotationColor(isSelected, selectedTag, annotation.tag);
                        return <Selection pageInfo={pageInfo} annotation={annotation} key={annotation.id} color={color} />;
                    })
            }

            {/** This section handles the drawing of a selection bounding box for a new annotation */}
            {isAnnotationPage && selection
                ? (() => {
                      const annotation = pageInfo.getAnnotationForBounds(normalizeBounds(selection), selectedTag, pdfBlocks);
                      const tokenIds = annotation ? annotation.tokenIds : [];
                      const color = getAnnotationColor(true, selectedTag, UNASSIGNED_TAG);

                      return (
                          <>
                              <SelectionBoundary bounds={selection} color={color} />
                              <SelectionTokens pageInfo={pageInfo} tokenIds={tokenIds} color={color} />
                          </>
                      );
                  })()
                : null}
        </PageAnnotationsContainer>
    );
};

/**
 * Get the color for an annotation depending on the selection status as well as it's tag related to the selected tag
 * @param selected
 * @param selectedTag
 * @param annotationTag
 * @returns a color for the Text Blob
 */
const getAnnotationColor = (selected: boolean, selectedTag: string, annotationTag: string): string => {
    if (selected) {
        return ActiveSpanColor;
    } else {
        if (annotationTag === UNASSIGNED_TAG) {
            return RedSpanColor;
        } else {
            if (selectedTag === annotationTag) {
                return SelectTagColor;
            } else {
                return UnassignedSpanColor;
            }
        }
    }
};

const getAnnotationTokenIds = (annotations: Annotation[]) => {
    const existingTokenIds = new Set<string>();
    for (const anno of annotations) {
        for (const t of anno.tokenIds) {
            existingTokenIds.add(t.toString());
        }
    }
    return existingTokenIds;
};

/**
 * Returns the provided bounds in their normalized form. Normalized means that the left
 * coordinate is always less than the right coordinate, and that the top coordinate is always
 * left than the bottom coordinate.
 *
 * This is required because objects in the DOM are positioned and sized by setting their top-left
 * corner, width and height. This means that when a user composes a selection and moves to the left,
 * or up, from where they started might result in a negative width and/or height. We don't normalize
 * these values as we're tracking the mouse as it'd result in the wrong visual effect. Instead we
 * rotate the bounds we render on the appropriate axis. This means we need to account for this
 * later when calculating what tokens the bounds intersect with.
 */
export function normalizeBounds(b: Bounds): Bounds {
    const normalized = Object.assign({}, b);
    if (b.right < b.left) {
        const l = b.left;
        normalized.left = b.right;
        normalized.right = l;
    }
    if (b.bottom < b.top) {
        const t = b.top;
        normalized.top = b.bottom;
        normalized.bottom = t;
    }
    return normalized;
}

export function getNewAnnotation(
    pageInfo: PDFPageInfo,
    selection: Bounds,
    selectedTag: string,
    pdfBlocks: Block[],
    existingTokenIds: Set<string>
): Optional<Annotation> {
    const normalized = normalizeBounds(selection);
    return pageInfo.getAnnotationForBounds(normalized, selectedTag, pdfBlocks, existingTokenIds);
}

function getPageBoundsFromCanvas(canvas: HTMLCanvasElement): Bounds {
    if (canvas.parentElement === null) {
        throw new Error('No canvas parent');
    }
    const parent = canvas.parentElement;
    const parentStyles = getComputedStyle(canvas.parentElement);

    const leftPadding = parseFloat(parentStyles.paddingLeft || '0');
    const left = parent.offsetLeft + leftPadding;

    const topPadding = parseFloat(parentStyles.paddingTop || '0');
    const top = parent.offsetTop + topPadding;

    const parentWidth = parent.clientWidth - leftPadding - parseFloat(parentStyles.paddingRight || '0');
    const parentHeight = parent.clientHeight - topPadding - parseFloat(parentStyles.paddingBottom || '0');
    return {
        left,
        top,
        right: left + parentWidth,
        bottom: top + parentHeight,
    };
}

const PageCanvas = styled.canvas`
    display: block;
`;

const PageAnnotationsContainer = styled.div(
    () => `
    position: relative;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.2);
    margin: 0 0 ${20};

    &:last-child {
        margin-bottom: 0;
    }
`
);

const getAnnotationsFromPdf = (ssieAnnotations: SsieAnnotations, pageInfo: PDFPageInfo) => {
    return ssieAnnotations.annotations.filter((a) => a.page === pageInfo.pdfPageProxy.pageNumber);
};

const intersectsAnnotation = (pageInfo: PDFPageInfo, annotation: Annotation, selection: Bounds) => {
    const { tokenIds } = annotation;
    if (tokenIds) {
        return (
            tokenIds
                .map((tokenId) => pageInfo.tokens[tokenId.tokenIndex])
                .filter((annotationToken) => doOverlap(pageInfo.getScaledTokenBounds(annotationToken), selection)).length > 0
        );
    }
    return false;
};
