import React, { useContext } from 'react';
import './Selection.css';

import { Bounds } from '../../types/Bounds';
import PDFPageInfo from '../../types/PDFPageInfo';
import { TokenId } from '../../types/TokenId';
import { Annotation, multipleRelationsContain, relationContains, SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import { ContextMenuTrigger } from 'react-contextmenu';
import { TAGGING_ANNOTATOR_MENU_ID } from '../../../TaggingAnnotator';
import { annotationsEqual, getOverlappingAnnotations, handleAnnotationRightClick, handleAnnotationSelect, isRightClick } from '../../utils/SsieUtils';
import { UNASSIGNED_TAG } from '../../../../store/TaggingStateStore';
import { StringMap } from '../../types/StringMap';
import { RedSpanColor, TagLabelColor } from '../../../../AnnotatorCommons';
import { SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT } from '../../utils/DefaultSsieCollection';
import ConnectionLabel from './components/ConnectionLabel/ConnectionLabel';

interface SelectionProps {
    pageInfo: PDFPageInfo;
    annotation: Annotation;
    showInfo?: boolean;
    color: string;
    isSecondary?: boolean;
}

/**
 * Component for showing the boundary, entity-type/tag label, and discontiguous/connection label of an annotation.
 * Screenshot: https://tiny.amazon.com/oo3dgqi3/drivcorpamazdocudnleCRpng
 */
export const Selection: React.FC<SelectionProps> = ({ pageInfo, annotation, showInfo = true, color, isSecondary = false }) => {
    const {
        setSsieAnnotationStore,
        selectedSsieAnnotations,
        ssieAnnotations,
        stagedRelation,
        showEntityLabels,
        showEntityConnections,
        secondarySsieAnnotations,
    } = useContext(SsieAnnotationStore);

    const [overlappingAnnotations, setOverlappingAnnotations] = React.useState<Annotation[]>([]);
    const [overlappingConnectionAnnotations, setOverlappingConnectionAnnotations] = React.useState<Annotation[]>([]);
    React.useEffect(() => {
        if (isSecondary) {
            const clickedSecondaryRelations = secondarySsieAnnotations.relations.filter((rel) => relationContains(rel, annotation.id));
            const singleAnnotationOverlappingAnnotations = getOverlappingAnnotations(annotation, ssieAnnotations.annotations);
            if (clickedSecondaryRelations.length > 0) {
                const sec_anno_map: StringMap<Annotation> = {};
                for (const anno of secondarySsieAnnotations.annotations) {
                    sec_anno_map[anno.id] = anno;
                }
                const clickedSecondaryAnnotations = clickedSecondaryRelations[0].map((id) => sec_anno_map[id]);
                const newOverlappingAnnotations: Annotation[] = [];
                for (const anno of clickedSecondaryAnnotations) {
                    Array.prototype.push.apply(newOverlappingAnnotations, getOverlappingAnnotations(anno, ssieAnnotations.annotations));
                }
                setOverlappingConnectionAnnotations(newOverlappingAnnotations);
            } else {
                // find relation for primary annotations and select those
                const singleAnnotationOverlappingRelations: string[][] = [];
                for (const anno of singleAnnotationOverlappingAnnotations) {
                    Array.prototype.push.apply(
                        singleAnnotationOverlappingRelations,
                        ssieAnnotations.relations.filter((rel) => relationContains(rel, anno.id))
                    );
                }
                if (singleAnnotationOverlappingRelations.length > 0) {
                    // get all annotations for all relations
                    const annotationsForEachRelation: Annotation[] = [];
                    for (const rel of singleAnnotationOverlappingRelations) {
                        const prim_anno_map: StringMap<Annotation> = {};
                        for (const anno of ssieAnnotations.annotations) {
                            prim_anno_map[anno.id] = anno;
                        }
                        Array.prototype.push.apply(
                            annotationsForEachRelation,
                            rel.map((id) => prim_anno_map[id])
                        );
                    }
                    setOverlappingConnectionAnnotations(annotationsForEachRelation);
                } else {
                    // overlapping annotations are not part of any relation
                    setOverlappingConnectionAnnotations(singleAnnotationOverlappingAnnotations);
                }
            }
            setOverlappingAnnotations(singleAnnotationOverlappingAnnotations);
            return;
        }
        setOverlappingConnectionAnnotations([]);
        setOverlappingAnnotations([]);
    }, [isSecondary, annotation, ssieAnnotations, secondarySsieAnnotations]);

    const [hasAnnotationDifference, setHasAnnotationDifference] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (!isSecondary) {
            setHasAnnotationDifference(false);
        } else {
            if (overlappingAnnotations.length === 0) {
                setHasAnnotationDifference(true);
            } else {
                setHasAnnotationDifference(!annotationsEqual(overlappingAnnotations[0], annotation));
            }
        }
    }, [annotation, overlappingAnnotations, isSecondary]);

    const [hasConnectionDifference, setHasConnectionDifference] = React.useState<boolean>(false);
    React.useEffect(() => {
        if (!isSecondary) {
            setHasConnectionDifference(false);
        } else {
            const secondaryRelation = secondarySsieAnnotations.relations.filter((rel) => relationContains(rel, annotation.id));
            const primaryRelation =
                overlappingConnectionAnnotations.length > 0
                    ? ssieAnnotations.relations.filter((rel) => relationContains(rel, overlappingConnectionAnnotations[0].id))
                    : [];
            setHasConnectionDifference(JSON.stringify(primaryRelation) !== JSON.stringify(secondaryRelation));
        }
    }, [
        overlappingConnectionAnnotations,
        isSecondary,
        ssieAnnotations,
        secondarySsieAnnotations,
        stagedRelation,
        selectedSsieAnnotations,
        annotation,
    ]);

    const bounds = pageInfo.getScaledBounds(annotation.bounds);

    const onMouseDown = React.useCallback(
        (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
            if (isRightClick(e)) {
                return;
            }

            // shift-clicking allows mult-annotation selection if at least one annotation is already selected
            if (e.shiftKey && selectedSsieAnnotations.length > 0) {
                handleAnnotationRightClick({ ssieAnnotations, setSsieAnnotationStore, selectedSsieAnnotations, stagedRelation, annotation });
            } else {
                handleAnnotationSelect({
                    setSsieAnnotationStore,
                    ssieAnnotations,
                    annotation,
                });
            }

            e.preventDefault();
            e.stopPropagation();
        },
        [selectedSsieAnnotations, annotation, ssieAnnotations, stagedRelation, setSsieAnnotationStore]
    );

    const onSecondaryEntityClick = React.useCallback(
        (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
            const clickedSecondaryRelations = secondarySsieAnnotations.relations.filter((rel) => relationContains(rel, annotation.id));
            let newSelectedSecondaryAnnotations = [annotation];
            if (clickedSecondaryRelations.length > 0) {
                const sec_anno_map: StringMap<Annotation> = {};
                for (const anno of secondarySsieAnnotations.annotations) {
                    sec_anno_map[anno.id] = anno;
                }
                newSelectedSecondaryAnnotations = clickedSecondaryRelations[0].map((id) => sec_anno_map[id]);
            }

            setSsieAnnotationStore({
                selectedSsieAnnotations: overlappingConnectionAnnotations,
                selectedSecondaryAnnotations: newSelectedSecondaryAnnotations,
            });
        },
        [annotation, overlappingConnectionAnnotations, setSsieAnnotationStore, secondarySsieAnnotations]
    );

    const onContextMenuClick = React.useCallback(
        (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
            if (isSecondary) {
                onSecondaryEntityClick(e);
                return;
            }

            // if clicked annotation is part of a relation, reset staged relations and select all the relation's annotations
            const relationsIndex = multipleRelationsContain(ssieAnnotations.relations, annotation.id);
            if (relationsIndex > -1) {
                // select all annotations in relation
                setSsieAnnotationStore({
                    selectedSsieAnnotations: ssieAnnotations.relations[relationsIndex].map(
                        (i) => ssieAnnotations.annotations.find((anno) => anno.id === i)!
                    ),
                    stagedRelation: [],
                });
            } else {
                // if clicked annotation isn't currently selected, clear selected annotation and staged relation and add it to both
                // otherwise do nothing
                const annotationIsSelected = selectedSsieAnnotations.find((anno) => anno.id === annotation.id);
                if (!annotationIsSelected) {
                    setSsieAnnotationStore({
                        selectedSsieAnnotations: [annotation],
                        stagedRelation: [annotation.id],
                    });
                }
            }

            e.preventDefault();
        },
        [annotation, ssieAnnotations, setSsieAnnotationStore, isSecondary, onSecondaryEntityClick, selectedSsieAnnotations]
    );

    return (
        <>
            <ContextMenuTrigger id={TAGGING_ANNOTATOR_MENU_ID}>
                <SelectionBoundary
                    bounds={bounds}
                    color={color}
                    adjustBoundsForOverlap={isSecondary && overlappingAnnotations.length > 0 && overlappingAnnotations[0].tag !== UNASSIGNED_TAG}
                    hasAnnotationDifference={hasAnnotationDifference}
                >
                    {showInfo ? (
                        <>
                            {showEntityLabels && annotation.tag !== UNASSIGNED_TAG && (
                                <span
                                    className={`selection-boundary-span-tag TagStyle NoSelect`}
                                    onMouseDown={isSecondary ? () => {} : onMouseDown}
                                    onContextMenu={onContextMenuClick}
                                    style={{ color: hasAnnotationDifference || hasConnectionDifference ? RedSpanColor : TagLabelColor }}
                                >
                                    {annotation.tag}
                                </span>
                            )}
                            {showEntityConnections && (
                                <ConnectionLabel
                                    isSecondary={isSecondary}
                                    annotation={annotation}
                                    onMouseDown={onMouseDown}
                                    onContextMenuClick={onContextMenuClick}
                                    overlappingAnnotations={overlappingAnnotations}
                                    hasConnectionDifference={hasConnectionDifference}
                                    hasAnnotationDifference={hasAnnotationDifference}
                                />
                            )}
                        </>
                    ) : null}
                </SelectionBoundary>
                {
                    // NOTE: It's important that the parent element of the tokens
                    // is the PDF canvas, because we need their absolute position
                    // to be relative to that and not another absolute/relatively
                    // positioned element. This is why SelectionTokens are not inside
                    // SelectionBoundary.
                    // NOTE: Secondary annotations (from blind2 arbitration annoations)
                    // do not have highlighted tokens.
                    !isSecondary && (
                        <SelectionTokens
                            key={`selection-tokens-${annotation.id}`}
                            pageInfo={pageInfo}
                            tokenIds={annotation.tokenIds}
                            color={color}
                            onMouseDown={onMouseDown}
                            onContextMenu={onContextMenuClick}
                        />
                    )
                }
            </ContextMenuTrigger>
        </>
    );
};

interface SelectionBoundaryProps {
    bounds: Bounds;
    children?: React.ReactNode;
    annotationId?: string;
    color: string;
    adjustBoundsForOverlap?: boolean;
    hasAnnotationDifference?: boolean;
}

export const SelectionBoundary: React.FC<SelectionBoundaryProps> = ({
    bounds,
    children,
    color,
    adjustBoundsForOverlap = false,
    hasAnnotationDifference = false,
}: SelectionBoundaryProps) => {
    const { showAnnotationBoundingBoxes } = useContext(SsieAnnotationStore);

    const width = bounds.right - bounds.left;
    const height = bounds.bottom - bounds.top + (adjustBoundsForOverlap ? SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT : 0);
    const rotateY = width < 0 ? -180 : 0;
    const rotateX = height < 0 ? -180 : 0;

    return (
        <span
            className="selection-boundary"
            style={{
                position: 'absolute',
                left: `${bounds.left}px`,
                top: `${bounds.top - (adjustBoundsForOverlap ? SECONDARY_ANNOTATION_BOUNDS_ADJUSTMENT : 0)}px`,
                width: `${Math.abs(width)}px`,
                height: `${Math.abs(height)}px`,
                transform: `rotateY(${rotateY}deg) rotateX(${rotateX}deg)`,
                transformOrigin: 'top left',
                border: showAnnotationBoundingBoxes ? `2px solid ${hasAnnotationDifference ? RedSpanColor : color}` : 'none',
                backgroundColor: 'transparent',
                pointerEvents: 'none',
            }}
        >
            {children ? children : null}
        </span>
    );
};

interface SelectionTokenProps {
    pageInfo: PDFPageInfo;
    tokenIds: TokenId[];
    color: string;
    onMouseDown?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
    onContextMenu?: (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => void;
}

export const SelectionTokens: React.FC<SelectionTokenProps> = ({
    pageInfo,
    tokenIds,
    color,
    onMouseDown = () => {},
    onContextMenu = () => {},
}: SelectionTokenProps) => {
    return (
        <>
            {tokenIds.map((t, i) => {
                const b = pageInfo.getScaledTokenBounds(pageInfo.tokens[t.tokenIndex]);
                return (
                    <div
                        className="selection-token"
                        key={`selection-token-${t.tokenIndex}`}
                        style={{
                            left: `${b.left}px`,
                            top: `${b.top}px`,
                            width: `${b.right - b.left}px`,
                            height: `${b.bottom - b.top}px`,
                            position: 'absolute',
                            background: color,
                            opacity: '0.2',
                            borderRadius: '3px',
                        }}
                        onMouseDown={onMouseDown}
                        onContextMenu={onContextMenu}
                    />
                );
            })}
        </>
    );
};
