import React from 'react';
import {PropertyDataStore} from "../../annotator/store/PropertyDataStore";
import {Collapse} from "reactstrap";
import {PropertyEditor} from "../../annotator/properties/PropertyEditor";
import {PropertyTargetType} from "../../annotator/types/BasePropertyType";

const DocumentPropertyEditor = (props: {
    open: boolean,
    store: PropertyDataStore,
    updateHandler: (store: PropertyDataStore) => void;
}) => {

    const {open, store, updateHandler} = props;

    const body = store === undefined ? <div/> : <PropertyEditor target="document"
                                                                targetType={PropertyTargetType.Document}
                                                                store={store}
                                                                broadcast={updateHandler}/>;


    return <React.Fragment>
        <Collapse isOpen={open}>
            {body}
        </Collapse>
    </React.Fragment>
};

export {DocumentPropertyEditor}