import {EntityLinkingSpanType} from "../types/EntityLinkingTypes";

class EntityLinkingSpanInstanceStore {
    public noRelated: boolean = false;
    public notSure: boolean = false;
    public goldId: string | undefined;
    readonly candidates: string[] = [];
    readonly span: EntityLinkingSpanType;
    private candidateIdSets = new Set<string>();

    constructor(span: EntityLinkingSpanType, private documentID: string) {
        const {candidates, gold_id: goldId, properties} = span;
        if (candidates) {
            this.candidates = candidates;
            this.candidates.map(c => this.candidateIdSets.add(c));
        }
        if (goldId) {
            this.goldId = goldId;
        }
        if (properties) {
            const {no_related: noRelated, not_sure: notSure} = properties;
            this.noRelated = noRelated;
            this.notSure = notSure;
        }

        this.span = span
    }

    get mentionId() {
        const {start, end, tag} = this.span;

        return `${start}_${end}_${this.documentID}_${tag}`;
    }


    get annotations(): EntityLinkingSpanType {

        const spanAnnotations = {
            start: this.span.start,
            end: this.span.end,
            tag: this.span.tag,
            mention_id: this.mentionId,
            extent: this.span.extent,
            candidates: this.candidates,
            properties: {
                no_related: this.noRelated,
                not_sure: this.notSure
            },
            gold_id: this.goldId
        };

        return spanAnnotations
    }


    public update(data: {
        gold_id?: string,
        no_related?: boolean,
        not_sure?: boolean
    }) {
        const {gold_id, no_related, not_sure} = data;
        this.goldId = gold_id;
        if (no_related !== undefined) {
            this.noRelated = no_related
        }
        if (not_sure !== undefined) {
            this.notSure = not_sure;
        }
    }
}

export {EntityLinkingSpanInstanceStore}