import React, {Component} from 'react';
import {observer} from 'mobx-react'
import './SentimentSelectorPanel'
import 'annotator/AnnotatorTagSpan.css'
import {Button, ButtonGroup} from "reactstrap";
import {SentimentTagSet} from "./SentimentTagConfig";

const SELECTOR_WIDTH = 230;

const SentimentSelectorPanel = observer(
    class SentimentSelectorPanel extends React.Component {

        constructor(props) {
            super(props);
            this.dynamicPosition = this.dynamicPosition.bind(this);
            this.getTagButtons = this.getTagButtons.bind(this);
            this.update = this.update.bind(this);
        }

        update(tag, sarcastic) {
            const {start, end} = this.props;
            console.log(start, end, tag, sarcastic);
            this.props.store.updateSentenceTag(start, end, tag, sarcastic);
        }

        dynamicPosition() {
            const domNode = this.props.container.refs;
            const key = Object.keys(domNode)[0];
            if (key && key.startsWith("SentimentTagSpan_")) {
                // find the right container
                const offsetLeft = domNode[key].offsetLeft;
                if (offsetLeft + SELECTOR_WIDTH > 750) {
                    // 900 is the width of the annotator window;
                    // meaning it will extend out of the window
                    const pad = (offsetLeft + SELECTOR_WIDTH - 900);
                    return "-" + (pad.toString()) + "px";
                } else {
                    return "85px";
                }
            }
            return "0px";
        }

        getTagButtons() {
            const numTags = Object.keys(SentimentTagSet).length;
            let buttons = [];
            for (let i = 0; i < numTags; i++) {
                const tagName = SentimentTagSet[i.toString()];
                const shortName = tagName.substring(0, 3);
                buttons.push(<Button className="SentimentSelectorButton"
                                     color="info"
                                     active={this.props.tag === SentimentTagSet[i]}
                                     onClick={(e) => {
                                         e.stopPropagation();
                                         this.update(tagName, this.props.sarcastic);
                                     }}
                                     key={tagName}>{shortName}</Button>)
            }
            // add the sarcasm button
            const color = this.props.sarcastic ? "danger" : "warning";
            buttons.push(<Button className="SentimentSelectorButton"
                                 color={color}
                                 onClick={(e) => {
                                     e.stopPropagation();
                                     const newSarcastic = !this.props.sarcastic;
                                     this.update(this.props.tag, newSarcastic);
                                 }}
                                 key={'sar'}>SAR
            </Button>);
            return buttons;
        }

        render() {
            const offsetLeft = this.dynamicPosition();
            const buttons = this.getTagButtons();
            return <div className="SentimentSelectorFloatStyle"
                        style={{"display": this.props.display, "left": offsetLeft}}>
                <ButtonGroup>
                    {buttons}
                </ButtonGroup>
            </div>


        }
    });

export default SentimentSelectorPanel