import React, {Component} from 'react';
import {Button, Label, Progress} from "reactstrap";
import './ImportExportPanel.css';
import 'editor/sidepanel/EditorSidePanel.css';
import {FileIoManager} from "editor/utils/FileIoManager";
import {FileImportButton} from "./FileImportButton";
import uuid from 'uuid';
import {TextInputBox} from "../utils/TextInputBox";

const DEFAULT_SCHEMA_NAME = "default_schema.json";

class SchemaImportExportPanel extends React.Component {
    constructor(props) {
        super(props);
        this.importSchema = this.importSchema.bind(this);
        this.exportSchema = this.exportSchema.bind(this);
    }


    importSchema(e) {
        const f = e.target.files[0];
        const reader = new FileReader();
        reader.onload = (event) => {
            const content = event.target.result;
            // const lines = content.split("\n");
            try {
                const schemaJSON = JSON.parse(content);
                this.props.store.schemaConfig.resume(schemaJSON);
            } catch (e) {
                this.props.store.alertStore.sendAlert("Invalid Schema", <p>Error: {e.message}</p>);
            }
        };
        if (f) {
            reader.readAsText(f);
        }
    }


    exportSchema() {
        this.props.store.alertStore.sendAlert(
            "Specify a schema version",
            <div>
                <div>
                    <p> Specify a schema version below, default to <b>unknown</b></p>
                </div>
                <TextInputBox submit={(version) => {
                    this.props.store.schemaConfig.setVersion(version);
                }} buttonText="Set Version"/>
            </div>,
            () => {
                const schema = this.props.store.schemaConfig.checkpoint();
                schema["exported_time"] = new Date().toJSON();
                schema["uuid"] = uuid.v4();
                const contents = [];
                contents.push(JSON.stringify(schema));
                FileIoManager.downloadAsFile(contents, DEFAULT_SCHEMA_NAME);
            },
            true,
        );
    }


    render() {
        return <div>
            <FileImportButton text="Override"
                              onClick={this.importSchema}/>
            <Button className="FileContainer"
                    color="warning"
                    style={{"margin": "5px"}}
                    onClick={this.exportSchema}>Unload
            </Button>
        </div>
    }

}

export {SchemaImportExportPanel};