import React from 'react';
import { HotKeys } from 'react-hotkeys';

const keyMap = {
    TOGGLE_ENTITY_LABELS: 'l',
    TOGGLE_SPAN_CONNECTIONS: 'c',
    TOGGLE_SECONDARY_ANNOTATIONS: 's',
    TOGGLE_ANNOTATION_BOUNDING_BOXES: 'b'
};

const HotKeyProvider: React.FC<{}> = (props) => {
    return <HotKeys keyMap={keyMap}>{props.children}</HotKeys>;
};

export default HotKeyProvider;
