import {DocumentStateStore} from "./DocumentStateStore";
import {UFSchema} from "annotator/core/UFSchema";
import {SpanFactory} from "annotator/core/Spans";
import RangeTree from "../core/RangeTree";
import {SpanLayerPlanner} from "./SpanLayerPlanner";
import {Namespaces} from "../core/Namespaces";
import {LayerIndex, LayerAttributes, OverlayMode} from "../span/Constants";

class TaggingDiffViewerStateStore extends DocumentStateStore {
    constructor(documentObject) {
        super(documentObject);
        this.toggleMode = this.registerAction(this.toggleMode.bind(this));
        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        const spanFactory = new SpanFactory(rawText);
        this.states.rawText = rawText;
        this.states.spanFactory = spanFactory;
        let annotations = documentObject[UFSchema.ANNOTATIONS][Namespaces.TAGGING_DIFF_VIEWER];
        if (!annotations) {
            annotations = {};
        }
        const runes = this.splitter.splitGraphemes(rawText);
        let v1Annotations = annotations["v1"] !== undefined ? annotations["v1"] : [];
        let v2Annotations = annotations["v2"] !== undefined ? annotations["v2"] : [];
        const convertToSpanObject = (s, layer) => {
            let spanObj = spanFactory.copySpan(s);
            super.expandSpanToHaveMultibyteOffset(spanObj, runes);
            spanObj.setAttribute(LayerAttributes.Layer, layer);
            return spanObj;
        };
        // convert to SpanContainer objects
        v1Annotations = v1Annotations.map((s) => {
            return convertToSpanObject(s, LayerIndex.FRONT);
        });
        v2Annotations = v2Annotations.map((s) => {
            return convertToSpanObject(s, LayerIndex.MIDDLE);
        });
        console.log(annotations);
        this.states.v1SpanTree = RangeTree.createRangeTree(v1Annotations);
        this.states.v2SpanTree = RangeTree.createRangeTree(v2Annotations);
        // add a mode variable
        this.states.mode = OverlayMode.OVERLAY;
    };

    toggleMode(mode) {
        this.states.mode = mode;
    }

    get spans() {
        const blankSpanCreator = (start, end) => {
            let spanObj = this.states.spanFactory.createSpan(start, end);
            // default layer for blank spans
            spanObj.setAttribute(LayerAttributes.Layer, LayerIndex.BACKGROUND);
            return spanObj;
        };
        const getSpan = (entry) => {
            let [s, e, spanObj] = entry;
            return spanObj;
        };
        const v1Spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.v1SpanTree,
            this.states.rawText.length, blankSpanCreator).map(getSpan);
        const v2Spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.v2SpanTree,
            this.states.rawText.length, blankSpanCreator).map(getSpan);
        // flatten and fragment overlapping spans into individual spans
        return new SpanLayerPlanner().doFragment(v1Spans, v2Spans);
    }

    get mode() {
        return this.states.mode;
    }
}

export {TaggingDiffViewerStateStore}