class EntityAttributesStore {
    constructor(attributesConfig) {
        this.attributesConfig = attributesConfig === undefined ? [] : attributesConfig;
        this.entityAttibuteConfig = new Map();
        this.allAttributes = [];
        // register attributes to entity
        this.attributesConfig.forEach((kv) => {
            const {key, value} = kv;
            // values are entities
            value.forEach((e) => {
                let attributeList = this.entityAttibuteConfig.get(e);
                if (!attributeList) {
                    attributeList = [];
                    this.entityAttibuteConfig.set(e, attributeList);
                }
                attributeList.push(key);
            });
            this.allAttributes.push(key);
        });

        this.getEntityAttributes = this.getEntityAttributes.bind(this);
    }

    getEntityAttributes(entity) {
        const attributeList = this.entityAttibuteConfig.get(entity);
        if (attributeList === undefined || attributeList.length === 0) {
            return
        }
        return attributeList;
    }

    getSpanEntityAttributes(span) {
        const keys = this.getEntityAttributes(span.tag);
        if (keys) {
            const attributes = span.getGroupAttributes(keys);
            keys.forEach((k) => {
                if (!attributes.hasOwnProperty(k)) {
                    // fill the default value if
                    // not presented
                    attributes[k] = undefined;
                }
            });
            return attributes;
        }
    }
}

export {EntityAttributesStore}