import RangeTree from "annotator/core/RangeTree";
import {UFSchema, LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER} from "annotator/core/UFSchema";
import {DocumentStateStore} from "./DocumentStateStore";
import {TagGroupStore} from "./TagGroupStore";
import {DefaultRateLimiter, ZeroRateLimiter} from "annotator/store/SpanRateLimiter";
import {SpanFactory} from "../core/Spans";
import {UndoEditLogManager} from "./UndoEditLogManager";
import {Namespaces} from "../core/Namespaces";
import {ComprehendCommonNERTagSet} from "../default/DefaultSchema";
import {EntityAttributesStore} from "./EntityAttributesStore";
import {RangeMap} from "../core/RangeMap";
import {LayerAttributes, LayerIndex} from "../span/Constants";
import {SpanLayerPlanner} from "./SpanLayerPlanner";
import {Attributes} from "../core/Spans";
import { regexIndexOf } from "../utils/Utils";

const TaggingActions = {
    Insert: 1,
    Remove: 2,
    Update: 3,
    BatchInsert: 4,
};

const UNASSIGNED_TAG = "UNASSIGNED";
const ATTEMPT_REMOVE_LOCKED_SPAN = "ATTEMPT_REMOVE_LOCKED_SPAN";

const TaggingStoreUndoMangerFactory = (store) => {
    const manager = new UndoEditLogManager();
    manager.registerUndoAction(TaggingActions.Insert, (context) => {
        const {span} = context;
        store.removeSpan(span.start, span.end, false);
    });
    manager.registerUndoAction(TaggingActions.Remove, (context) => {
        const {span} = context;
        store.insertTagSpan(span.start, span.end, span.tag, false);
    });
    manager.registerUndoAction(TaggingActions.Update, (context) => {
        const {span} = context;
        store.removeSpan(span.start, span.end, false);
        store.insertTagSpan(span.start, span.end, span.tag, false);
    });
    manager.registerUndoAction(TaggingActions.BatchInsert, (context) => {
        const {inserts} = context;
        inserts.forEach((span) => {
            const {start, end} = span;
            store.removeSpan(start, end, false);
        })
    });
    return manager;
};

class TaggingStateStore extends DocumentStateStore {
    constructor(documentObject,
                namespace = Namespaces.NER,
                tagSet = ComprehendCommonNERTagSet,
                enableRateLimit = false,
                attributesToEntityConfig = undefined,
                lockedSpanSet = undefined,
                referenceAnnotations = undefined,
                propertyDataStore = undefined,
                labelingTask = LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER) {
        super(documentObject, labelingTask);
        this.schemaKey = namespace;
        this.tagGroupStore = new TagGroupStore(tagSet);
        this.entityAttributesStore = new EntityAttributesStore(attributesToEntityConfig);
        this.propertyDataStore = propertyDataStore; // of type PropertyDataStore
        if (enableRateLimit) {
            this.rateLimiter = DefaultRateLimiter();
        } else {
            console.log("zeroRateLimiter is used");
            this.rateLimiter = ZeroRateLimiter();
        }
        this.undoManager = TaggingStoreUndoMangerFactory(this);
        this.lockedSpanSet = lockedSpanSet;
        if (this.lockedSpanSet === undefined) {
            // spans in this map cannot be removed or updated
            this.lockedSpanSet = new RangeMap();
        }
        // reference annotations are static annotations
        // that are not editable during the course. But could be useful for
        // checking against during the annotation process
        this.referenceAnnotations = referenceAnnotations;
        if (this.referenceAnnotations === undefined) {
            // reference annotations are assumed to be list of Span-like objects
            // based on the same raw text
            this.referenceAnnotations = [];
        }
        this.showOverlay = false;
        this._overrideReadOnly = false;
        this.removeSpan = this.registerAction(this.removeSpan.bind(this));
        this.insertTagSpan = this.registerAction(this.insertTagSpan.bind(this));
        this.updateActiveTag = this.registerAction(this.updateActiveTag.bind(this));
        this.updateActiveSpanAttributes = this.registerAction(this.updateActiveSpanAttributes.bind(this));
        this.batchInsertActiveSpan = this.registerAction(this.batchInsertActiveSpan.bind(this));
        this.toggleSpanSelection = this.registerViewAction(this.toggleSpanSelection.bind(this));
        this.setActiveSpan = this.registerViewAction(this.setActiveSpan.bind(this));
        this.getActiveSpanAttributes = this.getActiveSpanAttributes.bind(this);
        this.getActiveSpanProperties = this.getActiveSpanProperties.bind(this);
        this.insertSpan = this.insertSpan.bind(this);
        this.checkSpanLocked = this.checkSpanLocked.bind(this);
        this.toggleOverlay = this.registerViewAction(this.toggleOverlay.bind(this));
        this.updateTaggingSpanProperty = this.registerAction(this.updateTaggingSpanProperty.bind(this));
        this.removeTaggingSpanProperty = this.registerAction(this.removeTaggingSpanProperty.bind(this));
        // only needs to be observed
        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject) {
        console.log("triggered initStatesFromDocument");
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        let annotations = documentObject[UFSchema.ANNOTATIONS][this.schemaKey];
        if (!annotations) {
            annotations = [];
        }
        this.states.rawText = rawText;
        this.states.spanFactory = new SpanFactory(rawText);
        const runes = this.splitter.splitGraphemes(documentObject[UFSchema.RAW_TEXT]);
        let spanObjects = annotations.map((s) => {
            const span = this.states.spanFactory.copySpan(s);
            super.expandSpanToHaveMultibyteOffset(span, runes);
            return span
        });
        // now registered properties if provided
        if (this.propertyDataStore !== undefined) {
            annotations.forEach((s) => {
                const {start, end, tag, properties} = s;
                if (properties !== undefined) {
                    // properties exist;
                    // create instance store
                    const instanceStore = this.propertyDataStore.getInstanceStore({
                        start: start,
                        end: end,
                        tag: tag
                    });
                    // restore properties
                    if (instanceStore !== undefined) {
                        instanceStore.load(properties);
                    }
                }
            });
        }
        this.states.activeSpan = undefined;

        this.states.spanTree = RangeTree.createRangeTree(spanObjects);

        // create spanTree for v2 spans
        let v2SpanObjects = this.referenceAnnotations.map((s2) => {
            return this.states.spanFactory.copySpan(s2);
        });
        this.states.v2SpanTree = RangeTree.createRangeTree(v2SpanObjects);
    }

    isReadOnly() {
        console.log("Read Only? " + super.isReadOnly());
        const superReadOnly = super.isReadOnly();

        return this._overrideReadOnly || superReadOnly;
    }

    overrideReadOnly(readOnly) {
        this._overrideReadOnly = readOnly;
    }

    get spans() {
        const tree = this.states.spanTree;
        const rawText = this.states.rawText;
        let ranges = RangeTree.retrieveAllNonOverlappingRanges(tree, rawText.length);
        let spans = [];
        ranges.forEach(r => {
            let [start, end, value] = r;
            let tag = undefined;
            let attrs = undefined;
            if (value) {
                tag = value.tag;
                attrs = value.attrs;
            }
            spans.push(this.states.spanFactory.createSpan(start, end, tag, attrs));
        });
        return spans;
    }

    get overlaySpans() {
        // get blankSpan
        const blankSpanCreator = (start, end) => {
            let spanObj = this.states.spanFactory.createSpan(start, end);
            // default layer for blank spans
            spanObj.setAttribute(LayerAttributes.Layer, LayerIndex.BACKGROUND);
            return spanObj;
        };

        const addLayerToSpan = (layer) => {
            return (e) => {
                let [_, __, spanObject] = e;
                if (!spanObject.getAttribute(Attributes.BLANK)) {
                    // not a blank span, add layer
                    spanObject.setAttribute(LayerAttributes.Layer, layer);
                }
                return spanObject;
            };
        };

        const v1Spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.spanTree,
            this.states.rawText.length, blankSpanCreator).map(addLayerToSpan(LayerIndex.FRONT));
        const v2Spans = RangeTree.retrieveAllNonOverlappingRanges(this.states.v2SpanTree,
            this.states.rawText.length, blankSpanCreator).map(addLayerToSpan(LayerIndex.MIDDLE));
        // flatten and fragment overlapping spans into individual spans
        return new SpanLayerPlanner().doFragment(v1Spans, v2Spans);

    }

    convertStatesToAnnotations() {
        let newAnnotations = [];
        RangeTree.retrieveAllInsertedRanges(this.states.spanTree).forEach((item) => {
            let [_, __, span] = item;
            // not blank and not unassigned
            if (!span.attrs.blank && span.tag !== UNASSIGNED_TAG) {
                // do don't export attributes
                let spanAnnotation = span.exportAnnotation(true,
                    this.entityAttributesStore.allAttributes);
                if (this.propertyDataStore !== undefined) {
                    const {start, end, tag} = spanAnnotation;
                    const properties = this.propertyDataStore.getSpanPropertyAnnotations(
                        {
                            start: start,
                            end: end,
                            tag: tag
                        }
                    );
                    if (properties !== undefined) {
                        // only when there is valid properties annotated
                        spanAnnotation["properties"] = properties;
                    }
                }
                newAnnotations.push(spanAnnotation);
            }
        });
        let annotationObject = {};
        annotationObject[this.schemaKey] = newAnnotations;
        return annotationObject;
    }


    static doRemoveSpan(store, start, end, track) {
        if (store.states.spanTree.find([start, end]).valid) {
            // the span is find
            if (!store.rateLimiter.accept({start: start, end: end})) {
                // the removal comes too early, ignore
                return;
            }
            const spanFactory = store.states.spanFactory;
            // save the span for undo operation
            const oldSpan = store.states.spanTree.get([start, end]);
            if (track) {
                // only when initiated by user we do the append
                // other wise this will run in a circle

                store.undoManager.recordEdit(TaggingActions.Remove,
                    {span: spanFactory.copySpan(oldSpan)});
            }
            // do the actual remove
            store.states.spanTree = store.states.spanTree.remove([start, end]);
            return oldSpan;
        }
    }

    static doInsertTagSpan(store, start, end, tag, track) {
        if (!store.states.spanTree.find([start, end]).valid) {
            // only if not already inserted
            // console.log("update", start, end, tag);
            let newSpan = store.states.spanFactory.createSpan(start, end, tag);
            const spanFactory = store.states.spanFactory;
            if (track) {
                // only when initiated by user we do the append
                // other wise this will run in a circle
                store.undoManager.recordEdit(TaggingActions.Insert,
                    {span: spanFactory.copySpan(newSpan)});
            }
            store.states.spanTree = store.states.spanTree.insert([start, end], newSpan);
            // record the insertion time
            store.rateLimiter.record({start: start, end: end});
            // console.log("confirm inserted ", store.states.spanTree.find([start, end]).valid, newSpan);
            return newSpan;
        }
    }

    static doUpdateActiveTag(store, tag) {
        store.tagGroupStore.updateSelectTag(tag);
        if (store.states.activeSpan) {
            // update the selected span tag
            const {start, end} = store.states.activeSpan;
            // first remove it then insert it
            // here we don't track it. instead with separate undo
            const oldSpan = store.removeSpan(start, end, false);
            const spanFactory = store.states.spanFactory;
            // once you update the tag, the previous properties no longer holds
            this.doRemoveSpanProperty(store, oldSpan);
            // TODO: [Yuchen] do we want to track property editing history as well?
            store.undoManager.recordEdit(TaggingActions.Update,
                {span: spanFactory.copySpan(oldSpan)});
            const newSpan = store.insertTagSpan(start, end, tag, false);
            // reset the activeSpan
            store.states.activeSpan = undefined;
            return newSpan;
        }
    }

    static doToggleSpanSelection(store, span) {
        const {start, end} = span;
        if (!store.rateLimiter.accept({start: start, end: end})) {
            // the removal comes too early, ignore
            return;
        }
        const activeSpan = store.states.activeSpan;
        // console.log("activeSpan -> ", activeSpan);
        if (!activeSpan) {
            // no activeSpan
            store.states.activeSpan = span;
            return;
        }
        if (activeSpan) {
            if (activeSpan.rangeEqual(span)) {
                // user clicked on the same span
                // treat this as deselect
                store.states.activeSpan = undefined;
            } else {
                // different span
                store.states.activeSpan = span;
            }
        }
    }

    static doRemoveActiveSpan(store) {
        // removeSpan is a more general operation
        // which could be implicitly triggered by other actions
        // removeActiveSpan is the only entrance for user
        // to EXPLICITLY remove a span
        const activeSpan = store.states.activeSpan;
        if (activeSpan) {
            store.removeSpan(activeSpan.start, activeSpan.end);
            // NOTED, we don't have to call removeSpanProperty here
            // because it will be taken care by removeSpan method itself
            store.states.activeSpan = undefined;
        }
    }

    static doRemoveSpanProperty(store, span) {
        if (store.propertyDataStore !== undefined) {
            const {start, end, tag} = span;
            store.propertyDataStore.removeSpanProperty({
                start: start,
                end: end,
                tag: tag // the actual remove logic doesn't care tag, but this is hack, shrug
            })
        }
    }

    /**
     * This operation will find other instances of the same text as the selected active span,
     * and tagging other such spans with the same tag as the current one.
     * @param {*} track boolean to decide whether to track the action in the UndoManager
     * @param {*} token boolean to decide whether to find tokens only (whole words), default to false
     */
    batchInsertActiveSpan(track, token = false) {
        const activeSpan = this.states.activeSpan;
        if (activeSpan) {
            const {extent: searchText, tag: spanTag} = activeSpan;
            const spans = this.spans;
            let toInserts = [];
            spans.forEach((span) => {
                if (span.attrs.blank) {
                    // only search in blank span
                    const {start: blankStart, extent: blankText} = span;
                    let startIdx = 0;
                    while (startIdx < blankText.length) {
                        let foundIdx = token ?
                            regexIndexOf(blankText, new RegExp('\\b' + searchText + '\\b', "i"), startIdx) :
                            blankText.toLowerCase().indexOf(searchText.toLowerCase(), startIdx);
                        if (foundIdx >= 0) {
                            // Noted, the newStart/End are with respect
                            // to its positions in the global text, not just
                            // within the current blank span
                            const newStart = foundIdx + blankStart;
                            const newEnd = newStart + searchText.length;
                            toInserts.push({
                                start: newStart,
                                end: newEnd
                            });
                            // next position starts after the current found one
                            startIdx = foundIdx + searchText.length;
                        } else {
                            // after this indices, there is instance left
                            break;
                        }
                    }
                }
            });
            // now insert all such occurrences
            toInserts.forEach((span) => {
                const {start, end} = span;
                this.insertTagSpan(start, end, spanTag, false);
            });
            if (track && toInserts.length > 0) {
                // now record the action, only record as undo
                // when there is actual inserts
                this.undoManager.recordEdit(TaggingActions.BatchInsert, {
                    inserts: toInserts
                });
            }
        }
        // noted that the activeSpan has been reset
        // set it back to original span
        this.states.activeSpan = activeSpan;
    }

    static doUpdateSelectSpanAttributes(store, selectedSpan, attributes) {
        // update active span's attributes
        if (selectedSpan) {
            const filteredAttributes = {};
            if (attributes) {
                Object.keys(attributes).forEach((k) => {
                    if (attributes[k] !== undefined) {
                        // only pass in filtered non undefined
                        // attributes
                        filteredAttributes[k] = attributes[k];
                    }
                });
            }
            selectedSpan.setGroupAttributes(filteredAttributes);
        }
    }

    insertTagSpan(start, end, tag, track = true) {
        const newSpan = TaggingStateStore.doInsertTagSpan(this, start, end, tag, track);
        this.states.activeSpan = undefined;
        return newSpan;
    }

    checkSpanLocked(span) {
        if (this.lockedSpanSet.has(span)) {
            // span locked can't be removed
            // send alert to downstream listeners
            this.sendCustomNotification(ATTEMPT_REMOVE_LOCKED_SPAN,
                this.lockedSpanSet.get(span));

            return true;
        }
        return false;
    }

    removeSpan(start, end, track = true) {
        const span = {
            start: start,
            end: end
        };
        if (this.checkSpanLocked(span)) {
            // abort
            return;
        }
        const oldSpan = TaggingStateStore.doRemoveSpan(this, start, end, track);
        TaggingStateStore.doRemoveSpanProperty(this, oldSpan);
        return oldSpan;
    }

    updateActiveTag(tag) {
        if (this.states.activeSpan && this.checkSpanLocked(this.states.activeSpan)) {
            // abort
            return;
        }
        return TaggingStateStore.doUpdateActiveTag(this, tag);
    }

    insertSpan(start, end) {
        const newSpan = this.insertTagSpan(start, end, UNASSIGNED_TAG, true);
        this.tagGroupStore.markAsNoTagSelected();
        this.states.activeSpan = newSpan;
        return newSpan;
    }

    toggleSpanSelection(span) {
        return TaggingStateStore.doToggleSpanSelection(this, span);
    }

    setActiveSpan(span) {
        this.states.activeSpan = span;
    }

    removeActiveSpan() {
        return TaggingStateStore.doRemoveActiveSpan(this);
    }

    updateActiveSpanAttributes(attributes) {
        return TaggingStateStore.doUpdateSelectSpanAttributes(this,
            this.states.activeSpan, attributes);
    }

    getActiveSpanAttributes() {
        const activeSpan = this.states.activeSpan;
        if (activeSpan) {
            return this.entityAttributesStore.getSpanEntityAttributes(activeSpan);
        }
    }

    getActiveSpanProperties() {
        const activeSpan = this.states.activeSpan;
        if (activeSpan) {
            if (this.propertyDataStore !== undefined) {
                const {start, end, tag} = activeSpan;
                return this.propertyDataStore.retrieveSpanProperty({
                    start: start,
                    end: end,
                    tag: tag
                })
            }
        }
        // no properties
        return [];
    }

    updateTaggingSpanProperty(span, properties) {
        // Bad, pass-through, but need to be registered as stateChange handler
        if (this.propertyDataStore !== undefined) {
            this.propertyDataStore.updateSpanProperty(span, properties);
        }
    }

    removeTaggingSpanProperty(span) {
        // Bad, pass-through, but need to be registered as stateChange handler
        if (this.propertyDataStore !== undefined) {
            this.propertyDataStore.removeSpanProperty(span);
        }
    }

    get referenceAttached() {
        return this.referenceAnnotations !== undefined && this.referenceAnnotations.length > 0
    }

    toggleOverlay() {
        this.showOverlay = !this.showOverlay;
    }

    checkPropertyIntegrity() {
        const violations = [];
        if (this.propertyDataStore !== undefined) {
            // verify that no required properties are missing
            const nonBlankSpans = this.spans.filter((s) => !s.attrs.blank);
            nonBlankSpans.forEach((s1) => {
                const instance = this.propertyDataStore.getInstanceStore(s1);
                if (instance !== undefined) {
                    // this tag/entity have property associated with it
                    const instanceViolations = instance.checkIntegrity();
                    // associate the cause to individual target
                    instanceViolations.forEach((iv) => {
                        violations.push({
                            target: s1.exportAnnotation(false),
                            cause: iv.cause
                        })
                    });
                }
            });
            // now check document level property violations
            const docInstance = this.propertyDataStore.getDocumentInstanceStore();
            if (docInstance !== undefined) {
                const docViolations = docInstance.checkIntegrity();
                docViolations.forEach((dv) => {
                    violations.push({
                        target: "document",
                        cause: dv.cause
                    })
                })
            }
        }
        return violations;
    }

    setSsieProps(ssieProps) {
        this.tagGroupStore.setSsieProps(ssieProps);
    }
}

export {
    TaggingStateStore, UNASSIGNED_TAG,
    ATTEMPT_REMOVE_LOCKED_SPAN,
    TaggingStoreUndoMangerFactory
}