import {action, untracked} from 'mobx'
import RangeTree from "annotator/core/RangeTree";
import {DocumentStateStore} from "./DocumentStateStore";
import {SentimentTagToIdx, SentimentTagSet} from "../sentiment/SentimentTagConfig";
import {UFSchema} from "annotator/core/UFSchema";
import {SpanFactory} from "annotator/core/Spans";
import {TagGroupStore} from "./TagGroupStore";

// should be labeled, but haven't
const UNLABELED = "UNLABELED";
const SentimentNamespace = "sentiment";

const SentimentAttributes = {
    Sarcastic: "sarcastic"
};

class SentimentStateStore extends DocumentStateStore {
    constructor(documentObject) {
        super(documentObject);
        this.schemaKey = SentimentNamespace;
        this.tagGroupStore = new TagGroupStore(SentimentTagSet);
        // fill in the annotated sentiments if any
        this.updateSentenceTag = this.registerAction(this.updateSentenceTag.bind(this));
        this.setActiveSpan = this.registerAction(this.setActiveSpan.bind(this));
        this.updateDocumentLabel = this.registerAction(this.updateDocumentLabel.bind(this));
        this.isAnnotationComplete = this.isAnnotationComplete.bind(this);

        this.initStatesFromDocument(documentObject);
    };

    initStatesFromDocument(documentObject) {
        super.initStatesFromDocument(documentObject);
        const rawText = documentObject[UFSchema.RAW_TEXT];
        const spanFactory = new SpanFactory(rawText);
        this.states.rawText = rawText;
        this.states.spanFactory = spanFactory;
        // dealing with sentence segments
        let segments;
        try {
            // HACK for fast deep copy
            // credit to https://stackoverflow.com/a/4591639
            segments = JSON.parse(
                JSON.stringify(documentObject["preprocessing"]["segments"]["sentences"]));
        } catch (e) {
            console.log("Error extracting sentence segments from" +
                " document[\"preprocessing\"][\"segments\"][\"sentences\"] field. Document JSON: " +
                JSON.stringify(documentObject));
            // no preprocessing segments, treat whole document as a single
            // segment
            segments = [{start: 0, end: rawText.length}];
        }

        // TODO: call super.expandSpanToHaveMultibyteOffset for these spans
        let segmentSpans = segments.map((s) => {
            const {start, end} = s;
            let span = spanFactory.createSpan(start, end, UNLABELED);
            span.setAttribute(SentimentAttributes.Sarcastic, false);
            return span;
        });
        let spanTree = RangeTree.createRangeTree(segmentSpans);
        // dealing with existing annotations
        let annotations = documentObject[UFSchema.ANNOTATIONS][this.schemaKey];
        let sentencesSentiment = [];
        let documentSentiment = undefined;
        if (annotations) {
            sentencesSentiment = annotations["sentences"];
            documentSentiment = annotations["document"];
        }
        console.log("sentencesSentiment");
        console.log(sentencesSentiment);
        sentencesSentiment.forEach((s) => {
            const {start, end} = s;
            if (spanTree.find([start, end]).valid) {
                // segments has a label
                // remove the placeholder span
                spanTree = spanTree.remove([start, end]);
            }
            let span = spanFactory.createSpan(start, end, s.tag);
            let sarcastic = s.sarcastic ? s.sarcastic : false;
            span.setAttribute(SentimentAttributes.Sarcastic, sarcastic);
            spanTree = spanTree.insert([start, end], span);
        });
        this.states.spanTree = spanTree;
        // by default no span is active
        this.states.activeSpan = -1;
        // We need to init tagGroupStore a little differently here
        // since the selectedTag is observable in TagGroupStore
        // if the store is initialized in an observer, say, component
        // it will causing unexpected observing chain, thus unexpected behavior
        // solution now is wrap around this logic in action wrapper, so the
        // side effects are untracked.

        untracked(() => {
                if (documentSentiment) {
                    this.tagGroupStore.updateSelectTagByIndex(SentimentTagToIdx[documentSentiment]);
                } else {
                    this.tagGroupStore.selectedTag = UNLABELED;
                    this.tagGroupStore.selectedTagIndex = -1;
                }
            }
        )
    }

    get spans() {
        const spanTree = this.states.spanTree;
        const rawText = this.states.rawText;
        const spanFactory = this.states.spanFactory;
        let ranges = RangeTree.retrieveAllNonOverlappingRanges(spanTree, rawText.length);
        let spans = ranges.map(r => {
            let [start, end, value] = r;
            if (value) {
                // inserted span
                return value;
            } else {
                // blank span
                return spanFactory.createSpan(start, end)
            }
        });
        return spans;
    }

    convertStatesToAnnotations() {
        let newAnnotations = [];
        this.spans.forEach((s) => {
            if (!s.attrs.blank) {
                const {start, end, tag, attrs} = s;
                const segmentAnnotation = {
                    "start": start,
                    "end": end,
                    "tag": tag,
                    "sarcastic": attrs.sarcastic
                };
                newAnnotations.push(segmentAnnotation);
            }
        });
        let annotations = {"sentiment": {"sentences": newAnnotations}};
        annotations["sentiment"]["document"] = this.tagGroupStore.selectedTag;
        return annotations;
    }


    updateSentenceTag(start, end, tag, sarcastic) {
        if (this.states.spanTree.find([start, end]).valid) {
            // find the span
            const newSpan = this.states.spanFactory.createSpan(start, end, tag);
            newSpan.setAttribute(SentimentAttributes.Sarcastic, sarcastic);
            this.states.spanTree = this.states.spanTree.remove([start, end]);
            this.states.spanTree = this.states.spanTree.insert([start, end], newSpan);
            console.log("in updateSentenceTag");
            return this.convertStatesToAnnotations()
        }
    }

    setActiveSpan(spanContainerIdx) {
        console.log("setActiveSpan", spanContainerIdx);
        this.states.activeSpan = spanContainerIdx;
        console.log(this.states.activeSpan);
    }

    updateDocumentLabel(tag) {
        this.tagGroupStore.updateSelectTag(tag);
    }

    isAnnotationComplete() {
        const annotations = this.convertStatesToAnnotations();
        // check whether annotations for a document is finished or not
        return SentimentStateStore.checkAnnotationsCompleteStatus(annotations);
    }

    static checkAnnotationsCompleteStatus(annotations) {
        // check whether annotations for a document is finished or not
        let documentSentiment = undefined;
        try {
            documentSentiment = annotations["sentiment"]["document"];
        } catch (e) {
            return false;
        }

        if (!documentSentiment || documentSentiment === "UNLABELED") {
            return false;
        }
        let sentenceComplete = true;
        const sentences = annotations["sentiment"]["sentences"];
        if (!sentences || sentences.length === 0) {
            sentenceComplete = false;
        }
        if (sentences) {
            sentences.forEach((s) => {
                if (s["tag"] === "UNLABELED") {
                    sentenceComplete = false;
                }
            })
        }
        return sentenceComplete;
    }

}

export {SentimentStateStore};