import React, { useMemo, useCallback } from 'react';
import { observer } from 'mobx-react-lite';
import {ContextMenuSelector} from "annotator/menu/ContextMenuSelector";
import uuid from 'uuid';

const CONTEXT_MENU_ADD_TO_NEW_GROUP = 'ADD_TO_NEW_GROUP';
const CONTEXT_MENU_REMOVE_FROM_GROUP = 'REMOVE_FROM_GROUP';
const CONTEXT_MENU_AUTO_TAG = 'AUTO_TAG';

const CoreferenceContextMenu = observer((props) => {
    const { menuId, store, span } = props;
    // eslint-disable-next-line no-unused-vars
    const viewUpdateChange = store.viewUpdateChange; // Required for mobx state updates

    const {
        groupTags,
        groupItems,
        footerTags,
        footerItems
    } = useMemo(() => {
        const groupTags = [];
        const groupItems = [];
        store.groupList.forEach((g) => {
            groupTags.push("G" + g.groupId.toString());
            groupItems.push(g);
        });

        const footerTags = [];
        const footerItems = [];
        if (span && span.attrs.groupId >= 0) {
            // Show option to remove span from its current group
            footerTags.push(<span key={uuid.v4()}>Remove From Group {'"'}G{span.attrs.groupId}{'"'}</span>);
            footerItems.push(CONTEXT_MENU_REMOVE_FROM_GROUP);

            // Show option to auto-tag the span
            footerTags.push(<span key={uuid.v4()}>Auto-tag selected</span>);
            footerItems.push(CONTEXT_MENU_AUTO_TAG);
        }
        footerTags.push(<span key={uuid.v4()}>Add to new Group</span>);
        footerItems.push(CONTEXT_MENU_ADD_TO_NEW_GROUP);
        return {
            groupTags,
            groupItems,
            footerTags,
            footerItems
        };
    }, [store.groupList, span]);

    /**
     * Moves a span to a new group via context menu / right click 
     */
    const contextMenuClickHandler = useCallback((g) => {
        if (!span) {
            return;
        }
        
        if (g === CONTEXT_MENU_REMOVE_FROM_GROUP) {
            // User clicked remove from group action
            // Remove & return
            store.removeMentionFromGroup(span);
            return;
        }
        if (g === CONTEXT_MENU_AUTO_TAG) {
            store.addSimilarSpansToGroup(span); 
            return;
        }
        if (g === CONTEXT_MENU_ADD_TO_NEW_GROUP) {
            // Add span to a new group
            store.removeMentionFromGroup(span);
            const newGroup = store.createGroup();
            store.assignMentionToGroup(span.start,span.end,newGroup.groupId);
            return;
        }
        
        store.removeMentionFromGroup(span);
        store.assignMentionToGroup(span.start,span.end,g.groupId);
    }, [store, span]);

    return (<ContextMenuSelector 
        menuId={menuId}
        numItem={4}
        tags={groupTags}
        items={groupItems}
        footerTags={footerTags}
        footerItems={footerItems}
        clickHandler={contextMenuClickHandler}
    />);
});

export {CoreferenceContextMenu};