import React from 'react';
import { Button, Dropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap';
import { ConnectTagColor } from '../../../../../../AnnotatorCommons';
import { UNASSIGNED_TAG } from '../../../../../../store/TaggingStateStore';
import { Annotation, getEntityConnectName, SsieAnnotationStore } from '../../../../context/SsieAnnotationStore';
import { StringMap } from '../../../../types/StringMap';
import './SelectedEntity.css';

interface Props {
    fullText: string;
    onUpdateAnnotationText: (textOffsets: [number, number]) => void;
    tag: string;
    onCopyClick: () => void;
    onRemoveClick: () => void;
    onSelectClick: (overlap?: boolean) => void;
    relationIndex: number;
    textOffsets: [number, number];
}

const SelectedEntity: React.FC<Props> = ({
    textOffsets,
    fullText,
    onUpdateAnnotationText,
    tag,
    onCopyClick,
    onSelectClick,
    relationIndex,
    onRemoveClick,
}) => {
    const [dropdownIsOpen, setDropdownIsOpen] = React.useState<boolean>(false);

    const { setSsieAnnotationStore, ssieAnnotations } = React.useContext(SsieAnnotationStore);

    const handleDropdownToggle = React.useCallback(() => {
        setDropdownIsOpen(!dropdownIsOpen);
    }, [dropdownIsOpen]);

    const handleConnectionClick = React.useCallback(
        (e: React.MouseEvent<any, MouseEvent>) => {
            e.stopPropagation();

            const annotationMap: StringMap<Annotation> = {};
            for (const anno of ssieAnnotations.annotations) {
                annotationMap[anno.id] = anno;
            }
            const newSelectedSsieAnnotations = ssieAnnotations.relations[relationIndex].map((annoId) => annotationMap[annoId]);
            setSsieAnnotationStore({
                selectedSsieAnnotations: newSelectedSsieAnnotations,
            });
        },
        [setSsieAnnotationStore, ssieAnnotations, relationIndex]
    );

    const handleStartOffsetChange = React.useCallback(
        (e: any) => {
            onUpdateAnnotationText([e.target.value, textOffsets[1]]);
        },
        [textOffsets, onUpdateAnnotationText]
    );

    const handleEndOffsetChange = React.useCallback(
        (e: any) => {
            onUpdateAnnotationText([textOffsets[0], e.target.value]);
        },
        [textOffsets, onUpdateAnnotationText]
    );

    const handleResetTextOffsets = React.useCallback(() => {
        onUpdateAnnotationText([0, fullText.length]);
    }, [onUpdateAnnotationText, fullText]);

    return (
        <div className={`d-flex selected-entity__container`}>
            <div className="selected-entity__full-text">
                <Dropdown isOpen={dropdownIsOpen} toggle={handleDropdownToggle} className="selected-entity__dropdown-container">
                    <DropdownToggle className="selected-entity__dropdown-caret" title="Options">
                        &#9660;
                    </DropdownToggle>
                    <DropdownMenu>
                        <DropdownItem onClick={() => onSelectClick()} title="Select span">
                            SELECT SINGLE
                        </DropdownItem>
                        <DropdownItem onClick={() => onSelectClick(true)} title="Select span and all overlapping spans">
                            SELECT OVERLAPPING
                        </DropdownItem>
                        <DropdownItem onClick={onCopyClick} title="Copy span">
                            COPY
                        </DropdownItem>
                        <DropdownItem
                            onClick={onRemoveClick}
                            title="Remove span (related connection will be removed if it exists)"
                            style={{ color: 'red' }}
                        >
                            REMOVE
                        </DropdownItem>
                    </DropdownMenu>
                </Dropdown>
                <span
                    className={`selected-entity__tag ${tag === UNASSIGNED_TAG ? 'selected-entity__unassigned-tag' : ''}`}
                    onClick={() => onSelectClick()}
                    title="Select span"
                >
                    {tag}
                </span>
                {relationIndex > -1 && (
                    <span
                        className="selected-entity__connection"
                        onClick={handleConnectionClick}
                        style={{ backgroundColor: ConnectTagColor }}
                        title="Select all spans in connection"
                    >
                        {getEntityConnectName(relationIndex)}
                    </span>
                )}
                <span className="selected-entity__unselected-text">{fullText.substring(0, textOffsets[0])}</span>
                <span className="selected-entity__selected-text">{fullText.substring(textOffsets[0], textOffsets[1])}</span>
                <span className="selected-entity__unselected-text">{fullText.substring(textOffsets[1])}</span>
            </div>
            <div className="selected-entity__offset-inputs" title="Edit annotation text selection">
                <input
                    className="selected-entity__start-offset-input"
                    placeholder="Start offset"
                    value={textOffsets[0]}
                    min={0}
                    max={textOffsets[1] - 1}
                    type="number"
                    step="1"
                    onChange={handleStartOffsetChange}
                />
                <input
                    className="selected-entity__end-offset-input"
                    placeholder="End offset"
                    value={textOffsets[1]}
                    min={textOffsets[0] + 1}
                    max={fullText.length}
                    type="number"
                    step="1"
                    onChange={handleEndOffsetChange}
                />
                <Button
                    className="selected-entity__reset"
                    size="sm"
                    onClick={handleResetTextOffsets}
                    disabled={textOffsets[0] === 0 && textOffsets[1] === fullText.length}
                >
                    RESET
                </Button>
            </div>
        </div>
    );
};

export default SelectedEntity;
