import React, { useState } from 'react';
import {MultiGrid, Index} from 'react-virtualized'

import {DatabaseCandidateRecordType} from "../types/EntityLinkingTypes";

import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons'
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';

const COLUMN_WIDTH_INTERVAL_PX = 13;
const COLUMN_WIDTH_LOWER_BOUNDS_PX = 50;
const COLUMN_WIDTH_UPPER_BOUNDS_PX = 360;

type CellProps = {
    columnIndex: number; 
    key: string;
    rowIndex: number;
    style: any;
}

const HeaderCell = styled.div`
    display:flex;
    align-items:center;
    justify-content:center;
    font-weight:700;
    border-bottom:1px solid #ccc;
`;

type BodyCellProps = {
    primary?: boolean;
}
const BodyCell = styled.div`
    display:flex;
    align-items:center;
    justify-content:center;
    border-bottom:1px solid #ccc;
    font-weight: ${(p: BodyCellProps) => p.primary ? 'bold' : 'inherit'}; 
`;

const CellContent = styled.div`
    max-width:100%;
    text-overflow:ellipsis;
    overflow: hidden;
    white-space:nowrap;
    padding-right:1rem;
`;

const SelectLabel = styled.label`
    display:flex;
    width:100%;
    height:100%;
    cursor:pointer;
    align-items:center;
    justify-content:center;
    margin-bottom:0px;
`;

type RowInfoLinkProps = {
    selected: boolean;
}
const RowInfoLink = styled.a`
    color: ${ (p:RowInfoLinkProps) => p.selected ? '#007bff' : '#aaa'};
    display: flex;
    align-items:center;
    justify-content:center;
    height:100%;
    padding: 0 0.75em 0 1em;
    margin-right: 0.25em;
    &:hover {
        color: #7a7a7a;
    }
`;

const WideModal = styled(Modal)`
    max-width: 80%;
    pre {
        white-space: pre-wrap;
    }
`;

const calculateColumnWidthFromTextLength = (textLength: number) => {
    return Math.max(Math.min(textLength * COLUMN_WIDTH_INTERVAL_PX, COLUMN_WIDTH_UPPER_BOUNDS_PX), COLUMN_WIDTH_LOWER_BOUNDS_PX);
};


const DatabaseCandidateGrid = (props: {
    columns: string[];
    rows: DatabaseCandidateRecordType[];
    height: number;
    width: number;
    columnWidth: number;
    rowHeight: number;
    selectedRecordId: string | undefined;
    onSelectRecord: (recordId: string | undefined) => void;
}) => {
    const {
        columns,
        rows,
        height,
        width,
        onSelectRecord,
        selectedRecordId
    } = props;

    const [infoModalRecord, setInfoModalRecord] = useState<undefined | DatabaseCandidateRecordType>(undefined);

    const columnCount = columns.length;

    const cellRenderer = ({columnIndex, key, rowIndex, style}: CellProps) => {
        if (columnIndex === 0 && rowIndex === 0 ) {
            // Top left header cell (checkbox header)
            return (
                <HeaderCell key={key} style={style}></HeaderCell>
            );
        } else if (columnIndex === 0) {
            // Left column
            const actualRowIndex = rowIndex - 1;
            const recordId = rows[actualRowIndex].id;
            return (
                <BodyCell key={key} style={style}>
                    <RowInfoLink 
                        title="Record info"
                        href=""
                        selected={infoModalRecord !== undefined && infoModalRecord.id === recordId}
                        onClick={(e) => {
                            e.preventDefault();
                            setInfoModalRecord(rows[actualRowIndex]);
                        }}>
                            <FontAwesomeIcon icon={faInfoCircle} />
                    </RowInfoLink>
                    <SelectLabel title="Select this record">
                        <input 
                            type="checkbox" 
                            name="select-row" 
                            checked={recordId === selectedRecordId} 
                            style={{cursor:'pointer'}} 
                            onChange={ () => {
                                if (recordId === selectedRecordId) {
                                    // Uncheck
                                    onSelectRecord(undefined);
                                } else {
                                    // Check
                                    onSelectRecord(recordId);
                                }
                                
                            }}
                        />
                    </SelectLabel>
                </BodyCell>
            )
        } else if (rowIndex === 0) {
            // Header cells
            const column = columns[columnIndex - 1];
            return (
                <HeaderCell key={key} style={style} title={column}>
                    <CellContent>{column}</CellContent>
                </HeaderCell>
            );
        } else if (rowIndex > 0 && columnIndex === columnCount) {
            // ID Column Body Cells
            const actualRowIndex = rowIndex - 1;
            return (
                <BodyCell key={key} style={style} title={rows[actualRowIndex].id}>
                    <CellContent>{rows[actualRowIndex].id}</CellContent>
                </BodyCell>
            )
        } else {
            // Body cell
            const actualRowIndex = rowIndex - 1;
            const column = columns[columnIndex - 1];
            const primaryCell = columnIndex === 1;
            return (
                <BodyCell key={key} style={style} title={`${rows[actualRowIndex].attributes[column]}`} primary={primaryCell}>
                    <CellContent>{rows[actualRowIndex].attributes[column]}</CellContent>
                </BodyCell>
            )
        }
    }

    const columnWidthCalculator = (columnIndex: Index) => {
        if (columnIndex.index === 0) {
            // Checkbox column
            return 80;
        } 
        if (rows.length === 0) {
            // No rows. set a default header length.
            return 300;
        }
        if (columnIndex.index == (columnCount)) {
            // ID Column
            // Calculate ID column width based on the longest of the first 12 columns
            const textLength = Math.max(...rows.slice(0,12).map(r => r.id.length));
            return calculateColumnWidthFromTextLength(textLength);
        }
        // Calculate the column width based on the longest of the first 12 columns
        const column = columns[columnIndex.index-1];
        const textLength = Math.max(...[column.length, ...rows.slice(0,12).map(r => `${r.attributes[column]}`.length)]);
        return calculateColumnWidthFromTextLength(textLength);
    }

    return (
        <>
            <MultiGrid
                cellRenderer={cellRenderer}
                columnWidth={columnWidthCalculator}
                columnCount={columnCount + 1}
                fixedColumnCount={0}
                fixedRowCount={1}
                height={height}
                rowHeight={40}
                rowCount={rows.length + 1}
                width={width}
            />
            <WideModal 
                isOpen={infoModalRecord !== undefined} 
                toggle={() => setInfoModalRecord(undefined)}
            >
                <ModalHeader toggle={() => setInfoModalRecord(undefined)}>{
                    infoModalRecord !== undefined ? (
                        `Record Info: ID  ${infoModalRecord.id}`
                    ) : null
                }</ModalHeader>
                <ModalBody>
                    <pre>
                    {
                        infoModalRecord !== undefined ? (
                            JSON.stringify(infoModalRecord, null, 2)
                        ) : null
                    }
                    </pre>
                </ModalBody>
            </WideModal>
        </>
        
    )

    
}

export default DatabaseCandidateGrid;