import React, {Component} from 'react';

const LTR = "ltr";
const RTL = "rtl";

class AnnotatorTextArea extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        let direction = LTR;
        let alignment = "left";

        if (this.props.textDirection === RTL) {
            direction = RTL;
        }

        if (this.props.textAlignment === "right") {
            alignment = "right";
        }
        return <div id="AnnotatorTextArea"
                    style={{
                        "direction": direction,
                        "textAlign": alignment
                    }}>
            {this.props.children}
        </div>
    }
}

export {AnnotatorTextArea}