import {RangeMap} from "../core/RangeMap";
import {EVENT_EMPTY, EventInstanceDataStore} from "./EventInstanceDataStore";

class EventArgumentPropertyStore {
    constructor(eventDefinitionMap) {
        this.eventDefinitionMap = eventDefinitionMap;
        this.eventInstanceMap = new RangeMap();
        this.argumentValueTriggerReverseMap = new RangeMap();

        this.createTriggerEventInstance = this.createTriggerEventInstance.bind(this);
        this.removeTriggerEventInstance = this.removeTriggerEventInstance.bind(this);
        this.attachArgumentToTrigger = this.attachArgumentToTrigger.bind(this);
        this.detachArgumentFromTrigger = this.detachArgumentFromTrigger.bind(this);
        this.attachPropertyToTrigger = this.attachPropertyToTrigger.bind(this);
        this.detachPropertyFromTrigger = this.detachPropertyFromTrigger.bind(this);
        this.getInstanceStatus = this.getInstanceStatus.bind(this);
        this.getTriggerEventObject = this.getTriggerEventObject.bind(this);
    }

    attachArgumentToTrigger(triggerSpan, argumentName, argumentSpan, instanceId = 0) {
        if (!triggerSpan || !triggerSpan.tag) {
            // illegal input
            return;
        }
        const eventDefinition = this.eventDefinitionMap.get(triggerSpan.tag);
        if (!eventDefinition) {
            return;
        }
        if (!this.eventInstanceMap.get(triggerSpan)) {
            this.createTriggerEventInstance(triggerSpan);
        }
        let instanceList = this.eventInstanceMap.get(triggerSpan);

        if (instanceList.length <= instanceId) {
            // out of range; abort
            return;
        }

        const eventInstance = instanceList[instanceId];
        eventInstance.addArgumentToInstance(argumentName, argumentSpan);

        // Add trigger to argument value reverse map
        const associatedTriggerAndArgumentNameList = this.argumentValueTriggerReverseMap.get(argumentSpan);
        if (!associatedTriggerAndArgumentNameList) {
            this.argumentValueTriggerReverseMap.add(argumentSpan, [{triggerSpan: triggerSpan, argumentName: argumentName}])
        } else {
            associatedTriggerAndArgumentNameList.push({triggerSpan: triggerSpan, argumentName: argumentName})
        }

    }

    detachArgumentFromTrigger(triggerSpan, argumentName, argumentSpan, instanceId = 0) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (instanceList) {
            if (instanceList.length <= instanceId) {
                // out of range; abort;
                return;
            }
            const eventInstance = instanceList[instanceId];
            eventInstance.removeArgumentFromInstance(argumentName, argumentSpan);

            // Remove trigger from argument value reverse map
            const associatedTriggerAndArgumentNameList = this.argumentValueTriggerReverseMap.get(
                argumentSpan);
            if (associatedTriggerAndArgumentNameList) {
                const updatedAssociatedTriggerAndArgumentNameList = associatedTriggerAndArgumentNameList.filter(
                    function (associatedTriggerAndArgumentName) {
                        // Get All triggers except the current one
                        return (!isTriggerAndArgumentNameMatch(associatedTriggerAndArgumentName, triggerSpan, argumentName));
                    });
                this.argumentValueTriggerReverseMap.add(argumentSpan, updatedAssociatedTriggerAndArgumentNameList)
            }
        }
    }



    attachPropertyToTrigger(triggerSpan, propertyName, propertyValue, instanceId) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (instanceList) {
            if (instanceList.length <= instanceId) {
                // out of range; abort;
                return;
            }
            const eventInstance = instanceList[instanceId];
            eventInstance.addPropertyToInstance(propertyName, propertyValue);
        }
    }

    detachPropertyFromTrigger(triggerSpan, propertyName, instanceId) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (instanceList) {
            if (instanceList.length <= instanceId) {
                // out of range; abort;
                return;
            }
            const eventInstance = instanceList[instanceId];
            eventInstance.removePropertyFromInstance(propertyName);
        }
    }



    createTriggerEventInstance(triggerSpan) {
        const {tag} = triggerSpan;
        // tag is the trigger type here
        const eventDefinition = this.eventDefinitionMap.get(tag);
        if (eventDefinition) {
            if (this.eventInstanceMap.get(triggerSpan) === undefined) {
                this.eventInstanceMap.add(triggerSpan, []);
            }
            const instanceList = this.eventInstanceMap.get(triggerSpan);
            const eventInstance = new EventInstanceDataStore(triggerSpan, eventDefinition);
            instanceList.push(eventInstance);
            return eventInstance;
        }
    }

    removeTriggerEventInstance(triggerSpan, instanceId) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (instanceList === undefined || instanceList.length === 0 ||
            instanceList.length <= instanceId || instanceId < 0) {
            // illegal cases
            return;
        }
        // remove instances inplace
        instanceList.splice(instanceId, 1);
    }

    getTriggerEventObject(triggerSpan) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (instanceList) {
            const exportObject = [];
            instanceList.forEach((instance) => {
                exportObject.push(instance.dump());
            });
            return exportObject;
        }
    }

    getInstanceStatus(triggerSpan, instanceId = 0) {
        const instanceList = this.eventInstanceMap.get(triggerSpan);
        if (!instanceList) {
            // this event doesn't exist in this store
            // probably due to annotated trigger doesn't appear in the schema
            return EVENT_EMPTY;
        }
        if (instanceList.length <= instanceId) {
            // instance doesn't exist
            return EVENT_EMPTY
        }
        const instance = instanceList[instanceId];
        return instance.status;
    }
}

const isTriggerAndArgumentNameMatch = (associatedTriggerAndArgumentName,
    triggerSpan, argumentName) => {
    return (associatedTriggerAndArgumentName.triggerSpan.start
        === triggerSpan.start
        && associatedTriggerAndArgumentName.triggerSpan.end === triggerSpan.end
        && associatedTriggerAndArgumentName.argumentName === argumentName);
};

export {EventArgumentPropertyStore, isTriggerAndArgumentNameMatch}