import React from 'react';
import {observer} from "mobx-react";
import './Pages.css'
import {EditPage} from "./EditPage";
import {Pages} from "editor/store/PageStateStore";
import {AboutPage} from "./AboutPage";
import {ConfigPage} from "./ConfigPage";
import {BackupPage} from "./BackupPage";

const PageRouter = observer(
    class PageRouter extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            // subscribe to page state changes
            const subscribePageStateStore = this.props.store.pageStateStore.pageStateStoreUpdated;
            let page = <EditPage store={this.props.store}/>;
            if (this.props.store.pageStateStore.currentPage === Pages.ABOUT) {
                page = <AboutPage/>
            } else if (this.props.store.pageStateStore.currentPage === Pages.CONFIG) {
                page = <ConfigPage store={this.props.store}/>
            } else if (this.props.store.pageStateStore.currentPage === Pages.BACKUP) {
                page = <BackupPage store={this.props.store}/>
            }
            return <div className="PageContainer" id="AnnotatePage">
                {page}
            </div>
        }
    }
);

export {PageRouter}