import {extendObservable} from 'mobx'
import {Checkpointtable} from "editor/checkpoint/Checkpointtable";
import {CoreferenceAuditor} from "../../annotator/audit/CoreferenceAuditor";
import {RelationAuditor} from "../../annotator/audit/RelationAuditor";
import {ExportVerifier} from "../utils/ExportVerifier";
import {UFSchema} from "../../annotator/core/UFSchema";
import {isEmpty} from 'lodash';


class CollectionStateStore extends Checkpointtable {
    constructor(rootStore, collection) {
        super();
        console.log("create collection");
        this.rootStore = rootStore;
        this.collection = collection;
        this.collectionPath = "";
        extendObservable(this, {
            updated: 0, // a sequence id symbolize the common is updated
            activeDocument: 0
        });
        this.resume = this.resume.bind(this);
        this.getDocument = this.getDocument.bind(this);
        this.getActiveDocument = this.getActiveDocument.bind(this);
        this.setActiveDocument = this.setActiveDocument.bind(this);
        this.updateDocumentState = this.updateDocumentState.bind(this);
        this.updateActiveDocumentState = this.updateActiveDocumentState.bind(this);
        this.updateActiveDocumentMetadata = this.updateActiveDocumentMetadata.bind(this);
        this.convertToJsonPerLine = this.convertToJsonPerLine.bind(this);
    }

    resume(checkpoint) {
        const {documents, path} = checkpoint;
        // load processor could task specific
        let collection = [];
        documents.forEach((d) => {
            if (d["annotations"] === undefined) {
                // if no annotations is field is present
                // initialize on immediately
                d["annotations"] = {}
            }
            const updatedDoc = CollectionStateStore.copyConflictFieldsToMainAnnotationBody(d);
            collection.push(updatedDoc);
        });
        this.collection = collection;
        this.activeDocument = 0;
        if (path) {
            this.collectionPath = path;
        }
        this.updated += 1;
    }

    checkpoint() {
        return {
            documents: this.collection,
            path: this.collectionPath
        }
    }

    getDocument(docIdx) {
        return this.collection[docIdx];
    }

    getActiveDocument() {
        return this.getDocument(this.activeDocument);
    }

    get totalDocuments() {
        return this.collection.length;
    }

    get currentDocumentIdx() {
        return this.activeDocument;
    }

    setActiveDocument(docIdx) {
        if (docIdx >= 0 && docIdx < this.collection.length) {
            this.activeDocument = docIdx;
            this.updated += 1;
            return true;
        } else {
            console.log("invalid docIdx", docIdx);
        }
        return false;
    }

    updateDocumentState(docIdx, docUpdate) {
        let doc = this.collection[docIdx];
        console.log("update document");
        console.log(docUpdate);
        const annotations = doc["annotations"];
        document["annotations"] = Object.assign(annotations, docUpdate);
        console.log(doc);
    };

    updateActiveDocumentState(update) {
        this.updateDocumentState(this.activeDocument, update);
    }

    updateActiveDocumentMetadata(metadataUpdate) {
        let doc = this.getActiveDocument();
        let metadata = doc["metadata"];
        document["metadata"] = Object.assign(metadata, metadataUpdate);
        // force update
        // this.updated += 1;
    }

    convertToJsonPerLine() {
        return CollectionStateStore.doConvertToJsonPerLine(this.collection);
    }

    static doConvertToJsonPerLine(documents) {
        let lines = [];
        // console.log(this.collection);
        const exportVerifier = new ExportVerifier();
        documents.forEach((d) => {
            // do annotations audit here as well
            // so every exported document is guaranteed consistent
            d = CoreferenceAuditor.auditAll(d);
            d = RelationAuditor.auditAll(d);
            d = exportVerifier.filter(d);
            lines.push(JSON.stringify(d));
            lines.push("\n");
        });
        return lines;
    }

    /**
     * Copy over Conflicted fields from annotations_conflict_list to annotations
     * object, if the top-level field is not present at all in the writable
     * annotations object
     *
     * @param document - uf format document
     */
    static copyConflictFieldsToMainAnnotationBody(document) {
        const annotationsObject = document[UFSchema.ANNOTATIONS];
        // initial assumption is only 1 conflicted annotation per document object
        const conflictedAnnotationsObjectList = document[UFSchema.CONFLICTED_ANNOTATIONS_LIST];
        if (isEmpty(conflictedAnnotationsObjectList)) {
            return document;
        }
        const conflictedAnnotationsObject = conflictedAnnotationsObjectList[0];
        Object.keys(conflictedAnnotationsObject).forEach((key) => {
            if (isEmpty(annotationsObject[key])) {
                annotationsObject[key] = conflictedAnnotationsObject[key]
            }
        });
        return document;
    }
}

export {CollectionStateStore}
