import {extendObservable} from "mobx";
import {Checkpointtable} from "../checkpoint/Checkpointtable";

class BaseConfig extends Checkpointtable {
    configUpdated = 0;
    constructor() {
        super();
        console.log('subscribeSchemaConfigUpdate BaseConfig new')
        extendObservable(this, {
            configUpdated: this.configUpdated
        });
        this.selector = new Map();
        this.createConfig = this.createConfig.bind(this);
        this.mutate = this.mutate.bind(this);
    }

    createConfig(key, value) {
        // config are static by default
        // no double create
        if (!this.selector.has(key)) {
            this.selector.set(key, value);
            Object.defineProperty(this, key, {
                get() {
                    return this.selector.get(key);
                },
                set(newValue) {
                    // do nothing;
                }
            })
        }
    }

    mutate(key, value) {
        this.selector.set(key, value);
        this.configUpdated += 1;
    }

    //TODO: Add checkpoint/resume methods to BaseConfig
    checkpoint() {
    }

    resume(checkpoint) {
    }

}

export {BaseConfig}