import React from 'react';
import {
    Button
} from 'reactstrap';
import {observer} from 'mobx-react';
import 'annotator/AnnotatorButton.css'
import 'annotator/Annotator.css'
import {CoreferenceGroupButton} from "./CoreferenceGroupButton";

const CoreferenceGroupPanel = observer(
    class CoreferenceGroupPanel extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            const readOnly = this.props.store.isReadOnly();
            console.log("groups -> ", this.props.groups);
            console.log("selected ->", this.props.selected);
            let buttons = [];
            this.props.groups.forEach((g,index) => {
                let groupId = g.groupId;
                let color = "info";
                const readOnlyProps = !readOnly ? {
                    clickTrigger: () => this.props.store.selectGroup(groupId),
                    crossClickTrigger: () => this.props.store.removeGroup(groupId)
                } : {
                    clickTrigger: () => {},
                    disabled: true
                };
                buttons.push(
                    <CoreferenceGroupButton
                        key={index}
                        groupId={groupId}
                        color={color}
                        mouseOverTrigger={() => {}}
                        mouseLeaveTrigger={() => {}}
                        {...readOnlyProps}
                    >
                    </CoreferenceGroupButton>
                );
            });
            return <div id="CoreferenceGroupPanel">
                {buttons}
                <Button
                    className="TagButton"
                    disabled={readOnly}
                    color="success"
                    size="sm"
                    style={{
                        "position": "relative",
                        "marginBottom": "10px"
                    }} onClick={() => this.props.store.createGroup()}>
                    NEW GROUP
                </Button>
                <Button id="CorefRemoveButton"
                        color="danger"
                        className="TagButton"
                        disabled={readOnly}
                        style={{
                            "position": "relative",
                            "marginBottom": "10px"
                        }}
                        onClick={this.props.store.removeActiveSpanFromGroup}>
                    RM GROUP
                </Button>
                <Button id="CorefRemoveButton"
                        color="danger"
                        className="TagButton"
                        disabled={readOnly}
                        style={{
                            "position": "relative", "marginRight": "10px",
                            "marginBottom": "10px"
                        }}
                        onClick={this.props.store.removeActiveSpan}>
                    RM SPAN
                </Button>
            </div>
        }
    }
);

export {CoreferenceGroupPanel}