import {BaseSpanType} from "./CoreTypes";
import React from "react";

type _BasePropertyType = {
    name: string,
    export_key?: string,
    level: "document" | "tag",
    targets: string[],
    field_type: "KeyValue" | "Text",
    values?: string[],
    options?: { [key: string]: any }
}

export enum PropertyTargetType {
    TextSpan,
    Document,
    Block
}

type _TargetProperties = {
    tags: { [key: string]: _BasePropertyType[] };
    document: _BasePropertyType[];
}

type _PropertyValueType = string[]; // for simplicity for data handling, all values are assume as string arrays

type _PropertyValueContainer = {
    name: string,
    value?: _PropertyValueType;
}

type _PropertyInstance = {
    property: BasePropertyType,
    value: _PropertyValueType,
}


export type BasePropertyType = _BasePropertyType
export type TargetProperties = _TargetProperties
export type PropertyValueType = _PropertyValueType
export type PropertyKeyValueType = _PropertyValueContainer
export type PropertyInstance = _PropertyInstance
