import React from 'react';

import {PropertiesConfig} from "./PropertiesConfig";
import {Dropdown, DropdownItem, DropdownMenu, DropdownToggle} from "reactstrap";
import {RemovableTag} from "./RemovableTag";
import {deepCopy} from "../../annotator/utils/DeepCopy";
import {BasePropertyType} from "../../annotator/types/BasePropertyType";


type PropType = {
    property: BasePropertyType,
    namespace: string
    tagSet: string[],
    propertiesConfig: PropertiesConfig
}

type StateType = {
    dropDownOpen: boolean
}

class TargetsEditComponent extends React.Component<PropType, StateType> {
    state: StateType = {
        dropDownOpen: false
    };

    constructor(props: PropType) {
        super(props);
        // still necessary if the method is passed as callback
        // @ts-ignore
        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState((state: any) => ({
            dropDownOpen: !state.dropDownOpen
        }));
    }

    render() {
        const {property, namespace, tagSet, propertiesConfig} = this.props;
        const tags = property.targets;

        const components: any = [];
        if (property.level === "tag") {
            tags.forEach((t) => {
                const removeTagHandler = () => {
                    const sp = propertiesConfig.copySpanProperty(property);
                    // remove this tag
                    sp.targets = sp.targets.filter((t2) => t2 !== t);
                    propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
                };
                components.push(<RemovableTag text={t}
                                              color="danger"
                                              removeHandler={removeTagHandler}/>)
            });
            // now add dropdown to add new tags if required
        } else {
            // this is a document level property
            components.push(<RemovableTag text="DOCUMENT"
                                          color="secondary"
                                          removeHandler={() => {
                                          }}/>);
        }
        // display existing tags
        // add tags
        const addIndividualTagItems: any = [];
        tagSet.forEach((t2) => {
            addIndividualTagItems.push(<DropdownItem className="TagDropDownItem"
                                                     onClick={() => {
                                                         const sp = propertiesConfig.copySpanProperty(property);
                                                         // change the property level to "tag"
                                                         sp.level = "tag";
                                                         if (sp.targets.indexOf(t2) === -1) {
                                                             // add only when not find
                                                             sp.targets.push(t2);
                                                         }
                                                         propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
                                                     }}>
                {t2}
            </DropdownItem>)
        });

        // has to use inline-block to make this tag cloud block and
        // dropdown display in separate lines.
        return <div style={{"display": "inline-block"}}>
            <div style={{"float": "left"}}>
                {components}
            </div>
            <div>
                <Dropdown isOpen={this.state.dropDownOpen}
                          toggle={this.toggle}>
                    <DropdownToggle caret
                                    color="info"
                                    className="TagDropDown">
                        ATTACH
                    </DropdownToggle>
                    <DropdownMenu>
                        {addIndividualTagItems}
                        <DropdownItem divider/>
                        <DropdownItem
                            onClick={() => {
                                const sp = propertiesConfig.copySpanProperty(property);
                                // change the property level to "tag"
                                sp.level = "tag";
                                sp.targets = deepCopy(tagSet);
                                propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
                            }}>
                            <b>ALL TAGS</b>
                        </DropdownItem>
                        <DropdownItem divider/>
                        <DropdownItem
                            onClick={() => {
                                const sp = propertiesConfig.copySpanProperty(property);
                                sp.targets = []; // document level property has no targets
                                sp.level = "document";
                                propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
                            }}>
                            <b>DOCUMENT</b>
                        </DropdownItem>

                    </DropdownMenu>
                </Dropdown>
            </div>
        </div>
    }
}

export {TargetsEditComponent}