import React from 'react';
import {observer} from "mobx-react";
import {AnnotatorRouter} from "editor/annotation/AnnotatorRouter";
import './Pages.css'

const EditPage = observer(
    class EditPage extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            return <div id="AnnotatePage">
                <AnnotatorRouter store={this.props.store}/>
            </div>
        }
    });

export {EditPage}