import React from 'react';
import {PropertiesConfig} from "./PropertiesConfig";
import {Table} from "reactstrap";
import {BasePropertyType} from "../../annotator/types/BasePropertyType";


type PropType = {
    property: BasePropertyType,
    namespace: string
    propertiesConfig: PropertiesConfig
}


class OptionsEditComponent extends React.Component<PropType> {
    constructor(props: PropType) {
        super(props);
    }

    render() {
        const {property, namespace, propertiesConfig} = this.props;
        const options = property.options!;

        const optionCheckBoxes: any = [];

        for (const optionName in options) {
            const optionVal = options[optionName];
            const onChangeHandler = () => {
                const sp = propertiesConfig.copySpanProperty(property);
                sp.options![optionName] = !optionVal;
                propertiesConfig.updateTaskSpanPropertyConfig(namespace, sp);
            };
            optionCheckBoxes.push(
                <tr>
                    <td>
                        <b>{optionName} </b>
                    </td>
                    <td>
                        <input type="checkbox"
                               checked={optionVal}
                               onChange={onChangeHandler}/>
                    </td>
                </tr>
            );
        }

        return <Table style={{"margin": "0px"}}>
            <tbody>
            {optionCheckBoxes}
            </tbody>
        </Table>
    }
}


export {OptionsEditComponent}