import { createContext } from 'react';
import pdfjs from 'pdfjs-dist';
import PDFPageInfo from '../types/PDFPageInfo';

export enum ViewState {
    LOADING,
    LOADED,
    NOT_FOUND,
    ERROR,
}

interface _PDFStore {
    pageInfos?: PDFPageInfo[];
    setPageInfos: (pageInfos: PDFPageInfo[]) => void;
    doc?: pdfjs.PDFDocumentProxy;
    setDocument: (doc: pdfjs.PDFDocumentProxy | undefined) => void;
    onError: (err: Error) => void;
    viewState: ViewState;
    setViewState: (viewState: ViewState) => void;
    pdfBase64: string;
    pdfPage: string;
    pdfPages: string;
    currPdfPage: number;
    setCurrPdfPage: (page: number) => void;
}

export const PDFStore = createContext<_PDFStore>({
    onError: (_: Error) => {
        throw new Error('Unimplemented');
    },
    pageInfos: [],
    setPageInfos: (pageInfos: PDFPageInfo[]) => {
        throw new Error('Unimplemented');
    },
    doc: undefined,
    setDocument: (doc: pdfjs.PDFDocumentProxy | undefined) => {
        throw new Error('Unimplemented');
    },
    viewState: ViewState.LOADED,
    setViewState: (viewState: ViewState) => {
        throw new Error('Unimplemented');
    },
    pdfBase64: '',
    pdfPage: '1',
    pdfPages: '1',
    currPdfPage: 1,
    setCurrPdfPage: (page: number) => {
        throw new Error('Unimplemented');
    },
});
