import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import pdfjs from 'pdfjs-dist';
import PDFPageInfo from '../../types/PDFPageInfo';
import { PDFStore, ViewState } from '../../context/PDFStore';
import { Progress } from 'reactstrap';
import { getBinaryFromPdfBase64 } from '../../utils/SsieUtils';
import { SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import { Page } from '../Page/Page';

// This tells PDF.js the URL the code to load for it's webworker, which handles heavy-handed
// tasks in a background thread. Ideally we'd load this from the application itself rather
// than from the CDN to keep things local.
// TODO (@codeviking): Figure out how to get webpack to package up the PDF.js webworker code.
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

// This callback exists in the source code, but not in the TypeScript types. So we mix it in
// to make TypeScript happy.
// See: https://github.com/mozilla/pdf.js/blob/master/src/display/api.js#L510
export interface PDFLoadingTask extends pdfjs.PDFLoadingTask<pdfjs.PDFDocumentProxy> {
    onProgress?: (p: pdfjs.PDFProgressData) => void;
}

interface Props {}

/**
 * <PDF /> wrapper component for which loads the PDF's page information from pdfjs.
 */
export const PDFPage: React.FC<Props> = (props) => {
    const [progress, setProgress] = useState(0);

    const { setDocument, setPageInfos, doc, pageInfos, viewState, setViewState, pdfBase64, pdfPage, pdfPages, onError, currPdfPage } =
        React.useContext(PDFStore);

    const { pdfBlocks } = React.useContext(SsieAnnotationStore);

    useEffect(() => {
        if (pdfBase64 && pdfBlocks) {
            setViewState(ViewState.LOADING);

            const loadingTask: PDFLoadingTask = pdfjs.getDocument(getBinaryFromPdfBase64(pdfBase64));
            loadingTask.onProgress = (p: pdfjs.PDFProgressData) => {
                setProgress(Math.round((p.loaded / p.total) * 100));
            };

            // PDF.js uses their own `Promise` type, which according to TypeScript doesn't overlap
            // with the base `Promise` interface. To resolve this we (unsafely) cast the PDF.js
            // specific `Promise` back to a generic one. This works, but might have unexpected
            // side-effects, so we should remain wary of this code.
            (loadingTask.promise as unknown as Promise<pdfjs.PDFDocumentProxy>)
                .then((doc: pdfjs.PDFDocumentProxy) => {
                    setDocument(doc);

                    const loadPages: Promise<PDFPageInfo>[] = [];
                    for (let i = 0; i < parseInt(pdfPages); i++) {
                        loadPages.push(
                            doc.getPage(i + 1).then((p) => {
                                const { width, height } = p.getViewport({ scale: 1 });
                                return new PDFPageInfo(p, i + 1 === parseInt(pdfPage) ? pdfBlocks : [], width, height);
                            }) as unknown as Promise<PDFPageInfo>
                        );
                    }
                    return Promise.all(loadPages);
                })
                .then((pages) => {
                    setPageInfos(pages);
                    setViewState(ViewState.LOADED);
                })
                .catch((err: any) => {
                    if (err instanceof Error) {
                        // We have to use the message because minification in production obfuscates
                        // the error name.
                        if (err.message === 'Request failed with status code 404') {
                            setViewState(ViewState.NOT_FOUND);
                            return null;
                        }
                    }
                    console.error(`Error Loading PDF: `, err);
                    setViewState(ViewState.ERROR);
                });
        }
    }, [pdfBase64, pdfBlocks, pdfPage, pdfPages, setDocument, setPageInfos, setViewState]);

    switch (viewState) {
        case ViewState.LOADING:
            return <Progress value={progress} />;
        case ViewState.NOT_FOUND:
            return <div>PDF NOT FOUND</div>;
        case ViewState.LOADED:
            if (doc && pageInfos && pageInfos.length > 0) {
                return (
                    <PDFContainer>
                        <Page pageInfo={pageInfos[currPdfPage - 1]} />
                    </PDFContainer>
                );
            } else {
                return null;
            }
        // eslint-disable-line: no-fallthrough
        case ViewState.ERROR:
            return <div>ERROR LOADING PDF</div>;
    }
};

const PDFContainer = styled.div(
    ({ theme }) => `
    background: ${theme.secondary};
`
);
