export const areSameTag = (annotations: Annotation[]) => {
    return new Set(annotations.map((anno) => anno.tag)).size === 1;
};

export const getEntityConnectName = (relationIndex: number) => {
    return `CONN_${relationIndex}`;
};

/**
 * Check if an annotation relation is selected
 * If the first member of the annotation relation is equal to the selected ID, it is a match
 * @param selectedId first selected ID of a group of annotations
 * @param relations list of annotation id lists that are part of a relation
 * @returns true if a relation group's first member is equal to the selected ID or else false
 */
export const isSsieAnnotationRelationSelected = (selectedId: string, relations: string[][]) => {
    for (let i = 0; i < relations.length; i++) {
        if (new Set(relations[i]).has(selectedId)) {
            return true;
        }
    }
    return false;
};

export const multipleRelationsContain = (relations: string[][], id: string) => {
    for (let i = 0; i < relations.length; i++) {
        if (relationContains(relations[i], id)) {
            return i;
        }
    }
    return -1;
};

export const relationContains = (relation: string[], id: string) => {
    return new Set(relation).has(id);
};

export class Annotation {
    public readonly id: string;

    constructor(
        public bounds: Bounds,
        public readonly page: number,
        public readonly tag: string,
        public tokenIds: TokenId[] = [],
        id: string | undefined = undefined
    ) {
        this.id = id ? id : uuidv4();
    }

    toString() {
        return this.id;
    }
}

export class SsieAnnotations {
    constructor(public readonly annotations: Annotation[], public readonly relations: string[][]) {}

    saved(): SsieAnnotations {
        return new SsieAnnotations(this.annotations, this.relations);
    }

    updateAnnotations(a: Annotation[]): SsieAnnotations {
        const idSet = new Set(a.map((a) => a.id));
        const editedAnnotations = this.annotations.filter((anno) => !idSet.has(anno.id));
        return new SsieAnnotations([...editedAnnotations, ...a], this.relations);
    }

    withNewAnnotations(a: Annotation[]): SsieAnnotations {
        return new SsieAnnotations(this.annotations.concat(a), this.relations);
    }

    deleteAnnotations(a: Annotation[]): SsieAnnotations {
        const deleteSet = new Set(a.map((o) => o.id));
        const newAnnotations = this.annotations.filter((ann) => !deleteSet.has(ann.id));
        const newRelations = this.relations.filter((rel) => rel.filter((id) => deleteSet.has(id)).length === 0);
        return new SsieAnnotations(newAnnotations, newRelations);
    }

    /**
     * Add a new relation to the annotation store
     * @param r list of annotation ID's in a relation
     * @returns
     */
    withNewRelation(r: string[]): SsieAnnotations {
        return r.length > 1 ? new SsieAnnotations(this.annotations, [...this.relations, r]) : this;
    }

    /**
     * Delete relations from the annotation store
     * @param r list of first annotation ID's in a relation
     * @returns
     */
    deleteRelations(r: string[]): SsieAnnotations {
        const newRelations = this.relations.filter((rel) => !r.includes(rel[0]));
        return new SsieAnnotations(this.annotations, newRelations);
    }
}

