import React from 'react';
import { Button, CardHeader, Collapse } from 'reactstrap';
import { SsieAnnotations, SsieAnnotationStore } from '../../context/SsieAnnotationStore';
import { PDFStore } from '../../context/PDFStore';
import SemiStructuredEntityListItem from './components/SemiStructuredEntityListItem/SemiStructuredEntityListItem';
import './SemiStructuredEntityListSidePanel.css';
import { groupAnnotationsIntoEntities } from '../../utils/SsieUtils';

interface Props {}

/**
 * Component in the semi-structured view for a side panel showing all the document's entities/annotations.
 * Screenshot: https://tiny.amazon.com/npd28n9f/drivcorpamazdocudnleCRpng
 */
const SemiStructuredEntityListSidePanel: React.FC<Props> = () => {
    const { ssieAnnotations, pdfBlocks, setSsieAnnotationStore } = React.useContext(SsieAnnotationStore);
    const { currPdfPage, pdfPage } = React.useContext(PDFStore);

    const [showEntityList, setShowEntityList] = React.useState<boolean>(false);

    const handleRemoveAllClick = React.useCallback(() => {
        setSsieAnnotationStore({
            ssieAnnotations: new SsieAnnotations([], []),
            selectedSsieAnnotations: [], 
            selectedSecondaryAnnotations: [],
            stagedRelation: []
        });
    }, [setSsieAnnotationStore]);

    const completeEntities = groupAnnotationsIntoEntities(ssieAnnotations);

    return currPdfPage === parseInt(pdfPage) && ssieAnnotations.annotations.length > 0 ? (
        <div className="card" id="semi-structured-entity-list-side-panel__container">
            <CardHeader>
                <Button
                    className="TagButton"
                    id="semi-structured-entity-list-side-panel__title"
                    onClick={() => setShowEntityList((s) => !s)}
                    color="warning"
                >
                    ENTITY LIST {completeEntities.length}
                </Button>
                <span id="semi-structured-entity-list-side-panel__remove-all" onClick={() => handleRemoveAllClick()} title="Remove all annotations in the document page">
                    REMOVE ALL
                </span>
            </CardHeader>
            <Collapse isOpen={showEntityList}>
                <div className="card-body d-flex flex-column" id="semi-structured-entity-list-side-panel__list-container">
                    {completeEntities.map(({ annoGroup, relationIndex }) => {
                        const text: string[] = [];
                        const fullText: string[] = [];
                        for (const anno of annoGroup) {
                            for (const t of anno.tokenIds) {
                                const blockText = pdfBlocks[t.tokenIndex].Text;
                                text.push(blockText.substring(t.startIndex, t.endIndex));
                                fullText.push(blockText);
                            }
                        }
                        return (
                            <SemiStructuredEntityListItem
                                text={text}
                                fullText={fullText}
                                key={annoGroup[0].id}
                                tag={annoGroup[0].tag}
                                onClick={() => {
                                    setSsieAnnotationStore({ selectedSsieAnnotations: annoGroup });
                                }}
                                relationIndex={relationIndex}
                            />
                        );
                    })}
                </div>
            </Collapse>
        </div>
    ) : null;
};

export default SemiStructuredEntityListSidePanel;
