import {Checkpointtable} from "../checkpoint/Checkpointtable";

class EventDefinitionConfig extends Checkpointtable {
    constructor(trigger) {
        super();
        this.trigger = trigger;
        this.argumentList = [];

        this.addEventArgument = this.addEventArgument.bind(this);
        this.removeEventArgument = this.removeEventArgument.bind(this);
        this.checkConsistent = this.checkConsistent.bind(this);
        this.adaptToSchemaChange = this.adaptToSchemaChange.bind(this);
    }

    addEventArgument(argumentName,
                     argumentTypes,
                     optional = false,
                     multiValues = false) {
        let argIndex = -1;
        for (let i = 0; i < this.argumentList.length; i++) {
            if (this.argumentList[i].argumentName === argumentName) {
                argIndex = i;
                break;
            }
        }
        // if argument is found, overwrite it
        const newEventArgumentObject = {
            argumentName: argumentName,
            argumentTypes: argumentTypes,
            optional: optional,
            multiValues: multiValues
        };

        if (argIndex >= 0) {
            // if already listed, overwrite the existing one
            this.argumentList[argIndex] = newEventArgumentObject
        } else {
            // otherwise just append it
            this.argumentList.push(newEventArgumentObject)
        }
    }

    removeEventArgument(argumentName) {
        let argIndex = -1;
        for (let i = 0; i < this.argumentList.length; i++) {
            if (this.argumentList[i].argumentName === argumentName) {
                argIndex = i;
                break;
            }
        }

        if (argIndex >= 0) {
            this.argumentList.splice(argIndex, 1)
        }
    }

    checkConsistent(eventArgumentSet) {
        if (!(eventArgumentSet instanceof Set)) {
            // not a set, make it a set
            eventArgumentSet = new Set(eventArgumentSet)
        }
        let consistent = true;
        for (let i = 0; i < this.argumentList.length; i++) {
            const argument = this.argumentList[i];
            const newArgumentTypes = argument.argumentTypes.filter(t => eventArgumentSet.has(t));
            if (newArgumentTypes.length === 0) {
                // this means after the deletion
                // the event definition will have an no argument types
                consistent = false;
                break;
            }
        }
        return consistent;
    }


    adaptToSchemaChange(eventArgumentSet) {
        /**
         * This method works under the assumption that, certain event argument type are deleted
         * Thus the event definition needs to adapt accordingly.
         *
         * NOTE: This method assumes the change have been verified as consistent, so that there will
         * be no such cases as empty argumentType for a given argument
         */

        if (!(eventArgumentSet instanceof Set)) {
            // not a set, make it a set
            eventArgumentSet = new Set(eventArgumentSet)
        }

        for (let i = 0; i < this.argumentList.length; i++) {
            const argument = this.argumentList[i];
            argument.argumentTypes = argument.argumentTypes.filter(t => eventArgumentSet.has(t));
        }
    }

    checkpoint() {
        const argumentsSpecs = this.argumentList.map((arg) => {
            return {
                "name": arg.argumentName,
                "value_types": arg.argumentTypes,
                "optional": arg.optional,
                "multi_values": arg.multiValues,
            }
        });
        return {
            "type": this.trigger,
            "arguments": argumentsSpecs
        };
    }

    resume(checkpoint) {
        this.trigger = checkpoint["type"];
        const argList = checkpoint["arguments"].map((arg) => {
            return {
                argumentName: arg["name"],
                argumentTypes: arg["value_types"],
                optional: arg["optional"],
                multiValues: arg["multi_values"]
            }
        });
        this.argumentList = argList;
    }
}

export {EventDefinitionConfig}