import React, {Component} from 'react';
import {observer} from 'mobx-react'
import 'annotator/AnnotatorTagSpan.css'
import uuid from 'uuid';
import SentimentSelectorPanel from "./SentimentSelectorPanel";

const SentimentTagSpan = observer(
    class SentimentTagSpan extends Component {
        constructor(props) {
            super(props);
            // local state
            this.state = {
                toggled: this.props.toggle,
            };
            this.id = uuid.v4();
            this.toggle = this.toggle.bind(this);
            this.combineTag = this.combineTag.bind(this);
            this.shouldShowControl = this.shouldShowControl.bind(this);
        }

        toggle() {
            this.props.store.setActiveSpan(this.props.idx);
            this.setState({
                toggled: !this.state.toggled
            });
        }

        shouldShowControl() {
            if (this.props.idx === this.props.active) {
                // it is already active;
                console.log("showShowControl", this.props.idx, this.props.store.states.activeSpan);
                if (this.state.toggled) {
                    return true;
                }
            }
            return false;
        }

        combineTag() {
            const {tag, sarcastic} = this.props;
            const sarTag = sarcastic ? "SAR" : "NOT";
            console.log("combined " + tag + " " + sarTag);
                return tag +"/"+sarTag;
        }

        render() {
            console.log("Sentiment TagSpan rerender");
            const showUpperControl = this.shouldShowControl() ? "" : "none";
            const refId = "SentimentTagSpan_" + this.id;
            if (!this.props.blank) {
                //console.log(this.props);
                return <span ref={refId} onClick={this.toggle}>
                    <mark className="HighlightSpan"
                          style={{
                              "background": this.props.color,
                              "paddingLeft": "0.5rem",
                              "marginLeft": "0.5rem",
                              "userSelect": "none"
                          }}> {this.props.extent}
                        <span className="TagStyle" style={{"top": "-20px"}}>{this.combineTag()}</span>
                        <SentimentSelectorPanel
                            start={this.props.start}
                            end={this.props.end}
                            store={this.props.store}
                            display={showUpperControl}
                            tag={this.props.tag}
                            sarcastic={this.props.sarcastic}
                            container={this}/>
                          </mark>
                    </span>
            } else {
                return <span>{this.props.extent}</span>;

            }
        }
    }
);

export default SentimentTagSpan