import RangeTree from "./RangeTree";
import GraphemeSplitter from "grapheme-splitter";

const Attributes = {
    ID: "id",
    BLANK: "blank",
    Removed: "removed",
    Selectable: "selectable",
    Role: "role"
};

const _splitter = new GraphemeSplitter();

class SpanContainer {
    constructor(start, end, rawText, tag = undefined, attrs = undefined) {
        this.start = start;
        this.end = end;
        // we save the rawText rather than extent
        // is because the start and end might change
        this.rawText = rawText;
        this.extent = this.rawText.substring(start, end);
        this.tag = tag !== undefined ? tag : "";
        this.attrs = attrs !== undefined ? attrs : {};
        this.copy = this.copy.bind(this);
        this.setAttribute = this.setAttribute.bind(this);
        this.updateId = this.updateId.bind(this);
        this.getAttribute = this.getAttribute.bind(this);
        this.getGroupAttributes = this.getGroupAttributes.bind(this);
        this.getExtent = this.getExtent.bind(this);
        this.setGroupAttributes = this.setGroupAttributes.bind(this);
        this.exportAnnotation = this.exportAnnotation.bind(this);
        if (!this.tag) {
            this.setAttribute(Attributes.BLANK, true);
        } else {
            this.setAttribute(Attributes.BLANK, false);
        }
        this.updateId();
    }

    setAttribute(key, value) {
        this.attrs[key] = value;
        this.updateId();
        return this;
    }

    setGroupAttributes(attributes) {
        Object.assign(this.attrs, attributes);
        this.updateId();
        return this;
    }

    getAttribute(key) {
        return this.attrs[key];
    }

    getGroupAttributes(keys) {
        const group = {};
        keys.forEach((k) => {
            if (this.getAttribute(k)) {
                group[k] = this.getAttribute(k);
            }
        });
        return group;
    }

    get range() {
        return [this.start, this.end];
    }

    getExtent() {
        return this.rawText.substring(this.start, this.end);
    }

    updateId() {
        const id = this.start.toString() + "_" + this.end.toString() + "_" + this.tag;
        this.attrs.id = id;
    }

    rangeEqual(span2) {
        if (!span2) {
            return false;
        }
        const equality = RangeTree.rangeComparator(this.range, span2.range);
        return equality === 0;
    }

    exportAnnotation(withAttributes = true,
                     filterBy = undefined) {

        // Before exporting, adjust all offsets to account for multi-byte characters
        // eg: A single emoji will have total span of 2 or more. This should be converted to 1
        const startWithCondensedOffset = _splitter.splitGraphemes(this.rawText.substring(0, this.start)).length;
        const endWithCondensedOffset = startWithCondensedOffset + _splitter.splitGraphemes(this.extent).length;
        let ufSpan = {
            start: startWithCondensedOffset,
            end: endWithCondensedOffset,
            tag: this.tag,
            extent: this.getExtent()
        };
        if (withAttributes) {
            let spanAttrs = this.attrs;
            if (filterBy) {
                const filterAttrs = {};
                filterBy.forEach((k) => {
                    if (this.attrs[k] !== undefined) {
                        filterAttrs[k] = this.attrs[k];
                    }
                });
                spanAttrs = filterAttrs;
            }
            if (spanAttrs && Object.keys(spanAttrs).length > 0) {
                // this is non-empty span
                ufSpan.attrs = spanAttrs;
            }
        }
        return ufSpan;
    }

    copy() {
        const factory = new SpanFactory(this.rawText);
        return factory.copySpan(this);
    }

}

class SpanFactory {
    constructor(rawText) {
        this.rawText = rawText;
        this.createSpan = this.createSpan.bind(this);
        this.copySpan = this.copySpan.bind(this);
    }

    createSpan(start, end, tag, attrs) {
        return new SpanContainer(start, end, this.rawText, tag, attrs);
    }

    copySpan(span) {
        const {start, end, tag, attrs} = span;
        // we can only do a shallow copy here
        // this will copy all primitive values
        // which is good for most attrs use case
        const copyAttrs = Object.assign({}, attrs);
        return new SpanContainer(start, end, this.rawText, tag, copyAttrs);
    }
}

export {SpanContainer, SpanFactory, Attributes}