import React, {Component} from 'react';
import {Button, FormGroup, Input, InputGroup, InputGroupAddon, Label} from "reactstrap";


class NavigationChoicePanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mode: 0
        };
    }

    render() {
        return <div id="NavigationChoicePanel">
            <FormGroup check>
                <Label check>
                    <Input type="radio"
                           name="radio1"
                           checked={this.state.mode === 0}
                           onChange={() => {
                               this.props.store.mode = 0;
                               this.setState({
                                   mode: 0
                               })
                           }}/>
                    {' '} 1. Jump to the <b>position/index</b> of the document in the collection
                </Label>
                <Label check>
                    <Input type="radio"
                           name="radio2"
                           checked={this.state.mode === 1}
                           onChange={
                               () => {
                                   this.props.store.mode = 1;
                                   this.setState({
                                       mode: 1
                                   });
                               }}/>
                    {' '} 2. Jump to the document given the
                    specific <b>document_id</b> field
                </Label>
            </FormGroup>
        </div>;

    }
}

export {NavigationChoicePanel}