import {
    BasePropertyType,
    PropertyInstance,
    PropertyKeyValueType,
    TargetProperties
} from "../types/BasePropertyType";
import {PropertyInstanceDataStore} from "./PropertyInstanceDataStore";
import {BaseSpanType} from "../types/CoreTypes";
import {observable} from "mobx";
import {SSIEAnnotationTypeForProperties} from "../properties/PropertyEditorContainer";

type TargetKey = symbol | string

type TargetQuery = symbol | BaseSpanType | SSIEAnnotationTypeForProperties | undefined

const DocumentPropertyKey = Symbol("document place holder"); // placeholder symbol for document

class PropertyDataStore {
    targetProperties: TargetProperties;
    // key to spanPropertyInstances is in a special format
    // "{start}_{end}"
    private propertyInstances: Map<TargetKey, {
        target: TargetQuery,
        store: PropertyInstanceDataStore
    }>;
    public updateSubscriber: any;


    constructor(targetProperties: TargetProperties) {
        this.targetProperties = targetProperties;
        this.propertyInstances = new Map();
        this.updateSubscriber = observable({update: 1});
    }

    public mapQueryToKey(query: TargetQuery): TargetKey | undefined {
        if (query === undefined || query === DocumentPropertyKey) {
            // return as is
            return query;
        }

        if (PropertyDataStore.instanceOfBlockType(query)) {
            // Block use-case (SSIE)
            const {id, tag} = query;
            return id + "_" + tag;
        }

        // map span info to a unique id
        // @ts-ignore
        const {start, end, tag} = query;

        return start + "_" + end + "_" + tag;
    }

    public getInstanceStore(query: TargetQuery): PropertyInstanceDataStore | undefined {
        // retrieve instance data store or create if missing
        // undefined is returned in case instance data store can't be created
        const key = this.mapQueryToKey(query);
        let store: PropertyInstanceDataStore | undefined = undefined;
        if (key !== undefined) {
            // create instanceDataStore if needed
            if (!this.propertyInstances.has(key)) {
                let propertiesDef: BasePropertyType[];
                if (key === DocumentPropertyKey) {
                    propertiesDef = this.targetProperties.document;
                } else {
                    // @ts-ignore
                    const {tag} = query;
                    propertiesDef = this.targetProperties.tags[tag];
                }
                // assume we have a valid propertiesDef
                if (propertiesDef !== undefined) {
                    // initialize with the right definitions
                    const propertyInstance = new PropertyInstanceDataStore(propertiesDef);
                    this.propertyInstances.set(key,
                        {
                            target: query,
                            store: propertyInstance
                        }
                    )
                }
            }
            if (this.propertyInstances.has(key)) {
                // we need to check the key again
                // to make sure the store has actually been created
                store = this.propertyInstances.get(key)!.store;
            }
        }
        return store;
    }

    public getDocumentInstanceStore(): PropertyInstanceDataStore | undefined {
        return this.getInstanceStore(DocumentPropertyKey);
    }

    private retrieveInstanceProperty(query: TargetQuery): PropertyInstance[] {
        let store = this.getInstanceStore(query);
        if (store !== undefined) {
            return store.retrieveProperty();
        } else {
            // Note: this means the target has NO properties to be attached
            // doesn't mean the property values are left empty
            return [];
        }
    }

    private updateInstanceProperty(query: TargetQuery, properties: PropertyKeyValueType[]) {
        let store: PropertyInstanceDataStore | undefined = this.getInstanceStore(query);
        if (store !== undefined) {
            // if a store can be created
            store.updateProperty(properties);
        }
        this.updateSubscriber.update += 1;
    }

    private removeInstanceProperty(query: TargetQuery) {
        const key = this.mapQueryToKey(query);
        //@ts-ignore
        this.propertyInstances.delete(key);
        this.updateSubscriber.update += 1;
    }

    public retrieveDocumentProperty(): PropertyInstance[] {
        return this.retrieveInstanceProperty(DocumentPropertyKey)
    }

    public updateDocumentProperty(properties: PropertyKeyValueType[]) {
        this.updateInstanceProperty(DocumentPropertyKey, properties);
    }

    public removeDocumentProperty() {
        this.removeInstanceProperty(DocumentPropertyKey);
    }

    public retrieveBlockProperty(query: SSIEAnnotationTypeForProperties): PropertyInstance[] {
        return this.retrieveInstanceProperty(query)
    }

    public updateBlockProperty(query: SSIEAnnotationTypeForProperties, properties: PropertyKeyValueType[]) {
        this.updateInstanceProperty(query, properties);
    }

    public removeBlockProperty(query: SSIEAnnotationTypeForProperties) {
        this.removeInstanceProperty(query);
    }

    public retrieveSpanProperty(query: BaseSpanType): PropertyInstance[] {
        return this.retrieveInstanceProperty(query);
    }

    public updateSpanProperty(query: BaseSpanType, properties: PropertyKeyValueType[]) {
        this.updateInstanceProperty(query, properties);
    }

    public removeSpanProperty(query: BaseSpanType) {
        this.removeInstanceProperty(query);
    }

    private getInstancePropertyAnnotations(query: TargetQuery) {
        const key = this.mapQueryToKey(query);
        if (key !== undefined) {
            if (this.propertyInstances.has(key)) {
                const {store} = this.propertyInstances.get(key)!;
                if (!store.empty) {
                    // only return annotation
                    // when it is not empty
                    return store.dump();
                }
            }
        }
        // default
        return undefined;
    }

    public checkSpanContainsProperties(span: BaseSpanType): boolean {
        const key = this.mapQueryToKey(span);
        if (key !== undefined) {
            // try to get instance store
            const instanceStore = this.getInstanceStore(span);
            if (instanceStore) {
                return !instanceStore.empty;
            }
        }
        return false;
    }

    public getDocumentPropertyAnnotations() {
        return this.getInstancePropertyAnnotations(DocumentPropertyKey);
    }

    public getSpanPropertyAnnotations(query: TargetQuery) {
        return this.getInstancePropertyAnnotations(query);
    }

    static instanceOfBlockType(query: any): query is SSIEAnnotationTypeForProperties {
        return 'id' in query;
    }

}

export {PropertyDataStore}