import React from 'react';
import { Button } from 'reactstrap';
import { PDFStore } from '../../context/PDFStore';
import './PageButtons.css';
import { Namespaces } from '../../../../core/Namespaces';

interface Props {
    namespace: string;
}

const PageButtons: React.FC<Props> = ({ namespace }) => {
    const { setCurrPdfPage, currPdfPage, pdfPage, pdfPages } = React.useContext(PDFStore);

    const isAnnotationPage = currPdfPage === parseInt(pdfPage);

    return namespace === Namespaces.SSIE ? (
        <div id="PageButtons-Container">
            {!isAnnotationPage ? (
                <div className="ml-2 text-lg text-danger test" id="PageButtons-ReadOnlyText">
                    READ-ONLY
                </div>
            ) : null}
            <Button
                id="PageButtons-LeftArrow"
                size="sm"
                className="FunctionalButton bg-secondary"
                onClick={() => setCurrPdfPage(Math.max(1, currPdfPage - 1))}
                title="Move back one page"
            >
                {'<<'}
            </Button>
            <Button
                id="PageButtons-PageBtn"
                size="sm"
                className="FunctionalButton"
                onClick={() => setCurrPdfPage(parseInt(pdfPage))}
                title="Return to annotation page"
            >
                {currPdfPage}
            </Button>
            <Button
                id="PageButtons-RightArrow"
                size="sm"
                className="FunctionalButton bg-secondary"
                onClick={() => setCurrPdfPage(Math.min(currPdfPage + 1, parseInt(pdfPages)))}
                title="Move forward one page"
            >
                {'>>'}
            </Button>
        </div>
    ) : null;
};

export default PageButtons;
