import React, {useRef, useState} from 'react';
import {BaseSpanType} from "../types/CoreTypes";
import {TaggingStateStore} from "../store/TaggingStateStore";
import {BasePropertyType, PropertyInstance, PropertyKeyValueType} from "../types/BasePropertyType";
import {
    Button,
    CardBody,
    FormGroup,
    Input,
    ListGroup,
    ListGroupItem,
} from "reactstrap";


const ExpandableTextItem = (props: {
    text: string
    removeText: any,
    maxLength: number
}) => {
    const {text, removeText, maxLength} = props;
    const [show, setShow] = useState(false);

    let truncate = true;

    if (show || text.length <= maxLength) {
        truncate = false
    }

    console.log("truncate truncate", truncate);

    let displayText = text;
    if (truncate) {
        console.log("truncate truncate");
        displayText = text.substring(0, maxLength) + '......';
    }

    return <div>
        <p className="TextField">{displayText}</p>

        <Button size="sm"
                outline
                color="danger"
                className="SmallButton"
                onClick={removeText}>
            REMOVE
        </Button>

        <Button size="sm"
                color="info"
                outline
                className="SmallButton"
                style={{"display": text.length <= maxLength ? "none" : "inherit"}}
                onClick={() => setShow(!show)}>
            {truncate ? "SHOW ALL" : "HIDE"}
        </Button>

    </div>
};

const TextPropertyEditor = (props: {
    property: BasePropertyType,
    value: string[],
    updateHandler: { (properties: PropertyKeyValueType[]): void }
}) => {
    const {property, value: currentValue, updateHandler} = props;
    const {name: propertyName, options} = property;

    const ref = useRef<any>(null);

    const allowMultipleInstances = options!.multi_instances === true;

    const addTextInstance = (val: string) => {
        if (val.length > 0) {
            let v2 = [...currentValue];
            // Noted, Text field doesn't require elements to be unique
            // it is not KeyValue after all
            if (!allowMultipleInstances) {
                v2 = [val,]
            } else {
                v2.push(val)
            }
            updateHandler([{
                name: propertyName,
                value: v2
            }])
        }
    };

    const removeTextInstance = (idx: number) => {
        console.log("removeTextInstance idx", idx);
        let v2 = [...currentValue];
        v2.splice(idx, 1);
        updateHandler([{
            name: propertyName,
            value: v2
        }])
    };

    const currentTextElements = currentValue.map((t, idx) => {
        const i = idx;
        return <ListGroupItem>
            <ExpandableTextItem text={t}
                                maxLength={100}
                                removeText={() => removeTextInstance(i)}/>
        </ListGroupItem>

    });


    return <React.Fragment>
        <ListGroup>
            {currentTextElements}
        </ListGroup>

        <CardBody>
            <FormGroup>
                <Input type="textarea"
                       name="text"
                       innerRef={ref}
                       style={{"height": "100px"}}/>
            </FormGroup>
            <Button
                outline
                className="TextSubmitButton"
                onClick={() => {
                    addTextInstance(ref.current.value);
                    ref.current.value = '';
                }}>
                ADD
            </Button>
        </CardBody>
    </React.Fragment>
};

export {TextPropertyEditor}