import React from 'react';
import { Button } from 'reactstrap';
import { ConnectTagColor } from '../../../../../../AnnotatorCommons';
import { UNASSIGNED_TAG } from '../../../../../../store/TaggingStateStore';
import { getEntityConnectName } from '../../../../context/SsieAnnotationStore';
import './SemiStructuredEntityListItem.css';

interface Props {
    text: string[];
    fullText: string[];
    tag: string;
    onClick: () => void;
    relationIndex: number;
}

const SemiStructuredEntityListItem: React.FC<Props> = ({ text, fullText, tag, onClick, relationIndex }) => {
    return (
        <div className="d-flex semi-structured-entity-list-item__container">
            <p className="semi-structured-entity-list-item_full-text">
                <Button
                    className={`semi-structured-entity-list-item__tag ${
                        tag === UNASSIGNED_TAG ? 'semi-structured-entity-list-item__unassigned-tag' : ''
                    }`}
                    onClick={onClick}
                    title="Select all spans in connection"
                >
                    {tag}
                </Button>
                {relationIndex > -1 && (
                    <Button
                        className="semi-structured-entity-list-item__connection"
                        onClick={onClick}
                        style={{ backgroundColor: ConnectTagColor }}
                        title="Select all spans in connection"
                    >
                        {getEntityConnectName(relationIndex)}
                    </Button>
                )}
                {fullText.map((ft, idx) => (
                    <span key={`${ft}-${idx}-${relationIndex}`}>
                        <span className="semi-structured-entity-list-item__unselected-text">{ft.substring(0, ft.indexOf(text[idx]))}</span>
                        <span className="semi-structured-entity-list-item__selected-text">{text[idx]}</span>
                        <span className="semi-structured-entity-list-item__unselected-text">
                            {ft.substring(ft.indexOf(text[idx]) + text[idx].length)}{' '}
                        </span>
                    </span>
                ))}
            </p>
        </div>
    );
};

export default SemiStructuredEntityListItem;
