import React, {useState} from 'react';
import {observer} from "mobx-react";
import {
    Button, ButtonDropdown,
    Card,
    CardBody,
    CardFooter,
    CardHeader,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Table
} from "reactstrap";
import {SchemaConfig} from "../config/SchemaConfig";
import {GlobalViewConfig} from "../config/ViewMetadataConfig";
import {KeyValuePropertyConfigCell} from "../properties/KeyValuePropertyConfigCell";
import {TextPropertyConfigCell} from "../properties/TextPropertyConfigCell";
import {SelectablePropertyConfigCell} from "../properties/SelectablePropertyConfigCell";


import 'annotator/AnnotatorButton.css'
import {TextSubmitBar} from "./TextSubmitBar";
import {PropertiesConfig} from "../properties/PropertiesConfig";
import {randomElementKey} from "../../annotator/utils/Utils";


type PropsType = {
    store: any // rootStore
}

type StateType = {
    namespace: string,
    fieldType: "KeyValue" | "Text",
    selectedProperties: string[]
}

const TagDropdownSelector = (props: {
    items: string[],
    onItemClick: (i: string) => any,
    banner: string,
    currentValue: string
}) => {
    const [dropdownOpen, setOpen] = useState(false);
    const toggle = () => setOpen(!dropdownOpen);

    const items = props.items.map((t) => {
        return <DropdownItem onClick={
            () => {
                props.onItemClick(t)
            }}>
            <span className="SelectorDropDown"> {t} </span>
        </DropdownItem>

    });

    return <React.Fragment>
        <Button className="TagButton" color="primary" disabled
                style={{"marginRight": "5px"}}>
            {props.banner.toUpperCase()}
        </Button>
        <ButtonDropdown isOpen={dropdownOpen} toggle={toggle}>
            <DropdownToggle caret color="info" className="TagButton">
                {props.currentValue.toUpperCase()}
            </DropdownToggle>
            <DropdownMenu>
                {items}
            </DropdownMenu>
        </ButtonDropdown>
    </React.Fragment>
};


const PropertiesCustomizationPanel = observer(
    class PropertiesCustomizationPanel extends React.Component<PropsType, StateType> {
        state: StateType = {
            // @ts-ignore
            namespace: GlobalViewConfig.NER.namespace, // NER by default
            fieldType: "KeyValue", // KeyValue type by default
            selectedProperties: []
        };

        constructor(props: PropsType) {
            super(props);

            this.selectProperty = this.selectProperty.bind(this);
        }

        public selectProperty(propertyName: any) {
            this.setState((state) => {
                const properties = state.selectedProperties;
                if (properties.includes(propertyName)) {
                    return {selectedProperties: properties.filter(p => p !== propertyName)}
                } else {
                    properties.push(propertyName);
                    return {selectedProperties: properties}
                }
            });
        }

        public removeSelectedProperties(schemaConfig: SchemaConfig) {
            this.state.selectedProperties.forEach((name) => {
                schemaConfig.propertiesConfig
                    .removeTaskSpanPropertyConfigByName(this.state.namespace, name);
            });
            // empty the selection in the end;
            this.setState((state) => {
                return {
                    selectedProperties: []
                }
            });
        }

        render() {
            const rootStore = this.props.store;
            const schemaConfig: SchemaConfig = rootStore.schemaConfig;
            // trigger when schemaConfig changes
            // @ts-ignore
            const subscribeSchemaConfigUpdate = schemaConfig.configUpdated;


            const headers = ["Name", "Type", "Export Key", "Attached Targets", "Options", "Values", "Selected"];
            const tableHeaders = headers.map((h) => <th>{h}</th>);
            // @ts-ignore
            const namespace = this.state.namespace;
            const taskConfig = schemaConfig.propertiesConfig.getTaskConfig(namespace);
            let cells: any = [];


            if (taskConfig !== undefined) {
                const spanProperties = taskConfig.spanProperties;

                cells = spanProperties.map((sp) => {
                    let component: any;
                    // each field_type corresponds to its own type of configuration cell
                    // currently support KeyValue/Text
                    if (sp.field_type === "KeyValue") {
                        component = <KeyValuePropertyConfigCell namespace={namespace}
                                                                property={sp}
                                                                schemaConfig={schemaConfig}/>
                    } else {
                        component = <TextPropertyConfigCell namespace={namespace}
                                                            property={sp}
                                                            schemaConfig={schemaConfig}/>
                    }

                    const selected = this.state.selectedProperties.includes(sp.name);

                    return <tr>
                        <SelectablePropertyConfigCell selected={selected}
                                                      onSelect={() => {
                                                          this.selectProperty(sp.name)
                                                      }}>
                            {component}
                        </SelectablePropertyConfigCell>
                    </tr>
                });

            }

            // Text bar to submit new property name
            const addNewProperty = (name: string) => {
                const propertiesConfig: PropertiesConfig = schemaConfig.propertiesConfig;
                const sp = propertiesConfig.getTaskSpanPropertyTemplateByType(name,
                    this.state.fieldType);
                propertiesConfig.addTaskSpanPropertyConfig(this.state.namespace, sp);
            };


            // Button to Remove selected properties
            const removePropertyButton = <Button color="danger"
                                                 size="lg"
                                                 className="TagButton"
                                                 onClick={() => {
                                                     this.removeSelectedProperties(schemaConfig);
                                                 }}>
                REMOVE
            </Button>;

            console.log("cells", cells);
            console.log("spanPropertiesConfig", schemaConfig.propertiesConfig);

            const forceReconstructTextSubmitBarKey = randomElementKey();

            const newPropertyNameTextBar = <TextSubmitBar store={rootStore}
                                                          key={forceReconstructTextSubmitBarKey}
                                                          default={"NEW PROPERTY NAME"}
                                                          onSubmit={(name: string) => {
                                                              addNewProperty(name);
                                                          }}/>;


            return <div id="SpanPropertiesCustomizationPanel">
                <Card>
                    <CardHeader>
                        <TagDropdownSelector items={schemaConfig.propertiesConfig.tasks}
                                             onItemClick={(item: string) => {
                                                 this.setState((state) => ({namespace: item}))
                                             }}
                                             banner={"NAMESPACE"}
                                             currentValue={this.state.namespace}/>
                    </CardHeader>
                    <CardHeader>
                        <TagDropdownSelector items={["KeyValue", "Text"]}
                                             onItemClick={(item: string) => {
                                                 if (item === "KeyValue" || item === "Text") {
                                                     // typescript is really smart
                                                     this.setState((state) => ({fieldType: item}))
                                                 }
                                             }}
                                             banner={"FIELD TYPE"}
                                             currentValue={this.state.fieldType}/>
                    </CardHeader>

                    <CardHeader>
                        {newPropertyNameTextBar}
                    </CardHeader>
                    <CardBody>
                        <Table bordered>
                            <thead>
                            {tableHeaders}
                            </thead>
                            <tbody>
                            {cells}
                            </tbody>
                        </Table>
                    </CardBody>
                    <CardFooter>
                        {removePropertyButton}
                    </CardFooter>
                </Card>
            </div>
        }
    }
);

export {PropertiesCustomizationPanel}