import React, {Component} from 'react';
import {Button} from 'reactstrap';
import {observer} from 'mobx-react';
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import {SentimentTagColor} from "./SentimentTagConfig";

const SentimentTagGroup = observer(
    class SentimentTagGroup extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            let buttons = [];
            let {store} = this.props;
            let numberTags = store.tagGroupStore.numTags;
            for (let i = 0; i < numberTags; i++) {
                let tagName = store.tagGroupStore.getTagName(i);
                let color = SentimentTagColor[tagName];
                // pass a closure to capture
                // the button's local change
                let onClickEvent = function (e) {
                    e.stopPropagation();
                    // console.log("clicked");
                    this.props.store.updateDocumentLabel(tagName);
                };
                if (this.props.store.tagGroupStore.selectedTag === tagName) {
                    buttons.push(<Button key={"sent_button_" + i}
                                         className="TagButton SentimentBarButton"
                                         style={{"background": color}}
                                         onClick={onClickEvent.bind(this)}>{tagName}</Button>);
                } else {
                    buttons.push(<Button key={"sent_button_" + i}
                                         className="TagButton SentimentBarButton"
                                         outline
                                         style={{"background": color,}}
                                         onClick={onClickEvent.bind(this)}>{tagName}</Button>);
                }
            }

            return <div className="TagGroup" style={{"fontFamily": "'Roboto Condensed', sans-serif"}}>
                {buttons}
            </div>
        }
    }
);

export default SentimentTagGroup