import React from 'react';
import {Button} from 'reactstrap';
import {observer} from 'mobx-react';
import 'editor/sidepanel/EditorSidePanel.css';

const DocumentMetadataPanel = observer(
    class AlertModal extends React.Component {
        constructor(props) {
            super(props);
            this.showMetadata = this.showMetadata.bind(this);
        }

        showMetadata() {
            const document = this.props.store.collectionStore.getActiveDocument();
            const collectionPath = this.props.store.collectionStore.collectionPath;
            const {metadata} = document;
            this.props.store.sendAlert(
                "Document Metadata",
                <div style={{
                    "maxWidth": "600px",
                    "overflowWrap": "break-word"
                }}>
                    <ul>
                        <li><span><b>Project ID:</b> <span
                            style={{
                                "overflowWrap":
                                    "break-word"
                            }}>
                            {collectionPath} </span>
                        </span>
                        </li>
                        <li><span><b>Document ID:</b> {metadata["document_id"]}</span></li>
                        <li><span><b>Source:</b> {metadata["source"]}</span></li>
                        <li><span><b>Domain:</b> {metadata["domain"]}</span></li>
                        <li><span><b>Language:</b> {metadata["language"]}</span></li>
                    </ul>
                </div>
            )
        }

        render() {
            return (
                <div>
                    <Button color="warning"
                            className="PanelButton"
                            onClick={this.showMetadata}>Reveal</Button>
                </div>
            );
        }
    });

export default DocumentMetadataPanel