import React, {Component} from 'react';
import {Button} from 'reactstrap';
import 'annotator/AnnotatorButton.css'
import 'annotator/Annotator.css'
import {SvgCrossIcon} from "../svg/SvgCrossIcon";

const CoreferenceGroupButton = (props: {
    color: string;
    mouseOverTrigger: () => void;
    mouseLeaveTrigger: () => void;
    clickTrigger: () => void;
    crossClickTrigger?: () => void;
    groupId: number;
    disabled?: boolean;
}) => {
    const {
        color, mouseOverTrigger, mouseLeaveTrigger, clickTrigger, crossClickTrigger, groupId, disabled
    } = props;
    return (
        <Button 
            className="TagButton"
            style={{"position": "relative", "marginRight": "10px",
                "marginBottom": "10px"}}
            size="sm"
            color={color}
            onMouseEnter={() => mouseOverTrigger()}
            onMouseLeave={() => mouseLeaveTrigger()}
            disabled={disabled}
            onClick={
                (e) => {
                    e.stopPropagation();
                    clickTrigger()
                }}
        >
            {`G${groupId}`}
            {
                crossClickTrigger ? (
                    <SvgCrossIcon 
                        cssStyle="SvgCancelButtonTag"
                        clickTrigger={() => {
                            crossClickTrigger();
                        }}
                    />
                ) : null
            }
            
        </Button>
    )
}

export {CoreferenceGroupButton}