import {GlobalViewConfig} from "../config/ViewMetadataConfig";
import {UFSchema} from "../../annotator/core/UFSchema";

class ExportVerifier {
    constructor() {
        this.disabledNamespaces = new Set();
        GlobalViewConfig.values.forEach((v) => {
            if (v.disableExport === true) {
                this.disabledNamespaces.add(v.namespace);
            }
        });

        this.filter = this.filter.bind(this);
    }

    filter(document) {
        const annotations = document[UFSchema.ANNOTATIONS];
        this.disabledNamespaces.forEach((ns) => {
            if (annotations !== undefined && annotations[ns] !== undefined) {
                // this annotations exists, needs to be filtered

                // TODO: Side Effects, need to take care of this
                delete annotations[ns]
            }
        });
        // filter annotations_v2 stuff from the exported file
        if (document[UFSchema.ANNOTATIONS_V2] !== undefined) {
            delete document[UFSchema.ANNOTATIONS_V2]
        }
        // filter conflicted annotations from the exported file
        if (document[UFSchema.CONFLICTED_ANNOTATIONS_LIST] !== undefined) {
            delete document[UFSchema.CONFLICTED_ANNOTATIONS_LIST]
        }
        return document;
    }
}

export {ExportVerifier}