import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { App, GroundTruthApp } from './App';
import registerServiceWorker from './registerServiceWorker';
import { RootStore } from './RootStore';
import { DefaultCollection } from 'annotator/default/DefaultCollection';
import { TextInputBox } from './editor/utils/TextInputBox';
import { fetchContent } from './annotator/utils/Utils';
import { DOCUMENT_TYPE } from './annotator/tagging/pdf/utils/SsieUtils';

function setS3Data(doc_s3_ref, key, map) {
    console.log('fetching content from ', doc_s3_ref);
    return new Promise((resolve, reject) => {
        fetchContent(doc_s3_ref).then((result) =>
            setTimeout(() => {
                if (result === 'not_able_to_fetch') {
                    console.log('not able to fetch content', doc_s3_ref);
                    reject();
                } else {
                    console.log(result);
                    map[key] = result;
                    resolve();
                }
            }, 200)
        );
    });
}

function createGTApp(doc, rootStore, ssieProps) {
    const documents = [doc];

    rootStore.collectionStore.resume({
        documents: documents,
        path: '',
    });
    rootStore.progressStore.reset(documents);

    ReactDOM.render(
        <GroundTruthApp store={rootStore} ssieProps={ssieProps} />,
        document.getElementById('root')
    );
}

function decodeBase64(base64_str) {
    console.log('base_64', base64_str);
    var decoded = base64ToUtf8(base64_str);
    console.log('Decoded: ' + decoded);
    return JSON.parse(decoded);
}

function base64ToUtf8(b64) {
    var binstr = atob(b64);

    return binaryStringToUtf8(binstr);
}

function binaryStringToUtf8(binstr) {
    var escstr = binstr.replace(/(.)/g, function (m, p) {
        var code = p.charCodeAt(0).toString(16).toUpperCase();
        if (code.length < 2) {
            code = '0' + code;
        }
        return '%' + code;
    });

    return decodeURIComponent(escstr);
}

// override console.log
if (window.console && console.log) {
    if (process.env.NODE_ENV !== 'development') {
        console.log = (message) => {};
    }
}

const getSsiePropsObject = async ({
    setOutputValueCallback,
    pdfBase64S3ReadRef,
    pdfBlocksS3ReadRef,
    pdfBlocksS3Ref,
    pdfVersion,
    pdfType,
    metadata,
    annotatorMetadata,
    primaryAnnotationS3ReadRef,
    secondaryAnnotationS3ReadRef,
}) => {
    const metadataObj =
        metadata !== '{{task.input.taskObject.metadata}}' && metadata !== ''
            ? JSON.parse(metadata.replace(/&quot;/g, '"'))
            : undefined;
    const annotatorMetadataObj =
        annotatorMetadata !== '{{task.input.taskObject.annotatorMetadata}}' &&
        annotatorMetadata !== ''
            ? JSON.parse(annotatorMetadata.replace(/&quot;/g, '"'))
            : undefined;
    const ssieProps = {
        setOutputValueCallback,
        pdfBase64S3ReadRef,
        pdfBlocksS3ReadRef,
        pdfBlocksS3Ref,
        pdfType:
            pdfType !== '{{task.input.taskObject.pdfType}}' && pdfType !== ''
                ? pdfType
                : DOCUMENT_TYPE.NativePDF,
        pdfVersion,
        pdfPages: metadataObj ? metadataObj.pages : undefined,
        pdfPage: metadataObj ? metadataObj.page : undefined,
        metadata: metadataObj,
        annotatorMetadata: annotatorMetadataObj,
        primaryAnnotationS3ReadRef,
        secondaryAnnotationS3ReadRef,
    };
    if (
        ssieProps.pdfBlocksS3ReadRef !==
            '{{task.input.taskObject.pdfBlocksS3Ref | grant_read_access}}' &&
        ssieProps.pdfBlocksS3ReadRef !== ''
    ) {
        await setS3Data(
            ssieProps.pdfBlocksS3ReadRef.replace(/&amp;/g, '&'),
            'pdfBlocks',
            ssieProps
        );
        ssieProps.pdfBlocks = JSON.parse(ssieProps.pdfBlocks);
    }
    if (
        ssieProps.pdfBase64S3ReadRef !==
            '{{task.input.taskObject.pdfBase64S3Ref | grant_read_access}}' &&
        ssieProps.pdfBase64S3ReadRef !== ''
    ) {
        await setS3Data(
            ssieProps.pdfBase64S3ReadRef.replace(/&amp;/g, '&'),
            'pdfBase64',
            ssieProps
        );
    }
    if (
        ssieProps.primaryAnnotationS3ReadRef !==
            '{{task.input.taskObject.primaryAnnotationS3Ref | grant_read_access}}' &&
        ssieProps.primaryAnnotationS3ReadRef !== ''
    ) {
        await setS3Data(
            ssieProps.primaryAnnotationS3ReadRef.replace(/&amp;/g, '&'),
            'primaryAnnotation',
            ssieProps
        );
        ssieProps.primaryAnnotation = JSON.parse(ssieProps.primaryAnnotation);
    }
    if (
        ssieProps.secondaryAnnotationS3ReadRef !==
            '{{task.input.taskObject.secondaryAnnotationS3Ref | grant_read_access}}' &&
        ssieProps.secondaryAnnotationS3ReadRef !== ''
    ) {
        await setS3Data(
            ssieProps.secondaryAnnotationS3ReadRef.replace(/&amp;/g, '&'),
            'secondaryAnnotation',
            ssieProps
        );
        ssieProps.secondaryAnnotation = JSON.parse(ssieProps.secondaryAnnotation);
    }
    return ssieProps;
};

if (
    process.env.REACT_APP_VERSION !== 'toolkit' &&
    process.env.REACT_APP_VERSION !== 'groundtruth'
) {
    const rootStore = new RootStore(DefaultCollection);
    // add alert before the closing action
    if (process.env.NODE_ENV !== 'development') {
        window.addEventListener('beforeunload', (e) => {
            console.log('onbeforeunload called');
            // save before shutdown
            rootStore.checkpointManager.checkpointEditorState('before shutdown');
            e.returnValue = 'false';
        });
    }

    console.log('process.env ->', process.env);

    ReactDOM.render(<App store={rootStore} />, document.getElementById('root'));

    registerServiceWorker();

    const annotatorIdPopup = () => {
        rootStore.sendAlert(
            'Before you start...',
            <div>
                <p>Before you start, please make sure you specify your annotator id</p>
                <TextInputBox
                    buttonText="Click to Set"
                    submit={(annotatorId) => {
                        rootStore.sessionInfo['annotator_id'] = annotatorId;
                    }}
                />
            </div>,
            () => {
                // set a small timeout to show warning
                setTimeout(() => {
                    rootStore.checkStoreReady({
                        failureCallback: annotatorIdPopup,
                    });
                }, 200);
            }
        );
    };

    if (process.env.NODE_ENV !== 'development') {
        annotatorIdPopup();
    }
} else if (process.env.REACT_APP_VERSION === 'groundtruth') {
    // Load groundtruth editor
    console.info('Welcome To GroundTruthACEditor');
    const {
        docBase64 = '',
        sampleDoc,
        setOutputValueCallback,
        rawTextS3Ref = '',
        runtimeSchemaConfigRef = '',

        // SSIE properties
        pdfBase64S3ReadRef = '',
        pdfBlocksS3ReadRef = '',
        pdfBlocksS3Ref = '',
        pdfType = '',
        pdfVersion = '',
        metadata = '',
        annotatorMetadata = '',
        primaryAnnotationS3ReadRef = '',
        secondaryAnnotationS3ReadRef = '',
    } = window.GROUNDTRUTH;
    const rootStore = new RootStore();

    setOutputValueCallback(() => {
        const schemaVersion = rootStore.schemaConfig.version;
        const timestamp = Math.round(new Date().getTime() / 1000);
        const doc = rootStore.collectionStore.getDocument(0);
        doc['metadata']['timestamp'] = timestamp;
        doc['metadata']['schema_version'] = schemaVersion;
        return doc;
    });
    const doc =
        docBase64 !== '{{task.input.doc}}' && docBase64 !== ''
            ? decodeBase64(docBase64)
            : sampleDoc;
    console.log('Doc: ' + doc);
    (async () => {
        if (rawTextS3Ref !== '{{task.input.metadata.raw_text_s3_ref}}' && rawTextS3Ref !== '') {
            await setS3Data(rawTextS3Ref.replace(/&amp;/g, '&'), 'raw_text', doc);
        }
        if (
            runtimeSchemaConfigRef !==
                "{{'s3://TO_BE_REPLACE/assets/schema.json' | grant_read_access }}" &&
            runtimeSchemaConfigRef !== ''
        ) {
            await setS3Data(
                runtimeSchemaConfigRef.replace(/&amp;/g, '&'),
                'runtimeSchemaConfigRef',
                doc
            );
            doc.runtimeSchemaConfig = JSON.parse(doc.runtimeSchemaConfigRef);
            rootStore.schemaConfig.resume(doc.runtimeSchemaConfig);
        }

        const ssieProps = await getSsiePropsObject({
            setOutputValueCallback,
            pdfBase64S3ReadRef,
            pdfBlocksS3ReadRef,
            pdfBlocksS3Ref,
            pdfType,
            pdfVersion,
            metadata,
            annotatorMetadata,
            primaryAnnotationS3ReadRef,
            secondaryAnnotationS3ReadRef,
        });

        createGTApp(doc, rootStore, ssieProps);
    })();
}
