import React, {Component} from 'react';
import {
    Button, Card,
    CardBody,
    CardFooter,
    CardHeader,
    CardText, Col,
    Collapse, Row,
} from 'reactstrap';
import {observer} from 'mobx-react'
import {SingleLayerSpan} from '../span/SingleLayerSpan.js'
import NERTagGroup from "./TaggingTagGroup.js";
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import TaggingSpanSelector from "./TaggingSpanSelector";
import {
    LightSelectTagColor, UnselectTagColor,
    ActiveSpanColor
} from "annotator/AnnotatorCommons";
import KeyPressStore from 'annotator/utils/KeyPressStore';
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import uuid from 'uuid';
import classNames from 'classnames';
import {ProcessedSpanColor, UnassignedSpanColor} from "../AnnotatorCommons";
import {UNASSIGNED_TAG} from "../store/TaggingStateStore";
import {ContextMenuSelector} from "../menu/ContextMenuSelector";
import {DoubleLayerSpan} from "../span/DoubleLayerSpan";
import {OverlayMode} from "../span/Constants";
import {PropertyEditorContainer} from "../properties/PropertyEditorContainer";
import {PropertyTargetType} from "../types/BasePropertyType";


export const TAGGING_ANNOTATOR_MENU_ID = "TaggingAnnotatorMenu";

const TaggingAnnotator = observer(
    class TaggingAnnotator extends Component {

        constructor(props) {
            super(props);
            this.selector = new TaggingSpanSelector();
            this.handleSelection = this.handleSelection.bind(this);
            this.getTagSpanColor = this.getTagSpanColor.bind(this);
            this.renderSpans = this.renderSpans.bind(this);
            this.annotatorTextAreaRef = React.createRef();
            this.state = {
                toggleLinkingPanel: false,
            }
        }


        handleSelection(event) {
            if (!KeyPressStore.altKeyPressed() && !this.props.store.showOverlay) {
                // by press alt key, we give user the ability to select text
                // if showing overlay, disable selection
                event.stopPropagation();
                let selection = window.getSelection();
                const selectedTextRange = this.selector.convertSelectionToTextRange(selection);
                console.log("selectedTextRange", selectedTextRange);
                if (selectedTextRange) {
                    const {start, end, rangeNodes} = selectedTextRange;
                    if (rangeNodes.length === 1) {
                        // for regular annotator, we only consider annotation that happens
                        // within a single span container as valid
                        // if user select multiple parent spans, we do nothing
                        this.props.store.insertSpan(start, end);
                        // console.log("inserted", span);
                    }
                }
                selection.removeAllRanges();
            }
        }

        onMouseUpEvent(event) {
            this.handleSelection(event);
        }

        getTagSpanColor(spanObj) {
            if (this.props.store.states.activeSpan) {
                if (spanObj.rangeEqual(this.props.store.states.activeSpan)) {
                    console.log("find activeSpan", spanObj);
                    return ActiveSpanColor;
                }
            }

            if (spanObj.tag === UNASSIGNED_TAG) {
                return UnassignedSpanColor;
            }

            if (spanObj.tag === this.props.store.tagGroupStore.selectedTag) {
                return LightSelectTagColor;
            } else {
                return UnselectTagColor;
            }
        }

        renderSpans() {
            let tagSpans = [];
            const readOnly = this.props.store.isReadOnly();
            const propertyDataStore = this.props.store.propertyDataStore;
            if (!this.props.store.showOverlay) {
                // this is the default settings, where overlay is disabled
                // this view is editable

                const spans = this.props.store.spans;
                console.log("renderSpans spans", spans.map(s => s.getExtent()));
                const checkSpanProperties = (span) => {
                    if (propertyDataStore !== undefined) {
                        return propertyDataStore.checkSpanContainsProperties(span);
                    }
                    return false;
                };

                // console.log(spans);

                for (let i = 0; i < spans.length; i++) {
                    let spanObj = spans[i];
                    let key = "span_" + i;
                    let tagColor = this.getTagSpanColor(spanObj);
                    let toggleSpanTrigger = () => {
                    };
                    let hoverOver = undefined;
                    if (!spanObj.attrs.blank) {
                        toggleSpanTrigger = () => {
                            console.log("toggleSpanTrigger");
                            this.props.store.toggleSpanSelection(spanObj);
                        };
                        hoverOver = <span className="HoverText">{spanObj.tag}</span>;
                    }
                    let textDecoration = "";
                    if (checkSpanProperties(spanObj)) {
                        // use a different color for property annotated span
                        if (tagColor !== ActiveSpanColor) {
                            tagColor = ProcessedSpanColor;
                        }
                        textDecoration = "underline";
                    }
                    let tagText = spanObj.tag;
                    if (spanObj.tag === UNASSIGNED_TAG) {
                        tagText = "";
                    }
                    let cssStyle = "HighlightSpan";
                    if (this.props.store.referenceAttached) {
                        cssStyle += " InnerHighlightSpan"
                    }
                    tagSpans.push(<SingleLayerSpan key={key}
                                                   text={spanObj.extent}
                                                   textDecoration={textDecoration}
                                                   tag={tagText}
                                                   color={tagColor}
                                                   start={spanObj.start}
                                                   end={spanObj.end}
                                                   blank={spanObj.attrs.blank}
                                                   selectable={false}
                                                   transparent={false}
                                                   store={this.props.store}
                                                   cssStyle={cssStyle}
                                                   clickTrigger={toggleSpanTrigger}
                                                   hoverOver={hoverOver}
                                                   contextMenuAction={() => {
                                                       this.props.store.setActiveSpan(spanObj);
                                                   }}
                                                   rightClickMenuId={readOnly ? null : TAGGING_ANNOTATOR_MENU_ID}/>)
                }

            } else {
                // overlay mode
                // this view is static
                const overlaySpans = this.props.store.overlaySpans;
                const document_properties = this.props.store.propertyDataStore.retrieveDocumentProperty();
                for (let i = 0; i < overlaySpans.length; i++) {
                    let spanObj = overlaySpans[i];
                    let property = this.props.store.propertyDataStore.retrieveSpanProperty(spanObj);
                    let key = "overlay_span_" + i;
                    tagSpans.push(<DoubleLayerSpan key={key}
                                                   text={spanObj.extent}
                                                   property={property}
                                                   document_properties={document_properties}
                                                   layers={spanObj.attrs.base}
                                                   tags={spanObj.attrs.tags}
                                                   mode={OverlayMode.OVERLAY}
                                                   disableHover={false}/>)
                }
            }
            return tagSpans;
        }

        componentDidUpdate() {
            if (this.props.globalViewStateStore) {
                this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
            }
        }

        componentDidMount() {
            if (this.props.globalViewStateStore) {
                this.props.globalViewStateStore.updateElementScroll(this.annotatorTextAreaRef.current);
            }
        }

        render() {
            const viewUpdateChange = this.props.store.viewUpdateChange;
            console.log(this.props.store.tagGroupStore.selectedTag);
            const readOnly = this.props.store.isReadOnly();
            let tagSpans = this.renderSpans();
            let annotatorTextStyle = ["AnnotatorText",];
            if (this.props.store.referenceAttached) {
                annotatorTextStyle = ["OverlayAnnotatorText",];
            }
            // NOTE: VERY IMPORTANT
            // Look at this answer:
            // https://stackoverflow.com/questions/29074690/react-why-components-constructor-is-called-only-once
            // If no key is given React will infer and reuse components
            // which we DON'T want, we want it to recreate as span changes.

            // use this key to force recreation of the AttributesEditor
            const forceRecreateAttributesEditorKey = uuid.v4();

            console.log("store.activeSpan", this.props.store.states.activeSpan);

            let annotator =
                <div id="TaggingAnnotatorBody">
                    <CardHeader>
                        <NERTagGroup
                            store={this.props.store}
                            selectedTag={this.props.store.tagGroupStore.selectedTag}
                            disabled={readOnly}
                        />
                    </CardHeader>
                    <div
                        className="AnnotatorTextView card-body"
                        ref={this.annotatorTextAreaRef}
                        onScroll={
                            (e) => {
                                if (this.props.globalViewStateStore !== undefined) {
                                    // in editor mode only
                                    this.props.globalViewStateStore.setScrollPercentageFromElement(e.target);
                                }
                            }
                        }>
                        <AnnotatorTextArea textDirection={this.props.textDirection}
                                           textAlignment={this.props.textAlignment}>
                            <CardText className={classNames(annotatorTextStyle)}
                                      markable="parent"
                                      id="tag-container"
                                      onMouseUp={this.onMouseUpEvent.bind(this)}>
                                {tagSpans}
                            </CardText>
                        </AnnotatorTextArea>
                    </div>
                    <CardFooter>
                        <Button key={"button_remove"}
                                className="FunctionalButton"
                                color="danger"
                                disabled={readOnly}
                                size="lg"
                                style={{
                                    "marginRight": "5px"
                                }}
                                onClick={() => {
                                    this.props.store.removeActiveSpan();
                                }}>
                            REMOVE
                        </Button>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            style={{
                                "marginRight": "5px"
                            }}
                            disabled={readOnly}
                            onClick={this.props.store.undoManager.undoEdit}>
                            UNDO
                        </Button>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            style={{
                                "marginRight": "5px"
                            }}
                            disabled={readOnly}
                            onClick={() => {
                                this.props.store.batchInsertActiveSpan(true)
                            }}>
                            AUTO TAG
                        </Button>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            style={{
                                "marginRight": "5px"
                            }}
                            disabled={readOnly}
                            onClick={() => {
                                this.props.store.batchInsertActiveSpan(true, true)
                            }}>
                            AUTO TAG TOKEN
                        </Button>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            style={{
                                "marginRight": "5px",
                                "display": this.props.store.referenceAttached ? "" : "none"
                            }}
                            onClick={() => {
                                this.props.store.toggleOverlay();
                            }}>
                            {!this.props.store.showOverlay ? "SHOW OVERLAY" : "HIDE OVERLAY"}
                        </Button>

                    </CardFooter>
                    <ContextMenuSelector menuId={TAGGING_ANNOTATOR_MENU_ID}
                                         numItem={4}
                                         tags={this.props.store.tagGroupStore.tags}
                                         items={this.props.store.tagGroupStore.tags}
                                         clickHandler={(tag) => {
                                             this.props.store.updateActiveTag(tag);
                                         }}
                                         untagHandler={() => {
                                             this.props.store.removeActiveSpan();
                                         }}
                                         />
                </div>;

            if (this.props.frameWrapper) {
                annotator = this.props.frameWrapper(annotator);
            }

            if (this.props.store.propertyDataStore === undefined) {
                // there is no property annotation requirement
                // just return the plain old singleton annotator
                return annotator;
            }

            return <div id="TaggingAnnotatorWithPropertiesBody">
                <Row>
                    <Col md="8">
                        {annotator}
                    </Col>

                    <Col sm="3" style={{ minWidth: "350px", maxWidth: "500px" }}>
                        <PropertyEditorContainer target={this.props.store.states.activeSpan}
                                                 targetType={PropertyTargetType.TextSpan}
                                                 store={this.props.store.propertyDataStore}
                                                 broadcast={(propertyDataStore) => {
                                                     // this callback doesn't need propertyDataStore
                                                     // instance, but we pass in, nevertheless
                                                     this.props.store.notifyDocumentStateChange();
                                                     this.props.store.notifyViewUpdateChange();
                                                 }}/>
                    </Col>
                </Row>
            </div>
        }
    }
);

export {TaggingAnnotator}