import React, { Component } from 'react';
import { MenuItem } from 'react-contextmenu';
import { ContextMenu } from 'react-contextmenu';
import { Table } from 'reactstrap';
import { observer } from 'mobx-react';

const ContextMenuSelector = observer(
    class ContextMenuSelector extends Component {
        constructor(props) {
            super(props);
            this.state = {
                hoverTagId: -1,
            };
        }

        render() {
            const menuId = this.props.menuId;
            const tags = this.props.tags;
            const footerTags = this.props.footerTags || [];
            const items = this.props.items;
            const footerItems = this.props.footerItems || [];
            const clickHandler = this.props.clickHandler;

            // BEGIN SSIE-related //
            const { untagHandler, untagText } = this.props.untag ? this.props.untag : { untagHandler: this.props.untagHandler, untagText: 'REMOVE' };
            const { autoTagHandler, autoTagText } = this.props.autoTag ? this.props.autoTag : { autoTagHandler: undefined, autoTagText: undefined };
            const { connectHandler, connectText } = this.props.connect ? this.props.connect : { connectHandler: undefined, connectText: undefined };
            const { replacePrimaryAnnotationHandler, replacePrimaryAnnotationText } = this.props.connect
                ? this.props.replacePrimaryAnnotation
                : {
                      replacePrimaryAnnotationHandler: undefined,
                      replacePrimaryAnnotationText: undefined,
                  };
            const NON_TAG_BUTTON_TEXT = [untagText, autoTagText, connectText].filter((x) => x !== undefined);
            const NON_TAG_BUTTONS = [untagHandler, autoTagHandler, connectHandler].filter((x) => x !== undefined);
            const SSIE_ARBITRATION_SPECIFIC_BUTTONS = [replacePrimaryAnnotationHandler, replacePrimaryAnnotationText].filter((x) => x !== undefined);
            // END SSIE-related //

            const numItem = Math.max(this.props.numItem, 1);
            const rows = [];
            if (SSIE_ARBITRATION_SPECIFIC_BUTTONS.length > 0) {
                rows.push(
                    <tr key={'0-replace'}>
                        <td
                            colSpan="100%"
                            style={{
                                background: this.state.hoverTagId === 'replace' ? 'gray' : 'inherit',
                            }}
                            onMouseOver={() => this.setState({ hoverTagId: 'replace' })}
                            onClick={replacePrimaryAnnotationHandler}
                        >
                            {replacePrimaryAnnotationText}
                        </td>
                    </tr>
                );
            } else {
                const numTagRows = Math.ceil(tags.length / numItem);
                const numTagItems = numTagRows * numItem;
                const totalNumItems = numTagItems + NON_TAG_BUTTONS.length;
                const numTotalRows = Math.ceil(totalNumItems / numItem);
                for (let row = 0; row < numTotalRows; row++) {
                    const cols = [];
                    const startIdx = numItem * row;
                    const endIdx = numItem * row + numItem;
                    let itemIndex = startIdx;
                    while (itemIndex < endIdx) {
                        if (itemIndex < numTagItems) {
                            const background = itemIndex === this.state.hoverTagId ? 'gray' : 'inherit';
                            const actualCol = itemIndex;
                            const tagContext = tags[actualCol];
                            const clickFunction = () => tagContext ? clickHandler(items[actualCol]) : {};
                            cols.push(
                                <td
                                    key={`${row}-${itemIndex}`}
                                    style={{ background }}
                                    onMouseOver={() => this.setState({ hoverTagId: actualCol })}
                                    onClick={clickFunction}
                                >
                                    {tagContext}
                                </td>
                            );
                        } else {
                            const nonTagBtnIndex = itemIndex - numTagItems;
                            if (nonTagBtnIndex < NON_TAG_BUTTONS.length) {
                                const tagContext = NON_TAG_BUTTON_TEXT[nonTagBtnIndex];
                                const unhoveredColor = tagContext === 'REMOVE' ? '#dc3545' : '#6c757d';
                                const background = itemIndex === this.state.hoverTagId ? 'gray' : unhoveredColor;
                                const actualCol = itemIndex;
                                const clickFunction = () => NON_TAG_BUTTONS[nonTagBtnIndex]();
                                cols.push(
                                    <td
                                        key={`${row}-${itemIndex}`}
                                        style={{ background }}
                                        onMouseOver={() => this.setState({ hoverTagId: actualCol })}
                                        onClick={clickFunction}
                                    >
                                        {tagContext}
                                    </td>
                                );
                            } else {
                                cols.push(<td key={`${row}-${itemIndex}`}>{``}</td>);
                            }
                        }
                        itemIndex += 1;
                    }
                    rows.push(<tr key={row}>{cols}</tr>);
                }

                for (let i = 0; i < footerTags.length; i++) {
                    const rowNum = numTotalRows + i;
                    const hoverTagId = `footer-${i}`;
                    const background = hoverTagId === this.state.hoverTagId ? 'gray' : 'inherit';
                    rows.push(
                        <tr key={rowNum}>
                            <td
                                colSpan="100%"
                                style={{ background: background }}
                                onMouseOver={() => this.setState({ hoverTagId: hoverTagId })}
                                onClick={() => {
                                    clickHandler(footerItems[i]);
                                }}
                            >
                                {footerTags[i]}
                            </td>
                        </tr>
                    );
                }
            }

            return (
                <ContextMenu id={menuId} hideOnLeave={true}>
                    <div>
                        <MenuItem>
                            <Table dark>
                                <tbody className="MenuItem">{rows}</tbody>
                            </Table>
                        </MenuItem>
                    </div>
                </ContextMenu>
            );
        }
    }
);

export { ContextMenuSelector };
