import React from 'react';
import { TaggingAnnotator } from '../../annotator/tagging/TaggingAnnotator';
import { SentimentAnnotator } from '../../annotator/sentiment/SentimentAnnotator';
import { TaggingArbitrationAnnotator } from '../../annotator/tagging/TaggingArbitrationAnnotator';
import { TaggingArbitrationStateStore } from '../../annotator/store/TaggingArbitrationStateStore';
import {
    ATTEMPT_REMOVE_LOCKED_SPAN,
    TaggingStateStore,
} from '../../annotator/store/TaggingStateStore';
import { SentimentStateStore } from '../../annotator/store/SentimentStateStore';
import { CoreferenceAnnotator } from '../../annotator/coreference/CoreferenceAnnotator';
import { CoreferenceStateStore } from '../../annotator/store/CoreferenceStateStore';
import { RelationStateStore } from '../../annotator/store/RelationStateStore';
import { RelationAnnotator } from '../../annotator/relation/RelationAnnotator';
import { EventsAnnotator } from '../../annotator/event/EventsAnnotator';
import { TaggingDiffViewerStateStore } from 'annotator/store/TaggingDiffViewerStateStore';
import { TaggingDiffViewer } from '../../annotator/tagging/TaggingDiffViewer';
import { AnnotatorFrame } from './AnnotatorFrame';
import { GlobalViewConfig } from '../config/ViewMetadataConfig';
import { EventsStateStore } from '../../annotator/store/EventsStateStore';
import {
    separateEventArgumentTriggerAnnotations,
    combineEventArgumentTriggerAnnotations,
    extractCorefAnnotationSpans,
    extractEventAnnotationSpans,
    extractAnnotationsV2AsSeparateDocument,
} from './AnnotationPreprocessing';
import { PropertyDataStore } from '../../annotator/store/PropertyDataStore';
import {
    LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER,
    LABELING_TASK_NAMED_ENTITY_RECOGNITION,
    UFSchema,
} from '../../annotator/core/UFSchema';
import { Table } from 'reactstrap';
import { EntityLinkingStateStore } from '../../annotator/store/EntityLinkingStateStore';
import { EntityLinkingAnnotator } from '../../annotator/linking/EntityLinkingAnnotator';
import SemiStructuredAnnotator from '../../annotator/tagging/pdf/components/SemiStructuredAnnotator/SemiStructuredAnnotator'

const commonSyncHandler = (rootStore, beforeUpdateCallback) => {
    return (annotations) => {
        console.log('commonSyncHandler read new annotations -> ', annotations);
        if (beforeUpdateCallback !== undefined) {
            // preprocessCallback add additional wrapping logic
            // prior to the update
            annotations = beforeUpdateCallback(annotations);
        }
        rootStore.collectionStore.updateActiveDocumentState(annotations);
    };
};

const withAnnotatorFrame = ({
    annotator,
    rootStore,
    prev = undefined,
    next = undefined,
    metadata = undefined,
    readOnly = false,
    taskName = undefined,
    propertyDataStore = undefined,
    footer = true,
}) => {
    if (prev === undefined) {
        prev = rootStore.prevDocument;
    }
    if (next === undefined) {
        next = rootStore.nextDocument;
    }
    const rejectHandler = () => {
        const doc = rootStore.collectionStore.getActiveDocument();
        // get reject status
        const metadata = doc['metadata'];
        let rejectedStatus = metadata['rejected'] === true;
        rootStore.collectionStore.updateActiveDocumentMetadata({
            rejected: !rejectedStatus,
        });
    };
    const commentHandler = (comment) => {
        // update metadata comment
        rootStore.collectionStore.updateActiveDocumentMetadata({
            comment: comment,
        });
    };
    let propertyHandler = undefined;
    if (propertyDataStore !== undefined) {
        propertyHandler = (store) => {
            // NOTED: store is a PropertyDataStore instance
            const documentPropertyAnnotations = store.getDocumentPropertyAnnotations();
            if (documentPropertyAnnotations) {
                rootStore.collectionStore.updateActiveDocumentMetadata({
                    properties: documentPropertyAnnotations,
                });
            } else {
                rootStore.collectionStore.updateActiveDocumentMetadata({
                    properties: {},
                });
            }
        };
    }
    const currentRejectedStatus = metadata['rejected'] === true;
    const currentComment = metadata['comment'];
    // console.log("currentComment: " + currentComment);
    return (
        <AnnotatorFrame
            index={rootStore.collectionStore.currentDocumentIdx}
            total={rootStore.collectionStore.totalDocuments}
            rootStore={rootStore}
            rejectStatus={currentRejectedStatus}
            prev={prev}
            next={next}
            reject={rejectHandler}
            comment={currentComment}
            commentHandler={commentHandler}
            readOnly={readOnly}
            taskName={taskName}
            footer={footer}
            propertyHandler={propertyHandler}
            propertyDataStore={propertyDataStore}
        >
            {annotator}
        </AnnotatorFrame>
    );
};

const LTR = 'ltr';
const RTL = 'rtl';

const getTextDisplayOptions = (documentObject) => {
    if (documentObject !== undefined && documentObject['metadata'] !== undefined) {
        const metadata = documentObject['metadata'];
        if (metadata.direction !== undefined) {
            if (metadata.direction === RTL) {
                return { direction: RTL, alignment: 'right' };
            }
        }
    }
    return { direction: LTR, alignment: 'left' };
};

class FullEditorAnnotatorFactory {
    static TaggingAnnotatorFactory(
        rootStore,
        currentDocument,
        namespace,
        tagSet,
        attributesConfig,
        beforeUpdateCallback,
        lockedSpanSet,
        notificationHandlers,
        referenceAnnotations,
        displayName,
        propertyDataStore,
        labelingTask
    ) {
        const documentStore = new TaggingStateStore(
            currentDocument,
            namespace,
            tagSet,
            true,
            attributesConfig,
            lockedSpanSet,
            referenceAnnotations,
            propertyDataStore,
            labelingTask
        );
        if (notificationHandlers) {
            for (const [key, callback] of Object.entries(notificationHandlers)) {
                documentStore.registerNotification(key, callback);
            }
        }
        const handler = commonSyncHandler(rootStore, beforeUpdateCallback);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        if (propertyDataStore !== undefined) {
            const syncDocumentPropertyHandler = (_, __) => {
                // NOTED: store is a PropertyDataStore instance
                const documentPropertyAnnotations =
                    propertyDataStore.getDocumentPropertyAnnotations();
                if (documentPropertyAnnotations) {
                    rootStore.collectionStore.updateActiveDocumentMetadata({
                        properties: documentPropertyAnnotations,
                    });
                }
            };
            documentStore.onDocumentStateChange(syncDocumentPropertyHandler);
        }

        const checkThenNext = () => {
            const moveNext = () => {
                rootStore.nextDocument();
            };
            if (propertyDataStore !== undefined) {
                const violations = documentStore.checkPropertyIntegrity();
                if (violations.length === 0) {
                    // no violations
                    moveNext();
                } else {
                    const violationRow = [];
                    violations.forEach((v) => {
                        const { target, cause } = v;
                        violationRow.push(
                            <tr>
                                <td>
                                    {typeof target === 'string' ? target : JSON.stringify(target)}
                                </td>
                                <td>{cause}</td>
                            </tr>
                        );
                    });
                    const violationTable = (
                        <div>
                            <Table striped>
                                <thead>
                                    <tr>
                                        <th>LOCATION</th>
                                        <th>CAUSE</th>
                                    </tr>
                                </thead>
                                <tbody>{violationRow}</tbody>
                            </Table>
                        </div>
                    );
                    rootStore.sendAlert(
                        'Detected Property Violations',
                        violationTable,
                        () => {},
                        false
                    );
                }
            }
        };

        const frameWrapper = (annotator) => {
            return withAnnotatorFrame({
                annotator: annotator,
                rootStore: rootStore,
                metadata: currentDocument['metadata'],
                next: checkThenNext,
                readOnly: documentStore.isReadOnly(),
                taskName: displayName,
                propertyDataStore: propertyDataStore,
            });
        };

        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const annotator = (
            <TaggingAnnotator
                store={documentStore}
                textDirection={direction}
                textAlignment={alignment}
                globalViewStateStore={rootStore.globalViewStateStore}
                frameWrapper={frameWrapper}
                usingGroundTruth={rootStore.getUsingGroundTruth()}
            />
        );

        return annotator;
    }

    static SentimentAnnotatorFactory(rootStore, currentDocument) {
        const documentStore = new SentimentStateStore(currentDocument);
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const checkThenNext = () => {
            // check if the document has been rejected or not
            const metadata = rootStore.collectionStore.getActiveDocument()['metadata'];
            const rejectStatus = metadata['rejected'] === true;
            if (!documentStore.isAnnotationComplete() && !rejectStatus) {
                rootStore.sendAlert(
                    <span>
                        <b>Current Document Unfinished</b>
                    </span>,
                    <div>
                        <span>Your current document has unlabelled parts, please verify.</span>
                        <span>
                            {' '}
                            You can't proceed to next document if current one is incomplete
                        </span>
                    </div>
                );
            } else {
                rootStore.nextDocument();
            }
        };
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const annotator = (
            <SentimentAnnotator
                store={documentStore}
                textDirection={direction}
                textAlignment={alignment}
            />
        );

        return withAnnotatorFrame({
            annotator: annotator,
            rootStore: rootStore,
            metadata: currentDocument['metadata'],
            next: checkThenNext,
        });
    }

    static TaggingArbitrationAnnotatorFactory(
        rootStore,
        currentDocument,
        namespace,
        tagSet,
        attributesConfig
    ) {
        const documentStore = new TaggingArbitrationStateStore(
            currentDocument,
            namespace,
            tagSet,
            true,
            attributesConfig
        );
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const annotator = (
            <TaggingArbitrationAnnotator
                store={documentStore}
                textDirection={direction}
                textAlignment={alignment}
            />
        );
        return withAnnotatorFrame({
            annotator: annotator,
            rootStore: rootStore,
            metadata: currentDocument['metadata'],
        });
    }

    static CoreferenceAnnotatorFactory(
        rootStore,
        currentDocument,
        viewConfig,
        allowTagging,
        layout,
        taskName
    ) {
        // for coreference view we now need to define:
        // 1. Its own namespace
        // 2. The source entity namespace where the entities are from
        const namespace = viewConfig.namespace;
        const entityNamespace = viewConfig.entityNamespace;
        const documentStore = new CoreferenceStateStore(
            currentDocument,
            namespace,
            entityNamespace,
            allowTagging
        );
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const frameWrapper = (annotator) => {
            return withAnnotatorFrame({
                annotator: annotator,
                rootStore: rootStore,
                metadata: currentDocument['metadata'],
                readOnly: documentStore.isReadOnly(),
                taskName: taskName,
            });
        };
        return (
            <CoreferenceAnnotator
                store={documentStore}
                alertStore={rootStore.alertStore}
                textDirection={direction}
                textAlignment={alignment}
                frameWrapper={frameWrapper}
                globalViewStateStore={rootStore.globalViewStateStore}
                layout={layout}
            />
        );
    }

    static RelationAnnotatorFactory(rootStore, currentDocument, relations, viewConfig) {
        const namespace = viewConfig.namespace;
        const entityNamespace = viewConfig.entityNamespace;
        const documentStore = new RelationStateStore(
            currentDocument,
            relations,
            namespace,
            entityNamespace
        );
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const annotator = (
            <RelationAnnotator
                store={documentStore}
                textDirection={direction}
                textAlignment={alignment}
            />
        );
        return withAnnotatorFrame({
            annotator: annotator,
            rootStore: rootStore,
            metadata: currentDocument['metadata'],
        });
    }

    static TaggingDiffViewerFactory(rootStore, currentDocument) {
        // this is a static view
        const documentStore = new TaggingDiffViewerStateStore(currentDocument);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const annotator = (
            <TaggingDiffViewer
                store={documentStore}
                textDirection={direction}
                textAlignment={alignment}
            />
        );

        return withAnnotatorFrame({
            annotator: annotator,
            rootStore: rootStore,
            metadata: currentDocument['metadata'],
        });
    }

    static EventsAnnotatorFactory(
        rootStore,
        currentDocument,
        eventDefinitionsConfig,
        eventPropertiesConfig,
        displayName
    ) {
        const documentStore = new EventsStateStore(
            currentDocument,
            GlobalViewConfig.EVENTS.namespace,
            eventDefinitionsConfig,
            eventPropertiesConfig
        );
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const readOnly = documentStore.isReadOnly();
        const frameWrapper = (annotator) => {
            return withAnnotatorFrame({
                annotator: annotator,
                rootStore: rootStore,
                metadata: currentDocument['metadata'],
                readOnly: readOnly,
                taskName: displayName,
            });
        };
        return (
            <EventsAnnotator
                store={documentStore}
                frameWrapper={frameWrapper}
                globalViewStateStore={rootStore.globalViewStateStore}
                textDirection={direction}
                textAlignment={alignment}
            />
        );
    }

    static EntityLinkingFactory(rootStore, currentDocument) {
        const documentStore = new EntityLinkingStateStore(currentDocument);
        const handler = commonSyncHandler(rootStore);
        documentStore.onDocumentStateChange(handler);
        // always sync annotations back to collection after loading
        // since some validation and rewriting could happen during initDocumentState method
        handler(documentStore.annotations);
        const { direction, alignment } = getTextDisplayOptions(currentDocument);
        const frameWrapper = (annotator) => {
            return withAnnotatorFrame({
                annotator: annotator,
                rootStore: rootStore,
                metadata: currentDocument['metadata'],
                footer: false,
            });
        };

        return <EntityLinkingAnnotator store={documentStore} frameWrapper={frameWrapper} />;
    }

    static lockedSpanRemoveAlertHandler(rootStore) {
        return (spanWithLockReasons) => {
            rootStore.alertStore.sendAlert(
                'Attempting To Remove Locked Span',
                <React.Fragment>
                    <p>
                        Span "<b>{spanWithLockReasons.extent}</b>" has been referenced/used in the
                        following pages
                    </p>
                    {Object.keys(spanWithLockReasons.lockReasons).map((page, pageIndex) => (
                        <div key={pageIndex}>
                            <div>
                                <strong>{page}</strong>
                            </div>
                            <ul>
                                {spanWithLockReasons.lockReasons[page].map(
                                    (reason, reasonIndex) => (
                                        <li key={reasonIndex}>{reason}</li>
                                    )
                                )}
                            </ul>
                        </div>
                    ))}
                    <p>
                        To remove this span from the current view, first remove the above references
                    </p>
                </React.Fragment>
            );
        };
    }

    static SemiStructuredAnnotatorFactory(
        rootStore,
        currentDocument,
        namespace,
        tagSet,
        attributesConfig,
        lockedSpanSet,
        referenceAnnotations,
        propertyDataStore
    ) {
        const documentStore = new TaggingStateStore(
            currentDocument,
            namespace,
            tagSet,
            true,
            attributesConfig,
            lockedSpanSet,
            referenceAnnotations,
            propertyDataStore
        );

        const frameWrapper = (annotator) => {
            return withAnnotatorFrame({
                annotator: annotator,
                rootStore: rootStore,
                metadata: {}
            });
        };

        return (
            <SemiStructuredAnnotator
                store={documentStore}
                frameWrapper={frameWrapper}
                usingGroundTruth={rootStore.getUsingGroundTruth()}
            />
        );
    }

    static createAnnotator(rootStore, currentDocument) {
        let annotator;
        const currentTask = rootStore.editorStateStore.currentTask;
        const schemaConfig = rootStore.schemaConfig;
        const namespace = currentTask.namespace;
        // documentV2 for 'annotations_v2' namespace
        const documentV2 = extractAnnotationsV2AsSeparateDocument(currentDocument);
        let v2Annotations = documentV2[UFSchema.ANNOTATIONS][namespace];

        let eventsLockedSpans = undefined;
        let allLockedSpans = undefined;
        let targetProperties;
        if (schemaConfig.propertiesConfig.getTaskConfig('named_entity') !== undefined) {
            targetProperties =
                schemaConfig.propertiesConfig.getTaskConfig('named_entity').targetProperties;
        }
        let propertyDataStore = undefined;
        if (targetProperties !== undefined) {
            propertyDataStore = new PropertyDataStore(targetProperties);
            if (currentDocument['metadata']['properties'] !== undefined) {
                // to initialize document properties
                const documentProperties = currentDocument['metadata']['properties'];
                const instanceStore = propertyDataStore.getDocumentInstanceStore();
                instanceStore.load(documentProperties);
            }
        }

        let handlers = {};
        switch (currentTask) {
            case GlobalViewConfig.NER:
                annotator = this.TaggingAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.NERTagSet,
                    schemaConfig.LinkingAttributeSet,
                    undefined,
                    undefined,
                    undefined,
                    v2Annotations,
                    currentTask.displayName,
                    propertyDataStore,
                    LABELING_TASK_NAMED_ENTITY_RECOGNITION
                );
                break;
            case GlobalViewConfig.KPE:
                annotator = this.TaggingAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.KPETagSet
                );
                break;
            case GlobalViewConfig.NER_ARBITRATION:
                annotator = this.TaggingArbitrationAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.NERTagSet,
                    schemaConfig.LinkingAttributeSet
                );
                break;
            case GlobalViewConfig.KPE_ARBITRATION:
                annotator = this.TaggingArbitrationAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.KPETagSet
                );
                break;
            case GlobalViewConfig.SENTIMENT:
                annotator = this.SentimentAnnotatorFactory(rootStore, currentDocument);
                break;
            case GlobalViewConfig.COREFERENCE:
                annotator = this.CoreferenceAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    GlobalViewConfig.COREFERENCE,
                    false,
                    currentTask.displayName
                );
                break;
            case GlobalViewConfig.RELATION_EXTRACTION:
                annotator = this.RelationAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    schemaConfig.RelationSet,
                    GlobalViewConfig.RELATION_EXTRACTION
                );
                break;
            case GlobalViewConfig.TAGGING_DIFF_VIEWER:
                annotator = this.TaggingDiffViewerFactory(rootStore, currentDocument);
                break;
            case GlobalViewConfig.EVENT_ARGUMENTS:
                eventsLockedSpans = extractEventAnnotationSpans(currentDocument);
                allLockedSpans = extractCorefAnnotationSpans(currentDocument, eventsLockedSpans);
                handlers[ATTEMPT_REMOVE_LOCKED_SPAN] = this.lockedSpanRemoveAlertHandler(rootStore);
                annotator = this.TaggingAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.EventArgumentsTagSet,
                    undefined,
                    undefined,
                    allLockedSpans,
                    handlers,
                    v2Annotations,
                    currentTask.displayName,
                    propertyDataStore,
                    LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER
                );
                break;
            case GlobalViewConfig.EVENT_TRIGGERS:
                eventsLockedSpans = extractEventAnnotationSpans(currentDocument);
                allLockedSpans = extractCorefAnnotationSpans(currentDocument, eventsLockedSpans);
                handlers[ATTEMPT_REMOVE_LOCKED_SPAN] = this.lockedSpanRemoveAlertHandler(rootStore);
                annotator = this.TaggingAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.EventTriggersTagSet,
                    undefined,
                    undefined,
                    allLockedSpans,
                    handlers,
                    v2Annotations,
                    currentTask.displayName,
                    propertyDataStore,
                    LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER
                );
                break;
            case GlobalViewConfig.EVENT_ARGUMENTS_TRIGGERS:
                // combine trigger and arguments annotations
                combineEventArgumentTriggerAnnotations(currentDocument);
                combineEventArgumentTriggerAnnotations(documentV2);
                v2Annotations =
                    documentV2[UFSchema.ANNOTATIONS][
                        GlobalViewConfig.EVENT_ARGUMENTS_TRIGGERS.namespace
                    ];
                eventsLockedSpans = extractEventAnnotationSpans(currentDocument);
                allLockedSpans = extractCorefAnnotationSpans(currentDocument, eventsLockedSpans);
                handlers[ATTEMPT_REMOVE_LOCKED_SPAN] = this.lockedSpanRemoveAlertHandler(rootStore);
                // combine the tag set
                const combinedTagSet = [];
                schemaConfig.EventArgumentsTagSet.forEach((t) => combinedTagSet.push(t));
                schemaConfig.EventTriggersTagSet.forEach((t) => combinedTagSet.push(t));
                const beforeUpdateCallback = (annotations) => {
                    return separateEventArgumentTriggerAnnotations(annotations, schemaConfig);
                };
                annotator = this.TaggingAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    combinedTagSet,
                    undefined,
                    beforeUpdateCallback,
                    allLockedSpans,
                    handlers,
                    v2Annotations,
                    currentTask.displayName,
                    propertyDataStore,
                    LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER
                );
                break;
            case GlobalViewConfig.EVENT_RELATIONS:
                annotator = this.RelationAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    schemaConfig.EventRelationsSet,
                    GlobalViewConfig.EVENT_RELATIONS
                );
                break;
            case GlobalViewConfig.EVENT_ARGUMENTS_GROUPS:
                annotator = this.CoreferenceAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    GlobalViewConfig.EVENT_ARGUMENTS_GROUPS,
                    false,
                    'two_column',
                    currentTask.displayName
                );
                break;
            case GlobalViewConfig.EVENT_TRIGGERS_GROUPS:
                annotator = this.CoreferenceAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    GlobalViewConfig.EVENT_TRIGGERS_GROUPS,
                    false,
                    'two_column',
                    currentTask.displayName
                );
                break;
            case GlobalViewConfig.EVENTS:
                annotator = this.EventsAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    schemaConfig.EventDefinitionsSet,
                    schemaConfig.EventPropertiesSet,
                    currentTask.displayName
                );
                break;
            case GlobalViewConfig.ENTITY_LINKING:
                console.log(currentDocument);
                annotator = this.EntityLinkingFactory(rootStore, currentDocument);
                break;
            case GlobalViewConfig.DEVELOPMENT_MODE:
                console.log(currentDocument);
                annotator = this.EntityLinkingFactory(rootStore, currentDocument);
                break;
            case GlobalViewConfig.SSIE:
                annotator = this.SemiStructuredAnnotatorFactory(
                    rootStore,
                    currentDocument,
                    namespace,
                    schemaConfig.NERTagSet,
                    schemaConfig.LinkingAttributeSet,
                    undefined,
                    v2Annotations,
                    propertyDataStore
                );
                break;
        }
        console.log(currentTask);
        console.log(annotator);
        return annotator;
    }
}

export { FullEditorAnnotatorFactory, getTextDisplayOptions };
