import React from 'react';
import './Customization.css'
import 'annotator/AnnotatorButton.css'
import {Button, Card, CardBody, CardFooter, CardHeader, Table} from "reactstrap";
import {observer} from "mobx-react";
import {TableRowSelectionStore} from "../table/TableRowSelectionStore";
import {TextSubmitBar} from "./TextSubmitBar";
import {TableRowSelector} from "../table/TableRowSelector";
import uuid from 'uuid';

const EventPropertyCustomizationPanel = observer(
    class EventPropertyCustomizationPanel extends React.Component {
        constructor(props) {
            super(props);
            this.tableSelection = new TableRowSelectionStore();
            this.submitNewTag = this.submitNewTag.bind(this);
        }

        submitNewTag(tagName, namespace) {
            if (tagName.toUpperCase() === "ANY") {
                this.props.store.alertStore.sendAlert("Invalid Entity Name",
                    "ANY is a reserved internal type name, please use other names")
            } else {
                this.props.store.schemaConfig.addNERTag(tagName, namespace);
            }
        }

        render() {
            const rootStore = this.props.store;
            const schemaConfig = rootStore.schemaConfig;
            // trigger when schemaConfig changes
            const subscribeSchemaConfigUpdate = schemaConfig.configUpdated;

            let tableRows = [];
            schemaConfig.EventPropertiesSet.propertyList.forEach((property) => {
                const {name, values} = property;
                const valueColumn = [];
                // each value is a tag
                values.forEach((v) => {
                    let val = v;
                    valueColumn.push(<div style={{"marginBottom": "15px"}}>
                        <span style={{
                            "borderStyle": "solid",
                            "borderWidth": "1px",
                            "borderRadius": "5px",
                            "padding": "5px"
                        }}> {v}
                            <Button color="danger"
                                    size="sm"
                                    style={{
                                        "fontSize": "8px",
                                        "marginLeft": "5px",
                                        "padding": "2px",
                                        "fontWeight": "bold"
                                    }}
                                    onClick={() => {
                                        // bind a special handler to each (property val) pair
                                        schemaConfig.removeEventPropertyValue(name, val);
                                    }}>X</Button>
                            </span>
                    </div>);
                });
                const addValueColumn = <TextSubmitBar width="200px"
                                                      size="14px"
                                                      onSubmit={
                                                          (newValue) => (
                                                              schemaConfig.addEventPropertyValue(name, newValue))
                                                      }/>;
                tableRows.push([name, <div>{valueColumn}</div>, addValueColumn]);
            });

            // Give a new key to refresh TextSubmitBar after click
            const forceReconstructTextSubmitBarKey = uuid.v4();
            this.tableSelection.bindData(tableRows.map((r) => r[0]));

            return <div id="EventPropertyCustomizationPanel">
                <Card>
                    <CardHeader>
                        <TextSubmitBar
                            key={forceReconstructTextSubmitBarKey}
                            default="NEW PROPERTY NAME"
                            onSubmit={
                                (newPropertyName) => {
                                    schemaConfig.addEventProperty(newPropertyName);
                                }}/>
                    </CardHeader>
                    <CardBody>
                        <TableRowSelector store={this.tableSelection}
                                          headers={["Property", "Values", "Add"]}
                                          rows={tableRows}/>
                    </CardBody>
                    <CardFooter>
                        <Button className="TagButton"
                                color="danger"
                                onClick={
                                    () => {
                                        rootStore.alertStore.sendAlert("Warning: Are You Sure to Remove Those Properties?",
                                            "Click Confirm to proceed.",
                                            () => {
                                                const selectedRows = this.tableSelection.selectedRows;
                                                console.log("properties", selectedRows);
                                                selectedRows.forEach((name) => {
                                                    schemaConfig.removeEventProperty(name);
                                                })
                                            },
                                            true)
                                    }}>
                            REMOVE
                        </Button>
                    </CardFooter>
                </Card>
            </div>
        }
    }
);

export {EventPropertyCustomizationPanel}