import {observable} from 'mobx';
import {DocumentStateStore} from "./DocumentStateStore";
import {Namespaces} from "../core/Namespaces";
import {UFDocument, UFSchema} from "../core/UFSchema";
import {SpanContainer, SpanFactory} from "../core/Spans";
import {EntityLinkingSpanInstanceStore} from "./EntityLinkingSpanInstanceStore";
import {DatabaseCandidateRecordType, EntityLinkingSpanType} from "../types/EntityLinkingTypes";
import RangeTree from "../core/RangeTree";
import {DatabaseCandidateStore} from "./DatabaseCandidateStore";
import {BaseSpanType} from "../types/CoreTypes";
import {get} from 'lodash';

class EntityLinkingStateStore extends DocumentStateStore {
    public rawText: string;
    public spanFactory: SpanFactory;
    public spanTree: any; // unfortunately the current RBTree lib we are using have no type defs
    public instanceMap = new Map<string, EntityLinkingSpanInstanceStore>();
    public records: DatabaseCandidateRecordType[] = [];
    @observable
    public dbCandidateStore = DatabaseCandidateStore.getInstance();


    constructor(documentObject: UFDocument, public schemaKey: string = Namespaces.ENTITY_LINKING) {
        super(documentObject);
        this.rawText = documentObject.raw_text;
        this.spanFactory = new SpanFactory(this.rawText);
        this.updateSpanAnnotations = this.registerAction(this.updateSpanAnnotations.bind(this));
        this.initStatesFromDocument(documentObject);
    }

    initStatesFromDocument(documentObject: UFDocument) {
        super.initStatesFromDocument(documentObject);
        const annotations = documentObject.annotations;

        let spanObjects: SpanContainer[] = [];
        const runes = this.splitter.splitGraphemes(documentObject.raw_text);
        if (get(annotations, this.schemaKey, undefined)) {
            const mentions = annotations[this.schemaKey];
            mentions.forEach((span: any) => {
                let s1 = this.spanFactory.copySpan(span);
                super.expandSpanToHaveMultibyteOffset(s1, runes);
                spanObjects.push(s1);
                // now registered into instanceMap
                // keyed by mentionId
                const instance = new EntityLinkingSpanInstanceStore(span,
                    this.documentObject[UFSchema.METADATA]["document_id"]);
                this.instanceMap.set(instance.mentionId,
                    instance);
            })
        }

        this.spanTree = RangeTree.createRangeTree(spanObjects);
    }

    getSpanAnnotations(target: string | BaseSpanType) {
        // target can be either a mentionId or span
        let mentionId: string;
        if (typeof target === 'string') {
            mentionId = target;
        } else {
            mentionId = this.getMentionId(target);
        }

        if (this.instanceMap.has(mentionId)) {
            const instance = this.instanceMap.get(mentionId);
            return instance!.annotations;
        }

        return;
    };

    updateSpanAnnotations(target: string | BaseSpanType, data: {
        gold_id?: string,
        no_related?: boolean
        not_sure?: boolean,
    }) {
        // target can be either a mentionId or span
        let mentionId: string;
        if (typeof target === 'string') {
            mentionId = target;
        } else {
            mentionId = this.getMentionId(target);
        }

        if (this.instanceMap.has(mentionId)) {
            const instance = this.instanceMap.get(mentionId);
            instance!.update(data);
        }
    }

    getMentionId(span: BaseSpanType) {
        const {start, end, tag} = span;
        return `${start}_${end}_${this.documentObject[UFSchema.METADATA]["document_id"]}_${tag}`;
    }

    getSpanCandidateRecords(target: string | BaseSpanType): DatabaseCandidateRecordType[] {
        const candidateIds: string[] = this.getSpanDatabaseCandidateIds(target);
        return this.dbCandidateStore.retrieveCandidates();
    }

    getSpanDatabaseCandidateIds(target: string | BaseSpanType): string[] {
        let mentionId: string;
        if (typeof target === 'string') {
            mentionId = target;
        } else {
            mentionId = this.getMentionId(target);
        }
        if (this.instanceMap.has(mentionId)) {
            return this.instanceMap.get(mentionId)!.candidates;
        }
        return [];
    }

    convertStatesToAnnotations() {
        let annotations = {};
        let entityLinkingAnnotations: EntityLinkingSpanType[] = [];
        this.instanceMap.forEach((val, _) => {
            entityLinkingAnnotations.push(val.annotations);
        });
        entityLinkingAnnotations.sort((a, b) => a.start - b.start);
        // @ts-ignore
        annotations[this.schemaKey] = entityLinkingAnnotations;
        return annotations;
    }

    get spans() {
        const tree = this.spanTree;
        const rawText = this.rawText;
        let ranges = RangeTree.retrieveAllNonOverlappingRanges(tree, rawText.length);
        let spans: SpanContainer[] = [];
        ranges.forEach(r => {
            let [start, end, value] = r;
            let tag = undefined;
            let attrs = undefined;
            if (value) {
                tag = value.tag;
                attrs = value.attrs;
            }
            spans.push(this.spanFactory.createSpan(start, end, tag, attrs));
        });
        return spans;
    }
}

export {EntityLinkingStateStore}