import React from 'react';

type PropType = {
    selected: boolean,
    onSelect: any,
}

class SelectablePropertyConfigCell extends React.Component<PropType> {

    constructor(props: PropType) {
        super(props);
    }

    render() {
        const {selected, onSelect} = this.props;
        const checkbox = <input type="checkbox"
                                checked={selected}
                                onChange={onSelect}/>;

        return <React.Fragment>
            {this.props.children}
            <td style={{"width": "20px"}}>{checkbox}</td>
        </React.Fragment>
    }
}

export {SelectablePropertyConfigCell}