import React, {Component} from 'react';
import {Button, CardBody, CardFooter, CardHeader, CardText, Collapse} from 'reactstrap';
import {observer} from 'mobx-react'
import {SingleLayerSpan} from '../span/SingleLayerSpan.js'
import NERTagGroup from "./TaggingTagGroup.js";
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import TaggingSpanSelector from "./TaggingSpanSelector";
import {Role} from "annotator/store/TaggingArbitrationStateStore";
import {
    ActiveSpanColor,
    UnselectTagColor, LightSelectTagColor
} from "annotator/AnnotatorCommons";
import KeyPressStore from 'annotator/utils/KeyPressStore';
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import {AttributesEditor} from "./AttributesEditor";
import uuid from "uuid";


const TaggingArbitrationAnnotator = observer(
    class TaggingArbitrationAnnotator extends Component {

        constructor(props) {
            super(props);
            this.selector = new TaggingSpanSelector();
            this.handleSelection = this.handleSelection.bind(this);
        }

        handleSelection(event) {
            if (!KeyPressStore.altKeyPressed()) {
                // by press alt key, we give user the ability to select text
                event.stopPropagation();
                let selection = window.getSelection();
                const selectedTextRange = this.selector.convertSelectionToTextRange(selection);
                if (selectedTextRange) {
                    const {start, end, _} = selectedTextRange;
                    this.props.store.insertSpan(start, end);
                }
                selection.removeAllRanges();
            }
        }

        getTagSpanColor(spanObj) {
            if (this.props.store.states.activeSpan) {
                if (spanObj.rangeEqual(this.props.store.states.activeSpan)) {
                    console.log("find activeSpan", spanObj);
                    return ActiveSpanColor;
                }
            }
            if (spanObj.attrs.role === Role.Arbitrator) {
                return LightSelectTagColor;
            } else {
                return UnselectTagColor;
            }
        }

        onMouseUpEvent(event) {
            if (!this.props.store.states.focusMode) {
                //focusMode is the attributes editing mode
                this.handleSelection(event);
            }
        }


        render() {
            // subscribe to this;
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const spans = this.props.store.spans;
            var tagSpans = [];

            const toggleOriginalSpan = (args) => {
                console.log("toggleOriginalSpan trigger");
                const {start, end} = args;
                return this.props.store.toggleOriginalSpan(start, end)
            };

            for (let i = 0; i < spans.length; i++) {
                let spanObj = spans[i];
                let key = "span_" + i;
                let tagColor = this.getTagSpanColor(spanObj);
                let transparent = false;
                if (spanObj["attrs"]["removed"]) {
                    tagColor = "rgba(234,137,135, 0.6)";
                    transparent = true;
                }
                // depends on role of annotator, spans has different behaviors
                let clickTrigger = (args) => {
                };
                if (spanObj["attrs"]["role"] === Role.Annotator) {
                    clickTrigger = toggleOriginalSpan;
                } else if (spanObj["attrs"]["role"] === Role.Arbitrator) {
                    clickTrigger = () => {
                        return this.props.store.toggleActiveSpan(spanObj);
                    };
                }
                let hoverOver = undefined;
                if (!spanObj.attrs.blank) {
                    hoverOver = <span className="HoverText">{spanObj.tag}</span>
                }
                let cssStyle = !spanObj["attrs"]["removed"] ? "HighlightSpan" : "BackgroundHighlightSpan";
                tagSpans.push(<SingleLayerSpan key={key}
                                               text={spanObj.extent}
                                               tag={spanObj.tag}
                                               color={tagColor}
                                               start={spanObj.start}
                                               end={spanObj.end}
                                               blank={spanObj.attrs["blank"]}
                                               selectable={spanObj.attrs["selectable"]}
                                               transparent={transparent}
                                               store={this.props.store}
                                               cssStyle={cssStyle}
                                               clickTrigger={clickTrigger}
                                               hoverOver={hoverOver}/>);
            }

            const switchButtonText = this.props.store.states.focusMode === true ? "ATTRIBUTES MODE" : "TAGGING MODE";

            let warningButton = "";
            if (this.props.store.states.focusMode) {
                warningButton = <Button className="FunctionalButton"
                                        size="lg"
                                        disabled
                                        color="danger"
                                        style={{
                                            "marginLeft": "5px",
                                            "maxWidth": "350px",
                                        }}>
                    TAGGING NOW DISABLED
                </Button>
            }

            const forceRecreateAttributesEditorKey = uuid.v4();

            return (
                <div id="TaggingArbitrationAnnotatorBody">
                    <CardHeader>
                        <NERTagGroup store={this.props.store}
                                     selectedTag={this.props.store.tagGroupStore.selectedTag}/>
                    </CardHeader>
                    <CardBody className="AnnotatorTextView">
                        <AnnotatorTextArea textDirection={this.props.textDirection}
                                           textAlignment={this.props.textAlignment}>
                            <CardText className="AnnotatorText"
                                      markable="parent"
                                      id="tag-container"
                                      onMouseUp={this.onMouseUpEvent.bind(this)}>
                                {tagSpans}
                            </CardText>
                        </AnnotatorTextArea>
                    </CardBody>
                    <CardFooter>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            style={{"marginRight": "5px"}}
                            onClick={this.props.store.undoManager.undoEdit}>
                            UNDO
                        </Button>
                        <Button
                            className="FunctionalButton"
                            size="lg"
                            color="success"
                            style={{
                                "maxWidth": "200px",
                                "display": "none"
                            }}
                            onClick={() => {
                                this.props.store.switchMode();
                            }}>
                            {switchButtonText}
                        </Button>
                        {warningButton}
                    </CardFooter>

                    <Collapse isOpen={false}>
                        <AttributesEditor store={this.props.store}
                                          attributes={this.props.store.getFocusSpanAttributes()}
                                          onSubmit={(attributes) => {
                                              this.props.store.updateFocusSpanAttributes(attributes);
                                          }}
                                          key={forceRecreateAttributesEditorKey}/>
                    </Collapse>

                </div>
            );
        }
    }
);

export {TaggingArbitrationAnnotator}