import React, {ReactElement, useState} from 'react';
import {EntityLinkingStateStore} from "../store/EntityLinkingStateStore";
import {observer} from "mobx-react";
import {SpanContainer} from "../core/Spans";
import {ActiveSpanColor, ProcessedSpanColor, UnselectTagColor} from "../AnnotatorCommons";
import {SingleLayerSpan} from '../span/SingleLayerSpan';
import {AnnotatorTextArea} from "../AnnotatorTextArea";
import {Card, CardText, Row} from "reactstrap";
import {DatabaseCandidateRecordSelector} from "./DatabaseCandidateRecordSelector";

import styled from 'styled-components';
import { DatabaseCandidateStore } from '../store/DatabaseCandidateStore';

const EntityLinkingAnnotatorBody = styled.div`
    height:100%;
    width:100%;
    min-height:480px;
    display:flex;
    flex-direction:column;
    
    #AnnotatorFrame {
        display:flex;
        height:100%;
        flex-direction:column;
        > .card {
            height:100%; 
            > .card {
                flex-grow:1;
                overflow:auto;
            }
        }
    }
`;

const EntityLinkingTopContainer = styled.div`
    max-height: 45%;
    margin-bottom: 15px;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 0;
    
    
`;
const EntityLinkingBottomContainer = styled.div`
   flex-grow:1;
   flex-shrink:1;
   flex-basis:0;
   overflow:hidden;
   margin-bottom: 15px;
`;

type PropsType = {
    store: EntityLinkingStateStore
    frameWrapper: any
}


const TextSpanSelector = (props: {
    store: EntityLinkingStateStore, // just in case
    activeSpan: SpanContainer | null | undefined,
    onSelect: (span: SpanContainer) => void,
    spans: SpanContainer[]
}) => {
    const {store, activeSpan, onSelect, spans} = props;
    let tagSpans: ReactElement[] = [];

    const getSpanColor = (span: SpanContainer) => {
        if (activeSpan !== null && activeSpan !== undefined && span.rangeEqual(activeSpan)) {
            return ActiveSpanColor;
        }
        // if span has annotations
        const annotations = store.getSpanAnnotations(span);
        if (annotations !== undefined) {
            if (annotations.gold_id !== undefined &&
                annotations.gold_id !== null) {
                return ProcessedSpanColor;
            }

            if (annotations.properties!.no_related || annotations.properties!.not_sure) {
                // either is true then show processed color
                return ProcessedSpanColor;
            }
        }

        return UnselectTagColor;
    };

    spans.forEach((s, idx) => {
        let key = "span_" + idx;
        const color = getSpanColor(s);

        tagSpans.push(<SingleLayerSpan key={key}
                                       text={s.extent}
                                       tag={s.tag}
                                       color={color}
                                       start={s.start}
                                       end={s.end}
                                       blank={s.attrs.blank}
                                       selectable={false}
                                       transparent={false}
                                       cssStyle={"HighlightSpan"}
                                       clickTrigger={() => onSelect(s)}/>)
    });

    return <div className="AnnotatorTextView card-body">
        <AnnotatorTextArea>
            <CardText className="AnnotatorText"
                      markable="parent"
                      id="text-display-container">
                {tagSpans}
            </CardText>
        </AnnotatorTextArea>

    </div>;
};


const EntityLinkingAnnotator = observer((props: PropsType) => {

    const {store, frameWrapper} = props;
    // @ts-ignore
    const subscribeStateChange = store.viewUpdateChange; // subscribe to changes that triggers the view look change
    const [activeSpan, setActiveSpan] = useState<SpanContainer | null>();
    const spans = store.spans as SpanContainer[];

    const onSelectHandler = (span: SpanContainer) => {
        if (!span.attrs.blank) {
            // only applies to non-blank span
            if (activeSpan) {
                // active is set
                if (activeSpan.rangeEqual(span)) {
                    // activeSpan is clicked again
                    // toggle off in this case
                    setActiveSpan(null);
                } else {
                    // click another span
                    setActiveSpan(span);
                }
            } else {
                // activeSpan not set
                setActiveSpan(span);
            }
        }
    };

    let annotator: ReactElement = <Card style={{"maxHeight": "500px"}}>
        <TextSpanSelector store={store}
                          activeSpan={activeSpan}
                          onSelect={onSelectHandler}
                          spans={spans}/>

    </Card>;

    if (frameWrapper) {
        annotator = frameWrapper(annotator);
    }

    return <EntityLinkingAnnotatorBody>
        <EntityLinkingTopContainer>
            {annotator}
        </EntityLinkingTopContainer>
        <EntityLinkingBottomContainer>
            <DatabaseCandidateRecordSelector 
                activeSpan={activeSpan} 
                store={store}
                dbCandidateStore={DatabaseCandidateStore.getInstance()}
            />
        </EntityLinkingBottomContainer>

    </EntityLinkingAnnotatorBody>
});

export {EntityLinkingAnnotator}