import {extendObservable} from 'mobx'

const Pages = {
    EDIT: 0,
    CONFIG: 1,
    BACKUP: 2,
    ABOUT: 3
};

class PageStateStore {
    constructor() {
        extendObservable(this, {
            pageStateStoreUpdated: 0
        });
        this.currentPage = 0;
        this.jumpToPage = this.jumpToPage.bind(this);
    }

    jumpToPage(pageIdx) {
        this.currentPage = pageIdx;
        this.pageStateStoreUpdated += 1;
    }
}

export {Pages, PageStateStore}