import {extendObservable} from 'mobx'

class TableRowSelectionStore {
    constructor(exclusive = false) {
        extendObservable(this, {
            updated: 0
        });
        this.set = new Set();
        // meaning that we only have at most one
        // toggled item at any moment
        this.exclusive = exclusive;
        this.rows = [];

        this.add = this.add.bind(this);
        this.remove = this.remove.bind(this);
        this.bindData = this.bindData.bind(this);
        this.contains = this.contains.bind(this);
        this.clear = this.clear.bind(this);
        this.toggle = this.toggle.bind(this);
    }

    add(rowId) {
        if (this.exclusive) {
            this.set.clear();
        }
        this.set.add(rowId);
        this.updated += 1;
    }

    remove(rowId) {
        this.set.delete(rowId);
        this.updated += 1;
    }


    bindData(rows) {
        this.rows = rows;
        // this.updated += 1;
    }

    contains(rowId) {
        return this.set.has(rowId);
    }

    clear() {
        this.set.clear();
        this.updated += 1;
    }

    toggle(rowId) {
        if (this.contains(rowId)) {
            this.remove(rowId);
        } else {
            this.add(rowId);
        }
    }

    get selectedRowIds() {
        return this.set;
    }

    get selectedRows() {
        const selectedRows = [];
        this.set.forEach((rowId) => {
            selectedRows.push(this.rows[rowId]);
        });
        return selectedRows;
    }
}

export {TableRowSelectionStore}