import React from 'react';
import './Customization.css'
import 'annotator/AnnotatorButton.css'
import {Button, Card, CardBody, CardFooter, CardHeader, Table} from "reactstrap";
import {observer} from "mobx-react";
import {TableRowSelectionStore} from "../table/TableRowSelectionStore";
import {TextSubmitBar} from "./TextSubmitBar";
import {TableRowSelector} from "../table/TableRowSelector";
import uuid from 'uuid';

const NERCustomizationPanel = observer(
    class NERCustomizationPanel extends React.Component {
        constructor(props) {
            super(props);
            this.tableSelection = new TableRowSelectionStore();
            this.submitNewTag = this.submitNewTag.bind(this);
        }

        submitNewTag(tagName, namespace) {
            if (tagName.toUpperCase() === "ANY") {
                this.props.store.alertStore.sendAlert("Invalid Entity Name",
                    "ANY is a reserved internal type name, please use other names")
            } else {
                this.props.store.schemaConfig.addNERTag(tagName, namespace);
            }
        }

        render() {
            const rootStore = this.props.store;
            const namespace = this.props.namespace;
            const schemaConfig = rootStore.schemaConfig;
            // trigger when schemaConfig changes
            const subscribeSchemaConfigUpdate = schemaConfig.configUpdated;

            let entities = [];
            let tableRows = [];
            let i = 0;
            const tagSetKey = schemaConfig.getTagSetKey(namespace);
            schemaConfig[tagSetKey].forEach((e) => {
                entities.push(<Button color="info"
                                      disabled
                                      className="TagButton"
                                      style={{"margin": "2px", "fontSize": "18px"}}>
                    {e}
                </Button>);
                tableRows.push([e]);
                i += 1;
            });

            // Give a new key to refresh TextSubmitBar after click
            const forceReconstructTextSubmitBarKey = uuid.v4();
            this.tableSelection.bindData(tableRows);

            return <div id="NERCustomizationPanel">
                <Card>
                    <CardHeader>
                        {entities}
                    </CardHeader>
                    <CardHeader>
                        <TextSubmitBar store={rootStore}
                                       key={forceReconstructTextSubmitBarKey}
                                       default={"NEW_ENTITY"}
                                       onSubmit={(currentTag) => this.submitNewTag(currentTag, namespace)}/>
                    </CardHeader>
                    <CardBody className="TableContainer">
                        <TableRowSelector store={this.tableSelection}
                                          headers={["Entity Name"]}
                                          rows={tableRows}/>
                    </CardBody>
                    <CardFooter>
                        <Button className="TagButton"
                                color="danger"
                                onClick={() => {
                                    rootStore.alertStore.sendAlert("Warning: Cascading Removals",
                                        "To keep the schema consistent, other configurations that " +
                                        "depend on removed tags will also be removed. Please confirm before deletion",
                                        () => {
                                            try {
                                                const selectedRows = this.tableSelection.selectedRows;
                                                const tags = selectedRows.map((r) => r[0]);
                                                schemaConfig.batchRemoveNERTags(tags, namespace);
                                                // remember to clear all selections after
                                                // remove is done
                                                this.tableSelection.clear();
                                            } catch (e) {
                                                const message = e.message;
                                                rootStore.alertStore.sendAlert("Remove Operation Failed",
                                                    "Error message: " + message)
                                            }
                                        }, true);
                                }}>
                            REMOVE
                        </Button>
                    </CardFooter>
                </Card>
            </div>
        }
    }
);

export {NERCustomizationPanel}