export const getAnnotationText = (annotation: Annotation, pdfBlocks: Block[]) => {
    return annotation.tokenIds.map((tokenId) => pdfBlocks[tokenId.tokenIndex].Text.substring(tokenId.startIndex, tokenId.endIndex));
};

export type Optional<T> = T | undefined;

export const getBinaryFromPdfBase64 = (pdfBase64: string) => {
    var raw = window.atob(pdfBase64);
    var rawLength = raw.length;
    var array = new Uint8Array(new ArrayBuffer(rawLength));

    for (var i = 0; i < rawLength; i++) {
        array[i] = raw.charCodeAt(i);
    }
    return array;
};

export const getElementPosition = (element: Element) => {
    const rect = element.getBoundingClientRect();
    return { x: rect.left, y: rect.top };
};

export function getTokenBounds(t: Token): Bounds {
    return {
        left: t.x,
        top: t.y,
        right: t.x + t.width,
        bottom: t.y + t.height,
    };
}

/**
 * Computes a bound which contains all of the bounds passed as arguments.
 */
export function spanningBound(bounds: Bounds[], padding: number = 3): Bounds {
    // Start with a bounding box for which any bound would be
    // contained within, meaning we immediately update maxBound.
    const maxBound: Bounds = {
        left: Number.MAX_VALUE,
        top: Number.MAX_VALUE,
        right: 0,
        bottom: 0,
    };

    bounds.forEach((bound) => {
        maxBound.bottom = Math.max(bound.bottom, maxBound.bottom);
        maxBound.top = Math.min(bound.top, maxBound.top);
        maxBound.left = Math.min(bound.left, maxBound.left);
        maxBound.right = Math.max(bound.right, maxBound.right);
    });

    maxBound.top = maxBound.top - padding;
    maxBound.left = maxBound.left - padding;
    maxBound.right = maxBound.right + padding;
    maxBound.bottom = maxBound.bottom + padding;

    return maxBound;
}

export const annotationText = (annotation: Annotation, tokens: Token[]) => {
    return annotation.tokenIds.map(({ tokenIndex }) => tokens[tokenIndex]).join(' ');
};

export const isRightClick = (e: React.MouseEvent<HTMLElement, MouseEvent>) => {
    return ('which' in e && (e as any).which === 3) || ('button' in e && e.button === 2);
};

export const blockToToken = (block: Block, pageWidth: number, pageHeight: number): Token => {
    const {
        Geometry: {
            BoundingBox: { Width: width, Height: height, Top: y, Left: x },
        },
        Text: text,
        BlockType: type,
    } = block;
    return {
        text,
        width: width * pageWidth,
        height: height * pageHeight,
        x: x * pageWidth,
        y: y * pageHeight,
        type,
    };
};

export enum BLOCK_TYPE {
    WORD = 'WORD',
    LINE = 'LINE',
}

export enum DOCUMENT_TYPE {
    ScannedPDF = 'ScannedPDF',
    NativePDF = 'NativePDF',
}

export const flattenArray = (a: any[][]) => {
    return a.reduce((acc, val) => acc.concat(val), []);
};

export const disableEnterKey = (e: React.KeyboardEvent<any>) => {
    if (e.keyCode === 13) {
        e.preventDefault();
        e.stopPropagation();
    }
};

export const addBlockIndices = (blocks: Block[]) => {
    let currParentBlockIndex = 0;
    return blocks.map((block, idx) => {
        if (block.BlockType === BLOCK_TYPE.WORD) {
            if (block.parentBlockIndex && block.blockIndex) {
                return block;
            }
            return {
                ...block,
                parentBlockIndex: currParentBlockIndex,
                blockIndex: idx,
            };
        } else {
            currParentBlockIndex = idx;
            if (block.parentBlockIndex && block.blockIndex) {
                return block;
            }
            return {
                ...block,
                parentBlockIndex: -1,
                blockIndex: idx,
            };
        }
    });
};

export const getAnnotationTokenIndices = (annotation: Annotation) => annotation.tokenIds.map((token) => token.tokenIndex);

export const annotationTokensEqual = (tokens1: TokenId[], tokens2: TokenId[]) => {
    if (tokens1.length !== tokens2.length) {
        return false;
    }
    for (let i = 0; i < tokens1.length; i++) {
        if (
            tokens1[i].tokenIndex !== tokens2[i].tokenIndex ||
            tokens1[i].startIndex !== tokens2[i].startIndex ||
            tokens1[i].endIndex !== tokens2[i].endIndex
        ) {
            return false;
        }
    }
    return true;
};

export const annotationsEqual = (anno1: Annotation, anno2: Annotation) => {
    return anno1.tag === anno2.tag && annotationTokensEqual(anno1.tokenIds, anno2.tokenIds);
};

export const copyAnnotation = (annotation: Annotation, newId: boolean = false): Annotation => {
    return {
        bounds: {
            bottom: annotation.bounds.bottom,
            left: annotation.bounds.left,
            right: annotation.bounds.right,
            top: annotation.bounds.top,
        },
        id: newId ? uuidv4() : annotation.id,
        page: annotation.page,
        tag: annotation.tag,
        tokenIds: annotation.tokenIds.map((t) => ({
            endIndex: t.endIndex,
            startIndex: t.startIndex,
            pageIndex: t.pageIndex,
            tokenIndex: t.tokenIndex,
            tokenType: t.tokenType,
        })),
    };
};

export const getOverlappingAnnotations = (annotation: Annotation, searchAnnotations: Annotation[]) => {
    const tokenIndices = new Set(getAnnotationTokenIndices(annotation));
    const newOverlappingAnnotations = searchAnnotations.filter((anno) => getAnnotationTokenIndices(anno).some((idx) => tokenIndices.has(idx)));
    return newOverlappingAnnotations;
};

export const getRelationsFromAnnotations = (annotations: Annotation[], relations: string[][]) => {
    const annotationRelations = [];
    const annotationIdSet = new Set(annotations.map((anno) => anno.id));
    for (const rel of relations) {
        if (rel.some((id) => annotationIdSet.has(id))) {
            annotationRelations.push(rel);
        }
    }
    return annotationRelations;
};

export const getRelationIndexFromAnnotation = (annotation: Annotation, relations: string[][]) => {
    let retIndex = -1;
    for (let i = 0; i < relations.length; i++) {
        const relSet = new Set(relations[i]);
        if (relSet.has(annotation.id)) {
            retIndex = i;
            break;
        }
    }
    return retIndex;
};

export const handleAnnotationSelect = ({
    setSsieAnnotationStore,
    ssieAnnotations,
    annotation,
    selectOverlap = true,
}: {
    setSsieAnnotationStore: any;
    ssieAnnotations: SsieAnnotations;
    annotation: Annotation;
    selectOverlap?: boolean;
}) => {
    const selectedAnnotations = ssieAnnotations.annotations.filter((anno) => {
        if (anno.id === annotation.id) {
            return true;
        }
        if (!selectOverlap) {
            return false;
        }
        const currAnnoTokens = new Set(anno.tokenIds.map((t) => t.tokenIndex));
        return annotation.tokenIds.every((t) => currAnnoTokens.has(t.tokenIndex));
    });
    if (selectedAnnotations.length > 0) {
        setSsieAnnotationStore({
            selectedSsieAnnotations: selectedAnnotations,
            stagedRelation: selectedAnnotations.length === 1 ? [selectedAnnotations[0].id] : undefined,
        });
    }
};

export const handleAnnotationRightClick = ({
    setSsieAnnotationStore,
    ssieAnnotations,
    selectedSsieAnnotations,
    stagedRelation,
    annotation,
}: {
    setSsieAnnotationStore: any;
    ssieAnnotations: SsieAnnotations;
    selectedSsieAnnotations: Annotation[];
    stagedRelation: string[];
    annotation: Annotation;
}) => {
    // don't allow multi-selection if a relation is being clicked or if clicking an annotation that is part of a relation
    const relationAnnotationIds = new Set(flattenArray(ssieAnnotations.relations));
    // find any relations that contain the clicked annotation
    const relationsContainingClickedAnnotation = getRelationsFromAnnotations([annotation], ssieAnnotations.relations);
    const canAddToSelection = !(
        selectedSsieAnnotations.filter((anno) => relationAnnotationIds.has(anno.id)).length > 0 || relationsContainingClickedAnnotation.length > 0
    );
    if (canAddToSelection) {
        const newSelectedSsieAnnotations = [...selectedSsieAnnotations, annotation];

        const newStagedRelation = annotation.tag === selectedSsieAnnotations[0].tag ? [...stagedRelation, annotation.id] : [];

        setSsieAnnotationStore({
            selectedSsieAnnotations: newSelectedSsieAnnotations,
            stagedRelation: newStagedRelation,
        });
    }
};

export const updateSelectedSsieAnnotationsProperties = (
    tag: string,
    {
        selectedSsieAnnotations,
        ssieAnnotations,
        setSsieAnnotationStore,
    }: { selectedSsieAnnotations: Annotation[]; ssieAnnotations: SsieAnnotations; setSsieAnnotationStore: any }
) => {
    if (selectedSsieAnnotations.length > 0) {
        const selectedIds = new Set(selectedSsieAnnotations.map((anno) => anno.id));
        const editedAnnotations: Annotation[] = [];
        const annotationsWithUpdates = ssieAnnotations.annotations.map((anno) => {
            if (selectedIds.has(anno.id)) {
                const editedAnnotation = { ...anno, tag };
                editedAnnotations.push(editedAnnotation);
                return editedAnnotation;
            } else {
                return anno;
            }
        });

        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.updateAnnotations(annotationsWithUpdates),
            selectedSsieAnnotations: editedAnnotations,
        });
    }
};

export const removeActiveSsieAnnotations = (selectedSsieAnnotations: Annotation[], ssieAnnotations: SsieAnnotations, setSsieAnnotationStore: any) => {
    if (selectedSsieAnnotations.length > 0) {
        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.deleteAnnotations(selectedSsieAnnotations),
            selectedSsieAnnotations: [],
            stagedRelation: [],
        });
    }
};

export const handleConnectButtonClick = (
    setSsieAnnotationStore: any,
    selectedSsieAnnotations: Annotation[],
    ssieAnnotations: SsieAnnotations,
    stagedRelation: string[]
) => {
    if (isSsieAnnotationRelationSelected(selectedSsieAnnotations[0].id, ssieAnnotations.relations)) {
        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.deleteRelations([selectedSsieAnnotations[0].id]),
            stagedRelation: selectedSsieAnnotations.map((anno) => anno.id),
        });
    } else {
        setSsieAnnotationStore({
            ssieAnnotations: ssieAnnotations.withNewRelation(stagedRelation),
            stagedRelation: [],
        });
    }
};

export const groupAnnotationsIntoEntities = (ssieAnnotations: SsieAnnotations) => {
    const annotationIdsInRelation = new Set();
    const annotationMap: StringMap<Annotation> = {};
    for (const anno of ssieAnnotations.annotations) {
        annotationMap[anno.id] = anno;
    }
    const discontiguousEntities = ssieAnnotations.relations.map((rel, relIndex) => ({
        annoGroup: rel.map((annoId) => {
            annotationIdsInRelation.add(annoId);
            return annotationMap[annoId];
        }),
        relationIndex: relIndex,
    }));
    const entities = ssieAnnotations.annotations
        .filter((anno) => !annotationIdsInRelation.has(anno.id))
        .map((anno) => ({ annoGroup: [anno], relationIndex: -1 }));
    return [...discontiguousEntities, ...entities];
};

export const getBoundsForTokens = (tokens: Token[]) => {
    const bounds = tokens.map((t) => getTokenBounds(t));
    return spanningBound(bounds);
};

export const showConnectedButton = (selectedSsieAnnotations: Annotation[]) =>
    selectedSsieAnnotations.length > 1 && selectedSsieAnnotations[0].tag !== UNASSIGNED_TAG && areSameTag(selectedSsieAnnotations);
