import React from 'react';
import { PDFStore } from './PDFStore';
import pdfjs from 'pdfjs-dist';
import PDFPageInfo from '../types/PDFPageInfo';
import { PDF_BASE64, PDF_PAGE, PDF_PAGES } from '../utils/DefaultSsieCollection';

export enum ViewState {
    LOADING,
    LOADED,
    NOT_FOUND,
    ERROR,
}

interface PDFStoreProviderProps {
    pdfBase64?: string;
    pdfPages?: string;
    pdfPage?: string;
}

const PDFStoreProvider: React.FC<PDFStoreProviderProps> = ({ children, pdfBase64: pdfBase64Prop, pdfPages: pdfPagesProp, pdfPage: pdfPageProp }) => {
    const [doc, setDocument] = React.useState<pdfjs.PDFDocumentProxy>();
    const [pdfBase64, setPdfBase64] = React.useState<string>('');
    const [pdfPages, setPdfPages] = React.useState<string>('2');
    const [pdfPage, setPdfPage] = React.useState<string>('2');
    const [currPdfPage, setCurrPdfPage] = React.useState<number>(1);
    // 'pageInfos' is used for pdfjs and does not refer to the actual page of total # of pages in the PDF document
    const [pageInfos, setPageInfos] = React.useState<PDFPageInfo[]>();
    const [viewState, setViewState] = React.useState<ViewState>(ViewState.LOADING);

    React.useEffect(() => {
        if (pdfBase64Prop) {
            setPdfBase64(pdfBase64Prop);
        } else {
            setPdfBase64(PDF_BASE64);
        }
    }, [pdfBase64Prop]);

    React.useEffect(() => {
        if (pdfPageProp) {
            setPdfPage(pdfPageProp);
            setCurrPdfPage(parseInt(pdfPageProp));
        } else {
            setPdfPage(PDF_PAGE);
            setCurrPdfPage(parseInt(PDF_PAGE));
        }
    }, [pdfPageProp]);

    React.useEffect(() => {
        if (pdfPagesProp) {
            setPdfPages(pdfPagesProp);
        } else {
            setPdfPages(PDF_PAGES);
        }
    }, [pdfPagesProp]);

    // React's Error Boundaries don't work for us because a lot of work is done by pdfjs in
    // a background task (a web worker). We instead setup a top level error handler that's
    // passed around as needed so we can display a nice error to the user when something
    // goes wrong.
    //
    // We have to use the `useCallback` hook here so that equality checks in child components
    // don't trigger unintentional rerenders.
    const onError = React.useCallback(
        (err: Error) => {
            console.error('Unexpected Error rendering PDF', err);
            setViewState(ViewState.ERROR);
        },
        [setViewState]
    );

    return (
        <PDFStore.Provider
            value={{
                doc,
                setDocument,
                pageInfos,
                setPageInfos,
                onError,
                viewState,
                setViewState,
                pdfBase64,
                pdfPage,
                pdfPages,
                currPdfPage,
                setCurrPdfPage,
            }}
        >
            {children}
        </PDFStore.Provider>
    );
};

export default PDFStoreProvider;
