const UFSchema = {
    METADATA: "metadata",
    RAW_TEXT: "raw_text",
    ANNOTATIONS: "annotations",
    ANNOTATIONS_V2: "annotations_v2",
    CONFLICTED_ANNOTATIONS_LIST: "annotations_conflicts_list",
    START: "start",
    END: "end",
    TAG: "tag",
    DOCUMENT_ID: "document_id",
    LABELING_TASKS: "labeling_tasks",
    LABELING_TASK_NAME: "name",
    LABELING_TASK_READ_ONLY: "read_only"
};

export const LABELING_TASK_EVENTS_ARGUMENT_AND_TRIGGER = "SequenceLabeling-ArgumentAndTrigger";
export const LABELING_TASK_EVENTS_RE_SLOT_FILLING = "Re-Slot-Filling";
export const LABELING_TASK_COREF_GROUPING = "CoRef-Grouping";
export const LABELING_TASK_NAMED_ENTITY_RECOGNITION = "Named-Entity-Recognition";


export type UFDocumentLabelingTask = {
    name: "SequenceLabeling-ArgumentAndTrigger" | "Re-Slot-Filling" | "CoRef-Grouping" | "Named-Entity-Recognition";
    read_only: boolean;
}

export type UFDocumentMetadata = {
    document_id: string;
    language?: string;
    timestamp?: number;
    annotator_id?: string;
    annotator_id_v2?: number;
    schema_version?: string;
    slice?: string;
    slot?: string;
    comment?: string;
    labeling_tasks?: UFDocumentLabelingTask[];
}

export type UFDocument = {
    raw_text: string;
    annotations?: any;
    annotations_v2?: any;
    database_candidates?: any;
    metadata: UFDocumentMetadata
}

export type Span = {
    start: number;
    end: number;
    tag: string;
    extent?: string;
    attrs?: any;
}

export {UFSchema};
