const EVENT_EMPTY = 1;
const EVENT_INCOMPLETE = 2;
const EVENT_COMPLETE = 3;


class EventInstanceDataStore {
    constructor(triggerSpan, eventDefinition) {
        this.triggerSpan = triggerSpan;
        this.eventDefinition = eventDefinition;
        const argumentValueMap = new Map();

        eventDefinition.argumentList.forEach((arg) => {
            argumentValueMap.set(arg.argumentName, []);
        });

        this.argumentValueMap = argumentValueMap;
        this.propertyValueMap = new Map();

        this.addArgumentToInstance = this.addArgumentToInstance.bind(this);
        this.addPropertyToInstance = this.addPropertyToInstance.bind(this);
        this.removeArgumentFromInstance = this.removeArgumentFromInstance.bind(this);
        this.removePropertyFromInstance = this.removePropertyFromInstance.bind(this);
        this.getArgumentValue = this.getArgumentValue.bind(this);
        this.getPropertyValue = this.getPropertyValue.bind(this);
        this.dump = this.dump.bind(this);
    }

    addArgumentToInstance(argumentName, argumentSpan) {
        const valueList = this.argumentValueMap.get(argumentName);
        if (valueList !== undefined) {
            // avoid adding the same span multiple times
            const {start, end} = argumentSpan;
            const exist = valueList.filter((val) => {
                return val.start === start && val.end === end
            }).length > 0;
            if (!exist) {
                // now verify several things
                // is the span's tag type compatible with the definition?
                const argumentSpecs = this.eventDefinition.argumentList
                    .filter(arg => arg.argumentName === argumentName)[0];
                const argumentTypes = argumentSpecs.argumentTypes;
                const allowMultiValues = argumentSpecs.multiValues;
                if (argumentTypes.indexOf(argumentSpan.tag) > -1) {
                    if (allowMultiValues) {
                        // allowing multiple values, append
                        valueList.push(argumentSpan);
                    } else {
                        // else overwrite
                        valueList.pop();
                        valueList.push(argumentSpan);
                    }
                }
            }
        }
    }

    addPropertyToInstance(propertyName, propertyValue) {
        this.propertyValueMap.set(propertyName, propertyValue);
    }

    removeArgumentFromInstance(argumentName, argumentSpan) {
        const valueList = this.argumentValueMap.get(argumentName);
        if (valueList !== undefined) {
            // avoid adding the same span multiple times
            const {start, end} = argumentSpan;
            let valueIdx = -1;
            valueList.forEach((val, idx) => {
                if (val.start === start && val.end === end) {
                    valueIdx = idx;
                }
            });
            if (valueIdx >= 0) {
                valueList.splice(valueIdx, 1);
            }
        }
    }

    removePropertyFromInstance(propertyName) {
        this.propertyValueMap.delete(propertyName);
    }

    get status() {
        const eventDef = this.eventDefinition;
        const argumentList = eventDef.argumentList;
        let isEmpty = true;
        let isComplete = true;
        argumentList.forEach((arg) => {
            const {argumentName, optional} = arg;
            const valueList = this.argumentValueMap.get(argumentName);
            if (valueList.length > 0) {
                isEmpty = false;
            }
            if (valueList.length === 0 && optional === false) {
                // a required argument
                isComplete = false;
            }
        });
        if (isEmpty) {
            return EVENT_EMPTY;
        }
        if (isComplete) {
            return EVENT_COMPLETE;
        }
        return EVENT_INCOMPLETE;
    }

    getArgumentValue(argumentName) {
        return this.argumentValueMap.get(argumentName);
    }

    getPropertyValue(propertyName) {
        return this.propertyValueMap.get(propertyName);
    }

    dump() {
        const triggerObject = this.triggerSpan.exportAnnotation(false);
        let argumentsObject = [];
        this.eventDefinition.argumentList.forEach((arg) => {
            const name = arg.argumentName;
            const valueList = this.argumentValueMap.get(name);
            const valueObjectList = valueList.map(v => v.exportAnnotation(false));
            if (valueObjectList.length > 0) {
                // this is non-empty value list
                argumentsObject.push({
                    "name": name,
                    "values": valueObjectList
                })
            }
        });
        let propertyObject = {};
        this.propertyValueMap.forEach((value, name) => {
            propertyObject[name] = value;
        });
        return {
            "trigger": triggerObject,
            "arguments": argumentsObject,
            "properties": propertyObject
        }
    }

}


export {
    EventInstanceDataStore,
    EVENT_EMPTY,
    EVENT_INCOMPLETE,
    EVENT_COMPLETE
}