import {UFSchema} from "../core/UFSchema";

class Validator {
    /***
     * Skeleton for implementation of validator to report violation
     */
    constructor(namespace) {
        this.namespace = namespace;
        this.validate = this.validate.bind(this);
    }


    validate(document) {
        throw new Error("validate method not implemented")
    }

    static checkNamespaceExists(document, namespace) {
        if (document[UFSchema.ANNOTATIONS] === undefined) {
            return false;
        }
        return document[UFSchema.ANNOTATIONS][namespace] !== undefined;
    }
}

class Violation {
    constructor(document, namespace, cause) {
        this.document = document;
        this.namespace = namespace;
        this.cause = cause
    }

    get cell() {
        return {
            documentId: this.document["metadata"]["document_id"],
            namespace: this.namespace,
            cause: this.cause
        }
    }
}

class GroupValidator {
    constructor() {
        this.validators = [];
        this.register = this.register.bind(this);
        this.validateAll = this.validateAll.bind(this);
    }

    register(validator) {
        this.validators.push(validator);
        return this;
    }

    validateAll(document) {
        const allViolations = [];
        this.validators.forEach((validator) => {
            const vios = validator.validate(document);
            if (vios.length > 0) {
                allViolations.push(...vios);
            }

        });
        return allViolations;
    }

}

export {Validator, Violation, GroupValidator}