import {RangeOverlapChecker} from "../core/RangeOverlapChecker";
import {Namespaces} from "../core/Namespaces";
import {UFSchema} from "../core/UFSchema";
import {deepCopy} from "../utils/DeepCopy";
import {RangeMap} from "../core/RangeMap";


class CoreferenceAuditor {
    /**
     * This is a class check whether the coreference annotations
     * are consistent with current NER annotations. And if not
     * overwrite the coreference annotations with the current
     * NER result
     */

    static audit(documentObject, namespace = Namespaces.COREFERENCE, entityNamespace = Namespaces.NER) {

        // console.log("CoreferenceRegularizer called");

        if (documentObject[UFSchema.ANNOTATIONS] === undefined
            || documentObject[UFSchema.ANNOTATIONS][namespace] === undefined) {
            // there is no existing coreference annotations
            return documentObject;
        }

        let NERAnnotations = [];
        if (documentObject[UFSchema.ANNOTATIONS][entityNamespace] !== undefined) {
            NERAnnotations = deepCopy(documentObject[UFSchema.ANNOTATIONS][entityNamespace]);
        }

        const NEROverlapChecker = new RangeOverlapChecker(NERAnnotations);
        const NERSpanMap = RangeMap.create(NERAnnotations);

        const oldCorefAnnotations = documentObject[UFSchema.ANNOTATIONS][namespace];

        let corefOnlyMentions = [];
        if (oldCorefAnnotations["coref_only_mentions"]) {
            corefOnlyMentions = oldCorefAnnotations["coref_only_mentions"].filter((s) => {
                const overlapState = NEROverlapChecker.overlap(s);
                if (overlapState === 0 || overlapState === 1) {
                    // the coref only mentions are overlapping
                    // with existing NER entities
                    return false;
                }
                return true;
            })
        }

        const groups = oldCorefAnnotations["groups"];
        const newGroups = [];
        if (groups) {
            groups.forEach((g) => {
                const mentions = g["mentions"];
                const newMentions = [];
                if (mentions) {
                    mentions.forEach((m) => {
                        if (m.tag === "COREF") {
                            // this is coref only mention
                            // check if it overlap or exist in NER already
                            const overlapState = NEROverlapChecker.overlap(m);
                            if (overlapState === -1) {
                                // not contains and not overlap with any NER
                                // spans
                                newMentions.push(deepCopy(m));
                            }
                        } else if (m.tag !== undefined) {
                            // this is an 'existing' NER span
                            if (NEROverlapChecker.overlap(m) === 0) {
                                // only if it already exists in the
                                // NER annotations, we keep it
                                // otherwise, skip
                                const nm = deepCopy(m);
                                const nerEntity = NERSpanMap.get(m);

                                if (nm.tag !== nerEntity.tag) {
                                    // in case the NER keeps the span
                                    // but changes the tag, change the tag accordingly
                                    nm.tag = nerEntity.tag;
                                }
                                newMentions.push(nm);
                            }
                        }
                    })
                }
                if (newMentions.length > 0) {
                    const newG = {
                        "mentions": newMentions
                    };
                    newGroups.push(newG);
                }
            });
        }

        const newCoreferenceAnnotations = {
            "groups": newGroups,
            "coref_only_mentions": corefOnlyMentions
        };

        documentObject[UFSchema.ANNOTATIONS][namespace] = newCoreferenceAnnotations;

        return documentObject;
    }

    /**
     * audit all possible pairs of entities/coreference groups
     * including:
     * 1. named_entity -> coreference
     * 2. event_arguments -> event_arguments_groups
     * 3. event_triggers -> event_triggers_groups
     *
     * could be time consuming, need to take care
     **/
    static auditAll(document) {
        let d = CoreferenceAuditor.audit(document, Namespaces.COREFERENCE, Namespaces.NER);
        d = CoreferenceAuditor.audit(d, Namespaces.EVENT_ARGUMENTS_GROUPS,
            Namespaces.EVENT_ARGUMENTS);
        d = CoreferenceAuditor.audit(d, Namespaces.EVENT_TRIGGERS_GROUPS,
            Namespaces.EVENT_TRIGGERS);
        return d
    }
}

export {CoreferenceAuditor}