import React, {Component} from 'react';
import {Button, CardBody, CardHeader, CardText} from 'reactstrap';
import {observer} from 'mobx-react'
import 'annotator/Annotator.css'
import 'annotator/AnnotatorButton.css'
import {DoubleLayerSpan} from "annotator/span/DoubleLayerSpan";
import {AnnotatorTextArea} from "../AnnotatorTextArea";

import {OverlayMode} from "annotator/span/Constants";


const TaggingDiffViewer = observer(
    class TaggingDiffViewer extends Component {

        constructor(props) {
            super(props);
        }


        render() {
            const viewUpdateChange = this.props.store.viewUpdateChange;
            const spans = this.props.store.spans;
            let tagSpans = [];

            for (let i = 0; i < spans.length; i++) {
                let spanObj = spans[i];
                let key = "span_" + i;
                tagSpans.push(<DoubleLayerSpan key={key}
                                               text={spanObj.extent}
                                               layers={spanObj.attrs.base}
                                               tags={spanObj.attrs.tags}
                                               mode={this.props.store.mode}/>)
            }
            return (
                <div id="TaggingDiffViewerBody">
                    <CardHeader>
                        <div>
                            <Button color="info"
                                    className="TagButton"
                                    onClick={() => this.props.store.toggleMode(OverlayMode.SHOW_V1)}>
                                SHOW V1
                            </Button>
                            <Button color="info"
                                    className="TagButton"
                                    onClick={() => this.props.store.toggleMode(OverlayMode.SHOW_V2)}>
                                SHOW V2
                            </Button>
                            <Button color="info"
                                    className="TagButton"
                                    onClick={() => this.props.store.toggleMode(OverlayMode.OVERLAY)}>
                                OVERLAY
                            </Button>
                        </div>
                    </CardHeader>
                    <CardBody className="AnnotatorTextView">
                        <AnnotatorTextArea textDirection={this.props.textDirection}
                                           textAlignment={this.props.textAlignment}>
                            <CardText className="OverlayAnnotatorText"
                                      markable="parent"
                                      id="tag-container">
                                {tagSpans}
                            </CardText>
                        </AnnotatorTextArea>
                    </CardBody>
                </div>
            );
        }
    }
);

export {TaggingDiffViewer}