import React from 'react';
import {observer} from "mobx-react";
import {Table} from "reactstrap";


const TableRowSelector = observer(
    class TableRowSelector extends React.Component {
        constructor(props) {
            super(props);
        }

        render() {
            // subscribe to TableRowSelectionStore's update
            const subscribeSelectionUpdate = this.props.store.updated;
            const headers = this.props.headers;
            const rows = this.props.rows;

            let tableHeaders = "";
            if (headers !== undefined) {
                // prepend row id and append selection box
                let ths = [];
                ths.push(<th> </th>);
                headers.forEach((h) => {
                    ths.push(<th>{h}</th>);
                });
                ths.push(<th>Select</th>);
                tableHeaders = (<thead>
                <tr>
                    {ths}
                </tr>
                </thead>);
            }

            let tableRows = [];
            let i = 0;
            rows.forEach((r) => {
                let cols = [];
                let rowId = i;
                cols.push(<th scope="row">{rowId}</th>);
                r.forEach((rr) => {
                    if (this.props.store.contains(rowId)) {
                        // bolden selected rows
                        cols.push(<td><b>{rr}</b></td>);
                    } else {
                        cols.push(<td>{rr}</td>)
                    }
                });
                cols.push(<td><input type="checkbox"
                                     checked={this.props.store.contains(rowId)}
                                     onChange={() => this.props.store.toggle(rowId)}
                                     className="RowCheckBox"/></td>);
                i += 1;
                tableRows.push(<tr>{cols}</tr>)
            });

            return <Table bordered>
                {tableHeaders}
                <tbody>
                {tableRows}
                </tbody>
            </Table>
        }
    });

export {TableRowSelector}