import React, {Component} from 'react';
import {Button, Label, Progress} from "reactstrap";
import './ImportExportPanel.css';
import 'editor/sidepanel/EditorSidePanel.css';
import {FileImportButton} from "./FileImportButton";
import {AnnotationViolationDisplayPanel} from "./AnnotationViolationDisplayPanel";
import { FileIoManager } from '../utils/FileIoManager';
import { Namespaces } from '../../annotator/core/Namespaces';
import { prepareAsFile } from '../../annotator/tagging/pdf/context/SsieAnnotationStoreProvider';


const DISPLAY_DELAY = 500;

class ProjectImportExportPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            saveProgress: false
        };

        this.importProject = this.importProject.bind(this);
        this.exportProject = this.exportProject.bind(this);
        this.onCheckboxChange = this.onCheckboxChange.bind(this);
    }

    importProject(e) {
        const f = e.target.files[0];
        const reader = new FileReader();
        reader.onload = (event) => {
            const content = event.target.result;
            const lines = content.split("\n");
            console.log("loaded lines -> " + lines.length);
            let jsons = [];
            let error = undefined;
            let count = 0;
            for (let line of lines) {
                if (line && line.trim().length > 0) {
                    line = line.trim();
                    try {
                        const jsonDoc = JSON.parse(line);
                        jsons.push(jsonDoc);
                    } catch (e) {
                        // defer the handling to RootStore
                        jsons = undefined;
                        error = e;
                        break;
                    }
                }
                count += 1
            }
            // set a little timeout to show progress
            setTimeout(() => {
                this.props.store.loadProjectFile(jsons, f.name, error);
                this.setState({loading: false});
            }, DISPLAY_DELAY);
        };
        if (f) {
            this.setState({
                loading: true
            });
            reader.readAsText(f);
        }
    }

    onCheckboxChange() {
        const nextState = !this.state.saveProgress;
        this.setState({
            saveProgress: nextState
        });
    }

    exportProject() {
        let ts = parseInt(new Date().getTime() / 1000).toString();
        let fileName = "exported_annotation_" + ts + ".jsonl";
        const collectionPath = this.props.store.collectionStore.collectionPath;
        if (collectionPath !== undefined && collectionPath.length > 0) {
            // assuming the file name is split through .
            fileName = collectionPath.split(".")[0] + "_" + fileName;
        }

        const violations = this.props.store.checkCollectionViolations();
        this.props.store.alertStore.sendAlert(
            "Check Annotations Violations",
            <AnnotationViolationDisplayPanel violations={violations}/>,
            () => {
                const contents = this.props.store.editorStateStore.currentTask.namespace === Namespaces.SSIE ? 
                    prepareAsFile(this.props.ssieProps.getOutputObject()) :
                    this.props.store.saveProjectFile(this.state.saveProgress);
                FileIoManager.downloadAsFile(contents, fileName);
            },
            false
        );

    }


    render() {
        const displayState = this.state.loading ? "inline" : "none";
        return <div>
            <FileImportButton text="Import"
                              onClick={this.importProject}/>
            <Button className="FileContainer"
                    color="warning"
                    style={{"margin": "5px"}}
                    onClick={this.exportProject}>Export
            </Button>
            <div>
                <Label className="TaskPanelText"
                       check
                       style={{"fontSize": "16px"}}>
                    EXPORT PROGRESS
                </Label>
                <input className="CheckBox"
                       type="checkbox"
                       style={{"marginLeft": "10px"}}
                       checked={this.state.saveProgress}
                       onChange={this.onCheckboxChange}/>
            </div>
            <div className="TaskPanelSmallText" style={{"display": displayState}}>
                Loading
                <Progress animated color="warning" value="100"/>
            </div>
        </div>
    }

}

export {ProjectImportExportPanel};