import React from 'react';
import {Button, CardFooter, CardHeader, Col, Row} from 'reactstrap';
import {PDFStore} from '../../context/PDFStore';
import {isSsieAnnotationRelationSelected, SsieAnnotationStore} from '../../context/SsieAnnotationStore';
import MetadataSidePanel from '../MetadataSidePanel/MetadataSidePanel';
import SelectedEntitiesSidePanel from '../SelectedEntitiesSidePanel/SelectedEntitiesSidePanel';
import SemiStructuredEntityListSidePanel from '../SemiStructuredEntityListSidePanel/SemiStructuredEntityListSidePanel';
import {PDFPage} from '../PDFPage/PDFPage';
import {
    handleConnectButtonClick,
    removeActiveSsieAnnotations,
    showConnectedButton,
    updateSelectedSsieAnnotationsProperties,
} from '../../utils/SsieUtils';
import NERTagGroup from '../../../TaggingTagGroup';
import SemiStructuredContextMenuSelector from '../SemiStructuredContextMenuSelector/SemiStructuredContextMenuSelector';
import {PropertyEditorContainer} from "../../../../properties/PropertyEditorContainer";
import {PropertyTargetType} from "../../../../types/BasePropertyType";

interface Props {
    usingGroundTruth: boolean;
    store: any;
    frameWrapper: (annotator: any) => JSX.Element;
}

/**
 * Semi-structured annotator root component which renders all other semi-structured related components.
 */
const SemiStructuredAnnotator: React.FC<Props> = ({ usingGroundTruth, store, frameWrapper }) => {
    const { currPdfPage, pdfPage } = React.useContext(PDFStore);
    const {
        selectedSsieAnnotations,
        ssieAnnotations,
        undoSsieAction,
        autoTagSsieToken,
        setSsieAnnotationStore,
        stagedRelation,
        selectedTag,
        setSelectedTag,
        setPropertyDataStore,
    } = React.useContext(SsieAnnotationStore);
    React.useEffect(() => {
        store.setSsieProps({ setSelectedTag });
    }, [setSelectedTag]);

    React.useEffect(() => {
        setPropertyDataStore(store.propertyDataStore)
    }, [setPropertyDataStore, store]);

    const isSsieAnnotationPage = currPdfPage !== parseInt(pdfPage);

    return (
        <div id="TaggingAnnotatorWithPropertiesBody">
            <Row>
                <Col md="9">
                    {frameWrapper(
                        <div id="TaggingAnnotatorBody">
                            <CardHeader>
                                <NERTagGroup
                                    store={store}
                                    selectedTag={selectedTag}
                                    updateSelectedSsieAnnotationsProperties={(tag: string) =>
                                        updateSelectedSsieAnnotationsProperties(tag, {
                                            selectedSsieAnnotations,
                                            ssieAnnotations,
                                            setSsieAnnotationStore,
                                        })
                                    }
                                />
                            </CardHeader>
                            <div className="AnnotatorTextView card-body">
                                <PDFPage />
                            </div>
                            <CardFooter>
                                <Button
                                    className="FunctionalButton"
                                    color="danger"
                                    size="lg"
                                    style={{
                                        marginRight: '5px',
                                    }}
                                    disabled={isSsieAnnotationPage}
                                    onClick={() => removeActiveSsieAnnotations(selectedSsieAnnotations, ssieAnnotations, setSsieAnnotationStore)}
                                >
                                    REMOVE
                                </Button>
                                <Button
                                    className="FunctionalButton"
                                    size="lg"
                                    style={{
                                        marginRight: '5px',
                                    }}
                                    disabled={isSsieAnnotationPage}
                                    onClick={() => undoSsieAction()}
                                >
                                    UNDO
                                </Button>
                                <Button
                                    className="FunctionalButton"
                                    size="lg"
                                    style={{
                                        marginRight: '5px',
                                    }}
                                    disabled={isSsieAnnotationPage}
                                    onClick={() => autoTagSsieToken()}
                                >
                                    AUTO TAG
                                </Button>
                                {showConnectedButton(selectedSsieAnnotations) ? (
                                    <Button
                                        className="FunctionalButton float-right"
                                        size="lg"
                                        style={{ marginRight: '5px' }}
                                        onClick={() =>
                                            handleConnectButtonClick(setSsieAnnotationStore, selectedSsieAnnotations, ssieAnnotations, stagedRelation)
                                        }
                                    >
                                        {isSsieAnnotationRelationSelected(selectedSsieAnnotations[0].id, ssieAnnotations.relations)
                                            ? 'DISCONNECT'
                                            : 'CONNECT'}
                                    </Button>
                                ) : null}
                            </CardFooter>
                        </div>
                    )}
                </Col>
                <Col md="3">
                    <MetadataSidePanel usingGroundTruth={usingGroundTruth} />
                    {selectedSsieAnnotations.length > 0 && <PropertyEditorContainer target={selectedSsieAnnotations[0]}
                                             targetType={PropertyTargetType.Block}
                                             store={store.propertyDataStore}
                                             broadcast={(propertyDataStore) => {
                                                 // this callback doesn't need propertyDataStore
                                                 // instance, but we pass in, nevertheless
                                                 store.notifyDocumentStateChange();
                                                 store.notifyViewUpdateChange();
                                             }}/>}
                    <SelectedEntitiesSidePanel />
                    <SemiStructuredEntityListSidePanel />
                </Col>
                <SemiStructuredContextMenuSelector
                    store={store}
                    updateSelectedSsieAnnotationsProperties={updateSelectedSsieAnnotationsProperties}
                    removeActiveSsieAnnotations={removeActiveSsieAnnotations}
                    handleAutoTagClick={autoTagSsieToken}
                    handleConnectButtonClick={handleConnectButtonClick}
                />
            </Row>
        </div>
    );
};

export default SemiStructuredAnnotator;
