import {extendObservable, observable} from 'mobx'
import {Checkpointtable} from "../checkpoint/Checkpointtable";

class ProgressStateStore extends Checkpointtable {
    constructor(collection) {
        super();
        extendObservable(this, {
            maxRead: observable.map(),
            accepted: observable.map(),
            rejected: observable.map(),
            updated: 0,
        });
        this.totalDoc = collection ? collection.length : 0;
        this.markAsComplete = this.markAsComplete.bind(this);
        this.reset = this.reset.bind(this);
        this.findFirstUnreadDocument = this.findFirstUnreadDocument.bind(this);
    }

    mapToArray(map) {
        return [...map];
    }

    setFromArray(map, array) {
        map.clear();
        array.forEach((e) => {
            let key = e[0];
            let value = e[1];
            map.set(key, value);
        })
    }

    checkpoint() {
        let mr = this.mapToArray(this.maxRead);
        let acc = this.mapToArray(this.accepted);
        let rej = this.mapToArray(this.rejected);
        let totalDoc = this.totalDoc;
        let savedTime = parseInt(new Date().getTime() / 1000);

        let checkpoint = {
            maxRead: mr,
            accepted: acc,
            rejected: rej,
            totalDoc: totalDoc,
            savedTime: savedTime,
            type: "progressSnapshot"
        };

        return checkpoint;
    }

    resume(checkpoint) {
        console.log("restore");
        console.log(checkpoint);
        const {maxRead, accepted, rejected, totalDoc} = checkpoint;
        this.setFromArray(this.maxRead, maxRead);
        this.totalDoc = totalDoc;
        // NOTE: the total document number might has changed before/after loading
        //this.totalDoc = totalDoc;
        this.updated += 1;
        return this.findFirstUnreadDocument();
    };

    reset(collection) {
        console.log("progress reset");
        console.log(collection.length);
        this.maxRead.clear();
        this.totalDoc = collection.length;
        this.updated += 1;
    }

    findFirstUnreadDocument() {
        // this.maxRead.forEach((a, b,c) => console.log(b));
        for (let i = 0; i < this.totalDoc; i++) {
            if (!(this.maxRead.has(i))) {
                return i;
            }
        }
        // last document
        this.updated += 1;
        return this.totalDoc - 1;
    };

    markAsComplete(docId) {
        this.maxRead.set(docId, true);
        this.updated += 1;
    }

    rangeUpdateCompleteDocuments(docId) {
        let id = 0;
        while (id < docId) {
            this.markAsComplete(id);
            id += 1;
        }
        this.updated += 1;
    };
}

export {ProgressStateStore};